import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import get from 'lodash/get';
import * as utils from '../utils/utils';

import {
	UPDATE_NOTIFICATIONS_SUCCEEDED,
	UPDATE_NOTIFICATIONS,
	ALERT_STATUS_DELETED
} from '../constants';

/**
 * Batch update for alerts.
 *
 * Usage:
 * props.resources.alert.update({template_vars: {items: [{sys_id, status}]}});
 *
 * Each batched request must be aliased with a unique variable name currently. Hence the "update_{{sys_id}}: " portion
 * below.
 * @type {string}
 */
export const notificationUpdateQuery = `
	mutation {
		GlideRecord_Mutation {
			{{#items}}
				update_{{sys_id}}: update_ui_notification_inbox(sys_id: "{{sys_id}}", status: "{{status}}") {
					sys_id {
					  value
					}
					sys_created_on {
					  value
					}
					payload {
					  value
					}
					target_table{
					  value
					}
					target {
					  value
					}
					triggered_by {
					  value
					  displayValue
					}
					status {
						value
					}
				}
			{{/items}}
		}
	}
`;

// update alert query
const updateNotificationsEffect = createGraphQLEffect(notificationUpdateQuery, {
	templateVarList: ['items'],
	successActionType: UPDATE_NOTIFICATIONS_SUCCEEDED
});

const updateNotificationsSucceededEffect = ({
	action,
	state,
	properties,
	updateProperties,
	updateState
}) => {
	const result = get(action.payload, 'data.GlideRecord_Mutation', null);

	if (result) {
		// Only handles deleted alerts
		const deletedItem = Object.keys(result).reduce((acc, key) => {
			if (result[key].status.value === ALERT_STATUS_DELETED)
				acc = acc.concat(result[key]);
			return acc;
		}, []);

		const deleteCount = deletedItem.length;

		if (deleteCount > 0) {
			const { queue } = properties.notification;
			const {
				deleteQueue,
				offset,
				totalRowCount
			} = utils.getUINotificationBehaviorState(state);

			const newAlertQueue = queue.filter(
				(item) => !result[`update_${item.alert_sys_id}`]
			);
			const newDeleteQueue = deleteQueue.filter(
				(sys_id) => !result[`update_${sys_id}`]
			);

			const newOffset = offset - deleteCount;
			const newTotalRowCount = totalRowCount - deleteCount;
			updateState({
				path: utils.UI_NOTIFICATION_BEHAVIOR_STATE_PATH,
				value: {
					deleteQueue: newDeleteQueue,
					offset: newOffset,
					totalRowCount: newTotalRowCount
				},
				operation: 'assign'
			}); // offset and totalRowCount should always update together

			updateProperties(() => ({
				path: 'notification',
				operation: 'assign',
				value: {
					queue: newAlertQueue,
					trayIsLoading: utils.isNotificationTrayLoading(
						newOffset,
						newTotalRowCount,
						newDeleteQueue
					)
				}
			}));
		}
	}
};

export const actionHandlers = {
	[UPDATE_NOTIFICATIONS]: updateNotificationsEffect,
	[UPDATE_NOTIFICATIONS_SUCCEEDED]: updateNotificationsSucceededEffect
};
