import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';

export function hydrateViewport({
	notificationQueue,
	notificationTrayDisplayed,
	notificationTrayIsLoading,
	toastItem,
	notificationUIRoute,
	notificationViewportElementId,
	notificationScreenRoute,
	dispatch
}) {
	dispatch('UXF_VIEWPORT#RENDER_BY_ID', {
		reinitialize: true,
		route: notificationScreenRoute,
		viewportElementId: notificationViewportElementId,
		fields: {
			toastItem,
			notificationUIRoute,
			notificationQueue,
			notificationTrayDisplayed,
			notificationTrayIsLoading
		}
	});
}

export function viewportEnabled(notificationConfiguration) {
	const viewportConfiguration = get(
		notificationConfiguration,
		'viewportConfiguration',
		{}
	);
	return (
		!isEmpty(viewportConfiguration.viewportElementId) &&
		!isEmpty(viewportConfiguration.screenRoute)
	);
}
