import '@servicenow/now-icon';
import '@servicenow/now-badge';
import '@servicenow/now-popover';
import '@servicenow/now-modal';
import { t } from 'sn-translate';

import CanvasTab from './subcomponents/canvas-tab';
import * as chromeTabsUtils from './utils';
import './subcomponents/sn-canvas-popover-content';
import './subcomponents/sn-canvas-tabs-menu';
import {
	MAIN_TAB_NEW_ALERT_CLEARED,
	TABS_CLOSE_CONFIRMED,
	TABS_CLOSE_CANCELLED
} from '../../../constants/constants';

/* schema : {
	dispatchFn: function, // Seismic dispatch function
	level: 0, //1 //2
	focusedTabIndex: -1,
	selectedTabId: {},
	parentTabId: {},
	tabList: [],
	type: {},
	newMenu: {},
	closeMenu: {},
	onNewTab: {},
	onClickTab: {},
	onCloseTab: {},
	onMenuClose: {},
	tabsMaxWidth: {}
}*/
export default (props) => {
	let showOverflowMenu = false;
	let overflowDisabled = chromeTabsUtils.getOverflowDisabled(props.tabList);
	let hasOverflowUpdate = false;
	let badgeCount = 0;

	const {
		tabsMaxOpen,
		stopperPrompt,
		tabsToCloseQueue,
		tabList,
		parentTabId,
		type,
		onCloseTab,
		selectedTabId,
		onClickTab,
		newMenu,
		onNewTab,
		focusedTabIndex,
		onShowModal,
		dir
	} = props;

	const visibleAndOverflowList = chromeTabsUtils.buildVisibleAndOverflowList(
		tabList,
		props.tabsMaxWidth,
		selectedTabId
	);

	const { visibleList, overflowList } = visibleAndOverflowList;

	const _transformedMenuItems = chromeTabsUtils.transformCloseMenuItems(
		props.closeMenu,
		tabList,
		selectedTabId
	);
	const optionItems = _transformedMenuItems.concat(overflowList);
	let _meatBallPopOverRef = undefined,
		_newMenuPopOverRef = undefined;
	const _hidePopover = (type) => {
		if (type === 'newMenu') {
			if (!_newMenuPopOverRef) return;
			_newMenuPopOverRef.opened = false;
		} else {
			if (!_meatBallPopOverRef) return;
			_meatBallPopOverRef.opened = false;
		}
	};

	const _propagateTabFocusUpdate = (level, tabIndex) => {
		props.dispatchFn('CANVAS_TAB_BAR#FOCUSED_TAB_SET', {
			tabIndex,
			level
		});
	};

	const _onClickTab = (tab_id, index, e) => {
		onClickTab(tab_id, e);
		_propagateTabFocusUpdate(props.level, index);
		_hidePopover();
	};

	const _onClickMenuItem = (menuItem, idx, e) => {
		_hidePopover();
		const { onMenuClose, onClickTab } = props;
		if (menuItem.itemType === 'menu') {
			onMenuClose({
				operation: menuItem.name,
				selectedTabId,
				type: props.type
			});
		} else {
			const { tab_id } = menuItem;
			onClickTab(tab_id, e);
		}
	};

	const _onCloseMenuItem = (menuItem, e) => {
		const { onCloseTab } = props;
		const { tab_id } = menuItem;
		onCloseTab(tab_id, e);
		_hidePopover();
	};

	const _onNewTab = (menuItem) => {
		onNewTab(menuItem);
		_hidePopover('newMenu');
	};

	const _handlePopoverInsert = ({ elm }) => {
		if (!elm) return;
		elm.addEventListener(
			'NOW_POPOVER#OPENED_SET',
			({
				detail: {
					payload: { value }
				}
			}) => {
				elm.opened = value;
				const button = elm.firstElementChild;
				if (button) button.setAttribute('aria-expanded', value);
			}
		);
	};

	const _handlePopoverDestroy = ({ elm }) => {
		if (!elm) return;
		elm.removeEventListener(
			'NOW_POPOVER#OPENED_SET',
			({
				detail: {
					payload: { value }
				}
			}) => {
				elm.opened = value;
				const triggerBtn = elm.firstElementChild;
				if (triggerBtn) triggerBtn.setAttribute('aria-expanded', value);
			}
		);
	};

	const _renderMenuPopover = (menuItems, onNewTab) => {
		const tooltipContent = t('Add');
		const ariaLabel = tooltipContent;
		return (
			<now-popover
				hide-tail
				positions={[{ target: 'bottom-end', content: 'top-end' }]}
				manage-opened
				ref={(_elm) => (_newMenuPopOverRef = _elm)}
				hook-insert={_handlePopoverInsert}
				hook-destroy={_handlePopoverDestroy}>
				<button
					slot="trigger"
					type="button"
					className="sn-chrome-tabs-btn"
					id="chrome-add-new-button"
					aria-haspopup="menu"
					aria-expanded="false"
					aria-label={ariaLabel}
					hook-insert={({ elm }) =>
						chromeTabsUtils.insertTooltipEl(elm, tooltipContent)
					}
					hook-destroy={({ elm }) => chromeTabsUtils.removeTooltipEl(elm)}>
					<now-icon icon="plus-outline" />
				</button>
				<sn-canvas-tabs-menu
					slot="content"
					menuItems={menuItems}
					onNewTab={onNewTab}
				/>
			</now-popover>
		);
	};

	const _renderNewMenu = (newMenu, onNewTab, type) => {
		if (!newMenu || !Array.isArray(newMenu) || newMenu.length === 0)
			return null;
		if (type !== 'primary') return null;

		let classname = 'sn-chrome-tabs-btn';
		const buttonId = 'chrome-add-new-button';
		const tooltipContent = t('Add {0}', newMenu[0].label);
		const ariaLabel = tooltipContent;
		if (newMenu.length > 1) return _renderMenuPopover(newMenu, onNewTab);

		return (
			<button
				type="button"
				className={classname}
				on-click={() => onNewTab(newMenu[0])}
				id={buttonId}
				aria-label={ariaLabel}
				hook-insert={({ elm }) =>
					chromeTabsUtils.insertTooltipEl(elm, tooltipContent)
				}
				hook-destroy={({ elm }) => chromeTabsUtils.removeTooltipEl(elm)}>
				<now-icon component-id={buttonId} icon="plus-outline" />
			</button>
		);
	};

	/** renderer for modal used when the user requests a new tab that goes beyond configured limit */
	const _renderTabOpenAlert = (tabsMaxOpen, stopperPrompt) => {
		const openDialog = stopperPrompt && stopperPrompt.request == true;
		const title = stopperPrompt ? stopperPrompt.title : t('Max Tab Reached');
		const warningMessage = stopperPrompt
			? stopperPrompt.message
			: t(
					'You have reached the max number of tabs you can have open at one time. Close tabs in order to open more.'
			  );
		const buttonTitle = stopperPrompt
			? stopperPrompt.buttonTitle
			: t('Dismiss');
		return (
			<now-modal
				opened={openDialog}
				size="sm"
				header-label={title}
				content={warningMessage}
				manageOpened
				footer-actions={[
					{
						variant: 'primary',
						label: buttonTitle,
						clickActionType: MAIN_TAB_NEW_ALERT_CLEARED
					}
				]}
			/>
		);
	};

	/** renderer for modal used when a user requests to close a dirty tab */
	const _renderTabCloseConfirmation = (tabsToCloseQueue) => {
		let openDialog = false;
		if (
			tabsToCloseQueue &&
			tabsToCloseQueue.tabList &&
			tabsToCloseQueue.tabList[0]
		) {
			const firstTabInQueue = tabsToCloseQueue.tabList[0];
			openDialog = firstTabInQueue.isDirty && !firstTabInQueue.dirtyModalId;

			const showCustomConfirmation =
				firstTabInQueue.isDirty &&
				firstTabInQueue.dirtyModalId &&
				firstTabInQueue.confirmationStatus === 'pending';
			const showCustomWarning =
				firstTabInQueue.hasWarning &&
				firstTabInQueue.warningModalId &&
				firstTabInQueue.confirmationStatus === 'pending';

			if (showCustomConfirmation) {
				onShowModal(firstTabInQueue.dirtyModalId, true);
				return;
			} else if (!openDialog && showCustomWarning) {
				onShowModal(firstTabInQueue.warningModalId, true);
				return;
			}
		}

		const title = t('Close Tab');
		const warningMessage = t(
			'Closing this tab will cause your changes to be lost. Do you want to continue?'
		);
		return (
			<now-modal
				opened={openDialog}
				size="sm"
				header-label={title}
				content={warningMessage}
				manageOpened
				footer-actions={[
					{
						variant: 'primary',
						label: t('Continue'),
						clickActionType: TABS_CLOSE_CONFIRMED
					},
					{
						variant: 'secondary',
						label: t('Cancel'),
						clickActionType: TABS_CLOSE_CANCELLED
					}
				]}
			/>
		);
	};

	const _renderCounterBadge = () => {
		let counterBadge = null;
		let overflowLength = overflowList.length;
		if (overflowLength > 0)
			counterBadge = (
				<now-badge
					round
					size="sm"
					status="critical"
					value={overflowLength}
					variant="primary"
				/>
			);
		return counterBadge;
	};

	const overflowLabel = t('More');
	const overflowTooltipContent = overflowDisabled ? null : overflowLabel;
	const overflowClassNames = chromeTabsUtils.buildOverflowStyle(
		type,
		overflowDisabled,
		badgeCount,
		hasOverflowUpdate
	);
	const overflowButtonId = `ellipsis-h ${type}`;

	const _renderPopover = (_optionItems) => (
		<now-popover
			hide-tail
			positions={[
				{ target: 'bottom-end', content: 'top-end' },
				{ target: 'bottom-center', content: 'top-end' },
				{ target: 'bottom-start', content: 'top-end' }
			]}
			manage-opened
			ref={(_elm) => (_meatBallPopOverRef = _elm)}
			hook-insert={_handlePopoverInsert}
			hook-update={({ elm }) => {
				setTimeout(() => elm && elm.refit(), 200);
			}}
			hook-destroy={_handlePopoverDestroy}>
			<button
				slot="trigger"
				disabled={overflowDisabled}
				type="button"
				className={overflowClassNames}
				id={overflowButtonId}
				aria-label={overflowLabel}
				aria-haspopup="menu"
				aria-expanded="false"
				aria-controls={showOverflowMenu ? 'popover-body-2' : null}
				hook-insert={({ elm }) =>
					chromeTabsUtils.insertTooltipEl(elm, overflowTooltipContent)
				}
				hook-update={({ elm }) => {
					chromeTabsUtils.removeTooltipEl(elm); // safe-guard against multipe tooltips
					chromeTabsUtils.insertTooltipEl(elm, overflowTooltipContent);
				}}
				hook-destroy={({ elm }) => chromeTabsUtils.removeTooltipEl(elm)}>
				<now-icon component-id={overflowButtonId} icon={'ellipsis-h-fill'} />
				{_renderCounterBadge()}
			</button>
			<sn-canvas-popover-content
				slot="content"
				optionItems={_optionItems}
				onItemClick={_onClickMenuItem}
				onItemClose={_onCloseMenuItem}
			/>
		</now-popover>
	);

	const _renderTabs = (
		_visibleList,
		_type,
		_selectedTabId,
		_focusedTabIndex
	) => (
		<ul role="tablist" className="sn-chrome-tabs-group">
			{_visibleList.map((t, i) => {
				const { tab_id } = t;
				const key = parentTabId ? `${parentTabId}_${tab_id}` : tab_id;
				const isHighlighted =
					_focusedTabIndex === i ||
					(_focusedTabIndex == -1 ? tab_id === _selectedTabId : false);
				return (
					<CanvasTab
						key={key}
						component-id={tab_id}
						index={i}
						tab={t}
						tabType={type}
						isSelected={tab_id === _selectedTabId}
						isHighlighted={isHighlighted}
						dir={dir}
						onCloseTab={onCloseTab}
						onClickTab={_onClickTab}
						onKeyUp={onClickTab}
					/>
				);
			})}
		</ul>
	);

	return (
		<div className={'sn-chrome-tabs chromeTabs -' + type}>
			<div className="sn-chrome-tabs-content tab-list">
				{_renderTabs(visibleList, type, selectedTabId, focusedTabIndex)}
				{_renderNewMenu(newMenu, _onNewTab, type)}
				{_renderTabOpenAlert(tabsMaxOpen, stopperPrompt)}
				{_renderTabCloseConfirmation(tabsToCloseQueue)}
			</div>
			<div className="sn-chrome-tabs-content close-option">
				{_renderPopover(optionItems)}
			</div>
		</div>
	);
};
