/*  MODULE NOTES
 *  This code was originally part of Workspace, and has been adapted to sn-canvas-chrome.
 *  All functionality related to closing tabs is in this module.
 *  Tab closing is opinionated, and follows these rules:
 *  1. Upon close, all child tabs are closed, and state is propagated up the ancenstor chain
 *  2. The user is asked to verify the close operation if this tab or any of its children are dirty.
 *     a) The user is prompted individually for each dirty child.
 *     b) The tabs are closed one at a time, independently.
 *     c) The first "cancel" on a dirty tab cancels all remaining close operations.
 *  3. If the tab being closed is the currently selected tab in its sub-tree, then a sibling tab is assigned
 *     as the selected tab according to this simple algorithm:
 *       a. select the next tab, if there is one
 *       b. else select the previous tab
 *  4. If the last child tab within a parent is closed, and the parent's canClose is true, then close the parent as well.
 */

/** given a tab_id, return the tab object from the tabsData structure */
function getTab(tabsData, tab_id) {
	const tabList = Object.values(tabsData);
	return tabList.find((tab) => tab.tab_id === tab_id);
}

/** return true if parentTab should be closed based on its current state */
function shouldCloseParent(parentTab) {
	return parentTab.tabList.length === 0 && parentTab.canClose;
}

/** recompute the dirty state of a parent tab assumint that tabToClose has been deleted */
function isParentDirtyAfterChildClose(tabsData, tabToClose) {
	let parentTab = getTab(tabsData, tabToClose.parentTabId);
	if (parentTab) {
		let i;
		for (i = 0; i < parentTab.tabList.length; i++) {
			const siblingTab = getTab(tabsData, parentTab.tabList[i]);
			if (siblingTab.tab_id != tabToClose.tab_id) {
				if (siblingTab && siblingTab.isDirty) return true;
			}
		}
	}
	return false;
}

/** recompute the dirty state of a parent tab assumint that tabToClose has been deleted */
function parentHasWarningAfterChildClose(tabsData, tabToClose) {
	let parentTab = getTab(tabsData, tabToClose.parentTabId);
	if (parentTab) {
		let i;
		for (i = 0; i < parentTab.tabList.length; i++) {
			const siblingTab = getTab(tabsData, parentTab.tabList[i]);
			if (siblingTab.tab_id != tabToClose.tab_id) {
				if (siblingTab && siblingTab.hasWarning) return true;
			}
		}
	}
	return false;
}

/** update the grandParent of a tab that is being closed
 *  every ancestor of the child tab being closed potentially needs to be updated
 *  1. if the child tab was the selected tab, then the new selectedTabId needs to popagate up
 *  2. the dirty state of each ancestor needs to be recomputed
 */
function updateParentTabOnChildUpdate(
	tabsData,
	childTab,
	selectedTabId,
	isDirty,
	hasWarning
) {
	if (childTab && childTab.parentTabId) {
		let parentTab = getTab(tabsData, childTab.parentTabId);
		if (parentTab) {
			parentTab.isDirty =
				isDirty || isParentDirtyAfterChildClose(tabsData, childTab);
			parentTab.hasWarning =
				hasWarning || parentHasWarningAfterChildClose(tabsData, childTab);
			updateParentTabOnChildUpdate(
				tabsData,
				parentTab,
				parentTab.tab_id,
				parentTab.isDirty,
				parentTab.hasWarning
			);
		}
	}
}

/* update the parent of a tab that is being closed
 *   1. update the parent's recomputed state, bubbling this change up to ancestors
 *   2. remove childTabToClose from parentTab's tablist and childContentKeys
 *   3. if the parent tab is now empty and is allowed to be closed, then close it as well
 */
function updateParentTabOnChildClose(tabsData, parentTab, childTabToClose) {
	let index = parentTab.tabList.findIndex((t) => t === childTabToClose.tab_id);
	if (index === -1) return; // illegal call, childTabToClose is not a child of parentTab

	parentTab.isDirty = isParentDirtyAfterChildClose(tabsData, childTabToClose);
	parentTab.hasWarning = parentHasWarningAfterChildClose(
		tabsData,
		childTabToClose
	);
	let newSelectedTabId = '-1';
	if (parentTab.selectedTabId === childTabToClose.tab_id) {
		// if the childTabToClose was selected, then pick a new selection
		if (parentTab.tabList.length > index + 1) {
			newSelectedTabId = parentTab.tabList[index + 1];
		} else if (index > 0) {
			newSelectedTabId = parentTab.tabList[index - 1];
		}
		parentTab.selectedTabId = newSelectedTabId;
	}
	// pass the new state up the ancestor chain
	updateParentTabOnChildUpdate(
		tabsData,
		parentTab,
		newSelectedTabId,
		parentTab.isDirty,
		parentTab.hasWarning
	);

	// remove the child tab from all data structures in the parent that reference it
	if (parentTab.childContentKeys[childTabToClose.contentKey]) {
		delete parentTab.childContentKeys[childTabToClose.contentKey];
	}
	parentTab.tabList.splice(index, 1);

	// once the parent has been updated, see if it should also be closed
	if (shouldCloseParent(parentTab)) {
		closeTabById(tabsData, parentTab.tab_id, true);
	}
}

/** close tab_id, which will delete the tab from tabsData and clean up all references
 *  1. close tab_id's children
 *  2. adjust parent tab and all ancestor tabs
 *  3. remove tab_id from tabsData
 */
export function closeTabById(tabsData, tab_id, updateParent) {
	let updatedTabsData = tabsData;
	let tabToDelete = getTab(tabsData, tab_id);
	if (!tabToDelete) return updatedTabsData;

	// close the children of this tab recursively, depth-first
	tabToDelete.tabList.forEach(function(childTabId) {
		updatedTabsData = closeTabById(updatedTabsData, childTabId, false);
		delete updatedTabsData[childTabId];
	});

	// adjust the parent of this tab
	if (updateParent) {
		if (tabToDelete.parentTabId && tabToDelete.parentTabId != '-1') {
			updateParentTabOnChildClose(
				tabsData,
				updatedTabsData[tabToDelete.parentTabId],
				tabToDelete
			);
		}
	}

	// delete this tab.  Must be done after parent has been updated so parent can select an appropriate selectedTabId
	delete updatedTabsData[tabToDelete.tab_id];

	// return the updated tabsData
	return updatedTabsData;
}

/** return a list of tabs to be closed, depth first, right to left order */
export function getTabsToCloseQueue(
	tabsData,
	operation,
	tab_id,
	tabsToCloseQueue,
	tabFilter
) {
	if (!tabsData[tab_id] || !tabsData[tab_id].tabList) return tabsToCloseQueue; // pre-condition
	if (tabFilter && !tabFilter(tabsData[tab_id])) return tabsToCloseQueue; // no data to process

	if (!tabsToCloseQueue.tabList) tabsToCloseQueue.tabList = []; // initialize the output struct
	// process child tabs first
	let i = tabsData[tab_id].tabList.length - 1;
	for (; i >= 0; i--) {
		tabsToCloseQueue = getTabsToCloseQueue(
			tabsData,
			operation,
			tabsData[tab_id].tabList[i],
			tabsToCloseQueue,
			null
		);
	}
	// add this tab to the queue
	const isDirty = tabsData[tab_id].isDirty ? tabsData[tab_id].isDirty : false;
	const hasWarning = tabsData[tab_id].hasWarning
		? tabsData[tab_id].hasWarning
		: false;
	let confirmationStatus = '';
	if (isDirty || hasWarning) confirmationStatus = 'pending';
	const queueItem = {
		tab_id: tab_id,
		isDirty: isDirty,
		confirmationStatus: confirmationStatus,
		dirtyModalId: tabsData[tab_id].dirtyModalId,
		hasWarning: hasWarning,
		warningModalId: tabsData[tab_id].warningModalId
	};
	tabsToCloseQueue.tabList.push(queueItem);
	return tabsToCloseQueue;
}
