import * as utils from './utils/utils';

import {
	UI_NOTIFICATION_BEHAVIOR,
	ALERT_STATUS_READ,
	NOTIFICATION_TOAST,
	NOTIFICATION_ITEM_CLICKED,
	NOTIFICATION_TRAY_TOGGLED,
	NOTIFICATION_LOAD_MORE
} from './constants';

import { actionHandlers as uiNotificationCountHandlers } from './effects/uiNotificationCountEffect';
import { actionHandlers as uiNotificationClearHandlers } from './effects/uiNotificationClearEffect';
import { actionHandlers as uiNotificationReadHandlers } from './effects/uiNotificationReadEffect';
import { actionHandlers as uiNotificationUpdaterHandlers } from './effects/uiNotificationUpdateEffect';
import { actionHandlers as uiNotificationAMBHandlers } from './effects/uiNotificationAMBEffect';
import { actionHandlers as uiNotificationLifeCycleHandlers } from './effects/uiNotificationLifeCycleEffect';

export const uiNotificationBehavior = {
	name: UI_NOTIFICATION_BEHAVIOR,
	initialState: {
		groupIdToDelete: '',
		deleteQueue: [],
		offset: 0,
		totalRowCount: 0
	},
	properties: {
		/**
		 * Contains notification data. It describes the state of notifications for components to render.
		 * @type {NotificationData}
		 */
		notification: {
			default: {
				queue: [],
				toastItem: {},
				trayDisplayed: false,
				trayIsLoading: false,
				toastDisplayed: false,
				badgeCount: 0,
				showBadge: true,
				badgeList: []
			},
			selectable: true
		},

		/**
		 * Input configuration properties for the component attatched to the behavior. Describes what configurations a
		 * screen should use in order for a component to be rendered within a viewport. uiRoute is a sys_id from the
		 * `ui_notification_route` table. Fetches notifications for the `ui_notification_route`.
		 * @type {NotificationConfiguration}
		 */
		notificationConfiguration: {
			default: {
				viewportConfiguration: {
					viewportElementId: '',
					screenRoute: ''
				},
				uiRoute: '',
				notificationsEnabled: true
			}
		}
	},
	actionHandlers: {
		[NOTIFICATION_LOAD_MORE]: {
			effect: ({ properties, state, dispatch }) => {
				const uiNotificationBehaviorState = utils.getUINotificationBehaviorState(
					state
				);
				const { notificationConfiguration, userMeta } = properties;
				const { sys_id: userId } = userMeta;
				const { offset, totalRowCount } = uiNotificationBehaviorState;
				const { uiRoute } = notificationConfiguration;

				utils.askToQueryAlerts(
					offset,
					totalRowCount,
					userId,
					uiRoute,
					dispatch
				);
			},
			modifier: { name: 'debounce', delay: 300 },
			stopPropagation: true
		},
		[NOTIFICATION_TRAY_TOGGLED]: {
			effect: ({ action, properties, updateProperties }) => {
				const { opened } = action.payload;
				const { badgeList } = properties.notification;
				updateProperties(() => ({
					path: 'notification',
					operation: 'assign',
					value: {
						trayDisplayed: opened,
						badgeList: opened ? [] : badgeList,
						badgeCount: opened ? 0 : badgeList.length
					}
				}));
			},
			stopPropagation: true
		},
		[NOTIFICATION_ITEM_CLICKED]: {
			effect: ({ state, properties, action, dispatch, updateProperties }) => {
				const { deleteQueue } = utils.getUINotificationBehaviorState(state);
				const { badgeList, queue } = properties.notification;

				const { payload } = action;

				const { mode, sysId, alertSysId, status, hideTray } = payload;

				if (mode === NOTIFICATION_TOAST && status === ALERT_STATUS_READ) {
					// decrease badgeCount when clicking toast content
					let idx = badgeList.indexOf(sysId);
					let newBadgeList = badgeList.slice();
					newBadgeList.splice(idx, 1);

					updateProperties(() => ({
						path: 'notification',
						operation: 'assign',
						value: {
							badgeList: newBadgeList,
							badgeCount: newBadgeList.length,
							trayDisplayed: false
						}
					}));
				}

				if (hideTray) {
					updateProperties(() => ({
						path: 'notification',
						operation: 'assign',
						value: {
							trayDisplayed: false
						}
					}));
				}

				// update status in memory
				const newAlertQueue = queue.map((it) => {
					if (it.alert_sys_id === alertSysId) {
						return { ...it, is_read: true };
					}
					return it;
				});

				updateProperties(() => ({
					path: 'notification',
					operation: 'assign',
					value: { queue: newAlertQueue }
				}));

				utils.askToUpdateNotifications(
					dispatch,
					[alertSysId],
					status,
					deleteQueue
				);
			},
			stopPropagation: true
		},
		...uiNotificationLifeCycleHandlers,
		...uiNotificationCountHandlers,
		...uiNotificationClearHandlers,
		...uiNotificationReadHandlers,
		...uiNotificationUpdaterHandlers,
		...uiNotificationAMBHandlers
	}
};
