import { navItemSchema } from '../../sn-nav-item/schemas';

const alertSchema = {
	type: 'object',
	properties: {
		table: { type: 'string' },
		filter: { type: 'string' }
	}
};

const actionValueSchema = {
	type: ' object',
	propeties: {
		name: { type: 'string' },
		payload: { type: 'object' }
	}
};

const toggleValueSchema = {
	type: 'object',
	properties: {
		value: { type: 'boolean' },
		action: { type: actionValueSchema }
	}
};

const menuItem = {
	definitions: {
		menuItem: {
			type: 'object',
			properties: {
				buttonId: { type: 'string' },
				label: { type: 'string' },
				icon: { type: 'string' },
				type: {
					type: 'string',
					enum: ['navigation', 'menu', 'action', 'toggle', 'divider']
				},
				position: { type: 'number' },
				primaryDisplay: {
					type: 'string',
					enum: ['none', 'label', 'icon', 'both']
				},
				alert: alertSchema,
				value: {
					anyOf: [
						actionValueSchema,
						navItemSchema,
						toggleValueSchema,
						{
							type: 'array',
							properties: { children: { $ref: '#definitions/menuItem' } }
						}
					]
				}
			}
		}
	}
};

const globalToolsSchema = {
	type: 'object',
	properties: {
		collapsingMenuId: { type: 'number' },
		primaryItems: {
			type: 'array',
			properties: menuItem
		},
		secondaryItems: {
			type: 'array',
			properties: menuItem
		}
	}
};

export const configurationsSchema = {
	type: 'object',
	properties: {
		global: {
			type: 'object',
			properties: {
				privatePage: {
					type: 'object',
					properties: {
						notificationsEnabled: { type: 'boolean' },
						searchEnabled: { type: 'boolean' },
						userPrefsEnabled: { type: 'boolean' },
						contextualConfigItemsEnabled: { type: 'boolean' },
						globalTools: globalToolsSchema
					}
				},
				publicPage: {
					type: 'object',
					properties: {
						actionButtons: { type: 'array' }
					}
				}
			}
		}
	}
};

export const experienceSchema = {
	type: 'string',
	enum: ['default', 'workspace']
};

export const sizeSchema = {
	type: 'string',
	enum: ['short', 'tall']
};
