import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get as _get, has as _has, isEmpty as _isEmpty } from 'lodash';

const ACTION_TYPES = {
	RECORD_WATCHER: {
		SUBSCRIBE_STARTED: 'RECORD_WATCHER_SUBSCRIBE_STARTED',
		SUBSCRIBED: 'RECORD_WATCHER_SUBSCRIBED',
		SUBSCRIBE_FAILED: 'RECORD_WATCHER_SUBSCRIBE_FAILED',
		UNSUBSCRIBED: 'RECORD_WATCHER_UNSUBSCRIBED',
		ITEM_CHANGED: 'RECORD_WATCHER_ITEM_CHANGED',
		SUBSCRIPTION_CHANGED: 'RECORD_WATCHER_SUBSCRIPTION_CHANGED'
	},
	AGGREGATE_COUNT: {
		FETCH: 'AGGREGATE_COUNT_FETCH',
		STARTED: 'AGGREGATE_COUNT_STARTED',
		SUCCEEDED: 'AGGREGATE_COUNT_SUCCEEDED',
		FAILED: 'AGGREGATE_COUNT_FAILED'
	}
};

const AGGREGATE_COUNT_GQL = `query($table: String!, $filter: String!) {
    GlideAggregate_Query(tableName: $table, queryConditions: $filter) {
      count
    }
  }`;

const handleBadgeCountFetch = createGraphQLEffect(AGGREGATE_COUNT_GQL, {
	variableList: ['table', 'filter'],
	startActionType: ACTION_TYPES.AGGREGATE_COUNT.STARTED,
	successActionType: ACTION_TYPES.AGGREGATE_COUNT.SUCCEEDED,
	errorActionType: ACTION_TYPES.AGGREGATE_COUNT.FAILED
});

const handleBadgeCountFetchSuccess = ({
	action,
	properties,
	updateProperties
}) => {
	const { badge } = properties;
	const count = _get(action, 'payload.data.GlideAggregate_Query[0].count', 0);
	updateProperties({ badge: { ...badge, count } });
};

const handleBadgeCountFetchFailure = ({ properties, updateProperties }) => {
	const { badge } = properties;
	const { table, filter } = badge;
	// eslint-disable-next-line no-console
	console.error(
		`Failed to fetch aggregate counts with for table, ${table} with filter, ${filter}`
	);
	updateProperties({ badge: { ...badge, count: 0 } });
};

const handleRecordWatcher = createAmbSubscriptionEffect(
	'/rw/default/:table/:filter',
	{
		subscribeStartedActionType: ACTION_TYPES.RECORD_WATCHER.SUBSCRIBE_STARTED,
		subscribeSucceededActionType: ACTION_TYPES.RECORD_WATCHER.SUBSCRIBED,
		subscribeFailedActionType: ACTION_TYPES.RECORD_WATCHER.SUBSCRIBE_FAILED,
		unsubscribeSucceededActionType: ACTION_TYPES.RECORD_WATCHER.UNSUBSCRIBED,
		messageReceivedActionType: ACTION_TYPES.RECORD_WATCHER.ITEM_CHANGED
	}
);

const handleItemChanged = ({ action, properties, updateProperties }) => {
	const countUpdate = _get(action, 'payload.data.count', '').split('');
	const { badge } = properties;
	if (countUpdate[0] === '+') {
		updateProperties({
			badge: { ...badge, count: badge.count + parseInt(countUpdate[1]) }
		});
	} else if (countUpdate[0] === '-') {
		updateProperties({
			badge: { ...badge, count: badge.count - parseInt(countUpdate[1]) }
		});
	}
};

const _getSubscriptionArgs = (table, filter, subscribe) => ({
	subscribe,
	table,
	filter: btoa(filter).replace(/=/g, '-')
});

const onConnect = ({ properties, dispatch }) => {
	const { badge } = properties;
	if (!_isEmpty(badge) && _has(badge, 'configurations')) {
		const { configurations, type } = badge;
		switch (type) {
			case 'record': {
				const { table, filter } = configurations;
				if (table && filter) {
					// Fetch initial data
					dispatch(ACTION_TYPES.AGGREGATE_COUNT.FETCH, { table, filter });

					// Subscribe to changes
					dispatch(
						ACTION_TYPES.RECORD_WATCHER.SUBSCRIPTION_CHANGED,
						_getSubscriptionArgs(table, filter, true)
					);
				}
				break;
			}
			default: {
				break;
			}
		}
	}
};

const onDisconnect = ({ properties, dispatch }) => {
	const { badge } = properties;

	if (!_isEmpty(badge) && _has(badge, 'configurations')) {
		const { table, filter } = badge.configurations;
		if (table && filter) {
			// unsubscribe to changes
			dispatch(
				ACTION_TYPES.RECORD_WATCHER.SUBSCRIPTION_CHANGED,
				_getSubscriptionArgs(table, filter, false)
			);
		}
	}
};

const effectHandlers = {
	handleBadgeCountFetch,
	handleBadgeCountFetchSuccess,
	handleBadgeCountFetchFailure,
	handleRecordWatcher,
	handleItemChanged,
	onConnect,
	onDisconnect
};

export default {
	effectHandlers,
	ACTION_TYPES
};
