import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snCanvasContentStyles from './_sn-canvas-content.scss';
import rtlBehavior from '@servicenow/behavior-rtl';

// eslint-disable-next-line no-unused-vars
const snCanvasContent = (state) => {
	const { containerProps } = state.properties;
	return (
		<section className="sn-canvas-content" style={containerProps}>
			<slot />
		</section>
	);
};

createCustomElement('sn-canvas-content', {
	properties: {
		/**
		 * Property to set the styles for the content
		 * @type {object} Style for container element
		 */
		containerProps: { default: {}, schema: { type: 'object' } }
	},
	renderer: {
		type: snabbdom,
		view: snCanvasContent
	},
	styles: snCanvasContentStyles,
	behaviors: [rtlBehavior]
});
