import { getFocusableNodes } from '@servicenow/behavior-focus';

import {
	HOME_KEY,
	ARROW_LEFT_KEY,
	ARROW_RIGHT_KEY,
	END_KEY,
	TAB_KEY
} from '../../constants/constants';

import { handleRtlFn } from '../../utils/rtl';

const CANVAS_MENU_ITEM_SELECTOR =
	'.sn-canvas-menu-nav-item > sn-nav-item, .sn-canvas-menu-nav-item > sn-canvas-menu-dropdown';

const IGNORED_ELEMENTS = ['sn-canvas-menu-nav-action-button'];

const _shouldIgnoreElement = (element) =>
	IGNORED_ELEMENTS.find((className) => element.classList.contains(className));

const _getMenuItems = (host) => [
	...host.shadowRoot.querySelectorAll(CANVAS_MENU_ITEM_SELECTOR)
];

const _getFocusedElement = (host) => host.shadowRoot.activeElement;

/**
 * Clears tabindexes for all items in the menu.
 * @param {HTMLElement} host
 */
const _clearTabIndex = (host) => {
	const menuItems = _getMenuItems(host);
	menuItems.forEach((menuItem) => menuItem.setAttribute('tabindex', '-1'));
};

/**
 * Sets focus to the passed in menu item. We remove attribute tabindex instead of setting to '0' to let the markup
 * within the shadowroot handle tabindex.
 * @param {HTMLElement} el
 */
const _setFocus = (el) => {
	el.removeAttribute('tabindex');
	el.focus();
};

/**
 * Gets the position of the item that is currently focused on.
 * @param {HTMLElement} host
 */
const _getFocusedElementIndex = (host) => {
	const menuItems = _getMenuItems(host);
	const { id } = _getFocusedElement(host).dataset;
	return menuItems.findIndex((item) => item.dataset.id === id);
};

/**
 * Focuses on the item in the first position of the menu.
 * @param {HTMLElement} host
 */
const _focusFirstItem = (host) => {
	const menuItems = _getMenuItems(host);
	_setFocus(menuItems[0]);
};

/**
 * Focuses on the item in the last position of the menu.
 * @param {HTMLElement} host
 */
const _focusLastItem = (host) => {
	const menuItems = _getMenuItems(host);
	_setFocus(menuItems[menuItems.length - 1]);
};

/**
 * Focuses on the next item in the menu.
 * @param {HTMLElement} host
 */
const _focusNextItem = (host) => {
	const currentFocusIndex = _getFocusedElementIndex(host);
	const menuItems = _getMenuItems(host);
	_clearTabIndex(host);
	if (currentFocusIndex < menuItems.length - 1) {
		_setFocus(menuItems[currentFocusIndex + 1]);
	} else {
		_focusLastItem(host);
	}
};

/**
 * Focuses on the previous item in the menu.
 * @param {HTMLElement} host
 */
const _focusPreviousItem = (host) => {
	const currentFocusIndex = _getFocusedElementIndex(host);
	const menuItems = _getMenuItems(host);
	_clearTabIndex(host);
	if (currentFocusIndex > 0) {
		_setFocus(menuItems[currentFocusIndex - 1]);
	} else {
		_focusFirstItem(host);
	}
};

const _clearOpenDropdown = (host) => {
	const menuItems = _getMenuItems(host);
	for (let idx = 0, len = menuItems.length; idx < len; idx++) {
		if (menuItems[idx].opened === true) {
			menuItems[idx].opened = false;
			break;
		}
	}
};

const focusFirstItemBeforeMenubar = (host) => {
	const navListEl = host.shadowRoot.querySelector('.sn-canvas-menu-nav-list');
	let nodeToCheck = getFocusableNodes(navListEl).firstFocusableElement;
	let focusableNodes = getFocusableNodes(document.body).tabbableNodes;
	let idx = 0;

	while (focusableNodes[idx] && !nodeToCheck.isSameNode(focusableNodes[idx])) {
		idx++;
	}
	focusableNodes[idx - 1].focus();
};

const focusFirstItemAfterMenubar = (host) => {
	const navListEl = host.shadowRoot.querySelector('.sn-canvas-menu-nav-list');
	let nodeToCheck = getFocusableNodes(navListEl).lastFocusableElement;
	let focusableNodes = getFocusableNodes(document.body).tabbableNodes;
	let idx = 0;
	// console.log(nodeToCheck, focusableNodes)
	while (focusableNodes[idx] && !nodeToCheck.isSameNode(focusableNodes[idx])) {
		idx++;
	}
	// console.log('idx: ', idx);
	// console.log('focusable node: ', focusableNodes[idx + 1]);
	focusableNodes[idx + 1].focus();
};

/**
 * Gives roving focus behavior to the `sn-canvas-menu` component. This follows the w3 spec outlined here:
 * https://www.w3.org/TR/wai-aria-practices/#kbd_roving_tabindex.
 */
const menubarKeyBindingEventHandler = [
	{
		capture: false,
		events: ['keydown'],
		effect({
			host,
			action: {
				payload: { event }
			}
		}) {
			// Don't invoke key binding if this is on an interactive element we want to ignore
			const activeElement = host.shadowRoot.activeElement;
			if (activeElement && _shouldIgnoreElement(activeElement)) {
				return;
			}
			const { key } = event;
			if (key === TAB_KEY) {
				event.preventDefault();
				if (event.shiftKey) {
					focusFirstItemBeforeMenubar(host);
				} else {
					focusFirstItemAfterMenubar(host);
				}
			} else if (event.srcElement.tagName === 'SN-CANVAS-MENU') {
				const dir = host.getAttribute('dir');

				const focusNextItem = handleRtlFn(
					_focusNextItem,
					_focusPreviousItem,
					dir
				);
				const focusPreviousItem = handleRtlFn(
					_focusPreviousItem,
					_focusNextItem,
					dir
				);

				_clearOpenDropdown(host);

				switch (key) {
					case HOME_KEY:
						_focusFirstItem(host);
						break;
					case ARROW_LEFT_KEY:
						focusPreviousItem(host);
						break;
					case ARROW_RIGHT_KEY:
						focusNextItem(host);
						break;
					case END_KEY:
						_focusLastItem(host);
						break;
				}
			}
		}
	}
];

export { menubarKeyBindingEventHandler };
