import get from 'lodash/get';
import { EMAIL_LINK_TYPE, PHONE_LINK_TYPE } from '../constants/constants';

export const formatURLByType = (linkValue = {}, type) => {
	const url = get(linkValue, 'href', '');
	switch (type) {
		case EMAIL_LINK_TYPE:
			return 'mailto:' + url;
		case PHONE_LINK_TYPE:
			return 'tel:' + url;
		default:
			return url;
	}
};
