import {
	findKey as _findKey,
	find as _find,
	isEmpty as _isEmpty
} from 'lodash';

export const getButtonElementRef = (toolbarButtonRef) => {
	return toolbarButtonRef.shadowRoot.querySelector('button');
};

export const getToolbarButtonRefByIndex = (host, index) => {
	const toolbarButtons = getToolbarButtons(host);
	return toolbarButtons[index];
};

export const getToolbarButtonFromEventPath = (path) => {
	return _find(path, (elem) => elem.localName === 'sn-canvas-toolbar-button');
};

export const getToolbarButtons = (host) => {
	const toolbarButtonGroupEls = host.shadowRoot.querySelectorAll(
		'.sn-canvas-toolbar .sn-canvas-toolbar-group'
	);
	let toolbarButtonEls = [];

	if (!_isEmpty([...toolbarButtonGroupEls])) {
		[...toolbarButtonGroupEls].forEach((grpEl) => {
			const buttons = grpEl.querySelectorAll('sn-canvas-toolbar-button');
			[...buttons].forEach((btn) => {
				toolbarButtonEls.push(btn);
			});
		});
	}
	return toolbarButtonEls;
};

export const getActiveToolbarButton = (host) => {
	return host.shadowRoot.activeElement;
};

export const getActiveToolbarButtonIndex = (host) => {
	const toolbarButtons = getToolbarButtons(host);
	const activeToolbarButton = getActiveToolbarButton(host);
	return parseInt(
		_findKey(
			toolbarButtons,
			(toolbarButton) => activeToolbarButton === toolbarButton
		),
		10
	);
};

export const getToolbarButtonIndex = (host, toolbarButtonRef) => {
	const toolbarButtons = getToolbarButtons(host);
	return parseInt(
		_findKey(
			toolbarButtons,
			(toolbarButton) => toolbarButtonRef === toolbarButton
		),
		10
	);
};

export const getNextToolbarButton = (host) => {
	const currentActiveToolbarButton = getActiveToolbarButton(host);
	return currentActiveToolbarButton.nextElementSibling;
};

export const getToolbarButtonsData = (host) => {
	return host.toolbarButtons;
};
