import { actionTypes } from '@servicenow/ui-core';

import isEqual from 'lodash/isEqual';
import isEmpty from 'lodash/isEmpty';
import isNil from 'lodash/isNil';
import get from 'lodash/get';

import { hydrateViewport, viewportEnabled } from '../utils/viewport';
import * as utils from '../utils/utils';

import { SUBSCRIPTION_CHANGED, QUERY_NOTIFICATION_COUNT } from '../constants';

const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_DISCONNECTED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

const fetchNotificationsAndInitializeAMBWatcher = ({
	state,
	properties,
	dispatch
}) => {
	// Assume userMeta present in properties. This is coming from sn-canvas-chrome-state
	const { userMeta, notificationConfiguration } = properties;

	const { notificationsEnabled = true } = notificationConfiguration;
	if (notificationsEnabled.toString().toLowerCase() === 'false') return;
	const { sys_id: userId } = userMeta;
	const { uiRoute } = notificationConfiguration;
	if (!isEmpty(userId) && !isEmpty(uiRoute)) {
		const uiNotificationBehaviorState = utils.getUINotificationBehaviorState(
			state
		);
		const { offset, totalRowCount } = uiNotificationBehaviorState;

		utils.askToQueryAlerts(
			offset,
			totalRowCount,
			userId,
			uiRoute,
			dispatch,
			true
		);

		const condition = utils.buildBadgeQueryCondition(userId, uiRoute);
		dispatch(QUERY_NOTIFICATION_COUNT, { condition });

		dispatch(SUBSCRIPTION_CHANGED, {
			table: 'ui_notification_inbox',
			filter: utils.buildAlertSubscriptionFilter(userId, uiRoute),
			subscribe: true
		});
	}
};

const noOpHandler = {
	get: function(target, prop) {
		return target.hasOwnProperty(prop) ? target[prop] : () => {};
	}
};

const componentPropertyChangedHandler = new Proxy(
	{
		['notification']: ({ properties, previousValue, value, dispatch }) => {
			const { userMeta } = properties;
			const { sys_id: userId } = userMeta;

			// Exit early if values are the same
			if (isEqual(previousValue, value)) {
				return;
			}

			/**
			 * If viewport configurations exist, then dispatch to render in a viewport.
			 * Notification tray can be opened with or without a viewport.
			 */
			if (viewportEnabled(properties.notificationConfiguration)) {
				const { queue, trayDisplayed, trayIsLoading, toastItem } = value;
				const {
					uiRoute,
					viewportConfiguration
				} = properties.notificationConfiguration;

				const { viewportElementId, screenRoute } = viewportConfiguration;

				hydrateViewport({
					value,
					dispatch,
					toastItem,
					notificationUIRoute: uiRoute,
					notificationViewportElementId: viewportElementId,
					notificationScreenRoute: screenRoute,
					notificationQueue: queue,
					notificationTrayDisplayed: trayDisplayed,
					notificationTrayIsLoading: trayIsLoading
				});
			}

			// Whenever the badge list is updated, set the count and, if the tray is open,
			// reset the session storage counter
			if (!isEqual(previousValue.badgeList, value.badgeList)) {
				const { trayDisplayed } = properties.notification;
				if (trayDisplayed) utils.storeBadgeCount(userId);
			}
		},
		['userMeta']: ({
			state,
			properties,
			previousValue,
			value,
			dispatch,
			updateProperties
		}) => {
			// Exit early if values are the same
			if (isEqual(previousValue, value)) return;
			const prevUserId = previousValue.sys_id;
			const userId = value.sys_id;
			if (!isEqual(prevUserId, userId)) {
				fetchNotificationsAndInitializeAMBWatcher({
					state,
					properties,
					dispatch
				});
			}
			const newShowBadgeVal = utils.getPreferenceValue(
				get(value, 'preferences', []),
				'workspace.notifications.showBadgeCount'
			);
			const previousShowBadgeVal = utils.getPreferenceValue(
				get(previousValue, 'preferences', []),
				'workspace.notifications.showBadgeCount'
			);
			if (newShowBadgeVal !== previousShowBadgeVal) {
				// if new value is null, use default for showBadge(true). else convert
				// string to bool
				const newShowBadge = isNil(newShowBadgeVal)
					? true
					: newShowBadgeVal.toString().toLowerCase() === 'true';
				updateProperties(() => ({
					path: 'notification',
					operation: 'assign',
					value: { showBadge: newShowBadge }
				}));
			}
		}
	},
	noOpHandler
);

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: fetchNotificationsAndInitializeAMBWatcher,
	[COMPONENT_DISCONNECTED]: ({ properties, dispatch }) => {
		const { userMeta, notificationConfiguration } = properties;
		const { notificationsEnabled = true } = notificationConfiguration;
		if (notificationsEnabled.toString().toLowerCase() === 'false') return;

		const { sys_id: userId } = userMeta;
		const { uiRoute } = notificationConfiguration;
		if (!isEmpty(userId) && !isEmpty(uiRoute)) {
			// unsubscribe to changes
			dispatch(SUBSCRIPTION_CHANGED, {
				table: 'ui_notification_inbox',
				filter: utils.buildAlertSubscriptionFilter(userId, uiRoute),
				subscribe: false
			});
		}
	},
	[COMPONENT_PROPERTY_CHANGED]: ({
		action,
		state,
		properties,
		dispatch,
		updateProperties
	}) => {
		const { notificationConfiguration } = properties;
		const { notificationsEnabled = true } = notificationConfiguration;
		if (notificationsEnabled.toString().toLowerCase() === 'false') return;
		const { name, previousValue, value } = action.payload;
		const propertyChangedEffect = componentPropertyChangedHandler[name];

		propertyChangedEffect({
			state,
			properties,
			previousValue,
			value,
			dispatch,
			updateProperties
		});
	}
};
