import { t } from 'sn-translate';

function buildMessage(short_description, priority) {
	const content = short_description || t('Short Description field is empty');

	return `${priority}: ${content}`;
}

function buildNewAssignedToastItem(payload) {
	const {
		record_display_value,
		short_description,
		priority,
		table_label
	} = payload;

	const prefix = record_display_value || table_label;
	const verb = t('is Assigned to');
	const suffix = t('You');

	return {
		tagline: record_display_value,
		title: `${prefix} ${verb} ${suffix}`,
		message: buildMessage(short_description, priority),
		infoIcon: 'user-plus-outline'
	};
}

function buildUpdatedToastItem(payload) {
	const {
		record_display_value,
		short_description,
		priority,
		table_label,
		updated_by,
		fields
	} = payload;

	let title;
	if (!updated_by) {
		let prefix = '';
		if (fields.length === 1) {
			prefix = fields[0].field_label;
		} else {
			prefix = record_display_value || table_label;
		}
		title = {
			prefix,
			verb: t('has been updated')
		};
	} else {
		if (fields.length === 1) {
			// Single Field
			title = {
				prefix: fields[0].field_label,
				verb: t('updated by'),
				suffix: updated_by
			};
		} else {
			// multiple fields or updated_by is empty
			title = {
				prefix: updated_by,
				verb: t('updated'),
				suffix: record_display_value || table_label
			};
		}
	}

	return {
		tagline: record_display_value,
		title: `${title.prefix} ${title.verb} ${title.suffix}`,
		message: buildMessage(short_description, priority),
		infoIcon: 'change-outline'
	};
}

export function buildAlertItem(payload, userId) {
	const {
		table,
		sys_id,
		fields,
		time_stamp,
		alert_sys_id,
		is_read,
		raw_time_stamp
	} = payload;

	let toastItem = {
		item_type: 'notification',
		table,
		sys_id,
		alert_sys_id,
		time_stamp,
		raw_time_stamp,
		is_read
	};

	const assignedToMe = fields.find(
		(f) => f.field_name === 'assigned_to' && f.field_value === userId
	);

	return assignedToMe
		? { ...toastItem, ...buildNewAssignedToastItem(payload) }
		: { ...toastItem, ...buildUpdatedToastItem(payload) };
}
