import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import '@servicenow/now-icon';
import { t } from 'sn-translate';

import '../sn-nav-item';
import '../internal/sn-canvas-badge-connected';
import snCanvasNotificationTriggerStyles from './_sn-canvas-notification-trigger.scss';
import { ESCAPE_KEY } from '../../constants/constants';

const getAriaBadgeText = (badgeCount) => {
	if (badgeCount === 0) return 'Notifications';
	if (badgeCount === 1) return badgeCount + ' Notification';
	return badgeCount + ' Notifications';
};

const { COMPONENT_DISCONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const getOnKeyUpHandler = (dispatch, iconRef) => {
	const onKeyUp = (e) => {
		if (e.code === ESCAPE_KEY) {
			dispatch('NOTIFICATION_TRAY_TOGGLED', { opened: false });
			if (iconRef) {
				iconRef.focus();
			}
		}
	};
	return onKeyUp;
};

const notificationTrigger = (state, { updateState }) => {
	const { iconRef, properties } = state;
	const { icon, showBadge, badgeCount, isOpen, experience } = properties;
	const setRef = (ref) => {
		if (iconRef) return;
		updateState({ iconRef: ref });
	};

	return (
		<Fragment>
			<sn-nav-item
				ref={setRef}
				type="action"
				experience={experience}
				variant="simple-nav"
				item-value={{
					name: 'NOTIFICATION_TRAY_TOGGLED',
					payload: { opened: !isOpen }
				}}
				tooltip-content={t('{0}', getAriaBadgeText(badgeCount))}
				config-aria={{
					button: {
						'aria-haspopup': true,
						'aria-label': t('{0}', getAriaBadgeText(badgeCount)),
						'aria-expanded': isOpen
					}
				}}>
				<now-icon size="lg" slot="startsWith" icon={icon} />
			</sn-nav-item>
			{showBadge ? (
				<sn-canvas-badge-connected className="gt-badge" count={badgeCount} />
			) : null}
		</Fragment>
	);
};
/***
	The notification trigger allows users to interact with the notification tray. 
	Clicking the notification trigger will make the notification tray appear 
	and dissappear.  Notification trigger will also optionally display the number 
	of outstanding notifications.

	```jsx
		<sn-canvas-notification-trigger
			icon="bell-outline"
			show-badge={true}
			badge-count={10}
		/>
	```
*/
createCustomElement('sn-canvas-notification-trigger', {
	renderer: {
		type: snabbdom,
		view: notificationTrigger
	},
	styles: snCanvasNotificationTriggerStyles,
	initialState: {
		iconRef: null,
		onKeyUpHandler: null
	},
	properties: {
		/**
		 * Name of NDS icon to be used for the notification-trigger button.
		 * @type {string}
		 */
		icon: {
			schema: { type: 'string' },
			default: 'bell-outline'
		},
		/**
		 * One of "default" or "workspace", to indicate sn-nav-item styling.
		 * @type {string}
		 */
		experience: {
			default: 'default',
			schema: {
				type: 'string',
				enum: ['default', 'workspace']
			}
		},
		/**
		 * Determines whether the badge, holding the number of outstanding
		 * notifications, should be displayed as part of the notification-trigger button.
		 * @type {boolean}
		 */
		showBadge: {
			schema: { type: 'boolean' },
			default: true
		},
		/**
		 * Number of outstanding notifications to be displayed on a badge as part
		 * of the notification-trigger button.  Will only display if showBadge is true.
		 * @type {number}
		 */
		badgeCount: {
			schema: { type: 'number' },
			default: 0
		},
		/**
		 * Indicates whether notification-tray is open or not.
		 * @type {boolean}
		 */
		isOpen: {
			schema: { type: 'boolean' },
			default: false
		}
	},
	behaviors: [rtlBehavior],
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: ({ state, dispatch, updateState }) => {
			const { iconRef, onKeyUpHandler } = state;
			if (iconRef && !onKeyUpHandler) {
				const keyUpHandler = getOnKeyUpHandler(dispatch, iconRef);
				document.addEventListener('keyup', keyUpHandler);
				updateState({ onKeyUpHandler: keyUpHandler });
			}
		},
		[COMPONENT_DISCONNECTED]: ({ state }) => {
			const { onKeyUp } = state;
			document.removeEventListener('keyup', onKeyUp);
		}
	}
});
