import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { getFocusableNodes } from '@servicenow/behavior-focus';

import {
	ARROW_RIGHT_KEY,
	ARROW_DOWN_KEY,
	ARROW_LEFT_KEY,
	ARROW_UP_KEY,
	HOME_KEY,
	END_KEY,
	TAB_KEY
} from '../../../../constants/constants';
import { handleRtlFn } from '../../../../utils/rtl';

/**
 * @typedef FocusableMenuPanelItems
 * @type {object}
 * @property {HTMLElement} firstFocusableElement
 * @property {HTMLElement} lastFocusableElement
 * @property {Array.<HTMLElement>} focusableElements
 */

let actionableEl = new Set(['NOW-BUTTON', 'BUTTON', 'A']);

/**
 *
 * @param {Array.<HTMLElement>} elements
 * @returns {FocusableMenuPanelItems}
 */
const getFocusableMenuPanelItems = (elements) => {
	let focusableElements = [];
	let firstFocusableElement;
	let lastFocusableElement;

	for (let idx = 0; idx < elements.length; idx++) {
		const focusableNodes = getFocusableNodes(elements[idx]);
		if (idx === 0) {
			firstFocusableElement = focusableNodes.firstFocusableElement;
		}
		if (idx === elements.length - 1) {
			lastFocusableElement = focusableNodes.lastFocusableElement;
		}
		focusableElements.push(focusableNodes.tabbableNodes);
	}

	return {
		firstFocusableElement,
		lastFocusableElement,
		focusableElements
	};
};

/**
 * Gets a set of focusable menu panel items. When there is a sidebar, each sidebar item maps to a menu panel.
 * Maps menu content container id to the menu panel content. The Menu Content Container ID is mapped to a sidebar item.
 *
 * @param {HTMLElement} host
 * @returns {Object.<string, FocusableMenuPanelItems}}
 */
const getSetOfFocusableMenuPanelItems = (host) => {
	let menuContentContainer = host.nowRootNode.querySelectorAll(
		'.sn-canvas-mega-menu-linkset-container'
	);

	let focusableSection = {};

	for (let idx = 0; idx < menuContentContainer.length; idx++) {
		const id = menuContentContainer[idx].getAttribute('id');
		const focusableNodes = getFocusableMenuPanelItems(
			menuContentContainer[idx].querySelectorAll('now-link-set, now-button')
		);
		focusableSection[id] = focusableNodes;
	}
	return focusableSection;
};

/**
 * Gets all Focusable Elements for the mega menu. There are two cases:
 * 1) When the mega menu contains the sidebar elements
 * 2) When the mega menu doesn't
 *
 * We must handle both separately as the interactions differ.
 *
 * @param {HTMLElement} host
 */
export const getFocusableItems = (host) => {
	let sidebarEl = host.nowRootNode.querySelector(
		'.sn-canvas-mega-menu-sidebar'
	);

	let menuPanelItem = host.nowRootNode
		.querySelector('.sn-canvas-mega-menu-linkset-container')
		.querySelectorAll('now-link-set, now-button');

	let focusableSidebar = null;
	let focusableMenuPanelItems;

	if (sidebarEl) {
		focusableSidebar = getFocusableNodes(sidebarEl);
		focusableMenuPanelItems = getSetOfFocusableMenuPanelItems(host);
		return {
			focusableSidebar: focusableSidebar.tabbableNodes,
			firstFocusableElement: focusableSidebar.firstFocusableElement,
			lastFocusableElement: focusableSidebar.lastFocusableElement,
			focusableMenuPanelItems: focusableMenuPanelItems
		};
	}

	focusableMenuPanelItems = getFocusableMenuPanelItems(menuPanelItem);

	return {
		focusableSidebar,
		firstFocusableElement: focusableMenuPanelItems.firstFocusableElement,
		lastFocusableElement: focusableMenuPanelItems.lastFocusableElement,
		focusableMenuPanelItems: focusableMenuPanelItems.focusableElements
	};
};

/**
 * Resets focusable state position to the default values.
 *
 * @param {Object} menuItems
 */
export const resetFocusablePositions = (menuItems) => {
	const selectedIndex = get(menuItems, 'root.childList[0]', '');
	return {
		selectedIndex,
		focusPosition: { x: 0, y: 0 },
		sidebarPosition: 0,
		focusedMenuPanel: '',
		currentFocusedEl: null
	};
};

export default [
	{
		capture: false,
		events: ['keydown'],
		effect({
			action: {
				payload: { event }
			},
			dispatch,
			state,
			properties,
			updateState
		}) {
			let {
				sidebarPosition,
				focusPosition,
				focusableItems,
				focusedMenuPanel
			} = state;

			const { dir } = properties;

			const includeSidebarNavigation = (focusableItems) =>
				focusableItems.focusableSidebar !== null;

			const focus = (x, y, elements, sidebarElements) => {
				if (x === -1 && y === -1) {
					if (isEmpty(sidebarElements))
						dispatch('NOW_POPOVER#OPENED_SET', { opened: false });
					else {
						updateState({
							focusedMenuPanel: '',
							focusPosition: { x: 0, y: 0 }
						});
						focusSidebarItem(sidebarPosition, sidebarElements);
					}
					return;
				}
				elements[x][y].focus();
				updateState([
					{
						path: 'focusPosition',
						value: { x, y },
						operation: 'assign',
						shouldRender: false
					},
					{
						path: 'currentFocusedEl',
						value: elements[x][y],
						operation: 'set',
						shouldRender: false
					}
				]);
			};

			const focusSidebarItem = (idx, elements) => {
				if (idx === -1) {
					dispatch('NOW_POPOVER#OPENED_SET', { opened: false });
					return;
				}
				let element = elements[idx];
				let id = element.getAttribute('id');
				element.focus();

				let batchedStateUpdates = [
					{
						path: 'sidebarPosition',
						value: idx,
						operation: 'assign',
						shouldRender: false
					},
					{
						path: 'currentFocusedEl',
						value: element,
						operation: 'set',
						shouldRender: false
					}
				];

				if (id) {
					batchedStateUpdates.push({
						path: 'selectedIndex',
						value: id,
						operation: 'set',
						shouldRender: true
					});
				}

				updateState(batchedStateUpdates);
			};

			/**
			 * Create move functions for when focus is in the main menu content panel.
			 *
			 * @param {{x: number, y: number}} originalCoord
			 * @param {HTMLElement} firstFocusableElement
			 * @param {HTMLElement} lastFocusableElement
			 * @param {FocusableMenuPanelItems} focusableMenuPanelItems
			 * @param {Array.<HTMLElement>} sidebarElements
			 */
			const createMenuContentMoveFunctions = (
				originalCoord,
				firstFocusableElement,
				lastFocusableElement,
				focusableMenuPanelItems,
				sidebarElements
			) => {
				let { x, y } = originalCoord;

				/**
				 * Move up in the y axis.
				 * If the current item is on the first focusable element, then we want to focus out of the menupanel.
				 * If the y axis hits 0, and it's on a positive x axis, then we want to move left (The previous linkset).
				 */
				const moveUp = () => {
					if (firstFocusableElement === focusableMenuPanelItems[x][y]) {
						return;
					} else if (y !== 0) {
						y--;
					} else if (x > 0) {
						x--;
						y = focusableMenuPanelItems[x].length - 1;
					}
					focus(x, y, focusableMenuPanelItems, sidebarElements);
				};

				/**
				 * Move right in the x axis.
				 * If the current item is the last focusable element, then we want to focus out of the menupanel.
				 */
				const moveRight = () => {
					if (
						lastFocusableElement === focusableMenuPanelItems[x][y] ||
						x === focusableMenuPanelItems.length - 1
					) {
						return;
					} else if (x !== focusableMenuPanelItems.length - 1) {
						x++;
						y = 0;
					}
					focus(x, y, focusableMenuPanelItems, sidebarElements);
				};

				/**
				 * Move left in the x axis.
				 * If the current item is the first focusable element, then we want to focus out of the menupanel.
				 */
				const moveLeft = () => {
					if (
						firstFocusableElement === focusableMenuPanelItems[x][y] ||
						x === 0
					) {
						if (!sidebarElements) return;
						else {
							x = -1;
							y = -1;
						}
					} else if (x !== 0) {
						x--;
						y = 0;
					}
					focus(x, y, focusableMenuPanelItems, sidebarElements);
				};

				/**
				 * Move down in the y axis.
				 * If the current item is the last focusable element, then we want to focus out of the menupanel.
				 * If the y axis hits the last item of the set, and it's on a positive x axis,
				 * then we want to move right (The next linkset).
				 */
				const moveDown = () => {
					if (lastFocusableElement === focusableMenuPanelItems[x][y]) {
						return;
					} else if (y !== focusableMenuPanelItems[x].length - 1) {
						y++;
					} else if (x < focusableMenuPanelItems.length - 1) {
						x++;
						y = 0;
					}

					focus(x, y, focusableMenuPanelItems, sidebarElements);
				};

				/**
				 * Move to the first focusable element.
				 */
				const moveHome = () => {
					focus(0, 0, focusableMenuPanelItems, sidebarElements);
				};

				/**
				 * Move to the last focusable element.
				 */
				const moveEnd = () => {
					let lastX = focusableMenuPanelItems.length - 1;
					let lastY = focusableMenuPanelItems[lastX].length - 1;

					focus(lastX, lastY, focusableMenuPanelItems, sidebarElements);
				};

				return {
					moveUp,
					moveRight,
					moveLeft,
					moveDown,
					moveHome,
					moveEnd
				};
			};

			/**
			 *
			 * @param {number} sidebarCoord - Position where the user is focused on the sidebar
			 * @param {HTMLElement} firstFocusableElement - First focusable sidebar element
			 * @param {HTMLElement} lastFocusableElement - Last focusable sidebar element
			 * @param {Array.<HTMLElement>} focusableSidebarElement - List of focusable sidebar elements
			 * @param {Object.<string, HTMLElement>} focusableMenuPanelItems - List of focusable MenuPanel Items.
			 */
			const createSidebarMoveFunctions = (
				sidebarCoord,
				firstFocusableElement,
				lastFocusableElement,
				focusableSidebarElement,
				focusableMenuPanelItems
			) => {
				let y = sidebarCoord;

				/**
				 * Move up along the y axis.
				 * If element is the firstFocusableElement, then we want to exit the menu.
				 */
				const moveUp = () => {
					if (firstFocusableElement === focusableSidebarElement[y]) {
						return;
					} else if (y !== 0) {
						y--;
					}
					focusSidebarItem(y, focusableSidebarElement);
				};

				/**
				 * Move into the menupanel from the sidebar.
				 */
				const moveRight = () => {
					let currentSidebarID = focusableSidebarElement[y].getAttribute(
						'aria-controls'
					);
					if (!currentSidebarID) {
						return;
					}
					let element =
						focusableMenuPanelItems[currentSidebarID].firstFocusableElement;
					element.focus();
					updateState([
						{
							path: 'focusedMenuPanel',
							value: currentSidebarID,
							operation: 'assign',
							shouldRender: false
						},
						{
							path: 'currentFocusedEl',
							value: element,
							operation: 'set',
							shouldRender: false
						}
					]);
				};

				/**
				 * No-op, should do nothing.
				 */
				const moveLeft = () => {};

				/**
				 * Move down along the y axis.
				 * If element is the lastFocusableElement do nothing.
				 */
				const moveDown = () => {
					if (lastFocusableElement === focusableSidebarElement[y]) {
						return;
					} else if (y !== focusableSidebarElement.length - 1) {
						y++;
					}
					focusSidebarItem(y, focusableSidebarElement);
				};

				/**
				 * Move to the first focusable element.
				 */
				const moveHome = () => {
					focusSidebarItem(0, focusableSidebarElement);
				};

				/**
				 * Move to the last focusable element.
				 */
				const moveEnd = () => {
					let end = focusableSidebarElement.length - 1;
					focusSidebarItem(end, focusableSidebarElement);
				};

				return {
					moveUp,
					moveRight,
					moveLeft,
					moveDown,
					moveHome,
					moveEnd
				};
			};

			const createMoveFunctions = (
				sidebarPosition,
				menuCoord,
				focusableItems,
				focusedMenuPanel = ''
			) => {
				const {
					firstFocusableElement,
					lastFocusableElement,
					focusableMenuPanelItems,
					focusableSidebar
				} = focusableItems;

				let movementFn = {};
				let currentFocusedSection = {};

				const isFocusedOnSidebar =
					includeSidebarNavigation(focusableItems) && isEmpty(focusedMenuPanel);
				const isFocusedOnMenuPanelWithSidebarParent = includeSidebarNavigation(
					focusableItems
				);

				// Create move functions when a sidebar exists, and a user has focus on the sidebar
				if (isFocusedOnSidebar) {
					// Handle sidebar navigation
					currentFocusedSection = focusableSidebar;
					movementFn = createSidebarMoveFunctions(
						sidebarPosition,
						firstFocusableElement,
						lastFocusableElement,
						focusableSidebar,
						focusableMenuPanelItems
					);
				}
				// Create move functions when a sidebar exists, and a user has focus on the menu panel
				else if (isFocusedOnMenuPanelWithSidebarParent) {
					currentFocusedSection = focusableMenuPanelItems[focusedMenuPanel];
					movementFn = createMenuContentMoveFunctions(
						menuCoord,
						currentFocusedSection.firstFocusableElement,
						currentFocusedSection.lastFocusableElement,
						currentFocusedSection.focusableElements,
						focusableSidebar
					);
				}
				// Create move functions when there is no sidebar present
				else {
					currentFocusedSection = focusableMenuPanelItems;
					movementFn = createMenuContentMoveFunctions(
						menuCoord,
						firstFocusableElement,
						lastFocusableElement,
						focusableMenuPanelItems
					);
				}

				return movementFn;
			};

			const {
				moveRight,
				moveUp,
				moveDown,
				moveLeft,
				moveHome,
				moveEnd
			} = createMoveFunctions(
				sidebarPosition,
				focusPosition,
				focusableItems,
				focusedMenuPanel
			);
			const { key, view } = event;

			const moveNext = handleRtlFn(moveRight, moveLeft, dir);
			const movePrevious = handleRtlFn(moveLeft, moveRight, dir);

			switch (key) {
				case ARROW_RIGHT_KEY:
					moveNext();
					break;

				case ARROW_UP_KEY:
					moveUp();
					// Prevent window from scrolling
					view.event.preventDefault();
					break;

				case ARROW_DOWN_KEY:
					moveDown();
					// Prevent window from scrolling
					view.event.preventDefault();
					break;

				case ARROW_LEFT_KEY:
					movePrevious();
					break;

				case HOME_KEY:
					moveHome();
					break;

				case END_KEY:
					moveEnd();
					break;
				case TAB_KEY:
					event.preventDefault();
					break;
			}
		}
	},
	{
		events: ['mouseup'],
		effect({
			action: {
				payload: { event }
			},
			state
		}) {
			const { currentFocusedEl } = state;
			// Do not refocus if an actionable el node was clicked
			if (event.path.find((el) => actionableEl.has(el.tagName))) {
				return;
			}
			// Blur event on elements will always happen before mouseup. We want to restore focus on the element that was
			// blurred, so the user can still use keyboard navigation if they click anywhere on the megamenu panel.
			if (currentFocusedEl) currentFocusedEl.focus();
		}
	}
];
