import { isEqual, cloneDeep, get, set, has, each, isEmpty } from 'lodash';
import { actionTypes } from '@servicenow/ui-core';
import * as closeTabs from './tabsData.closeTabs';
import * as tabUtils from '../utils/tabsData.utils';
import { reduceTabsData } from './tabsData.reducers';
import { saveTabs } from './tabsData.storage';
import * as constants from '../../../constants/constants';

const { COMPONENT_CONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

function createTabsDataEffect(tabEffect) {
	return {
		effect: tabsDataEffectWrapper,
		args: [tabEffect]
	};
}

/**
 * return true if the requested additional tab is legal
 * the checks that are done are for the max # of tab limits - primary tabs as well as subtabs
 * side effects: sets stopperPrompt property if limit exceeded
 */
function allowAdditionalTabs(
	tabsDataCopy,
	tabsDataNew,
	dispatch,
	updateProperties,
	tabsConfig
) {
	const stopper = tabUtils.shouldUpdateTabData(
		tabsDataCopy,
		tabsDataNew,
		tabsConfig
	);
	if (stopper) {
		stopper.request = true;
		updateProperties({ stopperPrompt: stopper }); //this tells an ancestor element to show whatever UI it wants to
		const selectedContentCurrent = tabUtils.getNavItemSelectedPayload(
			tabsDataCopy
		);
		if (selectedContentCurrent) {
			selectedContentCurrent.redirect = true;
			dispatch(constants.NAV_ITEM_SELECTED, selectedContentCurrent);
		}
		return false;
	}
	return true;
}

/**
 * return true if the requested initial tab is legal
 * the checks that are done are for the max # of tab limits - primary tabs as well as subtabs
 * side effects: sets stopperPrompt property if limit exceeded
 */
function allowInitialTabs(tabsDataCopy, tabsDataNew, tabsConfig) {
	const stopper = tabUtils.shouldUpdateTabData(
		tabsDataCopy,
		tabsDataNew,
		tabsConfig
	);
	if (stopper) {
		stopper.request = true;
		return stopper;
	}
	return null;
}

// if we were asked to display a new tab during init phase, but doing so would violate the max tab limit,
// then we use the primary tab as the default selected content
function selectedContentForInitialTabs(tabsDataNew) {
	const tabList = Object.values(tabsDataNew);
	return tabList[1].selectedContent;
}

/**
 * Wraps all tab data effects with before and after behavior
 * @param effect
 * @param coeffects
 */
function tabsDataEffectWrapper(effect, coeffects) {
	const {
		action: { payload },
		dispatch,
		properties,
		updateProperties
	} = coeffects;
	const { tabsData } = properties;
	let tabsDataCopy = cloneDeep(tabsData);
	let tabsDataNew = effect(tabsDataCopy, payload, coeffects);

	// eslint-disable-next-line no-unused-vars
	const { passiveNavigation, inbound, ...restPayload } = payload;

	if (!tabsDataNew) return;

	if (tabsDataCopy) {
		if (
			!allowAdditionalTabs(
				tabsDataCopy,
				tabsDataNew,
				dispatch,
				updateProperties,
				properties.tabConfig
			)
		) {
			return;
		}
	} else {
		const stopper = allowInitialTabs(
			tabsDataCopy,
			tabsDataNew,
			properties.tabConfig
		);
		if (stopper) {
			updateProperties({ stopperPrompt: stopper }); //this tells an ancestor element to show whatever UI it wants to

			const selectedContent = selectedContentForInitialTabs(
				tabsDataNew,
				dispatch
			);
			tabsDataNew = initContent(tabsDataCopy, selectedContent, coeffects);

			// NAV_ITEM_SELECTED is required to set the URL correctly
			const selectedContentCurrent = tabUtils.getNavItemSelectedPayload(
				tabsDataNew
			);
			if (selectedContentCurrent) {
				selectedContentCurrent.redirect = true;
				dispatch(constants.NAV_ITEM_SELECTED, selectedContentCurrent);
			}
			return;
		}
	}

	tabsDataNew = tabUtils.updateSelectedTabs(tabsDataNew);
	updateProperties({ tabsData: tabsDataNew });

	const results = tabUtils.getRemovedTabs(tabsData, tabsDataNew);
	if (
		results &&
		results.removedContentKeys &&
		results.removedContentKeys.length > 0
	) {
		dispatch(constants.NAVIGATION_CLOSED, {
			closedNavigationIds: [...results.removedContentKeys],
			closedIdsInfo: [...results.removedTabs]
		});
	}

	if (passiveNavigation === true || inbound === true) return;

	let selectedContentNew = tabUtils.getNavItemSelectedPayload(tabsDataNew);

	if (selectedContentNew)
		dispatch(constants.NAV_ITEM_SELECTED, selectedContentNew);
}

/** close main tab invoked from menu (close one, close other, close all) */
function mainTabCloseClickedEffect(tabsData, payload, coeffects) {
	const { tab_id } = payload;
	if (!tab_id) return tabsData;
	const selectedTab = tabsData[tab_id];
	const tabList =
		selectedTab && selectedTab.parentTabId && tabsData[selectedTab.parentTabId]
			? tabsData[selectedTab.parentTabId].tabList
			: null;
	if (!tabList) return tabsData;
	function tabFilter(tab) {
		return tab.canClose;
	}
	return tabCloseClickedEffect(
		tabsData,
		payload,
		coeffects,
		tabList,
		tabFilter,
		tabFilter
	);
}

/** close sub tab invoked from menu (close one, close other, close all)  */
function subTabCloseClickedEffect(tabsData, payload, coeffects) {
	const { tab_id } = payload;
	const selectedTab = tabsData[tab_id];
	const tabList = tabsData[selectedTab.parentTabId].tabList;
	function tabFilterAll() {
		return true;
	}
	function tabFilterOther(tab) {
		return !(tab.canClose === false);
	}
	return tabCloseClickedEffect(
		tabsData,
		payload,
		coeffects,
		tabList,
		tabFilterAll,
		tabFilterOther
	);
}

/** implementation of close behavior, common for main and sub tabs */
function tabCloseClickedEffect(
	tabsData,
	payload,
	coeffects,
	tabList,
	tabFilterAll,
	tabFilterOther
) {
	const { operation, tab_id } = payload;
	const { dispatch, updateProperties } = coeffects;
	// build the list of tabs to be closed
	// note that we close then right to left, so we iterate through tabList backwards
	let tabsToCloseQueue = {};
	if (operation === constants.CLOSE_ALL_TABS_MENU_ITEM) {
		let i = tabList.length - 1;
		for (; i >= 0; i--) {
			tabsToCloseQueue = closeTabs.getTabsToCloseQueue(
				tabsData,
				operation,
				tabsData[tabList[i]].tab_id,
				tabsToCloseQueue,
				tabFilterAll
			);
		}
	} else if (operation === constants.CLOSE_OTHER_TABS_MENU_ITEM) {
		let i = tabList.length - 1,
			activityKeys = [];
		for (; i >= 0; i--) {
			if (tabsData[tabList[i]].tab_id != tab_id) {
				const _tab = tabsData[tabList[i]];
				activityKeys.push({ key: _tab.contentKey });
				tabsToCloseQueue = closeTabs.getTabsToCloseQueue(
					tabsData,
					operation,
					tabsData[tabList[i]].tab_id,
					tabsToCloseQueue,
					tabFilterOther
				);
			}
		}
	} else {
		// close selected tab
		if (tabsData[tab_id].canClose) {
			tabsToCloseQueue = closeTabs.getTabsToCloseQueue(
				tabsData,
				operation,
				tab_id,
				tabsToCloseQueue,
				null
			);
		}
	}
	// stash the queue of tabs to be closed in state, and dispatch the message to process that queue
	if (tabsToCloseQueue.tabList) {
		updateProperties({ tabsToCloseQueue: tabsToCloseQueue });
		dispatch(constants.TABS_CLOSE_REQUESTED, tabsData);
	}
	return tabsData;
}

/** current tab in close queue required confirmation, and confirmation was granted.
 *  change the state of the queue entry for that tab to 'confirmed' and continue processing the queue
 */
function mainTabCloseConfirmedEffect(tabsData, payload, coeffects) {
	const { properties, dispatch, updateProperties } = coeffects;
	let tabsToCloseQueue = properties.tabsToCloseQueue;
	if (
		tabsToCloseQueue &&
		tabsToCloseQueue.tabList &&
		tabsToCloseQueue.tabList.length > 0
	) {
		tabsToCloseQueue.tabList[0].confirmationStatus = 'confirmed';
		updateProperties({ tabsToCloseQueue: tabsToCloseQueue });
		dispatch(constants.TABS_CLOSE_REQUESTED, tabsData);
		const modalId =
			tabsToCloseQueue.tabList[0].dirtyModalId ||
			tabsToCloseQueue.tabList[0].warningModalId;
		if (modalId) {
			dispatch(constants.MODAL_SELECTED, {
				modalId,
				showModal: false
			});
			dispatch(constants.NOW_CONFIRM_MODAL_CLEAR);
		}
	}
	return tabsData;
}

/** current tab in close queue required confirmation, and confirmation was denied.
 *  change the state of the queue entry for that tab to 'cancelled' and continue processing the queue
 *  we have to verify that we're in the middle of a close operation here, because we have to handle
 *  modal dialog dismissed events, and it might not be the tab close dialog (NOW_MODAL#OPENED_SET)
 */
function mainTabCloseCancelledEffect(tabsData, payload, coeffects) {
	const { properties, dispatch, updateProperties } = coeffects;
	let tabsToCloseQueue = properties.tabsToCloseQueue;
	if (
		tabsToCloseQueue &&
		tabsToCloseQueue.tabList &&
		tabsToCloseQueue.tabList.length > 0
	) {
		tabsToCloseQueue.tabList[0].confirmationStatus = 'cancelled';
		updateProperties({ tabsToCloseQueue: tabsToCloseQueue });
		dispatch(constants.TABS_CLOSE_REQUESTED, tabsData);
		const modalId =
			tabsToCloseQueue.tabList[0].dirtyModalId ||
			tabsToCloseQueue.tabList[0].warningModalId;
		if (modalId) {
			dispatch(constants.MODAL_SELECTED, {
				modalId,
				showModal: false
			});
			dispatch(constants.NOW_CONFIRM_MODAL_CLEAR);
		}
	}
	return tabsData;
}

/** process the queue of tabs that are pending close */
function tabCloseRequestedEffect(tabsData, payload, coeffects) {
	const { properties, updateProperties, dispatch } = coeffects;
	let pendingConfirmation = false;
	let tabsToCloseQueue = properties.tabsToCloseQueue;
	if (tabsToCloseQueue.tabList) {
		let i;
		for (i = 0; i < tabsToCloseQueue.tabList.length; i++) {
			if (!tabsData[tabsToCloseQueue.tabList[i].tab_id])
				// already deleted
				continue;

			if (
				!tabsToCloseQueue.tabList[i].isDirty &&
				!tabsToCloseQueue.tabList[i].hasWarning
			) {
				// close clean tab
				tabsData = closeTabs.closeTabById(
					tabsData,
					tabsToCloseQueue.tabList[i].tab_id,
					true
				);
			} else if (
				tabsToCloseQueue.tabList[i].confirmationStatus === 'confirmed'
			) {
				// we already asked for confirmation, and we were told to force-close this tab
				tabsData = closeTabs.closeTabById(
					tabsData,
					tabsToCloseQueue.tabList[i].tab_id,
					true
				);
				// if the content had a custom modal, tell it to close itself
				const modalId =
					tabsToCloseQueue.tabList[i].dirtyModalId ||
					tabsToCloseQueue.tabList[i].warningModalId;
				if (modalId) {
					dispatch(constants.MODAL_SELECTED, {
						modalId,
						showModal: false
					});
				}
			} else if (
				tabsToCloseQueue.tabList[i].confirmationStatus === 'cancelled'
			) {
				// we were told to stop processing this close operation
				// set state by removing all remaining tabs in the queue, and break
				updateProperties({ tabsToCloseQueue: {} });
				break;
			} else {
				// tab is dirty but we haven't yet asked to close it, so set up state to do so and break
				tabsToCloseQueue.tabList.splice(0, i);
				tabsToCloseQueue.tabList[0].confirmationStatus = 'pending';
				updateProperties({ tabsToCloseQueue: tabsToCloseQueue });
				pendingConfirmation = true;
				break;
			}
		}
		// if there are no more tabs to close, reset the state
		if (!pendingConfirmation) updateProperties({ tabsToCloseQueue: {} });
	}
	return tabsData;
}

function mainTabNewAlertClearedEffect(tabsData, payload, coeffects) {
	const { updateProperties } = coeffects;
	updateProperties({ stopperPrompt: {} });
	return tabsData;
}

function mainTabClickedEffect(tabsData, payload, coeffects) {
	const { tab_id } = payload;
	const { dispatch, properties } = coeffects;
	//const tabPayload = generators.getSelectTabPayload([tab_id]);
	const tab = tabsData[tab_id];
	if (
		tab &&
		tab.isPrimaryTab === true &&
		tab.parentTabId === 'root' &&
		tab.viewportInfo
	) {
		dispatch(constants.VIEWPORT_SELECTED, {
			id: tab.id,
			viewportInfo: {
				...tab.viewportInfo,
				fields: { opened: !tab.viewportInfo.fields.opened }
			}
		});
		return;
	}
	return reduceTabsData(
		constants.OPEN_OR_FOCUS_TAB_REDUCER,
		tabsData,
		payload,
		properties
	);
}

function selectTabEffect(tabsData, payload, coeffects) {
	let {
		tab_ids = [],
		_implicitTabResolution = false,
		_tabResolutionStrategy = 'ACTIVE_OR_FIRST_SESSION_TAB'
	} = payload;

	const { properties } = coeffects;

	if (tab_ids.length === 0 && _implicitTabResolution === true) {
		const _tabId = tabUtils.tabResolutionStrategyMap[_tabResolutionStrategy](
			tabsData
		);
		if (_tabId === undefined || _tabId === null) return;
		tab_ids = tab_ids.concat(_tabId);
	}
	const reducerPayload = { tab_id: tab_ids[0] };
	return reduceTabsData(
		constants.OPEN_OR_FOCUS_TAB_REDUCER,
		tabsData,
		reducerPayload,
		properties
	);
}

const drillDownBackClickedEffect = (tabsData, payload, coeffects) => {
	const { properties } = coeffects;
	return reduceTabsData(
		constants.CLOSE_ONE_DRILLDOWN_REDUCER,
		tabsData,
		payload,
		properties
	);
};

//Property change effects
const screenContentChangedEffect = (tabsData, payload, coeffects) => {
	const { properties } = coeffects;
	return reduceTabsData(
		constants.LIVE_UPDATE_TABS_REDUCER,
		tabsData,
		payload,
		properties
	);
};

const initContent = (tabsData, payload, coeffects) => {
	const { properties } = coeffects;
	return reduceTabsData(
		constants.INIT_OR_UPDATE_TABS_REDUCER,
		tabsData,
		payload,
		properties
	);
};

const selectedContentChangedEffect = (tabsData, payload, coeffects) => {
	const { properties } = coeffects;
	return reduceTabsData(
		constants.INIT_OR_UPDATE_TABS_REDUCER,
		tabsData,
		payload,
		properties
	);
};

const primaryTabPropChangedEffect = (tabsData, payload, coeffects) => {
	const { properties } = coeffects;
	set(coeffects, 'action.payload.inbound', true);
	return reduceTabsData(
		constants.UPDATE_PRIMARY_TAB_REDUCER,
		tabsData,
		payload,
		properties
	);
};

//Property Change actions
const handleTabConfigPropChange = (tabConfig, properties, dispatch) => {
	if (!tabConfig || typeof tabConfig === 'string') return;
	tabUtils.initTabConfig(tabConfig);
	const { selectedContent } = properties;
	if (selectedContent) {
		const rest = tabUtils.filterSelectedContent(selectedContent);
		dispatch(constants.TABS_SELECTED_CONTENT_CHANGED, {
			...rest,
			inbound: true
		});
	}
};

const handleSelectedContentPropChange = (
	value,
	previousValue,
	properties,
	dispatch
) => {
	if (!value || typeof value === 'string') return;
	const { tabConfig, tabsData } = properties;
	const selectedContent = tabUtils.filterSelectedContent(value);
	if (!tabConfig || !selectedContent || !selectedContent.route) return null;
	if (tabConfig && !tabsData) tabUtils.initTabConfig(tabConfig);
	const prevSelectedContent = tabUtils.filterSelectedContent(previousValue);
	if (!isEqual(selectedContent, prevSelectedContent))
		dispatch(constants.TABS_SELECTED_CONTENT_CHANGED, {
			...selectedContent,
			inbound: true
		});
};

const handleChangedScreenContentPropChange = (
	changedScreenContent,
	dispatch
) => {
	const { status, isScreenKeyUpdated = false } = changedScreenContent;
	dispatch(constants.TABS_SCREEN_CONTENT_CHANGED, {
		...changedScreenContent,
		...(status !== constants.SCREEN_STATUS_ACTION_CLOSED &&
			isScreenKeyUpdated === false && { inbound: true })
	});
};

const handleScreenCloseConfirmationChange = (
	screenCloseConfirmation,
	dispatch
) => {
	if (screenCloseConfirmation === 'true')
		dispatch(constants.SCREEN_CLOSED_CONFIRMED);
	else if (screenCloseConfirmation === 'false')
		dispatch(constants.SCREEN_CLOSED_CANCELLED);
};

const handlePrimaryTabPropChange = (primaryTabPropData, dispatch) => {
	if (!primaryTabPropData) return;
	dispatch(constants.PRIMARY_TAB_PROP_CHANGED, {
		primaryTabPropData,
		inbound: true
	});
};

const handleActiveRecords = (tabsData, updateProperties) => {
	let activeParentId = '-1',
		tabList = [],
		activeRecords = [];

	if (has(tabsData, 'root')) {
		if (get(tabsData, 'root.selectedTabId') !== 'tab_0') {
			activeParentId = get(
				tabsData[get(tabsData, 'root.selectedTabId', {})],
				'selectedContent.fields.sysId',
				'-1'
			);
		}

		tabList = get(tabsData, 'root.tabList', []);
		each(tabList, (tabId) => {
			if (!get(tabsData[tabId], 'isPrimaryTab', false)) {
				let activeRecord = get(tabsData[tabId], 'selectedContent.fields', {});
				!isEmpty(activeRecord) ? activeRecords.push(activeRecord) : null;
			}
		});
	}

	updateProperties({ activeParentId, activeRecords });
};

// eslint-disable-next-line no-unused-vars
const tabsPropertyChangedEffect = ({
	action,
	properties,
	dispatch,
	updateProperties
}) => {
	const { name, value, previousValue } = action.payload;
	switch (name) {
		case 'tabConfig':
			handleTabConfigPropChange(value, properties, dispatch);
			break;

		case 'selectedContent':
			handleSelectedContentPropChange(
				value,
				previousValue,
				properties,
				dispatch
			);
			break;

		case 'changedScreenContent':
			handleChangedScreenContentPropChange(value, dispatch);
			break;

		case 'screenCloseConfirmation':
			handleScreenCloseConfirmationChange(value, dispatch);
			break;

		case 'primaryTab':
			handlePrimaryTabPropChange(value, dispatch);
			break;

		case 'tabsData':
			handleActiveRecords(value, updateProperties);
	}
};

/** handle generic modal dialog close message, which comes from the "x" close icon.
 *  if 'NOW_MODAL#OPENED_SET' reaches this code for a modal that doesn't belong to us,
 *  then it results in a no-op.
 */
function modalClosedEffect(tabsData, payload, coeffects) {
	mainTabCloseCancelledEffect(tabsData, payload, coeffects);
	mainTabNewAlertClearedEffect(tabsData, payload, coeffects);
}

function tabsDataConnectedEffect(coeffects) {
	const { host } = coeffects;
	const tabsUnloadHandler = () => {
		if (host && host.getProperty) {
			const tabsData = host.getProperty('tabsData');
			const userId = host.getProperty('userId');
			saveTabs(tabsData, userId);
		}
		window.removeEventListener('beforeunload', tabsUnloadHandler);
	};
	window.addEventListener('beforeunload', tabsUnloadHandler);
}

const actionTabsDataEffectMap = {
	[constants.MAIN_TAB_CLOSE_CLICKED]: mainTabCloseClickedEffect,
	[constants.MAIN_TAB_NEW_ALERT_CLEARED]: mainTabNewAlertClearedEffect,
	[constants.MAIN_TAB_NEW_ALERT_CLEARED_OK]: mainTabNewAlertClearedEffect,
	[constants.MAIN_TAB_CLICKED]: mainTabClickedEffect,
	[constants.SUB_TAB_CLOSE_CLICKED]: subTabCloseClickedEffect,
	[constants.SELECT_TAB]: selectTabEffect,
	[constants.TABS_SELECTED_CONTENT_CHANGED]: selectedContentChangedEffect,
	[constants.TABS_SCREEN_CONTENT_CHANGED]: screenContentChangedEffect,
	[constants.TABS_CLOSE_REQUESTED]: tabCloseRequestedEffect,
	[constants.TABS_CLOSE_CONFIRMED]: mainTabCloseConfirmedEffect,
	[constants.TABS_CLOSE_CANCELLED]: mainTabCloseCancelledEffect,
	[constants.NOW_MODAL_OPENED_SET]: modalClosedEffect, // this is the generic msg from close icon on modal
	[constants.DRILLDOWN_BACK_CLICKED]: drillDownBackClickedEffect,
	[constants.SCREEN_CLOSED_CONFIRMED]: mainTabCloseConfirmedEffect,
	[constants.SCREEN_CLOSED_CANCELLED]: mainTabCloseCancelledEffect,
	[constants.PRIMARY_TAB_PROP_CHANGED]: primaryTabPropChangedEffect
};

const tabsDataActionHandlers = Object.keys(actionTabsDataEffectMap).reduce(
	(result, key) => {
		return {
			...result,
			[key]: createTabsDataEffect(actionTabsDataEffectMap[key])
		};
	},
	{}
);

export const rootActionHandlers = {
	...tabsDataActionHandlers,
	[COMPONENT_CONNECTED]: tabsDataConnectedEffect,
	[COMPONENT_PROPERTY_CHANGED]: tabsPropertyChangedEffect,
	[constants.NOW_BUTTON_BARE_CLICKED]: {
		effect: (coeffects) => {
			const {
				action: { payload },
				dispatch
			} = coeffects;
			if (payload.drillDowns)
				dispatch(constants.DRILLDOWN_BACK_CLICKED, payload);
		},
		stopPropagation: true
	}
};
