import { createCustomElement, actionTypes } from '@servicenow/ui-core';

import { TabOverflow, ControlMenu } from '../index';
import styles from './_style.scss';
import {
	POPOVER_HIGHLIGHT_INDEX_CHANGED,
	POPOVER_HIGHLIGHT_INDEX_RESET_REQUESTED
} from './constants';
import { handleKeyPressed } from './utils';

const { COMPONENT_RENDERED } = actionTypes;
const MoreOptionMenu = (state, { updateState }) => {
	const props = state.properties;
	const { optionItems, onItemClick, onItemClose } = props;
	const { highlightIndex, isNodeFocused } = state;
	let tabs = optionItems.filter((it) => it.itemType === 'tab');
	let menuItems = optionItems.filter((it) => it.itemType === 'menu');

	const onNodeFocus = (index) => {
		if (index >= 0 && index < tabs.length)
			updateState({
				highlightIndex: index,
				isNodeFocused: true
			});
	};

	return (
		<div className="sn-chrome-popover-content" role="menu" tabIndex="0">
			<TabOverflow
				optionItems={tabs}
				highlightIndex={highlightIndex}
				onItemClick={onItemClick}
				onItemClose={onItemClose}
				onNodeFocus={onNodeFocus}
				isNodeFocused={isNodeFocused}
			/>
			<ControlMenu
				optionItems={menuItems}
				highlightIndex={highlightIndex - tabs.length}
				onItemClick={onItemClick}
			/>
		</div>
	);
};

createCustomElement('sn-canvas-popover-content', {
	view: MoreOptionMenu,
	styles,
	properties: {
		optionItems: { default: null },
		onItemClick: { default: () => {} },
		onItemClose: { default: () => {} }
	},
	initialState: {
		highlightIndex: -1,
		isNodeFocused: false
	},
	actionHandlers: {
		[POPOVER_HIGHLIGHT_INDEX_CHANGED]: (coeffects) => {
			const { highlightIndex } = coeffects.state;
			const { optionItems } = coeffects.properties;
			const { handler } = coeffects.action.payload;
			const tabs = optionItems.filter((it) => it.itemType === 'tab');
			const menuItems = optionItems.filter((it) => it.itemType === 'menu');
			const nextHighlightIndex = handler.call(null, highlightIndex, [
				...tabs,
				...menuItems
			]);
			coeffects.updateState({
				highlightIndex: nextHighlightIndex,
				isNodeFocused: false
			});
		},
		[POPOVER_HIGHLIGHT_INDEX_RESET_REQUESTED]: ({
			host,
			state,
			updateState
		}) => {
			const { isNodeFocused } = state;
			if (!isNodeFocused) {
				updateState({ highlightIndex: -1 });
				const nowPopover = host.parentElement;
				if (!nowPopover) return;
				const triggerEl = nowPopover.querySelector('[slot=trigger]');
				if (triggerEl) {
					nowPopover.opened = false;
					triggerEl.focus();
				}
			}
		},
		[COMPONENT_RENDERED]: ({ action, updateState }) => {
			const { hoistHost } = action.meta;
			if (!hoistHost) return;
			const activeElm = hoistHost.shadowRoot.activeElement;
			if (activeElm) return;
			updateState({ highlightIndex: -1 });
		}
	},
	eventHandlers: [
		{
			events: ['keyup'],
			effect({ action, dispatch }) {
				action.preventDefault();
				handleKeyPressed(action.payload.event, dispatch);
			}
		}
	]
});
