import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import { t } from 'sn-translate';
import styles from './_sn-canvas-tabs.scss';
import { canvasTabs } from './sn-canvas-tabs.view';
import GlobalResizeListener from '../../utils/global-resize-listener';

const {
	COMPONENT_DOM_READY,
	COMPONENT_DISCONNECTED,
	COMPONENT_RENDERED
} = actionTypes;

createCustomElement('sn-canvas-tabs', {
	renderer: {
		type: snabbdom,
		view: canvasTabs
	},
	styles,
	properties: {
		tabsLevel: {
			default: null
		},
		tabConfig: {
			default: null
		},
		tabsData: {
			default: null
		},
		tabsToCloseQueue: {
			default: null
		},
		stopperPrompt: {
			default: null
		}
	},
	initialState: {
		maxWidth: 0,
		tabsMaxOpen: 3,
		menuConf: {
			closeMenu: {
				header: null,
				items: [
					{ name: 'CLOSE_TAB_MENU_ITEM', displayValue: t('Close Tab') },
					{
						name: 'CLOSE_OTHER_TABS_MENU_ITEM',
						displayValue: t('Close Other Tabs')
					},
					{
						name: 'CLOSE_ALL_TABS_MENU_ITEM',
						displayValue: t('Close All Tabs')
					}
				]
			}
		},
		focusedTabIndex: {
			'1': -1, //level 1: focusedTabIndex -1
			'2': -1
		}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({ host, updateState }) => {
			const nowId = host.getAttribute('now-id');
			GlobalResizeListener.add(nowId, () => {
				updateState({ maxWidth: host.clientWidth });
			});
		},
		/* If the width after rendering is different that what we have in our state, then update it here.
		 * At initial load, we may render twice.
		 * For example, if there is an L1 in the layout,
		 * then we might render once prior to L1 render, then get reflowed when L1 has completed rendering
		 * and taken up horizontal space that was previously allocated to this component.
		 */
		[COMPONENT_RENDERED]: ({ host, updateState, state }) => {
			if (state.maxWidth != host.clientWidth)
				updateState({ maxWidth: host.clientWidth });
		},
		[COMPONENT_DISCONNECTED]: ({ host }) => {
			const nowId = host.getAttribute('now-id');
			GlobalResizeListener.remove(nowId);
		},

		'CANVAS_TAB_BAR#FOCUSED_TAB_SET': ({
			action: { payload },
			updateState,
			state
		}) => {
			const { focusedTabIndex } = state;
			const { level, tabIndex } = payload;

			let startIndex = level + 1;
			const maxIndex = Object.entries(focusedTabIndex).length;
			let _focusedTabIndex = { [level]: tabIndex };
			while (startIndex <= maxIndex) {
				_focusedTabIndex[startIndex] = -1;
				startIndex = startIndex + 1;
			}
			const newFocusedTabIndex = {
				...state.focusedTabIndex,
				..._focusedTabIndex
			};
			updateState({ focusedTabIndex: newFocusedTabIndex });
		}
	},
	behaviors: [rtlBehavior]
});
