import { getSessionStorageKey } from '../utils/browser';

let nextTabId = 0; // Math.round(Math.random() * 1000);

export function getNextTabId() {
	return 'tab_' + nextTabId++;
}

export function getStoredTabs(userId) {
	//let tabsData = getInitialTabsData();
	let tabsData;
	// try-catch for session storage support.
	try {
		const serializedTabsData = window.sessionStorage.getItem(
			getSessionStorageKey()
		);
		if (serializedTabsData) {
			const storedtabsData = JSON.parse(serializedTabsData);

			if (
				storedtabsData.userId === userId &&
				storedtabsData.root &&
				Array.isArray(storedtabsData.root.tabList) &&
				storedtabsData.root.tabList.length
			) {
				({ userId, nextTabId, ...tabsData } = storedtabsData);
				//move suspend logic here
			}
			//window.sessionStorage.removeItem(getSessionStorageKey());
		}
	} catch (e) {
		//Error reading Stored Tabs
	}
	return tabsData;
}

export function saveTabs(tabsData, userId) {
	// try-catch for session storage support. Just do nothing if unavailable.
	try {
		const serializedTabsData = JSON.stringify({
			nextTabId,
			userId,
			...tabsData
		});
		window.sessionStorage.setItem(getSessionStorageKey(), serializedTabsData);
	} catch (e) {
		return;
	}
}

//suspend tabs feature

export function clearTabs() {
	try {
		window.sessionStorage.removeItem(getSessionStorageKey());
	} catch (e) {
		// Session storage not supported.
	}
}
