import isEmpty from 'lodash/isEmpty';

export const linksetTransform = (
	{ target, label, value, leftIcon, type, rightIcon },
	selected = false
) => {
	const opensWindow = target === '_blank';
	const startsWith = leftIcon
		? { startsWith: { icon: leftIcon, align: 'left' } }
		: null;
	const endsWith = rightIcon
		? { endsWith: { type: 'icon', value: rightIcon, additionalAttrs: null } }
		: null;

	return {
		label,
		opensWindow,
		value,
		selected,
		type,
		download: false,
		configAria: {
			...(selected && { 'aria-current': 'page' })
		},
		...startsWith,
		...endsWith
	};
};

export const browseLinkTransform = (action, isSelected, level = null) => {
	if (isEmpty(action)) return null;
	const { label, rightIcon = 'chevron-end-fill' } = action;

	return {
		level,
		type: 'link',
		label: label,
		icon: rightIcon,
		link: linksetTransform({ ...action, type: 'route' }, isSelected)
	};
};

export const headingTransform = (heading) => ({
	type: 'text',
	hasIcon: false,
	icon: {},
	label: heading,
	link: {}
});
