import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { ControlMenu } from '../control-menu/controlMenu';
import { debounce } from 'lodash';
import styles from './_style.scss';

const { COMPONENT_RENDERED } = actionTypes;

const newMenu = (state) => {
	const { properties: props, highlightIndex } = state;
	if (!props.menuItems) return;
	const menuItems = props.menuItems.map((m) => {
		return { ...m, title: m.label, displayValue: m.label };
	});
	return (
		<div className="sn-canvas-tabs-menu" role="menu" tabIndex="0">
			<ControlMenu
				optionItems={menuItems}
				onItemClick={props.onNewTab}
				highlightIndex={highlightIndex}
				_className="sn-canvas-tabs-menu"
			/>
		</div>
	);
};

createCustomElement('sn-canvas-tabs-menu', {
	view: newMenu,
	initialState: {
		highlightIndex: -1
	},
	properties: {
		menuItems: {
			default: []
		},
		onNewTab: {
			default: () => {}
		}
	},
	styles,
	eventHandlers: [
		{
			events: ['keyup'],
			effect({ action, properties, state, updateState, host }) {
				action.preventDefault();
				const currentIndex = state.highlightIndex;
				const itemLength = (properties.menuItems || []).length;

				if (itemLength === 0) return;
				const _handler = () => {
					let _highlightIndex = currentIndex;
					switch (action.payload.event.keyCode) {
						case 38: //ArrowUp
							if (currentIndex <= 0) {
								_highlightIndex = itemLength - 1;
							} else {
								_highlightIndex = (currentIndex - 1) % itemLength;
							}
							break;
						case 40: //ArrowDown
							if (currentIndex === -1 || currentIndex === itemLength - 1) {
								_highlightIndex = 0;
							} else {
								_highlightIndex = (currentIndex + 1) % itemLength;
							}
							break;
						case 13: //Enter
						case 32: //Space
							properties.onNewTab(properties.menuItems[currentIndex]);
							break;
						case 9: //tab
							_highlightIndex = -1;
							//Create a scope so linter is happy
							{
								const nowPopover = host.parentElement;
								if (!nowPopover) return;
								const triggerEl = nowPopover.querySelector('[slot=trigger]');
								if (triggerEl) {
									nowPopover.opened = false;
									triggerEl.focus();
								}
							}
							break;
						default:
							break;
					}
					updateState({ highlightIndex: _highlightIndex });
				};
				//throttle the handler
				debounce(_handler, 150)();
			}
		}
	],
	actionHandlers: {
		[COMPONENT_RENDERED]: ({ action, updateState }) => {
			const { hoistHost } = action.meta;
			if (!hoistHost) return;
			const activeElm = hoistHost.shadowRoot.activeElement;
			if (activeElm) return;

			updateState({ highlightIndex: -1 });
		}
	}
});
