import { getFocusableNodes } from '@servicenow/behavior-focus';

function getStartAndEnd(scrollParent, threshold = 0) {
	// get the start and end position of the viewport
	const scroll = getScrollPosition(scrollParent);
	const start = Math.max(0, scroll - threshold);
	const end = scroll + scrollParent.clientHeight + threshold;

	return { start, end };
}

function getScrollPosition(scrollParent) {
	// Cache scroll position as this causes a forced synchronous layout.
	const actual = scrollParent.scrollTop;
	const max = scrollParent.scrollHeight - scrollParent.clientHeight;
	return Math.max(0, Math.min(actual, max));
}

function getOffset(el) {
	let offset = el.clientTop || 0;
	// eslint-disable-next-line no-cond-assign
	do offset += el.offsetTop || 0;
	while ((el = el.offsetParent));
	return offset;
}

/**
 * Focuses on the first focusable element within the host element.
 * @param {HTMLElement} host
 */
export function focusFirstElement(host) {
	// Need to set a timeout here in order for DOM to finish repainting.
	// This is the NDS recommended workaround:
	// https://sn-platform-dev.slack.com/archives/CDH3US89J/p1591751083399600
	setTimeout(() => {
		const focusableNodes = getFocusableNodes(host);
		const { firstFocusableElement } = focusableNodes;
		if (firstFocusableElement) {
			firstFocusableElement.focus();
		}
	}, 100);
}

export function reachedScrollBottom(scrollParent) {
	const { end } = getStartAndEnd(scrollParent);
	const itemEls = scrollParent.children;
	let elEnd = 0;

	if (itemEls.length) {
		const firstItemEl = itemEls[0];
		const lastItemEl = itemEls[itemEls.length - 1];
		elEnd =
			getOffset(lastItemEl) + lastItemEl.offsetHeight - getOffset(firstItemEl);
	}

	return elEnd <= end;
}
