import get from 'lodash/get';

import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { filterAriaAttributes } from '@servicenow/library-enhanced-utils';
import focusBehavior from '@servicenow/behavior-focus';
import rtlBehavior from '@servicenow/behavior-rtl';
import '@servicenow/now-tooltip';
import { t } from 'sn-translate';

import {
	NAV_ITEM_SELECTED,
	MODAL_SELECTED,
	SESSION_LOGGED_OUT,
	NOW_NAVIGATION_ITEM_CLICKED,
	EMAIL_LINK_TYPE,
	PHONE_LINK_TYPE,
	ROUTE_LINK_TYPE,
	MODAL_LINK_TYPE,
	EXTERNAL_LINK_TYPE,
	ACTION_LINK_TYPE,
	LOGOUT_LINK_TYPE,
	SPACE_KEY
} from '../../constants/constants';
import { formatURLByType } from '../../utils/link-util';
import snCanvasNavItemStyles from './_sn-nav-item.scss';

const { COMPONENT_DOM_READY } = actionTypes;

const _containsProps = (itemValue, properties) => {
	for (let idx = 0; idx < properties.length; idx++) {
		if (!itemValue.hasOwnProperty(properties[idx])) {
			return false;
		}
	}
	return true;
};

const _isLabelTruncated = (labelRef) => {
	if (!labelRef) return false;
	return (
		labelRef.scrollWidth > labelRef.clientWidth || // If x axis overflows. Handles 1 line truncation.
		labelRef.scrollHeight > labelRef.clientHeight // If y axis overflows. Handles 2 line truncation.
	);
};

const _renderTooltip = (targetRef, tooltipContent, label) => {
	if ((tooltipContent && _isLabelTruncated(targetRef)) || label.length === 0) {
		return <now-tooltip target-ref={targetRef} content={tooltipContent} />;
	}
};

const _renderNavButton = (
	variant,
	underlined,
	label,
	configAria,
	dispatch,
	maxDisplay,
	isSelected,
	interactionType,
	lines,
	experience
) => {
	return (
		<button
			type="button"
			on-click={(e) => {
				e.preventDefault();
				dispatch(() => ({
					type: NOW_NAVIGATION_ITEM_CLICKED,
					payload: {}
				}));
			}}
			class={{
				'sn-nav-item': true,
				['-' + variant]: variant,
				'show-underline': underlined,
				['-max-display']: maxDisplay,
				['-' + experience]: experience === 'workspace',
				['-' + interactionType]: true,
				['-is-selected']: isSelected
			}}
			role={interactionType === 'menubar' ? 'menuitem' : undefined}
			title={label}
			{...filterAriaAttributes(configAria, 'button')}>
			<slot name="startsWith" />
			<div
				class={{
					'sn-nav-item-label': true,
					'-show-two-lines': lines === 2,
					'-show-one-line': lines === 1
				}}>
				{label}
			</div>
			<slot name="endsWith" />
		</button>
	);
};

const _renderNavAnchor = (
	variant,
	underlined,
	label,
	configAria,
	dispatch,
	itemValue,
	type,
	opensWindow,
	download,
	maxDisplay,
	interactionType,
	lines,
	experience
) => {
	const _uniqueId = (prefix = '') => {
		const id = Math.random()
			.toString(36)
			.substr(2, 9);
		return String(prefix) + id;
	};

	const linkAriaDescById = _uniqueId('linkLabel-');

	return (
		<a
			class={{
				'sn-nav-item': true,
				['-' + variant]: variant,
				'show-underline': underlined,
				['-max-display']: maxDisplay,
				['-' + experience]: experience === 'workspace',
				['-' + interactionType]: true
			}}
			on-click={() => {
				dispatch(() => ({
					type: NOW_NAVIGATION_ITEM_CLICKED,
					payload: {}
				}));
			}}
			href={formatURLByType(itemValue, type)}
			target={opensWindow ? '_blank' : undefined}
			rel={opensWindow ? 'noopener' : undefined}
			{...filterAriaAttributes(configAria, 'link')}
			attr-download={download}
			role={interactionType === 'menubar' ? 'menuitem' : undefined}
			aria-describedby={
				type === 'external' || opensWindow ? linkAriaDescById : undefined
			}>
			<slot name="startsWith" />
			<div
				class={{
					'sn-nav-item-label': true,
					'-show-two-lines': lines === 2,
					'-show-one-line': lines === 1
				}}>
				{label}
			</div>
			<slot name="endsWith" />
			{// assist screen readers to announce external links that opens in new tab window
			type === 'external' || opensWindow ? (
				<span id={linkAriaDescById} hidden>
					{t('Link opens in new window')}
				</span>
			) : null}
		</a>
	);
};

const navigationItemView = (state, { dispatch }) => {
	const {
		type,
		itemValue = {},
		opensWindow = false,
		variant,
		label,
		underlined,
		configAria,
		tooltipContent,
		download,
		maxDisplay,
		isSelected,
		interactionType,
		lines,
		experience
	} = state.properties;

	const { targetRef } = state;

	const isInProductNav =
		type === ROUTE_LINK_TYPE ||
		type === MODAL_LINK_TYPE ||
		type === ACTION_LINK_TYPE ||
		type === LOGOUT_LINK_TYPE;

	const isMenubarInteraction =
		type === ACTION_LINK_TYPE || interactionType === 'menubar';
	return (
		<Fragment>
			{isInProductNav || isMenubarInteraction
				? _renderNavButton(
						variant,
						underlined,
						label,
						configAria,
						dispatch,
						maxDisplay,
						isSelected,
						interactionType,
						lines,
						experience
				  )
				: _renderNavAnchor(
						variant,
						underlined,
						label,
						configAria,
						dispatch,
						itemValue,
						type,
						opensWindow,
						download,
						maxDisplay,
						isSelected,
						lines,
						experience
				  )}
			{_renderTooltip(targetRef, tooltipContent, label)}
		</Fragment>
	);
};

createCustomElement('sn-nav-item', {
	renderer: {
		type: snabbdom,
		view: navigationItemView
	},
	styles: snCanvasNavItemStyles,

	properties: {
		/**
		 * Type of navigation that will drive how the URL is processed, the html
		 * element is rendered and the click behavior.
		 * @type {string}
		 */
		type: {
			default: EXTERNAL_LINK_TYPE,
			schema: {
				type: 'string',
				enum: [
					ACTION_LINK_TYPE,
					EXTERNAL_LINK_TYPE,
					ROUTE_LINK_TYPE,
					EMAIL_LINK_TYPE,
					PHONE_LINK_TYPE,
					MODAL_LINK_TYPE,
					LOGOUT_LINK_TYPE
				]
			}
		},
		/**
		 * Object describing click action - href, modal or route payloads.
		 * Depends on the type of the nav-item
		 * @type {object}
		 */
		itemValue: {
			default: {},
			schema: { type: 'object' }
		},
		/**
		 * Flag to indicate whether anchor tag or button should occupy all available
		 * space
		 * @type {object}
		 */
		maxDisplay: {
			default: false
		},
		/**
		 * Text displayed as a link.
		 * @type {string}
		 */
		label: {
			default: '',
			schema: { type: 'string' }
		},
		/**
		 * Sets link colors.
		 * @type {('simple-nav'|'main-nav'|'sub-nav'|'simple-link-nav')}
		 */
		variant: {
			default: 'simple-nav',
			schema: {
				type: 'string',
				enum: ['simple-nav', 'main-nav', 'sub-nav', 'simple-link-nav']
			}
		},
		/**
		 * Sets experience, which decides which set of css variables to use.
		 * @type {('default'|'workspace')}
		 */
		experience: {
			default: 'default',
			schema: {
				type: 'string',
				enum: ['default', 'workspace']
			}
		},
		/**
		 * Opens link in a new window or tab (depending on user's browser
		 * configuration). "Link opens in new window" is announced
		 * to assistive technology users.
		 *
		 * @type {boolean}
		 */
		opensWindow: { default: false, schema: { type: 'boolean' } },
		/**
		 * An object whose items, all aria properties, are set on the inner
		 * html `<a>` element.
		 * See https://www.w3.org/TR/wai-aria-1.1/#button for properties and
		 * accepted values.
		 * @type {{ 'aria-*': string }}
		 */
		configAria: {
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {
							link: {
								type: 'object',
								patternProperties: {
									'^aria-': { type: 'string' }
								},
								additionalProperties: false
							}
						},
						additionalProperties: false
					},
					{
						type: 'object',
						patternProperties: {
							'^aria-': { type: 'string' }
						},
						additionalProperties: false
					}
				]
			}
		},
		/**
		 * Set this flag to instruct the browser to download a file instead of
		 * navigating to a URL. Provide the value as a string to pre-populate the
		 * filename of the downloaded file.
		 * @type {(string|boolean)}
		 */
		download: {
			default: false,
			schema: { oneOf: [{ type: 'string' }, { type: 'boolean' }] }
		},
		/**
		 * Set this flag to underline the link at all times, not only on hover
		 * (all links show underline on hover).
		 * @type {boolean}
		 */
		underlined: {
			default: false,
			schema: { type: 'boolean' }
		},
		/**
		 * Optional tooltip to display on truncated nav-items.
		 * @type {string}
		 */
		tooltipContent: {
			default: '',
			schema: { type: 'string' }
		},
		/**
		 * Visually represent the link as selected.
		 *
		 * @type {boolean}
		 */
		isSelected: {
			default: false,
			schema: { type: 'boolean' }
		},
		/**
		 * Determines what interaction type this link is used for. A regular link or a link within main-nav navigation.
		 * @type {('link'|'menubar)}
		 */
		interactionType: {
			default: 'link',
			schema: { type: 'string', enum: ['link', 'menubar'] }
		},
		/**
		 * Determines how many lines to display before the label truncates.
		 * @type {(1 | 2)}
		 */
		lines: {
			default: 2,
			schema: { type: 'number', enum: [1, 2] }
		}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({ properties, host, updateState }) => {
			const { label } = properties;
			const targetRef =
				label && label.length > 0
					? host.shadowRoot.querySelector('.sn-nav-item-label')
					: host.shadowRoot.querySelector('.sn-nav-item');
			updateState({ targetRef });
		},
		[NOW_NAVIGATION_ITEM_CLICKED]: {
			effect: (coeffects) => {
				const { properties, dispatch } = coeffects;
				const { type, itemValue = {} } = properties;
				switch (type) {
					case ROUTE_LINK_TYPE:
						dispatch(NAV_ITEM_SELECTED, itemValue);
						break;
					case MODAL_LINK_TYPE:
						dispatch(MODAL_SELECTED, itemValue);
						break;
					case ACTION_LINK_TYPE:
						dispatch(get(itemValue, 'name'), get(itemValue, 'payload', {}));
						break;
					case LOGOUT_LINK_TYPE:
						if (_containsProps(itemValue, ['href']))
							dispatch(SESSION_LOGGED_OUT, {
								callback: () => (window.location.href = itemValue.href)
							});
						else if (_containsProps(itemValue, ['route', 'fields'])) {
							dispatch(SESSION_LOGGED_OUT, {});
							dispatch(NAV_ITEM_SELECTED, itemValue);
						}
						break;
					default:
						break;
				}
			}
		}
	},
	eventHandlers: [
		{
			capture: false,
			events: ['keydown'],
			effect({
				host,
				action: {
					payload: { event }
				}
			}) {
				const { key } = event;
				switch (key) {
					case SPACE_KEY: {
						const activeElement = host.nowRootNode.activeElement;
						if (activeElement) {
							event.preventDefault();
							event.stopPropagation();
							activeElement.click();
						}
						break;
					}
				}
			}
		}
	],
	behaviors: [focusBehavior, rtlBehavior],
	slots: {
		startsWith: {},
		endsWith: {}
	}
});
