import { getLinksEL } from './elements';

const countLinksLines = (linksEL) => {
	let linksElHeight, lineHeight;
	let linesCount = 0;
	if (linksEL instanceof HTMLElement) {
		linksElHeight = linksEL.offsetHeight;
		lineHeight = parseInt(getComputedStyle(linksEL, null).lineHeight);
		linesCount = Math.floor(linksElHeight / lineHeight);
	}
	return linesCount;
};

export const isLinksMultiRow = (host) => {
	const linksEL = getLinksEL(host);
	return countLinksLines(linksEL) > 1;
};
