import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import '@servicenow/now-link-set';
import '@servicenow/now-tooltip';
import focusBehavior from '@servicenow/behavior-focus';
import rtlBehavior from '@servicenow/behavior-rtl';

import { megaMenuItemSchema } from './schemas';
import '../../sn-nav-item';
import GlobalResizeListener from '../../../utils/global-resize-listener';
import { NOW_POPOVER_OPENED_SET } from '../../../constants/constants';

import { LEVEL_ONE, LEVEL_TWO, LEVEL_THREE } from './constants';

import {
	getStyePropertyIntegerValue,
	getTotalGridWidth,
	maxItemCount,
	getHoistedContent,
	linksetTransform,
	browseLinkTransform,
	headingTransform,
	measureText,
	deferUntilVisible
} from './utils';

import VerticalStackedGrid from './utils/VerticalStackedGrid';
import ObserverList from './utils/ObserverList';
import KeyboardEventHandler, {
	getFocusableItems,
	resetFocusablePositions
} from './utils/KeyboardEventHandler';

import snCanvasMegaMenuStyles from './_sn-canvas-mega-menu.scss';

const {
	COMPONENT_DISCONNECTED,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DOM_READY
} = actionTypes;

const _createSelectedLinkSet = (selectedItem, menuItems) => {
	let selectedLinkSet = new Set();
	let current = selectedItem;
	while (current !== null) {
		selectedLinkSet.add(current);
		current = get(menuItems, `[${current}].parentID`, null);
	}

	return selectedLinkSet;
};
const snCanvasMegaMenu = (state, { updateState, dispatch }) => {
	const {
		totalInternalWidth,
		totalInternalHeight,
		selectedIndex,
		visible,
		sidebarButtonStyle,
		targetRefs
	} = state;

	const { menuItems, verticalStackingEnabled, selectedItem } = state.properties;

	const { childList, action, depth } = menuItems.root;
	const selectedLinks = _createSelectedLinkSet(selectedItem, menuItems);

	const renderSidebarTabTooltip = (ref, content) => {
		if (!ref) return;
		if (ref.scrollHeight > ref.clientHeight) {
			return <now-tooltip target-ref={ref} content={content} />;
		}
	};

	const SidebarTabs = ({ items, action }) => {
		const actionButton = menuItems[action];
		const { isTruncated, label, str } = measureText(
			actionButton.label,
			sidebarButtonStyle.width,
			sidebarButtonStyle.font
		);
		return (
			<div className="sn-canvas-mega-menu-sidebar-container">
				<ul
					className="sn-canvas-mega-menu-sidebar-list"
					aria-label="Menu sidepanel list">
					{items.map(({ label, id }, idx) => {
						return (
							<li className="sn-canvas-mega-menu-sidebar-item">
								<button
									id={id}
									class={{ ['-is-selected']: selectedLinks.has(id) }}
									on-click={() =>
										dispatch('SN_CANVAS_MEGA_MENU#SIDEBAR_BUTTON_CLICKED', {
											selectedIndex: id,
											sidebarPosition: idx
										})
									}
									aria-current={selectedLinks.has(id) && 'page'}
									aria-controls={`menu_content_${id}`}>
									<span
										hook-insert={({ elm }) =>
											updateState({
												path: `targetRefs[${idx}]`,
												value: elm,
												operation: 'set'
											})
										}>
										{label}
									</span>
								</button>
								{renderSidebarTabTooltip(targetRefs[idx], label)}
							</li>
						);
					})}
				</ul>
				{actionButton ? (
					<now-button
						size="md"
						variant="primary"
						label={label}
						configAria={{ 'aria-label': str }}
						appendToPayload={{
							value: actionButton.value,
							type: 'navigationButton'
						}}
						tooltipContent={isTruncated ? str : ''}
					/>
				) : (
					undefined
				)}
			</div>
		);
	};

	const Linkset = ({ items, action }) => {
		const links = items.map((linkID) =>
			linksetTransform(menuItems[linkID].value, selectedLinks.has(linkID))
		);
		const browseLink = browseLinkTransform(
			menuItems[action],
			selectedLinks.has(action)
		);
		return (
			<div
				class={{
					'sn-canvas-mega-menu-linkset-container': true,
					'-open': true
				}}>
				<div
					style={{ width: `${totalInternalWidth}px` }}
					className="sn-canvas-mega-menu-linkset-section">
					<now-link-set
						{...(menuItems[action] ? { browseLink: browseLink } : undefined)}
						links={links}
					/>
				</div>
			</div>
		);
	};

	const LinksetGroup = ({
		action,
		items,
		id,
		buttonVariant = 'primary',
		open = true
	}) => {
		const linksetGroup = items.map((linkID) => {
			const { action: linksetAction, value, childList } = menuItems[linkID];
			const links = childList.map((linkID) =>
				linksetTransform(menuItems[linkID].value, selectedLinks.has(linkID))
			);
			const browseLink = browseLinkTransform(
				menuItems[linksetAction],
				selectedLinks.has(action)
			);
			const heading = headingTransform(value.label);

			return {
				heading,
				links,
				browseLink
			};
		});

		const actionButton = menuItems[action];
		const getMenuContentID = id ? `menu_content_${id}` : null;
		return (
			<div
				id={getMenuContentID}
				style={{ width: `${totalInternalWidth}px` }}
				class={{
					'sn-canvas-mega-menu-linkset-container': true,
					'-open': open
				}}
				aria-hidden={!open}>
				<ul
					aria-labelledby={id}
					class={{
						'sn-canvas-mega-menu-linkset-section': true,
						'-grid': true,
						'-vertical-stacking-enabled': verticalStackingEnabled
					}}>
					{linksetGroup.map(({ heading, links, browseLink }) => {
						return (
							<li>
								<now-link-set
									heading={heading}
									links={links}
									{...(browseLink ? { browseLink: browseLink } : undefined)}
								/>
							</li>
						);
					})}
				</ul>
				{actionButton ? (
					<Fragment>
						<hr className="divider" />
						<div className="sn-canvas-mega-menu-browse-group">
							<now-button
								size="md"
								variant={buttonVariant}
								label={actionButton.label}
								appendToPayload={{
									value: actionButton.value,
									type: 'navigationButton'
								}}
							/>
						</div>
					</Fragment>
				) : (
					undefined
				)}
			</div>
		);
	};

	const LinksetGroupWithSidebar = ({ items, action }) => {
		let sidebarItems = [];
		let linksetGroups = [];

		items.forEach((linkID) => {
			const {
				action: linksetAction,
				value,
				childList,
				linkID: childLinkID
			} = menuItems[linkID];
			sidebarItems.push({ ...value, id: childLinkID });
			linksetGroups.push({
				action: linksetAction,
				items: childList,
				id: childLinkID
			});
		});

		return (
			<div className="sn-canvas-mega-menu-layout-sidebar">
				<div className="sn-canvas-mega-menu-sidebar">
					<SidebarTabs action={action} items={sidebarItems} />
				</div>

				<div className="sn-canvas-mega-menu-content">
					{linksetGroups.map(({ action, items, id }) => (
						<LinksetGroup
							action={action}
							buttonVariant="secondary"
							items={items}
							open={selectedIndex === id}
							id={id}
						/>
					))}
				</div>
			</div>
		);
	};

	const MegaMenuPanel = () => {
		switch (depth) {
			case LEVEL_ONE:
				return <Linkset action={action} items={childList} />;
			case LEVEL_TWO:
				return <LinksetGroup action={action} items={childList} />;
			case LEVEL_THREE:
				return <LinksetGroupWithSidebar action={action} items={childList} />;
		}
	};

	return (
		<div
			class={{
				'sn-canvas-mega-menu': true,
				'-hidden': !visible
			}}
			role="region"
			style={{ maxHeight: `${totalInternalHeight}px` }}>
			<MegaMenuPanel />
		</div>
	);
};

createCustomElement('sn-canvas-mega-menu', {
	initialState: {
		sidebarButtonStyle: {},
		totalInternalWidth: 0,
		totalInternalHeight: 0,
		depth: 0,
		selectedIndex: '',
		visible: false,
		observers: null,
		targetRefs: [],
		focusPosition: {
			x: 0,
			y: 0
		},
		sidebarPosition: 0,
		focusedMenuPanel: '',
		focusableItems: {},
		currentFocusedEl: null
	},
	properties: {
		menuItems: {
			default: {},
			schema: { megaMenuItemSchema }
		},
		opened: {
			default: false,
			schema: { type: 'boolean' }
		},
		positionTarget: {
			default: null
		},
		selectedItem: {
			default: {}
		},
		width: {
			default: 1600,
			schema: { type: 'number' }
		},
		verticalStackingEnabled: {
			default: false,
			schema: { type: 'boolean' }
		}
	},
	renderer: {
		type: snabbdom,
		view: snCanvasMegaMenu
	},
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			state,
			updateState,
			properties
		}) => {
			const { totalInternalWidth, observers } = state;
			const { verticalStackingEnabled, menuItems } = properties;
			const { name, value, host } = action.payload;

			const saveFocusableItemsToState = () => {
				const focusableItems = getFocusableItems(host);
				updateState({
					focusableItems,
					currentFocusedEl: focusableItems.firstFocusableElement
				});
			};

			// Set keyboard navigation dom here.
			if (name === 'opened' && value) {
				saveFocusableItemsToState();
			} else if (name === 'opened' && !value) {
				// Reset focus position
				updateState(resetFocusablePositions(menuItems));
			}

			if (name === 'opened' && verticalStackingEnabled) {
				if (value) {
					const hoistedContent = getHoistedContent(host);
					const megaMenuEl = hoistedContent.querySelector(
						'.sn-canvas-mega-menu'
					);
					const gridEl = hoistedContent.querySelectorAll(
						'.sn-canvas-mega-menu-linkset-section.-grid'
					);

					// This logic should be moved to DOM tree ready when we upgrade to newer version of seismic.
					// However will need to figure out how to measure height, since it is slotted into now-popover,
					// but not actually rendered.
					gridEl.forEach((grid) => {
						const stackedGrid = new VerticalStackedGrid(grid, {
							itemSelector: 'now-link-set',
							itemWidth: 300
						});
						stackedGrid.update(totalInternalWidth);
						observers.addObserver(stackedGrid);
					});
					deferUntilVisible(megaMenuEl, saveFocusableItemsToState);
					updateState({ visible: true });
				} else {
					// Need to remove all observeres. Noticing in seismic hoist, new elements are being created each
					// time in the hoisted content.
					observers.removeAllObservers();
					updateState({ visible: false });
				}
			}
		},
		[COMPONENT_DOM_READY]: ({ action, properties, updateState }) => {
			const { host } = action.payload;
			const {
				verticalStackingEnabled,
				positionTarget,
				width: maxWidth,
				menuItems
			} = properties;

			const { depth, childList } = menuItems.root;
			const columnGap = getStyePropertyIntegerValue(
				host,
				'--sn-canvas-menu-grid-column-gap'
			);
			const linksetWidth = getStyePropertyIntegerValue(
				host,
				'--sn-canvas-menu-linkset-width'
			);
			const linksetSectionPadding = getStyePropertyIntegerValue(
				host,
				'--sn-canvas-menu-linkset-section-padding'
			);
			const sidebarWidth = getStyePropertyIntegerValue(
				host,
				'--sn-canvas-menu-sidebar-width'
			);

			const sidebarButtonTextWidth = getStyePropertyIntegerValue(
				host,
				'--sn-canvas-menu-sidebar-button-text-width'
			);

			const sidebarButtonFontFamily = `1rem ${getComputedStyle(
				host
			).getPropertyValue('font-family')}`;

			const linksetCount = maxItemCount(menuItems);
			const sidebarOffset = depth === LEVEL_THREE ? sidebarWidth : 0;
			const nowID = host.getAttribute('now-id');

			let observers = new ObserverList();

			// Resize listener to update the internal width of the panel.
			const calculateWidth = () => {
				const {
					x: positionTargetX,
					y: positionTargetY,
					height,
					right
				} = positionTarget.current.getBoundingClientRect();

				const windowWidth = window.innerWidth;
				const windowHeight = window.innerHeight;
				const fromStart = Math.floor(
					windowWidth - positionTargetX - linksetSectionPadding
				);
				const fromEnd = Math.floor(right - linksetSectionPadding);
				const calcWidth = Math.max(fromStart, fromEnd);
				const width = Math.min(calcWidth, maxWidth);
				const totalInternalWidth = getTotalGridWidth({
					columnGap,
					itemCount: linksetCount,
					maxWidth: width - sidebarOffset,
					itemWidth: linksetWidth,
					padding: linksetSectionPadding
				});

				// Set height of the popover to 500 if it's a level one menu
				const totalInternalHeight =
					depth !== LEVEL_ONE
						? windowHeight - (height + positionTargetY) - 40
						: 500;

				observers.notify(totalInternalWidth);

				updateState({
					totalInternalWidth,
					totalInternalHeight
				});
			};

			GlobalResizeListener.add(
				`calculate_width_${nowID}`,
				calculateWidth,
				true
			);

			// Always show visible if verticalStacking mode is false.
			updateState({
				observers,
				depth,
				selectedIndex: childList[0],
				sidebarButtonStyle: {
					font: sidebarButtonFontFamily,
					width: sidebarButtonTextWidth
				},
				visible: !verticalStackingEnabled
			});
		},
		/* Should move the vertical stacking JavaScript code to this lifecycle handler,
		once track/uxf uptakes seismic@19.2.0-tree-ready-rc.0 */
		// [COMPONENT_DOM_TREE_READY]: () => {
		// },
		[COMPONENT_DISCONNECTED]: ({ action, state }) => {
			const { observers } = state;
			const { host } = action.payload;
			const nowID = host.getAttribute('now-id');

			// Remove event listeners
			GlobalResizeListener.remove(`calculate_width_${nowID}`);

			// Remove all observers
			observers.removeAllObservers();
		},
		['SN_CANVAS_MEGA_MENU#SIDEBAR_BUTTON_CLICKED']: ({
			host,
			action,
			updateState
		}) => {
			const { selectedIndex, sidebarPosition } = action.payload;
			const hoistedContent = getHoistedContent(host);
			const currentFocusedEl = hoistedContent.querySelector(
				`button#${selectedIndex}`
			);
			updateState({ selectedIndex, sidebarPosition, currentFocusedEl });
		},
		['NOW_LINK_SET#LINK_CLICKED']: ({ dispatch }) => {
			// Close the popover whenever a link has been clicked
			dispatch(NOW_POPOVER_OPENED_SET, { opened: false });
		},
		['NOW_BUTTON#CLICKED']: ({ dispatch }) => {
			// Close the popover whenever a button has been clicked
			dispatch(NOW_POPOVER_OPENED_SET, { opened: false });
		}
	},
	eventHandlers: KeyboardEventHandler,
	behaviors: [focusBehavior, rtlBehavior],
	styles: snCanvasMegaMenuStyles
});
