const LEFT = 'left';
const BOTTOM = 'bottom';
const TOP = 'top';
const RIGHT = 'right';
const AUTO = 'auto';

const valToPx = (val) => `${val}px`;

export function getCenterPositioning(
	target,
	tooltip,
	offset = 5,
	placement = BOTTOM
) {
	if (!target || !tooltip) return;

	const targetRect = target.getBoundingClientRect();
	const tooltipRect = tooltip.getBoundingClientRect();

	let horizontalCenterOffset = (targetRect.width - tooltipRect.width) / 2;
	let verticalCenterOffset = (targetRect.height - tooltipRect.height) / 2;
	let targetLeft = targetRect.left;
	let targetTop = targetRect.top;
	const OFFSET = offset;

	let tooltipLeft, tooltipTop;

	switch (placement || TOP) {
		case TOP:
			tooltipLeft = valToPx(targetLeft + horizontalCenterOffset);
			tooltipTop = valToPx(targetTop - tooltipRect.height - OFFSET);
			break;
		case BOTTOM:
			tooltipTop = valToPx(targetRect.bottom + OFFSET);
			tooltipLeft = valToPx(targetLeft + horizontalCenterOffset);
			break;
		case RIGHT:
			tooltipLeft = valToPx(targetLeft + targetRect.width + OFFSET);
			tooltipTop = valToPx(targetTop + verticalCenterOffset);
			break;
		case LEFT:
			tooltipLeft = valToPx(targetLeft - tooltipRect.width - OFFSET);
			tooltipTop = valToPx(targetTop + verticalCenterOffset);
			break;
		default:
			break;
	}

	let top = tooltipTop;
	let left = tooltipLeft;
	let right = AUTO;
	let bottom;

	//reposition tooltip if it doesn't fit in the viewport.
	//this reposition does not work for all cases right now. It will only check for the opposite case to be true.
	if (placement === TOP && targetRect.top <= tooltipRect.height + OFFSET) {
		top = targetRect.bottom + OFFSET;
		bottom = AUTO;
	}

	if (
		placement === BOTTOM &&
		window.innerHeight - targetRect.bottom <= tooltipRect.height + OFFSET
	) {
		top = valToPx(targetTop - tooltipRect.height - OFFSET);
		bottom = AUTO;
	}

	if (placement === LEFT && targetRect.left <= tooltipRect.width + OFFSET) {
		left = valToPx(targetLeft + targetRect.width + OFFSET);
		right = AUTO;
	}

	if (
		placement === RIGHT &&
		targetRect.right - window.innerWidth <= tooltipRect.width + OFFSET
	) {
		left = valToPx(targetLeft - tooltipRect.width - OFFSET);
		right = AUTO;
	}

	return {
		top,
		left,
		right,
		bottom
	};
}
