import isEmpty from 'lodash/isEmpty';
import debounce from 'lodash/debounce';

let _funcs = {};

const runFuncs = debounce(() => {
	Object.values(_funcs).forEach((fn) => fn());
}, 500);

const GlobalResizeListener = {
	add(id, fn, runImmediately) {
		if (runImmediately) {
			fn();
		}
		if (window && isEmpty(_funcs)) {
			window.addEventListener('resize', runFuncs);
		}
		_funcs[id] = fn;
	},
	remove(id) {
		delete _funcs[id];
		if (isEmpty(_funcs)) {
			window.removeEventListener('resize', runFuncs);
		}
	}
};

Object.freeze(GlobalResizeListener);
export default GlobalResizeListener;
