import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import isEqual from 'lodash/isEqual';
import isEmpty from 'lodash/isEmpty';
import debounce from 'lodash/debounce';
import rtlBehavior from '@servicenow/behavior-rtl';

import '@servicenow/now-message';
import '@servicenow/now-card';
import '@servicenow/now-button';
import '@servicenow/now-heading';

import '../internal/sn-canvas-time-ago';
import { focusFirstElement, reachedScrollBottom } from './utils/dom';
import snCanvasNotificationTrayStyles from './_sn-canvas-notification-tray.scss';

const NOTIFICATION_TRAY = 'tray';
const ALERT_STATUS_READ = 'read';
const ALERT_STATUS_DELETED = 'deleted';

const NOTIFICATION_CLOSE_ACTION_ID = 'NOTIFICATION_CLOSE_ACTION_ID';

const {
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_CONNECTED,
	COMPONENT_DOM_READY,
	COMPONENT_DISCONNECTED
} = actionTypes;

const PASSIVE = { passive: true };
const NOOP = () => {};

const _onScrollOrResize = (scrollParent, dispatch) => {
	if (scrollParent) {
		const reachBottom = reachedScrollBottom(scrollParent);
		if (reachBottom) {
			dispatch('NOTIFICATION_LOAD_MORE', {});
		}
	}
};

const _addListeners = (scrollParent, handler) => {
	if (scrollParent) {
		window.addEventListener('resize', handler);
		scrollParent.addEventListener('scroll', handler, PASSIVE);

		// You have to attach mousewheel listener to the scrollable element.
		// Just an empty listener. After that onscroll events will be fired synchronously.
		scrollParent.addEventListener('mousewheel', NOOP, PASSIVE);
	}
};

const _removeListeners = (scrollParent, handler) => {
	if (scrollParent) {
		// Need to store function in state
		window.removeEventListener('resize', handler);
		scrollParent.removeEventListener('scroll', handler, PASSIVE);
		scrollParent.removeEventListener('mousewheel', NOOP, PASSIVE);
	}
};

const _getDateRangeToClear = (itemsToClear, groupId) => {
	if (itemsToClear.length <= 0) return;

	const { raw_time_stamp, time_stamp } = itemsToClear[0];
	const latestDate = new Date(time_stamp);

	if (groupId === 'today' || groupId === 'yesterday') {
		const year = latestDate.getUTCFullYear();
		const month = latestDate.getUTCMonth() + 1;
		const day = latestDate.getUTCDate();

		const startDate = `${year}-${month}-${day} 00:00:00`;

		return {
			startDate: startDate,
			endDate: raw_time_stamp
		};
	}

	if (groupId === 'older') {
		let dateDaysBefore = new Date();
		dateDaysBefore.setDate(dateDaysBefore.getDate() - 7);

		const year = dateDaysBefore.getUTCFullYear();
		const month = dateDaysBefore.getUTCMonth() + 1;
		const day = dateDaysBefore.getUTCDate();

		const hours = dateDaysBefore.getUTCHours();
		const mins = dateDaysBefore.getUTCMinutes();
		const secs = dateDaysBefore.getUTCSeconds();

		const startDate = `${year}-${month}-${day} ${hours}:${mins}:${secs}`;

		return {
			startDate: startDate,
			endDate: raw_time_stamp
		};
	}
};

const snCanvasNotificationTray = (state) => {
	const { notificationQueue, isLoading } = state.properties;
	const hasNoNotifications =
		!notificationQueue || notificationQueue.length <= 3;

	const renderSection = (header, body, sectionId) => {
		return (
			<li className="sn-canvas-notification-tray-section" key={sectionId}>
				{header}
				<div>
					<ul>{body}</ul>
				</div>
			</li>
		);
	};

	const renderTrayItem = (item, i) => {
		const {
			title,
			message,
			infoIcon,
			alert_sys_id,
			table,
			sys_id,
			time_stamp,
			is_read
		} = item;

		const notificationItemPayload = {
			table,
			sysId: sys_id,
			alertSysId: alert_sys_id,
			mode: NOTIFICATION_TRAY
		};
		const readStatus = is_read ? 'is-read' : 'is-unread';
		const headingLabel = !isEmpty(title)
			? { label: title, size: 'sm', lines: 1 }
			: {};
		return (
			<li
				className="sn-canvas-notification-tray-item"
				key={`${i}_${alert_sys_id}`}>
				<now-card
					role="button"
					configAria={{ button: { 'aria-label': `${title} ${message}` } }}
					className={`sn-canvas-notification-tray-card --${readStatus}`}
					append-to-payload={notificationItemPayload}
					interaction="select">
					<now-card-header
						className="sn-canvas-notification-tray-card-header"
						heading={headingLabel}>
						<now-icon
							className="sn-canvas-notification-tray-card-header-icon"
							slot="identifier"
							icon={infoIcon}
						/>
					</now-card-header>

					{!isEmpty(message) ? (
						<p className="sn-canvas-notification-tray-card-body">{message}</p>
					) : null}

					<sn-canvas-time-ago
						className="sn-canvas-notification-tray-card-footer"
						mode="footer"
						timeStamp={time_stamp}
					/>
				</now-card>
				<now-button
					className="sn-canvas-notification-tray-item-close-btn"
					icon="close-outline"
					tooltipContent={t('Delete Notification')}
					variant="inherit"
					size="md"
					bare
					configAria={{ 'aria-label': t('Delete Notification') }}
					appendToPayload={{
						notificationItemPayload,
						type: NOTIFICATION_CLOSE_ACTION_ID
					}}
				/>
			</li>
		);
	};

	const renderSectionHeader = (headerItem) => {
		const { name, label } = headerItem;
		return (
			<div key={name} className="sn-canvas-notification-tray-section-heading">
				<now-heading
					className="sn-canvas-notification-tray-section-heading-label"
					hasNoMargin
					label={t(label)}
					level="1"
					variant="header-secondary"
				/>
				<now-button-bare
					className="sn-canvas-notification-tray-section-heading-btn"
					hidePadding
					append-to-payload={{ groupId: name }}
					config-aria={{
						'aria-label': t('Clear notifications from {0}', name)
					}}
					label={t('Clear')}
					size="md"
					variant="secondary"
				/>
			</div>
		);
	};

	const NotificationEmptyTray = () => {
		return (
			<div
				className="sn-canvas-notification-tray-empty now-align -inline-center -block-center"
				role="region"
				aria-live="polite">
				{isLoading ? (
					<p>{t('Loading...')}</p>
				) : (
					<now-message alignment="vertical-centered">
						<div slot="message">
							<h3 className="sn-canvas-notification-tray-empty-message now-heading--md now-m-block--lg">
								{t('You have no updates')}
							</h3>
							<p className="sn-canvas-notification-tray-empty-submessage now-m-block--0 now-text--sm">
								{t('Notification updates display for 7 days')}
							</p>
						</div>
					</now-message>
				)}
			</div>
		);
	};

	const NotificationTray = () => {
		let content = [];
		const { length } = notificationQueue;
		let i = 0;
		while (i < length && notificationQueue[i].item_type === 'group') {
			const groupItem = notificationQueue[i++];
			const header = renderSectionHeader(groupItem);

			let body = [];
			while (i < length && notificationQueue[i].item_type === 'notification') {
				const item = notificationQueue[i++];
				body = body.concat(renderTrayItem(item, i));
			}

			if (body.length > 0)
				content = content.concat(renderSection(header, body, groupItem.name));
		}

		return content;
	};

	return (
		<div className="sn-canvas-notification-tray">
			{hasNoNotifications ? <NotificationEmptyTray /> : <NotificationTray />}
		</div>
	);
};

/**
 * An element that renders a list of UI Notification items as cards. This component does not manipulate any
 * data and is a simple view component that only renders data. It will dispatch global navigation actions handled by
 * the uiNotificationBehavior. See here for more information
 * [uiNotificationBehavior](https://code.devsnc.com/dev/sn-canvas-chrome/tree/master/src/behaviors/ui-notifications)
 *
 * ```jsx
 * 	const notificationItems = [
 *   	{
 * 			"item_type":"group",
 * 			"name":"today",
 * 			"label":"Today"
 * 		},
 * 		{
 *			"item_type":"notification",
 *			"table":"incident",
 *			"sys_id":"971634d323821010fd91121727bf65f3",
 *			"alert_sys_id":"131634d323821010fd91121727bf65f7",
 *			"time_stamp":1595374312000,
 *			"raw_time_stamp":"2020-07-21 23:31:52",
 *			"is_read":false,
 *			"tagline":"INC0010034",
 *			"title":"System Administrator updated INC0010034",
 *			"message":"5 - Planning: Creating incident number: 12",
 *			"infoIcon":"flag-fill",
 *			"group":"today"
 * 		},
 *     	{
 * 			"item_type":"group",
 * 			"name":"yesterday",
 * 			"label":"Yesterday"
 * 		},
 *    	{
 * 			"item_type":"group",
 * 			"name":"older",
 * 			"label":"Older"
 * 		}
 * 	];
 * 	const workspaceUIRoute = 'cb1af195872023002941b53046cb0b4e'
 *
 * 	<sn-canvas-notification-tray
 * 		opened
 * 		notificationQueue={notificationItems}
 * 		route={workspaceUIRoute}
 * 	/>
 * ```
 *
 * @seismicElement sn-canvas-notification-tray
 * @summary A container that renders a list of notification cards
 */
createCustomElement('sn-canvas-notification-tray', {
	renderer: {
		type: snabbdom,
		view: snCanvasNotificationTray
	},
	initialState: {
		scrollParent: null,
		onScrollOrResizeHandler: NOOP
	},
	properties: {
		/**
		 * Determines whether notification tray is in a loading state.
		 * @type {boolean}
		 */
		isLoading: {
			default: false
		},
		/**
		 * Queue of notifications to be rendered in the tray.
		 * @type {Array.<NotificationItem>}
		 */
		notificationQueue: {
			default: []
		},
		/**
		 * Whether the tray should be opened or not. If it's closed the component will be suspended. If open the
		 * component will be active. See here for more information
		 * [nowMode](https://code.devsnc.com/dev/sn-seismic/blob/master/docs/advanced/component-modes/component-modes.md)
		 * @type {boolean}
		 */
		opened: {
			default: false
		},
		/**
		 * The UI Route the notification is for. This will be the sys_id of a ui_notification_route record.
		 * @type {string}
		 */
		route: {
			default: ''
		}
	},
	actionHandlers: {
		[COMPONENT_CONNECTED]: ({ properties, updateProperties }) => {
			const { opened } = properties;

			if (opened) {
				updateProperties({ nowMode: 'active' });
			} else {
				updateProperties({ nowMode: 'suspend' });
			}
		},
		[COMPONENT_DISCONNECTED]: ({ state }) => {
			const { scrollParent, onScrollOrResizeHandler } = state;
			_removeListeners(scrollParent, onScrollOrResizeHandler);
		},
		[COMPONENT_DOM_READY]: ({ action, updateState, dispatch }) => {
			const { host } = action.payload;
			const scrollParent = host.shadowRoot.querySelector(
				'.sn-canvas-notification-tray'
			);

			focusFirstElement(host);
			const onScrollOrResizeDebounced = debounce(_onScrollOrResize, 300);

			const onScrollOrResizeHandler = () => {
				onScrollOrResizeDebounced(scrollParent, dispatch);
			};
			updateState({ scrollParent, onScrollOrResizeHandler });
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ state, action, updateProperties }) => {
			const { name, previousValue, value } = action.payload;

			if (name === 'opened' && !isEqual(previousValue, value)) {
				const { scrollParent, onScrollOrResizeHandler } = state;
				if (value) {
					updateProperties({ nowMode: 'active' });
					_addListeners(scrollParent, onScrollOrResizeHandler);
					setTimeout(() => onScrollOrResizeHandler());
				} else {
					updateProperties({ nowMode: 'suspend' });
					_removeListeners(scrollParent, onScrollOrResizeHandler);
				}
			}
		},
		['NOW_CARD#SELECTED_SET']: {
			effect: ({ action, dispatch }) => {
				const { table, sysId, alertSysId, mode } = action.payload;
				dispatch('NOTIFICATION_ITEM_CLICKED', {
					table,
					mode,
					sysId,
					alertSysId,
					hideTray: true,
					status: ALERT_STATUS_READ
				});
			},
			stopPropagation: true
		},
		['NOW_BUTTON#CLICKED']: {
			effect: ({ action, dispatch }) => {
				const { type, notificationItemPayload } = action.payload;
				const { table, sysId, alertSysId, mode } = notificationItemPayload;

				if (type === NOTIFICATION_CLOSE_ACTION_ID)
					dispatch('NOTIFICATION_ITEM_CLICKED', {
						table,
						mode,
						sysId,
						alertSysId,
						hideTray: false,
						status: ALERT_STATUS_DELETED
					});
			},
			stopPropagation: true
		},
		['NOW_BUTTON_BARE#CLICKED']: {
			effect: ({ action, properties, dispatch }) => {
				const { groupId } = action.payload;
				const { notificationQueue, route } = properties;
				const rawItemsToClear = notificationQueue.filter(
					(item) => item.group === groupId
				);
				const { startDate, endDate } = _getDateRangeToClear(
					rawItemsToClear,
					groupId
				);

				if (startDate && endDate) {
					const data = {
						startDateTime: startDate,
						endDateTime: endDate,
						status: ALERT_STATUS_DELETED,
						route
					};
					dispatch('ASK_TO_CLEAR_SECTION', { data, groupId });
				}
			},
			stopPropagation: true
		}
	},
	dispatches: {
		/**
		 * Dispatched whenever a notification item is clicked or close. Clicking on a notification set the status as
		 * read. Closing a notification will set the status to deleted.
		 */
		NOTIFICATION_ITEM_CLICKED: {},
		/**
		 * Dispatched whenever a section is cleared, via the clear button.
		 */
		ASK_TO_CLEAR_SECTION: {},
		/**
		 * Dispatched whenever the scrolling has reached to the bottom of the page. This is to allow lazy loading of
		 * notifications.
		 */
		NOTIFICATION_LOAD_MORE: {}
	},
	styles: snCanvasNotificationTrayStyles,
	behaviors: [rtlBehavior]
});
