import { getStyePropertyIntegerValue } from './dom';

export default class VerticalStackedGrid {
	constructor(container, options) {
		const { itemSelector, itemWidth, verticalStacking, panelWidth } = options;

		this.cachedHeights = {};

		this.container = container;
		this.itemWidth = itemWidth;
		this.verticalStacking = verticalStacking;
		this.panelWidth = panelWidth;

		this.items = container.querySelectorAll(itemSelector);

		this.rowTrack = 0;
		this.gridRowGap = getStyePropertyIntegerValue(container, 'grid-row-gap');
		this.gridColumnGap = getStyePropertyIntegerValue(
			container,
			'grid-column-gap'
		);

		this.trackSize = this.gridRowGap;
	}

	update(panelWidth) {
		let columnSize = Math.floor(panelWidth / this.itemWidth);
		let rowSize = Math.ceil(this.items.length / columnSize);

		this.items.forEach((item, idx) => {
			// Wrapping this code
			let colNumber = Math.ceil((idx + 1) / rowSize);

			let heightOfGridItem = this._getLinkSetHeight(item) + this.gridRowGap;
			let span = Math.ceil(heightOfGridItem / this.trackSize);

			// Set style in of grid item
			item.style.gridColumn = colNumber;
			item.style.gridRowEnd = `span ${span}`;
		});
	}

	_getLinkSetHeight(item) {
		const { nowId } = item;
		// Linkset heights will not change. Can cache this to avoid un-necessary calls to getBoundingClientRect, and
		// un-neccessary style changes.
		if (this.cachedHeights.hasOwnProperty(nowId)) {
			return this.cachedHeights[nowId];
		}

		// In order to measure height, set display to block and then immediately remove. JavaScript will finish running
		// before repaint happens, so we don't need to worry about flickering.
		item.parentElement.parentElement.style.display = 'block';
		const height = item.shadowRoot
			.querySelector('.now-link-set')
			.getBoundingClientRect().height;
		item.parentElement.parentElement.style.display = '';

		this.cachedHeights[nowId] = height;
		return height;
	}
}
