import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import get from 'lodash/get';
import * as utils from '../utils/utils';
import {
	QUERY_NOTIFICATIONS,
	QUERY_NOTIFICATIONS_SUCCEEDED
} from '../constants';

const notificationReadQuery = `
	query($condition: String, $offset: Int, $limit: Int) {
	  GlideRecord_Query {
		ui_notification_inbox(queryConditions: $condition, pagination: {limit: $limit, offset: $offset}) {
		  _rowCount
		  _results {
			sys_id {
			  value
			}
			sys_created_on {
			  value
			}
			payload {
			  value
			}
			target_table{
			  value
			}
			target {
			  value
			}
			triggered_by {
			  value
			  displayValue
			}
			status {
				value
			}
		  }
		}
	  }
	}
`;

export const actionHandlers = {
	[QUERY_NOTIFICATIONS]: createGraphQLEffect(notificationReadQuery, {
		variableList: ['condition', 'limit', 'offset'],
		successActionType: QUERY_NOTIFICATIONS_SUCCEEDED
	}),
	[QUERY_NOTIFICATIONS_SUCCEEDED]: ({
		action,
		state,
		properties,
		updateState,
		updateProperties
	}) => {
		const alertChunk = get(
			action.payload,
			'data.GlideRecord_Query.ui_notification_inbox._results',
			null
		);

		if (alertChunk) {
			const totalRowCount = get(
				action.payload,
				'data.GlideRecord_Query.ui_notification_inbox._rowCount',
				0
			);
			const uiNotificationBehaviorState = utils.getUINotificationBehaviorState(
				state
			);
			const { offset } = uiNotificationBehaviorState;
			// Assume userMeta present in properties. This is coming from sn-canvas-chrome-state
			const { notification, userMeta } = properties;
			const { timeZoneOffset: userTimeZoneOffset, sys_id: userId } = userMeta;
			const { queue } = notification;

			const newOffset = offset + alertChunk.length;

			// Get all current notifications
			const { alertsToKeep, alertsToDelete } = utils.purgeOldAlerts2(
				queue,
				alertChunk,
				uiNotificationBehaviorState,
				userId,
				userTimeZoneOffset
			);

			const newAlertQueue = utils.addDateGroupings(alertsToKeep);

			updateState({
				path: utils.UI_NOTIFICATION_BEHAVIOR_STATE_PATH,
				value: {
					offset: newOffset,
					totalRowCount,
					deleteQueue: alertsToDelete
				},
				operation: 'assign'
			}); // offset and totalRowCount should always update together

			updateProperties(() => ({
				path: 'notification',
				operation: 'assign',
				value: {
					queue: newAlertQueue,
					trayIsLoading: utils.isNotificationTrayLoading(
						newOffset,
						totalRowCount,
						alertsToDelete
					)
				}
			}));
		}
	}
};
