import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-badge';
import { badgeDataProvider } from './sn-canvas-badge-data-provider';

const snCanvasBadgeConnected = ({ properties }) => {
	const { count } = properties;
	return count ? (
		<now-badge
			round
			maxDigits="3"
			size="sm"
			status="critical"
			value={count}
			variant="primary"
		/>
	) : null;
};

createCustomElement('sn-canvas-badge-connected', {
	renderer: {
		type: snabbdom,
		view: snCanvasBadgeConnected
	},
	properties: {
		count: {
			default: 0
		}
	},
	behaviors: [badgeDataProvider]
});
