import { t } from 'sn-translate';
import '@servicenow/now-icon';

export const CanvasOverflowItem = (props) => {
	const { tab, hasUpdate, isHighlighted, isNodeFocused } = props;
	const { tab_id, title } = tab;
	const styleClass =
		'sn-chrome-popover-content-list-item ' + (hasUpdate ? 'has-update' : '');
	const closeBtn_tooltip = t('Close Tab');

	const _onItemClick = (e) => {
		const { tab, onItemClick } = props;
		onItemClick(tab, tab.tabOrder, e);
	};

	const _onItemClose = (e) => {
		e.stopPropagation();
		const { tab, onItemClose } = props;
		onItemClose(tab, e);
	};

	const _onItemKeyUp = (e) => {
		if (e.keyCode !== 13 /*enter*/ && e.keyCode !== 32 /*space*/) return;
		_onItemClick(e);
	};

	const _onKeyUp = (e) => {
		if (e.keyCode === 9) {
			//tab
			const { index, onNodeFocus } = props;
			onNodeFocus(index);
		} else if (e.keyCode === 13 /*enter*/ || e.keyCode === 32 /*space*/)
			_onItemClose(e);

		if (elmRef) elmRef.setAttribute('tabindex', '-1');
	};
	let elmRef = null;
	return (
		<li className={styleClass} role="presentation">
			<button
				type="button"
				className={`sn-chrome-popover-content-list-item-button`}
				on-click={_onItemClick}
				on-keyup={_onItemKeyUp}
				role="menuitem"
				tabIndex="-1"
				ref={(elm) => (elmRef = elm)}
				hook-update={({ elm }) => {
					if (!elm) return;
					if (isHighlighted && !isNodeFocused) {
						elm.focus();
						elm.setAttribute('tabindex', '0');
					} else {
						elm.setAttribute('tabindex', '-1');
					}
				}}>
				<span className="sn-chrome-popover-content-text">{title}</span>
			</button>
			<button
				type="button"
				className="overflow-close-btn sn-chrome-popover-content-list-item-close"
				aria-label={closeBtn_tooltip}
				tooltip={closeBtn_tooltip}
				on-click={_onItemClose}
				on-keyup={_onKeyUp}>
				<now-icon
					component-id={'overflow-close-tab-' + tab_id}
					icon="circle-close-outline"
					size="sm"
				/>
			</button>
			{/* <sn-component-tooltip /> */}
		</li>
	);
};
