/* eslint-disable jsx-a11y/anchor-is-valid */
import '@servicenow/now-icon';
import '@servicenow/now-badge';
import '@servicenow/now-tooltip';
import '@servicenow/now-popover';

import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import isEmpty from 'lodash/isEmpty';
import { getCenterPositioning } from './canvas-tab-preview.util';
import * as chromeTabsUtils from '../../utils';
import { handleRtlFn } from '../../../../../utils/rtl';
let showTabPreviewTimer;

const _buildClassName = (
	isSelected,
	type,
	disabled,
	hasUpdate,
	isPrimaryTab = false
) => {
	type = type === 'primary' ? 'meta' : type;
	let tmpClassList = ['sn-chrome-one-tab', `-${type}`];
	if (isSelected) tmpClassList.push('is-selected');
	if (disabled) tmpClassList.push('is-disabled');
	if (hasUpdate) tmpClassList.push('is-updated');
	if (isPrimaryTab) tmpClassList.push('is-primary');
	return tmpClassList.join(' ');
};

const _move = (elm) => (next) => {
	if (!elm) return;
	let currentId = elm.id;
	if (!currentId) {
		currentId = elm.parentElement && elm.parentElement.firstElementChild.id;
	}
	const containerElm = elm.parentElement && elm.parentElement.parentElement;
	if (!containerElm) return;
	const children = Array.from(containerElm.children || []);
	const focusedElmIndex = children.findIndex(
		(e) => e.firstElementChild && e.firstElementChild.id === currentId
	);
	if (focusedElmIndex === -1) return;
	const toBeFocusedIndex = next(focusedElmIndex, children.length);
	if (toBeFocusedIndex < 0 || toBeFocusedIndex >= children.length) return;
	if (!children[toBeFocusedIndex].firstElementChild) return;
	children[toBeFocusedIndex].firstElementChild.focus();
};

const _moveLeft = (elm) =>
	_move(elm)((index, length) =>
		index === 0 ? length - 1 : (index - 1) % length
	);
const _moveRight = (elm) => _move(elm)((index, length) => (index + 1) % length);

export default (props) => {
	const { tab, tabType, isSelected, isHighlighted } = props;
	const {
		tab_id,
		style,
		tooltipContent,
		tooltipPosition,
		title,
		table,
		disabled,
		hasUpdate,
		iconStatus,
		canClose = true,
		icon,
		isDirty,
		tooltipPreview = {},
		size
	} = tab;

	const { primaryTitle, secondaryContent } = tooltipPreview;

	const shouldRenderTabPreview = !isEmpty(tooltipPreview);

	const tabRef = createRef();
	const tabPreviewRef = createRef();

	let classname = _buildClassName(
		isSelected,
		tabType,
		disabled,
		hasUpdate,
		tab.isPrimaryTab
	);

	/**
	 * Add event listeners to the chrome tab container. This is so we do not need to create a new seismic component,
	 * and handle styles related events in a functional component.
	 *
	 * @param {HTMLElement} elm
	 */
	const addEventListener = (elm) => {
		if (!elm) return;

		const applyStyles = (element, styles) => {
			for (let rule in styles) {
				element.style[rule] = styles[rule];
			}
		};

		elm.addEventListener('CANVAS_TAB#MOUSE_ENTER', ({ detail }) => {
			const { containerStyle } = detail;
			// Tightly coupled with the dom structure of _renderTabPreview

			let tabPreviewContainer = elm.querySelector(
				'.sn-chrome-tab-preview-container'
			);
			let tabPreview = elm.querySelector('.sn-chrome-tab-preview ');
			if (tabPreviewContainer && tabPreview) {
				tabPreviewContainer.classList.add('is-open');
				applyStyles(tabPreview, containerStyle);
			}
		});

		elm.addEventListener('CANVAS_TAB#MOUSE_LEAVE', () => {
			// Tightly coupled with the dom structure of _renderTabPreview
			let tabPreviewContainer = elm.querySelector(
				'.sn-chrome-tab-preview-container'
			);
			if (tabPreviewContainer) {
				tabPreviewContainer.classList.remove('is-open');
			}
		});
	};

	const _clearPopoverTimers = () => {
		if (showTabPreviewTimer) clearTimeout(showTabPreviewTimer);
	};

	const _showFormPreview = () => {
		_clearPopoverTimers();

		if (!isSelected) {
			const containerStyle = getCenterPositioning(
				tabRef.current,
				tabPreviewRef.current
			);
			const event = new CustomEvent('CANVAS_TAB#MOUSE_ENTER', {
				detail: { containerStyle }
			});

			showTabPreviewTimer = setTimeout(() => {
				tabRef.current.dispatchEvent(event);
			}, 100);
		}
	};

	const _hideFormPreview = () => {
		_clearPopoverTimers();
		const event = new Event('CANVAS_TAB#MOUSE_LEAVE');
		tabRef.current.dispatchEvent(event);
	};

	const _onMouseEnter = () => {
		_showFormPreview();
	};

	const _onMouseLeave = () => {
		_hideFormPreview();
	};

	const _onClick = (e) => {
		const { index, tab } = props;
		_hideFormPreview();
		props.onClickTab(tab.tab_id, index, e);
	};

	const _moveNext = handleRtlFn(_moveRight, _moveLeft, props.dir);
	const _movePrevious = handleRtlFn(_moveLeft, _moveRight, props.dir);

	const _onKeyUp = (e) => {
		switch (e.keyCode) {
			case 13: // enter
			case 32: // space
				props.onKeyUp(props.tab.tab_id, e);
				break;
			case 37: // left arrow
				_movePrevious(e.target);
				break;
			case 39: // right arrow
				_moveNext(e.target);
				break;
			default:
				break;
		}
	};

	const _onClose = (e) => {
		e.stopPropagation();
		const { tab, onCloseTab } = props;
		onCloseTab(tab.tab_id, e);
	};

	const _insertTooltipEl = (el, content) => {
		if (shouldRenderTabPreview || isSelected) return;
		chromeTabsUtils.insertTooltipEl(el, content);
	};

	const _renderStatusIndicator = () => {
		const indicator = isDirty ? 'unsaved' : 'hidden';
		return (
			<div className="sn-chrome-one-tab-content has-presence">
				<span className={'sn-presence-dot is-' + indicator} />
			</div>
		);
	};

	const _renderIcon = () => {
		if (!icon) return;

		return (
			<div className={'sn-chrome-one-tab-content has-icon'}>
				<now-icon
					component-id={tab_id + '__session-type'}
					icon={icon}
					class={{
						'sn-canvas-tab-icon': true,
						'is-positive': iconStatus === 'active'
					}}
				/>
			</div>
		);
	};

	const _renderCloseBtn = (tab_id, title) => {
		const tooltip = t('Close Tab {0}', title);
		return (
			<button
				type="button"
				className="sn-chrome-one-tab-close"
				aria-label={tooltip}
				on-click={_onClose}
				on-keyup={(e) =>
					e.keyCode == 13 || e.keyCode == 32 ? _onClose(e) : _onKeyUp(e)
				}
				on-mouseenter={() =>
					chromeTabsUtils.removeTooltipEl(tabRef && tabRef.current)
				}
				on-mouseleave={() => _insertTooltipEl(tabRef && tabRef.current, title)}
				hook-insert={({ elm }) => chromeTabsUtils.insertTooltipEl(elm, tooltip)}
				hook-update={({ elm }) => {
					chromeTabsUtils.removeTooltipEl(elm);
					chromeTabsUtils.insertTooltipEl(elm, tooltip);
				}}
				hook-destroy={({ elm }) => chromeTabsUtils.removeTooltipEl(elm)}>
				<now-icon
					component-id={'close-tab-' + tab_id}
					icon="close-outline"
					size="sm"
				/>
			</button>
		);
	};

	const _renderTabPreview = () => {
		if (!shouldRenderTabPreview) return null;

		return (
			<div className="sn-chrome-tab-preview-container">
				<div
					className="sn-chrome-tab-preview has-card hoverEnabled"
					ref={tabPreviewRef}>
					<div className="sn-chrome-tab-preview-body">
						<div className="sn-card">
							{primaryTitle ? (
								<div className="sn-card--row">
									<div className="sn-card--title">
										<div className="sn-card--title--name">
											<h5 className="sn-card--title--name--truncated">
												{primaryTitle}
											</h5>
										</div>
									</div>
								</div>
							) : null}
							{secondaryContent && secondaryContent.length > 0 ? (
								<div className="sn-card--row">
									<table className="sn-card--table">
										<tbody>
											{secondaryContent.map(({ fieldLabel, displayValue }) => {
												return (
													<tr key={fieldLabel} className="sn-card--table--row">
														<td className="sn-card--table--cell">
															<div className="sn-card--table--content">
																<span>{fieldLabel}</span>
															</div>
														</td>
														<td className="sn-card--table--cell">
															<div className="sn-card--table--content">
																<span>{displayValue}</span>
															</div>
														</td>
													</tr>
												);
											})}
										</tbody>
									</table>
								</div>
							) : null}
						</div>
					</div>
				</div>
			</div>
		);
	};

	const tabTitle = title || (table ? t('Loading...') : '');
	return (
		<li
			className="sn-chrome-one-tab-container"
			style={style}
			tooltip={tooltipContent}
			tooltip-position={tooltipPosition}
			hook-insert={({ elm }) => {
				addEventListener(elm);
				_insertTooltipEl(elm, title);
			}}
			hook-update={({ elm }) => {
				if (isSelected) chromeTabsUtils.removeTooltipEl(elm);
				else
					!shouldRenderTabPreview &&
						chromeTabsUtils.insertTooltipEl(elm, title);
			}}
			ref={tabRef}>
			<a
				role="tab"
				aria-selected={isSelected ? 'true' : 'false'}
				aria-label={tabTitle}
				hook-insert={({ elm }) => elm && isHighlighted && elm.focus()}
				id={tab_id}
				className={classname}
				style={size ? { width: size } : {}}
				tabIndex={isSelected ? '0' : '-1'}
				on-click={_onClick}
				on-keyup={_onKeyUp}
				on-mouseenter={_onMouseEnter}
				on-mouseleave={_onMouseLeave}>
				{_renderStatusIndicator()}
				{_renderIcon()}
				<div className="sn-chrome-one-tab-content">
					<span>{tabTitle}</span>
				</div>
			</a>
			{canClose ? _renderCloseBtn(tab_id, title) : null}
			{_renderTabPreview()}
		</li>
	);
};
