import { createHttpEffect } from '@servicenow/ui-effect-http';
import * as utils from '../utils/utils';
import {
	CLEAR_NOTIFICATION_SECTION,
	CLEAR_NOTIFICATION_SECTION_SUCCEEDED,
	CLEAR_NOTIFICATIONS_ENDPOINT
} from '../constants';

function reloadEffect({ properties, state, dispatch }) {
	const {
		groupIdToDelete,
		offset,
		totalRowCount
	} = utils.getUINotificationBehaviorState(state);

	const { userMeta, notificationConfiguration, notification } = properties;
	const { uiRoute } = notificationConfiguration;
	const { queue } = notification;

	const groups = ['today', 'yesterday', 'older'];
	const groupIdx = groups.indexOf(groupIdToDelete);

	let needReLoading = true;
	if (groupIdx < groups.length - 1) {
		const nextGroupId = groups[groupIdx + 1];
		const hasNextGroupItem = queue.find((item) => item.group === nextGroupId);
		if (hasNextGroupItem) needReLoading = false;
	}

	if (needReLoading)
		utils.askToQueryAlerts(
			offset,
			totalRowCount,
			userMeta.sys_id,
			uiRoute,
			dispatch
		);
}

function clearSectionSucceededInterceptor({ effects, coeffects }) {
	return {
		coeffects,
		effects: [
			...effects,
			{
				effect: reloadEffect,
				args: [coeffects]
			}
		]
	};
}

function clearSectionInterceptor({ effects, coeffects }) {
	const httpEffect = createHttpEffect(CLEAR_NOTIFICATIONS_ENDPOINT, {
		method: 'PUT',
		dataParam: 'data',
		successActionType: CLEAR_NOTIFICATION_SECTION_SUCCEEDED
	});
	return {
		coeffects,
		effects: [
			...effects,
			{
				...httpEffect,
				args: [...httpEffect.args, coeffects]
			}
		]
	};
}

// clear alerts by section
const clearSectionEffect = ({ action, updateState }) =>
	updateState({
		path: utils.UI_NOTIFICATION_BEHAVIOR_STATE_PATH,
		value: {
			groupIdToDelete: action.payload.groupId
		},
		operation: 'assign'
	});

const clearSectionSucceededEffect = ({
	state,
	properties,
	updateProperties,
	updateState
}) => {
	// The section is partially loaded, reloading
	const {
		groupIdToDelete,
		offset,
		totalRowCount,
		deleteQueue
	} = utils.getUINotificationBehaviorState(state);

	const { queue } = properties.notification;

	const newAlertQueue = queue.filter(
		(item) => item.group !== groupIdToDelete || item.item_type === 'group'
	);
	const deleteCount = queue.length - newAlertQueue.length;
	const newOffset = offset - deleteCount,
		newTotalRowCount = totalRowCount - deleteCount;

	updateState({
		path: utils.UI_NOTIFICATION_BEHAVIOR_STATE_PATH,
		value: {
			offset: newOffset,
			totalRowCount: newTotalRowCount
		},
		operation: 'assign'
	}); // offset and totalRowCount should always update together

	updateProperties(() => ({
		path: 'notification',
		operation: 'assign',
		value: {
			queue: newAlertQueue,
			trayIsLoading: utils.isNotificationTrayLoading(
				newOffset,
				newTotalRowCount,
				deleteQueue
			)
		}
	}));
};

export const actionHandlers = {
	[CLEAR_NOTIFICATION_SECTION]: {
		effect: clearSectionEffect,
		interceptors: [clearSectionInterceptor]
	},
	[CLEAR_NOTIFICATION_SECTION_SUCCEEDED]: {
		effect: clearSectionSucceededEffect,
		interceptors: [clearSectionSucceededInterceptor]
	}
};
