import { isEmpty as _isEmpty, get as _get, forOwn as _forOwn } from 'lodash';

import {
	getFieldsFromViewportRoutes,
	getParamsFromViewportRoutes
} from './utils';

/**
 * loadViewport
 * @param viewportRoutes
 * @param viewportInfo
 * @param dispatch
 * @param updateProperties
 */
const loadViewport = (
	viewportRoutes,
	viewportInfo,
	dispatch,
	updateProperties
) => {
	_forOwn(viewportRoutes, (viewportRouteConfig, route) => {
		if (route === _get(viewportInfo, 'route')) {
			dispatch('UXF_VIEWPORT#RENDER_BY_ID', {
				...viewportInfo,
				reinitialize: true
			});

			// turn off preload, to stop re-firing on component prop change
			updateProperties({ preload: false });
		}
	});
};

/**
 * loadViewportWithFieldsAndParams
 * @param properties
 * @param dispatch
 * @param updateProperties
 */
const loadViewportWithFieldsAndParams = (
	properties,
	dispatch,
	updateProperties
) => {
	const {
		viewportRoutes,
		options: { viewportInfo }
	} = properties;

	_forOwn(viewportRoutes, (viewportRouteConfig, route) => {
		if (route === _get(viewportInfo, 'route')) {
			dispatch('UXF_VIEWPORT#RENDER_BY_ID', {
				...viewportInfo,
				fields: {
					...getFieldsFromViewportRoutes(viewportRoutes, properties)
				},
				params: {
					...getParamsFromViewportRoutes(viewportRoutes, properties)
				},
				reinitialize: true
			});

			// turn off preload, to stop re-firing on component prop change
			updateProperties({ preload: false });
		}
	});
};

/**
 * handleComponentDOMReady
 * @param properties
 * @param dispatch
 * @param updateProperties
 */
const handleComponentDOMReady = ({
	properties,
	dispatch,
	updateProperties
}) => {
	const {
		preload,
		viewportRoutes,
		options,
		includesFieldsParams = false
	} = properties;
	const { viewportInfo } = options;
	/**
	 * preload viewports - Preload viewports that don't require any fields & params
	 * e.g. Ongoing sidebar, and CTI use cases
	 **/
	if (
		preload &&
		!includesFieldsParams &&
		!_isEmpty(_get(viewportInfo, 'viewportElementId')) &&
		!_isEmpty(viewportRoutes)
	) {
		loadViewport(viewportRoutes, viewportInfo, dispatch, updateProperties);
	}
};

/**
 * onMaxTabLimitChange
 * @param properties
 * @param dispatch
 * @param updateProperties
 */
const onMaxTabLimitChange = (properties, dispatch, updateProperties) => {
	const {
		preload,
		activeRecords,
		maxTabLimit,
		viewportRoutes,
		options: { viewportInfo },
		includesFieldsParams = false
	} = properties;

	if (
		preload &&
		includesFieldsParams &&
		!_isEmpty(_get(viewportInfo, 'viewportElementId')) &&
		!_isEmpty(viewportRoutes)
	) {
		// preload viewports, that require maxTabLimit & activeRecords e.g. inbox viewport
		if (maxTabLimit && Array.isArray(activeRecords)) {
			loadViewportWithFieldsAndParams(properties, dispatch, updateProperties);
		}
	}
};

/**
 * onActiveParentIdChange
 * @param properties
 * @param dispatch
 * @param updateProperties
 */
const onActiveParentIdChange = (properties, dispatch, updateProperties) => {
	const {
		preload,
		activeParentId,
		activeRecords,
		viewportRoutes,
		options,
		includesFieldsParams = false
	} = properties;

	const { viewportInfo } = options;

	// update viewports on activeParentId change
	if (
		!preload &&
		includesFieldsParams &&
		!_isEmpty(_get(viewportInfo, 'viewportElementId')) &&
		!_isEmpty(viewportRoutes)
	) {
		if (!_isEmpty(activeParentId) && Array.isArray(activeRecords)) {
			loadViewportWithFieldsAndParams(properties, dispatch, updateProperties);
		}
	}
};

/**
 * onActiveRecordsChange
 * @param properties
 * @param dispatch
 * @param updateProperties
 */
const onActiveRecordsChange = (properties, dispatch, updateProperties) => {
	const {
		preload,
		activeParentId,
		activeRecords,
		maxTabLimit,
		viewportRoutes,
		options: { viewportInfo },
		includesFieldsParams = false
	} = properties;

	if (
		preload &&
		includesFieldsParams &&
		!_isEmpty(_get(viewportInfo, 'viewportElementId')) &&
		!_isEmpty(viewportRoutes)
	) {
		// preload agent chat shell
		if (!_isEmpty(activeParentId) && Array.isArray(activeRecords)) {
			loadViewportWithFieldsAndParams(properties, dispatch, updateProperties);
		}
	}

	// update viewports on activeRecords change
	if (
		!preload &&
		includesFieldsParams &&
		!_isEmpty(_get(viewportInfo, 'viewportElementId')) &&
		!_isEmpty(viewportRoutes)
	) {
		if (maxTabLimit) {
			loadViewportWithFieldsAndParams(properties, dispatch, updateProperties);
		}

		if (!_isEmpty(activeParentId)) {
			loadViewportWithFieldsAndParams(properties, dispatch, updateProperties);
		}
	}
};

/**
 * handleComponentPropertyChanged
 * @param action
 * @param properties
 * @param dispatch
 * @param updateProperties
 */
const handleComponentPropertyChanged = ({
	action,
	properties,
	dispatch,
	updateProperties
}) => {
	const { name } = action.payload;
	switch (name) {
		case 'maxTabLimit':
			onMaxTabLimitChange(properties, dispatch, updateProperties);
			break;
		case 'activeParentId':
			onActiveParentIdChange(properties, dispatch, updateProperties);
			break;
		case 'activeRecords':
			onActiveRecordsChange(properties, dispatch, updateProperties);
			break;
		default:
			break;
	}
};

const effectHandlers = {
	handleComponentDOMReady,
	handleComponentPropertyChanged
};

export default {
	effectHandlers
};
