import { isEmpty, cloneDeep } from 'lodash';
import * as tabUtils from '../utils/tabsData.utils';
import * as constants from '../../../constants/constants';
import * as generators from './tabsData.generators';
import { getStoredTabs } from './tabsData.storage';
import { closeTabById } from './tabsData.closeTabs';

const reducerTypeMap = {
	[constants.INIT_OR_UPDATE_TABS_REDUCER]: initOrUpdateTabs,
	[constants.OPEN_OR_FOCUS_TAB_REDUCER]: openOrFocusTabs,
	[constants.LIVE_UPDATE_TABS_REDUCER]: liveUpdateTabs,
	[constants.CLOSE_ONE_DRILLDOWN_REDUCER]: closeOneDrillDown,
	[constants.UPDATE_PRIMARY_TAB_REDUCER]: updatePrimaryTab
};

/**
 * Generic reducer function uses type prop to determine tab reducer
 * @param tabsData
 * @param payload
 * @returns {*}
 */
export function reduceTabsData(reducer_type, tabsData, payload, properties) {
	if (!payload) return tabsData;
	const reducer = reducerTypeMap[reducer_type];
	if (reducer) {
		const { uxrEnableDebug } = properties;
		const newTabsData = reducer(tabsData, payload, properties);
		verifyTabsPrivate(newTabsData, reducer_type, uxrEnableDebug);
		return newTabsData;
	}

	return tabsData;
}

function setPrimaryTab(tabsData, primaryTabData, passiveNavigation) {
	let newTabsData;
	const { tab_id, contentKey = {} } = primaryTabData;
	let primaryTabId;
	if (
		tabsData &&
		tabsData.root &&
		tabsData.root.tabList &&
		tabsData.root.tabList.length > 0
	) {
		primaryTabId = tabsData.root.tabList[0];
		if (
			primaryTabId &&
			tabsData[primaryTabId] &&
			tabsData[primaryTabId].isPrimaryTab !== true
		) {
			primaryTabId = null;
		}
	}
	primaryTabId = primaryTabId || tab_id;
	newTabsData = {
		...tabsData,
		[primaryTabId]: {
			...(primaryTabId &&
				tabsData[primaryTabId] && { ...tabsData[primaryTabId] }),
			...primaryTabData,
			tab_id: primaryTabId
		}
	};
	newTabsData.root.tabList[0] = primaryTabId;
	newTabsData.root.childContentKeys = {
		...newTabsData.root.childContentKeys,
		[contentKey]: primaryTabId
	};
	if (!passiveNavigation) newTabsData.root.selectedTabId = primaryTabId;

	return newTabsData;
}

function reduceTabs(tabsData, tabs = [], passiveNavigation) {
	if (!tabs || !tabs.length > 0) return tabsData;

	const { tab_id: topTabId, contentKey: topTabContentKey = {} } = tabs[0];

	const tabsObj = tabs.reduce((obj, t) => {
		obj[t.tab_id] = t;
		return obj;
	}, {});

	let newTabsData = { ...tabsData, ...tabsObj };

	if (!passiveNavigation) newTabsData.root.selectedTabId = topTabId;

	if (
		newTabsData.root.tabList &&
		newTabsData.root.tabList.indexOf(topTabId) === -1
	)
		newTabsData.root.tabList.push(topTabId);

	newTabsData.root.childContentKeys = {
		...newTabsData.root.childContentKeys,
		[topTabContentKey]: topTabId
	};

	return newTabsData;
}

function initTabs(tabsData, payload, properties) {
	const { userId } = properties;
	let storedTabsData = getStoredTabs(userId);

	if (storedTabsData) storedTabsData = tabUtils.clearAllFlags(storedTabsData);
	const { selectedContent, tabType, primaryRoute, contextualRoute } = payload;

	const primaryTabData = generators.getInitPrimaryTabData(payload);

	let newTabsData = storedTabsData
		? { ...storedTabsData }
		: { ...generators.generateTabsDataRoot() };

	if (
		(!storedTabsData && primaryTabData) ||
		(storedTabsData && tabType === 'PRIMARY' && primaryRoute)
	)
		newTabsData = setPrimaryTab(newTabsData, primaryTabData);

	if (tabType !== 'PRIMARY' && !primaryRoute) {
		//create details tab if launched directly in contextual route
		if (contextualRoute && selectedContent.parent) {
			const _payload = {
				...payload,
				selectedContent: { ...payload.selectedContent.parent }
			};
			const detailsTab = generators.getTabPayload(newTabsData, _payload);
			newTabsData = reduceTabs(newTabsData, detailsTab);
		}

		const tabs = generators.getTabPayload(newTabsData, payload);
		newTabsData = reduceTabs(newTabsData, tabs);
	}

	return newTabsData;
}

function initOrUpdateTabs(tabsData, payload, properties) {
	const { passiveNavigation = false, ...restPayload } = payload;
	const transformedPayload = generators.preProcessSelectedContent(restPayload);

	if (!tabsData) return initTabs(tabsData, transformedPayload, properties);

	let newTabsData = { ...tabsData };

	const { selectedContent, tabType, contextualRoute } = transformedPayload;

	if (tabType === 'PRIMARY' && transformedPayload.primaryRoute) {
		const primaryTabId = newTabsData.root.tabList[0];
		const { label, icon } = transformedPayload.primaryRoute;
		const options = {
			canClose: false,
			isPrimaryTab: true,
			size: null,
			viewportInfo: null
		};
		const primaryTabData = generators.getTabDefinition({
			tabId: primaryTabId,
			selectedContent: { ...selectedContent, label, icon },
			options
		});
		newTabsData = setPrimaryTab(newTabsData, primaryTabData, passiveNavigation);
	} else {
		let newTabsDataClone = cloneDeep(newTabsData);
		// test for the case where the "details" default subtab does not exist, and create it if necessary
		if (contextualRoute && selectedContent && selectedContent.parent) {
			const parentContentKey = tabUtils.getContentKey(selectedContent.parent);
			const parentTabId =
				newTabsDataClone.root.childContentKeys[parentContentKey];
			const detailsTabId = newTabsDataClone[parentTabId]
				? newTabsDataClone[parentTabId].childContentKeys[parentContentKey]
				: null;
			if (detailsTabId == null) {
				const _payload = {
					...transformedPayload,
					selectedContent: { ...transformedPayload.selectedContent.parent }
				};
				const detailsTab = generators.getTabPayload(newTabsDataClone, _payload);
				newTabsDataClone = reduceTabs(newTabsDataClone, detailsTab);
			}
		}

		const tabs = generators.getTabPayload(newTabsDataClone, transformedPayload);
		newTabsData = reduceTabs(newTabsDataClone, tabs, passiveNavigation);
	}
	return newTabsData;
}

function openOrFocusTabs(tabsData, payload) {
	let { tab_id } = payload;
	let newTabsData = { ...tabsData };
	if (!tab_id || !newTabsData[tab_id]) return;

	let tabId = tabUtils.getActiveLeafTabId(newTabsData);

	if (tabId && tabId === tab_id) {
		//clear drilldowns if active main tab is clicked again
		if (newTabsData[tabId] && newTabsData[tabId].selectedContent.drillDowns) {
			const selectedContent = newTabsData[tabId].selectedContent;
			// eslint-disable-next-line no-unused-vars
			const { drillDowns, ...rest } = selectedContent;
			newTabsData = {
				...newTabsData,
				[tabId]: { ...newTabsData[tabId], selectedContent: { ...rest } }
			};
			return newTabsData;
		}
		return;
	}

	//check if an active tab is clicked
	const selectedTab = newTabsData[tab_id];
	const selectedParentTab =
		selectedTab && selectedTab.parentTabId
			? newTabsData[selectedTab.parentTabId]
			: null;
	const activeTabId = selectedParentTab
		? selectedParentTab.selectedTabId
		: null;
	if (activeTabId === tab_id) {
		return;
	}

	while (tab_id != null) {
		const currTab = newTabsData[tab_id];
		const parentTabId = currTab.parentTabId;
		if (parentTabId == null) break;
		const parent = { ...newTabsData[parentTabId] };
		parent.selectedTabId = tab_id;
		newTabsData[parentTabId] = parent;
		tab_id = parentTabId;
	}

	return newTabsData;
}

function liveUpdateTabs(tabsData, payload) {
	const {
		screenParams,
		screenKey,
		title,
		message,
		isDirty,
		hasError,
		hasUpdate,
		dirtyModalId,
		tooltipPreview = {},
		status,
		componentId,
		hasWarning,
		warningMessage,
		warningModalId
	} = payload;

	if (!screenKey) return tabsData;

	let { icon } = payload;

	if (status && status === constants.SCREEN_STATUS_ACTION_CLOSED) {
		const tabToDelete = Object.values(tabsData).find((tabValue) => {
			return tabValue.contentKey === screenKey;
		});
		return tabToDelete
			? closeTabById(tabsData, tabToDelete.tab_id, true)
			: tabsData;
	}

	let { newTabsData, newContentKey: contentKey } = screenParams
		? updateContentKeys({
				tabsData: { ...tabsData },
				payload: { ...screenParams, screenKey }
		  })
		: { newTabsData: { ...tabsData }, newContentKey: screenKey };

	let iconStatus;
	if (typeof icon === 'object') {
		const { name, status } = icon; //status: active  = icon in green color. More status, color mappings to follow.
		icon = name;
		iconStatus = status;
	}
	const nonDetailsObj = {
		...(title && { title }),
		...(icon && { icon }),
		...(icon && { iconStatus })
	};

	if (!isEmpty(nonDetailsObj)) {
		const ignoreDetailsFilterFn = (tab) =>
			tab.contentKey === contentKey && !tab.isDetailsTab;
		newTabsData = tabUtils.applyToAllTabs(
			newTabsData,
			nonDetailsObj,
			ignoreDetailsFilterFn
		);
		if (title) tabUtils.setTabTitleCacheByScreen(contentKey, title);
	}

	const isTooltipPreviewEmpty =
		isEmpty(tooltipPreview.primaryTitle) &&
		isEmpty(tooltipPreview.secondaryContent);

	// resolve the tab's overall dirty state from the cached dirty state of all components contained within the tab
	const resolvedIsDirty = resolveDirtyState(
		newTabsData,
		contentKey,
		componentId,
		isDirty,
		dirtyModalId
	);

	const objToApply = {
		...(message && { message }),
		...(!isTooltipPreviewEmpty && { tooltipPreview }),
		...((isDirty === true || isDirty === false) && {
			isDirty: resolvedIsDirty
		}),
		...((hasError === true || hasError === false) && { hasError }),
		...((hasUpdate === true || hasUpdate === false) && { hasUpdate }),
		...(dirtyModalId && { dirtyModalId }),

		...((hasWarning === true || hasWarning === false) && { hasWarning }),
		...(warningMessage && { warningMessage }),
		...(warningModalId && { warningModalId })
	};

	if (!isEmpty(objToApply)) {
		const filterFn = (tab) => tab.contentKey === contentKey;
		newTabsData = tabUtils.applyToAllTabs(newTabsData, objToApply, filterFn);
	}
	return newTabsData;
}

/**
 * resolve overall dirty state for content from the cached dirty state of all components contained within the content
 */
function resolveDirtyState(
	tabsData,
	contentKey,
	componentId,
	isDirty,
	dirtyModalId
) {
	// find the leaf tab
	let leafTab;
	const allTabs = Object.values(tabsData);
	let i = 0;
	for (; i < allTabs.length; i++) {
		if (
			allTabs[i].contentKey === contentKey &&
			allTabs[i].tabList.length == 0
		) {
			leafTab = allTabs[i];
			break;
		}
	}
	if (leafTab) {
		// if we have not yet seen this component, create an entry for it
		if (!leafTab.componentList) {
			leafTab.componentList = [];
		}
		const componentForId = (entry) => (entry.componentId = componentId);
		let componentEntry = leafTab.componentList.some(componentForId);
		if (!componentEntry) {
			componentEntry = {
				componentId: componentId,
				isDirty: isDirty,
				dirtyModalId: dirtyModalId
			};
			leafTab.componentList.push(componentEntry);
		}

		// set the properties for this entry
		leafTab.componentList.forEach((entry) => {
			if (entry.componentId === componentId) {
				entry.isDirty = isDirty;
				entry.dirtyModalId = dirtyModalId;
			}
		});

		// return the resolved dirty state accounting for all components
		const isDirtyEntry = (entry) => entry.isDirty === true;
		return leafTab.componentList.some(isDirtyEntry);
	} else {
		// default to the dirty state that was passed in
		return isDirty;
	}
}

function closeOneDrillDown(tabsData) {
	let newTabsData = { ...tabsData };
	//const {contentKey, drillDowns} = payload;

	let tabId = tabUtils.getActiveLeafTabId(newTabsData);

	if (tabId) {
		const { selectedContent } = { ...tabsData[tabId] };
		let { drillDowns, ...rest } = { ...selectedContent };

		if (!drillDowns) return newTabsData;

		if (drillDowns.length > 0) drillDowns.pop();

		const newContent = {
			...rest,
			...(drillDowns.length === 0 && { drillDowns })
		};

		newTabsData = {
			...newTabsData,
			[tabId]: { ...newTabsData[tabId], selectedContent: newContent }
		};
	}

	return newTabsData;
}

function updateContentKeys({
	tabsData,
	payload: { screenKey, ...screenParams }
}) {
	const content = tabUtils.recoverContentFromKey(screenKey);
	const newContentKey = tabUtils.generateContentKey(
		{ ...content.fields, ...screenParams },
		content.route
	);
	let _tabsData = {};

	const tabsRequiringContentUpdate = Object.keys(tabsData).filter(
		(t) => tabsData[t].contentKey === screenKey && tabsData[t].selectedContent
	);

	const _length = tabsRequiringContentUpdate.length;
	if (!_length === 0) return { ...tabsData };
	let currentTab = { ...tabsData[tabsRequiringContentUpdate[_length - 1]] };
	while (currentTab) {
		let _contentKeys = currentTab.childContentKeys && {
			...currentTab.childContentKeys
		};
		let _selectedContent;
		if (tabsRequiringContentUpdate.includes(currentTab.tab_id)) {
			if (currentTab.hasOwnProperty('contentKey')) {
				currentTab.contentKey = newContentKey;
			}
			if (currentTab.hasOwnProperty('selectedContent')) {
				let selectedContent = { ...currentTab.selectedContent };
				let _fields = {
					...selectedContent.fields
				};

				if (selectedContent.multiInstField) {
					_fields = {
						..._fields,
						[selectedContent.multiInstField]:
							screenParams[selectedContent.multiInstField]
					};
					selectedContent.isMultiInstFieldValUpdated = true;
				} else {
					_fields = { ..._fields, ...screenParams };
				}

				delete selectedContent['multiInstField'];
				delete selectedContent['uid'];

				_selectedContent = { ...selectedContent, fields: _fields };
			}
		}

		let _currentTab = { ...currentTab };
		if (_contentKeys) {
			const oldContentKeyValue = _contentKeys[screenKey];
			delete _contentKeys[screenKey];
			_contentKeys[newContentKey] = oldContentKeyValue;
			_currentTab = { ..._currentTab, childContentKeys: _contentKeys };
		}

		if (_selectedContent)
			_currentTab = { ..._currentTab, selectedContent: _selectedContent };

		_tabsData[currentTab.tab_id] = _currentTab;
		currentTab = currentTab.parentTabId && {
			...tabsData[currentTab.parentTabId]
		};
	}

	const newTabsData = { ...tabsData, ..._tabsData };
	return { newTabsData, newContentKey };
}

function updatePrimaryTab(tabsData, payload) {
	if (!tabsData || !payload) return;

	const primaryTab = tabUtils.getPrimaryTab(tabsData);

	if (!primaryTab) return;
	// eslint-disable-next-line no-unused-vars
	const { contentKey, selectedContent, ...restPrimaryTab } = primaryTab;

	const { primaryTabPropData } = payload;
	const primaryTabContent = tabUtils.primaryTabContentUpdate(payload, tabsData);

	if (isEmpty(primaryTabPropData) || isEmpty(primaryTabContent)) return;

	const tabsDataNew = {
		...tabsData,
		[primaryTab.tab_id]: {
			...restPrimaryTab,
			...primaryTabPropData,
			contentKey: primaryTabContent.contentKey,
			selectedContent: primaryTabContent.selectedContent
		}
	};

	return tabsDataNew;
}

function verifyTabsPrivate(tabsData, caller, doVerify) {
	const verified = verifyTabs(tabsData, doVerify);
	if (verified.result === false) {
		// eslint-disable-next-line no-console
		console.debug('verifyTabs failed from ' + caller, verified, tabsData);
	}
	return verified;
}

/* verify that the tabsData structure is correct and consistent.
 * returns an object with property "result" set to true if everything is ok.
 * "result" will be set to false if there are any errors,
 * and the "errors" array will include descriptions of what is wrong.
 */
export function verifyTabs(tabsData, doVerify) {
	let verified = { result: true, errors: [] };
	if (!doVerify) return verified;
	if (!tabsData) return verified;

	if (!tabsData.root) {
		verified.result = false;
		verified.errors.push('missing root element');
		return verified;
	}
	const tabList = Object.values(tabsData);
	for (let tabIndex = 0; tabIndex < tabList.length; tabIndex++) {
		const parentTab = tabList[tabIndex];
		verifyTab(parentTab, tabList, verified);
	}
	return verified;
}

function verifyTab(tab, tabList, verified) {
	// verify that selected tab in parent is set to a child
	if (tab.selectedTabId && tab.selectedTabId != '-1') {
		if (!tab.tabList.includes(tab.selectedTabId)) {
			verified.result = false;
			verified.errors.push(
				'bad selectedTabId ' + tab.tab_id + ' / ' + tab.selectedTabId
			);
		}
	}
	// check for circular references.
	// 1. parentId cannot also be a child
	if (tab.tabList.includes(tab.parentTabId)) {
		verified.result = false;
		verified.errors.push('circular ref parent/child ' + tab.tab_id);
	}
	// 2. self cannot also be a child of itself
	if (tab.tabList.includes(tab.tab_id)) {
		verified.result = false;
		verified.errors.push('circular ref self/child ' + tab.tab_id);
	}
	// 3. self cannot be its own parent
	if (tab.parentTabId === tab.tab_id) {
		verified.result = false;
		verified.errors.push('circular ref self/parent ' + tab.tab_id);
	}

	// verify parent/child relationship
	for (
		let childTabIndex = 0;
		childTabIndex < tab.tabList.length;
		childTabIndex++
	) {
		const childTabId = tab.tabList[childTabIndex];
		const childTab = tabUtils.getTabById(tabList, childTabId);
		if (childTab.parentTabId !== tab.tab_id) {
			verified.result = false;
			verified.errors.push(
				'mismatch parent/child ' + tab.tab_id + ' > ' + childTab.tab_id
			);
		}
		if (tab.childContentKeys[childTab.contentKey] != childTab.tab_id) {
			verified.result = false;
			verified.errors.push(
				'bad childContentKeys entry ' +
					tab.tab_id +
					' > ' +
					childTab.tab_id +
					' / ' +
					tab.childContentKeys[childTab.contentKey]
			);
		}
	}
}
