import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import chromeStateEffect from './chromeStateEffect';
import { uiNotificationBehavior } from '../behaviors/ui-notifications';
import { IS_LOGGED_IN, DESTINATION_URL } from './utils';

const {
	USER_META_REQUEST_SUCCEEDED,
	PRIME_NAV_SOURCE_CHANGED,
	VIEWPORT_SELECTED,
	USER_PREF_UPDATED,
	USER_PREFERENCE_UPDATE_REQUEST_SUCCEEDED
} = chromeStateEffect.ACTION_TYPES;

const {
	handleUserMetaRequest,
	handleUserMetaResponse,
	handleUserPreferenceUpdateRequest,
	handleUserPreferenceUpdateResponse,
	handleButtonUpdate,
	handlePropertyChange,
	handleViewportSelection,
	handlePrimaryTabUpdate
} = chromeStateEffect.effectHandlers;

const {
	COMPONENT_CONNECTED,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DOM_READY
} = actionTypes;

createCustomElement('sn-canvas-chrome-state', {
	renderer: {
		type: snabbdom,
		view: () => <slot />
	},
	properties: {
		primaryTab: { default: {}, schema: { type: 'object' }, selectable: true },
		toolbarButtonsConfig: {
			default: [],
			schema: { type: 'array' },
			selectable: false
		},
		toolbarButtons: {
			default: [],
			schema: { type: 'array' },
			selectable: true
		},
		preferenceNames: { default: [], schema: { type: 'array' } },
		userMeta: { default: {}, schema: { type: 'object' }, selectable: true },
		processedUserPrefs: { default: {}, schema: { type: 'object' } },
		userPrefs: { default: {}, schema: { type: 'object' }, selectable: true },
		loginRedirectUrl: {
			default: '',
			schema: { type: 'string' },
			selectable: true
		},
		isLoggedIn: {
			default: false,
			schema: { type: 'boolean' },
			selectable: true
		}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({ updateProperties }) => {
			updateProperties({
				loginRedirectUrl: DESTINATION_URL,
				isLoggedIn: IS_LOGGED_IN
			});
		},
		[COMPONENT_CONNECTED]: handleUserMetaRequest,
		[COMPONENT_PROPERTY_CHANGED]: handlePropertyChange,
		[USER_META_REQUEST_SUCCEEDED]: handleUserMetaResponse,
		[USER_PREF_UPDATED]: handleUserPreferenceUpdateRequest,
		[USER_PREFERENCE_UPDATE_REQUEST_SUCCEEDED]: handleUserPreferenceUpdateResponse,
		[VIEWPORT_SELECTED]: handleViewportSelection,
		['PRIMARY_TAB_UPDATED']: handlePrimaryTabUpdate,
		[PRIME_NAV_SOURCE_CHANGED]: handleButtonUpdate
	},
	behaviors: [uiNotificationBehavior]
});
