import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import '@servicenow/now-card';

import snCanvasTimeAgoStyles from './_sn-canvas-time-ago.scss';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;

const WORD_LITERALS = {
	justNow: t('just now'),
	minute: 'm',
	hour: 'h',
	day: 'd'
};

const DAY = 24 * 60 * 60 * 1000;
const MINUTES = 60 * 1000;

const _daysBetween = (baseDate, actualDate) => {
	const normalizedBaseDate = new Date(baseDate.getTime());
	normalizedBaseDate.setHours(0, 0, 0);
	const normalizedActualDate = new Date(actualDate.getTime());
	normalizedActualDate.setHours(0, 0, 0);

	const diff = normalizedBaseDate.getTime() - normalizedActualDate.getTime();
	return (diff >= 0 || -1) * Math.round(Math.abs(diff / DAY));
};

const _getTime = (date) => {
	let hh = date.getHours(),
		mm = date.getMinutes();
	let suffix = 'AM';
	if (hh > 12) {
		hh -= 12;
		suffix = 'PM';
	}
	if (mm < 10) {
		mm = `0${mm}`;
	}
	return `${hh}:${mm} ${suffix}`;
};

const _getWords = (date, daysDiff) => {
	const baseDate = new Date(Date.now());
	const diffMills = baseDate - date;

	const seconds = diffMills / 1000;
	const minutes = seconds / 60;
	const hours = minutes / 60;

	let words = '';
	if (daysDiff === 0) {
		if (seconds < 60) words = WORD_LITERALS.justNow;
		else if (minutes < 60) words = t('{0}m ago', Math.floor(minutes));
		else if (hours < 24) words = t('{0}h ago', Math.floor(hours));
	} else if (daysDiff === 1) {
		words = _getTime(date);
	} else if (daysDiff > 1 && daysDiff <= 7) {
		words = t('{0}d ago', daysDiff);
	} else {
		words = 'Older than 7 days'; // Shouldn't see this
	}

	return words;
};
const _updateTime = (_date, daysDiff, updateState) => {
	const timeAgo = _getWords(_date, daysDiff);
	updateState({ timeAgo });
};

createCustomElement('sn-canvas-time-ago', {
	renderer: {
		type: snabbdom,
		view: (state) => {
			const { properties, timeAgo } = state;
			const { mode } = properties;

			switch (mode) {
				case 'footer':
					return <now-card-footer label={timeAgo} />;
				default:
					return <time>{timeAgo}</time>;
			}
		}
	},
	properties: {
		mode: {
			default: 'footer'
		},
		timeStamp: {
			default: ''
		}
	},
	initialState: {
		timeAgo: '',
		interval: null
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ properties, updateState }) => {
			const { timeStamp } = properties;

			const _date = new Date(timeStamp);
			const baseDate = new Date(Date.now());
			const daysDiff = _daysBetween(baseDate, _date);

			_updateTime(_date, daysDiff, updateState);

			if (daysDiff === 0) {
				const interval = setInterval(
					() => _updateTime(_date, daysDiff, updateState),
					MINUTES
				);
				updateState({ interval });
			}
		},
		[COMPONENT_DISCONNECTED]: ({ state }) => {
			const { interval } = state;
			if (interval) clearInterval(interval);
		}
	},
	styles: snCanvasTimeAgoStyles
});
