import { get } from 'lodash';

export const updateButtonPresenceStatus = (
	button,
	index,
	toolbarButtons,
	status,
	updateProperties
) => {
	toolbarButtons.splice(index, 1, {
		...button,
		presence: { status: status }
	});
	updateProperties({ toolbarButtons: [...toolbarButtons] });
};

