export const IS_LOGGED_IN = (() => {
	const gLoggedIn = get(window, 'ux_globals.isLoggedIn', false);
	return (
		gLoggedIn === true ||
		(typeof gLoggedIn === 'string' && gLoggedIn.toLowerCase() === 'true')
	);
})();

export const DESTINATION_URL = (() => {
	let destinationUrl = get(window, 'ux_globals.loginRedirectUrl', null);

	if (!destinationUrl) {
		const appRouteConfiguration = get(
			window,
			'ux_globals.routeConfiguration',
			null
		);
		destinationUrl = appRouteConfiguration
			? `/${appRouteConfiguration.siteName}/${
					appRouteConfiguration.landingPath
			  }`
			: '';
	}
	return destinationUrl;
})();
