import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snCanvasSidebarStyles from './_sn-canvas-sidebar.scss';
import '@servicenow/now-icon';
import { uxfViewportBehavior } from '@devsnc/library-uxf';
import { draggableBehavior } from '../../behaviors/draggable/draggableBehavior';
import { isEmpty as _isEmpty } from 'lodash';

const Docking = ({ dockingEnabled, status, updateProperties }) => {
	return dockingEnabled ? (
		status === 'DOCKED' ? (
			<now-icon
				className="close-icon"
				icon="clone-outline"
				size="md"
				on-click={() => {
					if (dockingEnabled && status === 'DOCKED') {
						updateProperties({ status: 'UN_DOCKED' });
					} else {
						updateProperties({ status: 'DOCKED' });
					}
				}}
			/>
		) : (
			<now-icon
				className="close-icon"
				icon="close-outline"
				size="md"
				on-click={() => {
					if (dockingEnabled && status === 'DOCKED') {
						updateProperties({ status: 'UN_DOCKED' });
					} else {
						updateProperties({ status: 'DOCKED' });
					}
				}}
			/>
		)
	) : null;
};

const snCanvasSidebar = (state, { updateProperties }) => {
	const {
		opened,
		heading,
		status,
		dockingEnabled,
		containerProps
	} = state.properties;

	return (
		<div
			class={{
				'sn-canvas-sidebar': true,
				'-undocked': status === 'UN_DOCKED',
				'-hidden': !opened
			}}
			style={{ ...containerProps }}>
			{!dockingEnabled && _isEmpty(heading) ? null : (
				<header className="sn-canvas-sidebar-header">
					<slot name="heading">
						<h1 className="sn-canvas-sidebar-header-title">{heading}</h1>
						<Docking
							dockingEnabled={dockingEnabled}
							status={status}
							updateProperties={updateProperties}
						/>
					</slot>
				</header>
			)}
			<div className="sn-canvas-sidebar-content">
				<slot name="content" />
			</div>
		</div>
	);
};

createCustomElement('sn-canvas-sidebar', {
	properties: {
		heading: { default: '' },
		opened: { default: false, manageable: true },
		containerProps: { default: {} },
		dockingEnabled: { default: false },
		status: { default: 'DOCKED' }
	},
	renderer: {
		type: snabbdom,
		view: snCanvasSidebar
	},
	styles: snCanvasSidebarStyles,
	behaviors: [uxfViewportBehavior, draggableBehavior]
});
