import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import focusBehavior from '@servicenow/behavior-focus';
import './sn-canvas-toolbar-button';
import snCanvasToolbarStyles from './_sn-canvas-toolbar.scss';
import keyBindingBehavior from '@servicenow/behavior-key-binding';
import rtlBehavior from '@servicenow/behavior-rtl';
import { handleKeyPressed } from './utils/helpers';
import { isEmpty as _isEmpty, filter as _filter } from 'lodash';
import { toolbarButtonsSchema } from './schemas/toolbar-buttons-schema';
import { routerSchema } from './schemas/router-schema';
import snCanvasToolbarEffect from './snCanvasToolbarEffect';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const {
	handleToolbarButtonClicked,
	handleActiveButtonSet,
	handleActiveButtonUpdated,
	handlePropertyChanged
} = snCanvasToolbarEffect.effectHandlers;

const {
	TOOLBAR_ACTIVE_BUTTON_SET,
	TOOLBAR_BUTTON_CLICKED,
	TOOLBAR_ACTIVE_BUTTON_UPDATED
} = snCanvasToolbarEffect.ACTION_TYPES;

/**
 * snCanvasToolbar: View function
 * @param state
 * @param dispatch
 * @returns {JSX.Element|null}
 */
const snCanvasToolbar = (state, { dispatch }) => {
	const {
		properties: { toolbarButtons, activeRoute, activeRecords },
		activeButton
	} = state;
	const _renderButtons = (group) => {
		if (!_isEmpty(activeButton) && !_isEmpty(toolbarButtons)) {
			const buttons = _filter(
				toolbarButtons,
				(button) => button.group === group
			);
			return buttons.map((button) => {
				const { show = true } = button;

				if (!show) {
					// when show is set to false
					return null;
				} else {
					// include the button and compute set button selection for top group based on active button
					return group === 'top' ? (
						<sn-canvas-toolbar-button
							{...button}
							isSelected={activeButton.id === button.id}
						/>
					) : group === 'bottom' ? (
						<sn-canvas-toolbar-button {...button} />
					) : null;
				}
			});
		}
	};

	if (
		_isEmpty(activeButton) &&
		!_isEmpty(activeRoute) &&
		!_isEmpty(toolbarButtons) &&
		typeof activeRoute === 'object' &&
		Array.isArray(activeRecords)
	) {
		dispatch(TOOLBAR_ACTIVE_BUTTON_SET, { activeRoute });
	}

	return !_isEmpty(toolbarButtons) && !_isEmpty(activeButton) ? (
		<nav
			className="sn-canvas-toolbar"
			aria-label={t('Main Workspace Navigation')}>
			<div className="sn-canvas-toolbar-group -top" role="radiogroup">
				{_renderButtons('top')}
			</div>
			<div className="sn-canvas-toolbar-group -bottom">
				{_renderButtons('bottom')}
			</div>
		</nav>
	) : null;
};

/**
 * A canvas toolbar that groups buttons to reference to a workspace module resource. There are
 * two button groups:
 *  - **top**: One appears at the top are radio group toolbar buttons. Only one button could be set selected at a time.
 *  - **bottom**: One appears at the bottom are toggle buttons.
 *
 *
 *
 * ```jsx
 * [
 * {
 *    "id": "home",
 *    "label": "Home",
 *    "icon": "home-outline",
 *    "viewportInfo": {},
 *    "routeInfo": {
 *      "route": "home"
 *    },
 *    "group": "top",
 *    "badge": {},
 *    "presence": {},
 *    "availability": {}
 *  },
 * {
 *    "id": "list",
 *    "label": "List",
 *    "icon": "list-outline",
 *    "viewportInfo": {},
 *    "routeInfo": {
 *      "route": "list"
 *    },
 *    "group": "top",
 *    "badge":{},
 *    "presence": {},
 *    "availability": {}
 *  },
 * {
 *    "id": "inbox",
 *    "label": "Inbox",
 *    "icon": "inbox-outline",
 *    "routeInfo": {},
 *    "viewportInfo": {
 *      "route": "inbox",
 *      "fields": {
 *        "opened": false
 *      },
 *      "viewportElementId": "wsSidebar"
 *    },
 *    "group": "top",
 *    "badge": {
 *      "count": 0
 *    },
 *    "presence": {
 *      "status": ""
 *    },
 *    "availability": {
 *      "roles": ["awa_agent","awa_admin","admin"],
 *      "plugin": "com.glide.interaction.awa"
 *    }
 *  },
 * {
 *    "id": "call",
 *    "label": "Call",
 *    "icon": "phone-outline",
 *    "routeInfo": {},
 *    "viewportInfo": {
 *      "route": "cti",
 *      "fields": {
 *        "opened": false
 *      },
 *      "viewportElementId": "wsCornerDialog"
 *    },
 *    "group": "bottom",
 *    "badge":{},
 *    "presence": {},
 *    "availability": {}
 *  }
 * ]
 *
 *  <sn-canvas-toolbar toolbarButtons={buttons} />
 *
 * ```
 *
 * @seismicElement sn-canvas-toolbar
 * @summary A vertical toolbar that provide an interactive reference to a resource. The target resource must be an
 * internal page of current application.
 */
createCustomElement('sn-canvas-toolbar', {
	properties: {
		/**
		 * Properties to configure the canvas toolbar buttons.
		 *
		 *
		 * Toolbar buttons provides a way to interaction and load the workspace canvas modules.
		 * All button clicks dispatches to NAV_ITEM_SELECTED to navigate to an internal page or screen
		 *
		 * An array of `ToolbarButton` item.
		 * @type {(Object.<ToolbarButtons>)}
		 */
		toolbarButtons: { default: [], schema: toolbarButtonsSchema },
		/**
		 * Defines the active route information.
		 * An object of `Router`. See here for more information
		 * [sn-canvas-router](https://code.devsnc.com/dev/sn-canvas-core#canvas-router)
		 *@type {(Object.<Router>)}
		 */
		activeRoute: { default: {}, schema: { type: routerSchema } },
		/**
		 * Represents the sysId of current selected session tab
		 * @type {(string)}
		 */
		activeParentId: { default: '-1', schema: { type: 'string' } },
		/**
		 * Represents the array of the active session tab <table: '', sysId: ''>
		 * When there are no session tabs, it's value is empty array []
		 *
		 * @type {Array.<{table: string, sysId: string}>}
		 */
		activeRecords: { default: [], schema: { type: 'array' } }
	},
	initialState: {
		activeButton: {}
	},
	renderer: {
		type: snabbdom,
		view: snCanvasToolbar
	},
	styles: snCanvasToolbarStyles,
	dispatches: {
		/**
		 * Dispatched when the toolbar button is selected.
		 * @type {{}}
		 */
		'SN_CANVAS_TOOLBAR#BUTTON_SELECTED': {}
	},
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: handlePropertyChanged,
		[TOOLBAR_ACTIVE_BUTTON_SET]: {
			effect: handleActiveButtonSet,
			stopPropagation: true
		},
		[TOOLBAR_BUTTON_CLICKED]: {
			effect: handleToolbarButtonClicked,
			stopPropagation: true
		},
		[TOOLBAR_ACTIVE_BUTTON_UPDATED]: {
			effect: handleActiveButtonUpdated,
			stopPropagation: true
		}
	},
	behaviors: [
		{ behavior: rtlBehavior },
		{ behavior: focusBehavior },
		{
			behavior: keyBindingBehavior,
			options: {
				keyBindings: {
					onKeyPress: ['ArrowUp', 'ArrowDown', ' ', 'Enter']
				},
				handlers: {
					onKeyPress: (host, event, dispatch) =>
						handleKeyPressed(host, event, dispatch)
				}
			}
		}
	]
});
