const DOCUMENT_EVENTS = [
	'mousemove',
	'mousedown',
	'mouseup',
	'touchmove',
	'touchstart',
	'touchend'
];

let activeItem = null;
let active = false;

const onDragStart = (e) => {
	// determine event object
	if (!e) {
		// eslint-disable-next-line no-unused-vars
		let e = window.event;
	}
	if (e.preventDefault) e.preventDefault();

	if (e.target !== e.currentTarget) {
		active = true;

		// this is the item we are interacting with
		activeItem = e.target;

		if (activeItem !== null) {
			if (!activeItem.xOffset) {
				activeItem.xOffset = 0;
			}

			if (!activeItem.yOffset) {
				activeItem.yOffset = 0;
			}

			if (e.type === 'touchstart') {
				activeItem.initialX = e.touches[0].clientX - activeItem.xOffset;
				activeItem.initialY = e.touches[0].clientY - activeItem.yOffset;
			} else {
				activeItem.initialX = e.clientX - activeItem.xOffset;
				activeItem.initialY = e.clientY - activeItem.yOffset;
			}
		}
	}
	return false;
};

const onDragEnd = () => {
	if (activeItem !== null) {
		activeItem.initialX = activeItem.currentX;
		activeItem.initialY = activeItem.currentY;
	}

	active = false;
	activeItem = null;
	return false;
};

const onDrag = (e) => {
	if (active) {
		if (e.type === 'touchmove') {
			e.preventDefault();

			activeItem.currentX = e.touches[0].clientX - activeItem.initialX;
			activeItem.currentY = e.touches[0].clientY - activeItem.initialY;
		} else {
			activeItem.currentX = e.clientX - activeItem.initialX;
			activeItem.currentY = e.clientY - activeItem.initialY;
		}

		activeItem.xOffset = activeItem.currentX;
		activeItem.yOffset = activeItem.currentY;

		setTranslate(activeItem.currentX, activeItem.currentY, activeItem);
	}
};

const setTranslate = (xPos, yPos, el) => {
	// -undocked class is added to the floating items
	// that support dock /un-dock
	const elNode = el.shadowRoot.querySelector('.-undocked');
	if (elNode) {
		elNode.style.transform = 'translate3d(' + xPos + 'px, ' + yPos + 'px, 0)';
	}
};

const clearTranslate = (el) => {
	if (el && el.shadowRoot && el.shadowRoot.childNodes) {
		el.shadowRoot.childNodes.forEach((node) => {
			node.style.removeProperty('transform');
		});
	}
};

const bindDraggableEvents = ({
	host,
	state,
	updateState,
	updateProperties
}) => {
	const targetRef = host;
	const parentTargetRef = host.parentElement;
	const { dockingEnabled } = state.properties;

	if (dockingEnabled && targetRef && parentTargetRef) {
		DOCUMENT_EVENTS.forEach((eventType) => {
			if (eventType === 'touchstart' || eventType === 'mousedown') {
				parentTargetRef.addEventListener(
					eventType,
					(e) => onDragStart(e, targetRef, updateProperties),
					false
				);
			} else if (eventType === 'touchend' || eventType === 'mouseup') {
				parentTargetRef.addEventListener(
					eventType,
					(e) => onDragEnd(e, targetRef, updateProperties),
					false
				);
			} else if (eventType === 'touchmove' || eventType === 'mousemove') {
				parentTargetRef.addEventListener(
					eventType,
					(e) => onDrag(e, targetRef, active),
					false
				);
			}
		});
	}

	updateState({ targetRef: host, parentTargetRef: host.parentElement });
};

const unbindDraggableEvents = ({ host, state }) => {
	const targetRef = host;
	const parentTargetRef = host.parentElement;
	const { dockingEnabled } = state.properties;

	if (dockingEnabled && targetRef && parentTargetRef) {
		DOCUMENT_EVENTS.forEach((eventType) => {
			if (eventType === 'touchstart' || eventType === 'mousedown') {
				parentTargetRef.removeEventListener(
					eventType,
					() => onDragStart,
					false
				);
			} else if (eventType === 'touchend' || eventType === 'mouseup') {
				parentTargetRef.removeEventListener(eventType, () => onDragEnd, false);
			} else if (eventType === 'touchmove' || eventType === 'mousemove') {
				parentTargetRef.removeEventListener(eventType, () => onDrag, false);
			}
		});
	}
};

const clearStyles = (coeffects) => {
	const {
		host,
		action: { payload }
	} = coeffects;
	const { name, previousValue, value } = payload;
	if (name === 'status' && previousValue !== value && value === 'DOCKED') {
		clearTranslate(host);
	}
};

const effectHandlers = {
	bindDraggableEvents,
	unbindDraggableEvents,
	clearStyles
};

export default {
	effectHandlers
};
