'use strict';
import {
	CLOSE_TAB_MENU_ITEM,
	CLOSE_OTHER_TABS_MENU_ITEM,
	CLOSE_ALL_TABS_MENU_ITEM
} from '../../../../constants/constants';

export function getOverflowDisabled(tabList) {
	const { length } = tabList;
	return length == 0 || (length === 1 && !tabList[0].canClose);
}

export function transformCloseMenuItems(closeMenu, tabList, selectedTabId) {
	const activeTab = tabList.find((t) => t.tab_id === selectedTabId) || {};
	const length = tabList.length;
	let isPivotActive = !!(activeTab && !activeTab.canClose);
	let isDetailsTabTheOnlyOtherTab =
		length === 2 && activeTab && activeTab.canClose && !tabList[0].canClose; // the only other tab is the details tab
	return closeMenu.items.map((it) => {
		let disabled = false;
		switch (it.name) {
			case CLOSE_TAB_MENU_ITEM:
				disabled = length === 0 || !activeTab || isPivotActive;
				break;
			case CLOSE_OTHER_TABS_MENU_ITEM:
				disabled = length <= 1 || !activeTab || isDetailsTabTheOnlyOtherTab;
				break;
			case CLOSE_ALL_TABS_MENU_ITEM:
				disabled = length <= 1;
				break;
		}
		return { ...it, itemType: 'menu', disabled };
	});
}

export function buildOverflowStyle(
	type,
	overflowDisabled,
	badgeCount,
	hasOverflowUpdate
) {
	let overflowClassNames = 'sn-chrome-tabs-btn -overflow';

	if (overflowDisabled) overflowClassNames += ' is-disabled';
	else {
		overflowClassNames += type === 'default' ? ' -default' : ' -close';

		if (badgeCount > 0)
			overflowClassNames +=
				' has-badge ' + (hasOverflowUpdate ? 'is-updated' : 'is-activated');
	}

	return overflowClassNames;
}

export function buildVisibleAndOverflowList(
	tabList,
	totalWidth,
	selectedTabId
) {
	totalWidth -= 20; // this gives us a little extra room in our calculations
	let visibleList = [];
	if (tabList.length === 0) return visibleList;

	let length = tabList.length;

	const selectedTabIdx = tabList.findIndex((t) => t.tab_id == selectedTabId);
	const selectedTab = tabList[selectedTabIdx];
	const isSelectedPrimaryTab = selectedTab.isPrimaryTab;

	/* note that the code below is filled with magic numbers.
	 * These were carried forward from legacy workspace tab code, and are not configurable.
	 * The value 280 was added to handle the case when the L1 tab is at its max width,
	 * as is the case when inbox is selected.  It was selected because empirically it works well,
	 * not from any specific math for handling the totalWidth space.
	 * This logic relies on tabList[0].size being set in this case, an not in any other case, which is fragile.
	 */
	const gap = 4, // right margin space among tabs
		minTabWidth = 88,
		maxTabWidth = 166,
		maxTabWidthL1Item = tabList[0].size ? 280 : 240,
		minTabWidthL1Item = 110;

	const standardTabSpace = maxTabWidth + gap,
		L1ItemTabSpace = maxTabWidthL1Item + gap,
		minTabSpace = minTabWidth + gap,
		// mustHaveSpace = L1ItemTabSpace + standardTabSpace,    // no longer used
		uncompressedTabSpace = standardTabSpace * (length - 1) + L1ItemTabSpace;

	let tabWidth = maxTabWidth,
		overflowList = [];
	visibleList = tabList;

	let count;
	// We don't have enough room and must compress tab width
	if (uncompressedTabSpace > totalWidth) {
		// Without shrinking the module tab or the active tab (if present), figure out how much space for each of
		// the remaining tabs
		let newTabSpace = isSelectedPrimaryTab
			? (totalWidth - L1ItemTabSpace) / (length - 1)
			: (totalWidth - (L1ItemTabSpace + standardTabSpace)) / (length - 2);

		newTabSpace = Math.floor(newTabSpace); // newTabSpace is an int, so round up
		// Ensure this isn't lower than our minimum
		newTabSpace = Math.max(minTabSpace, newTabSpace);

		// Figure out how many tabs we can fit in the available area using this width
		count = isSelectedPrimaryTab
			? Math.floor((totalWidth - L1ItemTabSpace) / newTabSpace) + 1
			: Math.floor(
					(totalWidth - (L1ItemTabSpace + standardTabSpace)) / newTabSpace
			  ) + 2;

		tabWidth = newTabSpace - gap;
	}

	// Calculate the space required for all tabs at the current width
	const spaceRequired = isSelectedPrimaryTab
		? L1ItemTabSpace + (tabWidth + gap) * (length - 1)
		: L1ItemTabSpace + standardTabSpace + (tabWidth + gap) * (length - 2);

	// We don't have enough space for all of the tabs and need to collapse some
	if (spaceRequired > totalWidth) {
		let newTabList = tabList.slice(0);
		const lastVisibleIdx = count - 1;

		// If the selected tab is further to the right of the last tab we have room for, move it to the last visible position
		if (selectedTabIdx > lastVisibleIdx) {
			newTabList.splice(selectedTabIdx, 1); // delete the activeTab from newTabList
			newTabList.splice(lastVisibleIdx, 0, selectedTab); // insert activeTab at lastVisibleIdx
		}

		visibleList = newTabList.slice(0, lastVisibleIdx + 1);
		overflowList = newTabList
			.slice(lastVisibleIdx + 1)
			.map((it, i) => ({ ...it, itemType: 'tab', itemOrder: i }));
	}

	visibleList = visibleList.map((t, i) => {
		const style = getTabStyle(t);
		return { ...t, style, tabOrder: i };
	});

	function getTabStyle(t) {
		if (t.isDetailsTab) return { width: `${maxTabWidth}px` };

		if (t.isPrimaryTab)
			return {
				minWidth: `${minTabWidthL1Item}px`,
				maxWidth: `${maxTabWidthL1Item}px`
			};

		if (t.tab_id === selectedTabId) return { width: `${maxTabWidth}px` };

		return { width: `${tabWidth}px` };
	}

	return { visibleList: visibleList, overflowList };
}

const _getChildElement = (parent, childTag) => {
	if (!parent || parent.childElementCount === 0) return undefined;

	return Array.from(parent.children).find((e) => e && e.tagName === childTag);
};

export const insertTooltipEl = (node, content, predicate = () => true) => {
	if (!node || !content) return;

	if (predicate.call(null) === false) return;

	const nowTooltip = document.createElement('now-tooltip');
	nowTooltip.targetRef = node;
	nowTooltip.content = content;

	const toolTip = _getChildElement(node, 'NOW-TOOLTIP');
	if (toolTip) return;

	node.appendChild(nowTooltip);
};

export const removeTooltipEl = (node) => {
	if (!node) return;
	const isHtmlElm = node instanceof HTMLElement;
	if (!isHtmlElm) return;

	let nowTooltip = _getChildElement(node, 'NOW-TOOLTIP');
	if (!nowTooltip) return;

	node.removeChild(nowTooltip);
};
