const OptionItem = (props) => {
	const { item, isHighlighted, _className } = props;
	const { disabled, displayValue } = item;

	const _renderDisabledItem = () => {
		const { item, index } = props;
		const { name, displayValue } = item;
		return (
			<li
				key={`${name}_${index}`}
				className={_className + '-list-item is-disabled'}
				role="presentation">
				<button
					type="button"
					className={_className + '-list-item-button'}
					disabled>
					<span className={_className + '-text'}>{displayValue}</span>
				</button>
			</li>
		);
	};

	const _onItemClick = (e) => {
		const { item, index, onItemClick } = props;
		onItemClick(item, index, e);
	};

	const _onKeyup = (e) => {
		if (e.keyCode !== 13 /*enter*/ && e.keyCode !== 32 /*space*/) return;
		_onItemClick(e);
	};

	return disabled ? (
		_renderDisabledItem()
	) : (
		<li className={_className + '-list-item'} role="presentation">
			<button
				type="button"
				className={_className + '-list-item-button'}
				role="menuitem"
				tabIndex="-1"
				on-click={_onItemClick}
				on-keyup={_onKeyup}
				hook-update={({ elm }) => elm && isHighlighted && elm.focus()}>
				<span className={_className + '-text'}>{displayValue}</span>
			</button>
		</li>
	);
};

export { OptionItem };
