import isNumber from 'lodash/isNumber';

const MINUTES = 60 * 1000;
const DAY = 24 * 60 * 60 * 1000;

export function parseSystemTime(timestamp, timeZoneOffset) {
	let dateParsed = new Date(timestamp);

	// Safari and IE doesn't recognize the date format yyyy-mm-dd hh:mm:ss
	if (isNaN(dateParsed)) {
		let time_arr = timestamp.replace(/-|:/g, ' ').split(' ');
		time_arr[1] = time_arr[1] - '0' - 1;
		dateParsed = new Date(...time_arr);
	}

	const currentOffset = -dateParsed.getTimezoneOffset() * MINUTES;
	let offset = 0;
	if (isNumber(timeZoneOffset)) {
		offset += currentOffset - timeZoneOffset;
	}

	// timestamp is system time
	offset += currentOffset;

	return dateParsed.getTime() + offset;
}

export function daysBetween(baseDate, actualDate) {
	const normalizedBaseDate = new Date(baseDate.getTime());
	normalizedBaseDate.setHours(0, 0, 0);
	const normalizedActualDate = new Date(actualDate.getTime());
	normalizedActualDate.setHours(0, 0, 0);

	const diff = normalizedBaseDate.getTime() - normalizedActualDate.getTime();
	return (diff >= 0 || -1) * Math.round(Math.abs(diff / DAY));
}
