import { get, isEmpty, cloneDeep, omit } from 'lodash';
import { t } from 'sn-translate';

import { getMaxTabLimit } from './tabsData.config';

let PRIMARY_TAB_MAP;
let CONTEXTUAL_TAB_MAP;

const TAB_TITLE_CACHE_BY_SCREEN = {};

export function initTabConfig(tabConfig) {
	if (!tabConfig) return;
	createPrimaryTabMap(tabConfig);
	createContextualTabMap(tabConfig);
}

export function createPrimaryTabMap(tabConfig) {
	if (!PRIMARY_TAB_MAP) PRIMARY_TAB_MAP = tabConfig.primaryRoute || {};

	return PRIMARY_TAB_MAP;
}

export function createContextualTabMap(tabConfig) {
	if (CONTEXTUAL_TAB_MAP) return CONTEXTUAL_TAB_MAP;

	CONTEXTUAL_TAB_MAP = {};
	if (tabConfig && tabConfig.contextual) {
		tabConfig.contextual.forEach((config) => {
			CONTEXTUAL_TAB_MAP[config] = config;
		});
	}
	return CONTEXTUAL_TAB_MAP;
}

export const getPrimaryTabMap = () => PRIMARY_TAB_MAP;
export const getContextualTabMap = () => CONTEXTUAL_TAB_MAP;

export const generateContentKey = (fields, route) => {
	return Object.keys(fields)
		.sort()
		.reduce((hash, key) => `${hash}~${key}~${fields[key]}`, route);
};

export const recoverContentFromKey = (key) => {
	if (!key) return;
	const parts = key.split('~');
	const route = parts[0];
	let fields = {};
	for (let i = 1; i < parts.length - 1; i = i + 2) {
		let j = i + 1;
		fields[parts[i]] = parts[j];
	}
	return { route, fields };
};

export function getContentKey(_selectedContent) {
	const selectedContent = cloneDeep(_selectedContent);
	let { route, fields, uid, multiInstField } = selectedContent;
	if (!fields) fields = {};

	if (multiInstField && fields[multiInstField]) {
		const parts = fields[multiInstField].split('_uid_');
		fields[multiInstField] = parts[0];
		if (parts.length > 1) uid = parts[1];
	}

	let key = generateContentKey(fields, route);
	if (uid) {
		key = `${key}${uid}`;
	}
	return key;
}

export function primaryRouteSelected(payload) {
	const primaryTabMap = getPrimaryTabMap();
	const { route } = payload;
	return primaryTabMap[route];
}

export function contextualRouteSelected(payload) {
	if (!payload) return;
	const contextualTabMap = getContextualTabMap();
	return contextualTabMap[payload.route];
}

export const setTabTitleCacheByScreen = (screenId, title) =>
	(TAB_TITLE_CACHE_BY_SCREEN[screenId] = title);

export const getTabTitleCachedByScreen = (screenId) =>
	TAB_TITLE_CACHE_BY_SCREEN[screenId];

const maxTabsPrompt = {
	title: t('Max Tab Reached'),
	message: t(
		'You have reached max number of tabs you can have open at one time. Close tabs in order to open more.'
	),
	buttonTitle: t('Dismiss'),
	buttonAction: () => {}
};

export function getSiblings(tabsData, tab) {
	const { parentTabId } = tab;
	let siblings = [];
	if (parentTabId) {
		const parentTab = tabsData[parentTabId];
		const chilren = parentTab.tabList;
		// eslint-disable-next-line no-unused-vars
		const [tab_id, ...restChildren] = chilren;
		if (restChildren && restChildren.length > 0) {
			siblings = restChildren.map((tabId) => tabsData[tabId]);
		}
	}
	return siblings;
}

function propagateFlag(tabsData, tab, flag, flagValue) {
	let { parentTabId } = tab;
	let value = flagValue;
	while (parentTabId) {
		const parentTab = tabsData[parentTabId];
		if (value === true) {
			if (parentTab[flag] === true) break;
			else parentTab[flag] = value;
		} else if (value === false) {
			parentTab[flag] = parentTab.tabList.some(
				(t) => tabsData[t][flag] === true
			);
		}
		parentTabId = parentTab.parentTabId;
		value = parentTab[flag];
	}
	return tabsData;
}
//passing no filterFn will apply obj to all tabs
export function applyToAllTabs(
	tabsData,
	objToApply = {},
	filterFn = () => true
) {
	const flagsArr = ['isDirty', 'hasError', 'hasUpdate', 'hasWarning'];
	const newTabsData = { ...tabsData };
	for (let [tabId, tab] of Object.entries(newTabsData)) {
		if (!filterFn || (filterFn && filterFn(tab))) {
			newTabsData[tabId] = { ...tab, ...objToApply };

			flagsArr.forEach((flag) => {
				if (objToApply.hasOwnProperty(flag) && tab[flag] !== objToApply[flag]) {
					propagateFlag(
						newTabsData,
						newTabsData[tabId],
						flag,
						objToApply[flag]
					);
				}
			});
		}
	}
	return newTabsData;
}

export function getTabById(tabList, tab_id) {
	return tabList ? tabList.find((t) => t.tab_id === tab_id) : null;
}

export function getTabIndexById(tabList, tabId) {
	return tabList.findIndex((t) => t.tab_id === tabId);
}

export function isDetailTab(tab, parentTab) {
	const { contentKey } = tab;
	const { contentKey: parentContentKey } = parentTab;

	return contentKey === parentContentKey;
}

/** returns a tabStopper object if nextTabsData includes some condition that should prevent it from being accepted
 *  currently, the only condition is too many open tabs, which includes separate limits for main tabs and sub-tabs.
 *  This is set up to allow some versatility with the stopper object that gets returned, but currently there is only one.
 *  It would make sense, for exampke, to have different stopper objects for the main tab limit and th sub tab limit.
 */
export function shouldUpdateTabData(preTabsData, nextTabsData, tabsConfig) {
	function isReachingTabsLimit(preTabsData, nextTabsData) {
		let reached = false;

		// Override tab limit if active tab says so
		if (get(nextActiveTab, 'additional_data.overrideTabLimit', false))
			return reached;

		const preTabList = preTabsData ? Object.values(preTabsData) : null;
		const nextTabList = Object.values(nextTabsData);

		const { mainLimit, subLimit } = getMaxTabLimit(tabsConfig);
		const preMainTabCount = countMainTabs(preTabList);
		const nextMainTabCount = countMainTabs(nextTabList);
		if (nextMainTabCount > preMainTabCount)
			reached = nextMainTabCount > mainLimit;

		if (!reached) {
			const nextSubTabCount = countSubTabs(nextTabList, nextMainTabCount);
			reached = nextSubTabCount > subLimit;
		}
		return reached;
	}

	if (!nextTabsData) return null; // if there is no new data to comapre against, return null
	const nextActiveTab = getSelectedTab(nextTabsData);
	let stopperPrompt = null;
	if (isReachingTabsLimit(preTabsData, nextTabsData)) {
		stopperPrompt = maxTabsPrompt;
	}

	return stopperPrompt;
}

function countMainTabs(tabList) {
	if (isEmpty(tabList)) return 0;
	// a main tab has a parent tab of root
	return tabList.filter((x) => x.parentTabId === 'root').length;
}

function countSubTabs(tabList, mainTabCount) {
	if (isEmpty(tabList)) return 0;
	if (!mainTabCount) mainTabCount = countMainTabs(tabList);
	return tabList.length - mainTabCount - 1; // subtract 1 for "root" which is in the tablist, but should be ignored because it is virtual
}

export function getSelectedTab(tabsData) {
	const { tabList, selectedTabId } = tabsData;
	return getTabById(tabList, selectedTabId);
}

export function clearUpdateFlag(tab) {
	// No update flag to clear
	if (!tab.hasUpdate) return tab;

	// If no children, clear flag
	const { tabList = [] } = tab;
	if (tabList.length === 0) return { ...tab, hasUpdate: false };

	// If a child tab has an update, don't clear flag
	if (tabList.some((t) => t.hasUpdate)) return tab;

	// Clear flag
	return { ...tab, hasUpdate: false };
}

export function updateSelectedTabs(tabsData) {
	if (!tabsData) return; // pre-condition, no work to do if tabsData is null
	const clearupdateFlagReducer = (t) => clearUpdateFlag(t);

	return applyToSelectedTabs(tabsData, clearupdateFlagReducer);
}

/**
 * Helper function applies a function to every selected tab of the tree, mutating the parent as necessary
 * @param tab
 * @param fn
 * @returns {*}
 */
export function applyToSelectedTabs(tabsData, tabReducer) {
	let newTabsData = { ...tabsData };
	const root = tabsData.root;
	let { selectedTabId } = root;

	while (selectedTabId != '-1') {
		const currNode = tabsData[selectedTabId];
		newTabsData[selectedTabId] = tabReducer(newTabsData[selectedTabId]);
		selectedTabId = currNode.selectedTabId;
	}

	return newTabsData;
}

export function getNavItemSelectedPayload(tabsData) {
	if (!tabsData) return;
	let navItemSelectedPayload;
	const root = tabsData.root;
	let { selectedTabId } = root;

	let i = 0;
	while (selectedTabId != '-1') {
		const currNode = tabsData[selectedTabId];
		const { selectedContent, isDetailsTab } = currNode;

		// adding title field in navItemSelectedPayload for dynamicly changing the browser tab title
		let { title } = currNode;
		if (isDetailsTab) {
			title = tabsData[currNode.parentTabId].title;
		}

		if (!navItemSelectedPayload) {
			if (isDetailsTab !== true)
				navItemSelectedPayload = { ...selectedContent, title };
		} else {
			if (isDetailsTab === true) {
				navItemSelectedPayload = { ...selectedContent, title };
			} else {
				navItemSelectedPayload = {
					...selectedContent,
					title,
					parent: { ...navItemSelectedPayload }
				};
			}
		}
		selectedTabId = currNode.selectedTabId;
		i++;
	}

	if (i == 1 && navItemSelectedPayload.drillDowns) {
		let drillDownPayload;
		let { drillDowns: drillDownsArr, ...rest } = navItemSelectedPayload;
		for (let j = drillDownsArr.length - 1; j >= 0; j--) {
			drillDownPayload = {
				...drillDownsArr[j],
				...(drillDownPayload && { parent: { ...drillDownPayload } })
			};
		}
		if (drillDownPayload) {
			navItemSelectedPayload = { ...drillDownPayload, parent: { ...rest } };
		}
	}
	const {
		isMultiInstFieldValUpdated,
		..._navItemSelectedPayload
	} = navItemSelectedPayload;
	if (isMultiInstFieldValUpdated) {
		navItemSelectedPayload = { ..._navItemSelectedPayload, redirect: true };
	}
	return navItemSelectedPayload;
}

export function getDescendantTabIds(tabsData, tabId) {
	if (!tabsData || !tabsData[tabId]) return [];
	const descendants = [];
	let i = 0;
	while ((descendants.length == 0 && i == 0) || i < descendants.length) {
		const id = i == 0 ? tabId : descendants[i];
		const tab = tabsData[id];
		descendants.push(...tab.tabList);
		i++;
	}
	return descendants;
}

export function getAllLeafNodes(tabsData) {
	return Object.values(tabsData).filter((tab) => tab.tabList.length === 0);
}

export function getActiveLeafTabId(tabsData) {
	let currNode = tabsData.root;
	let { selectedTabId } = currNode;
	while (selectedTabId != '-1') {
		currNode = tabsData[selectedTabId];
		selectedTabId = currNode.selectedTabId;
	}
	return currNode && currNode.tab_id !== 'root' ? currNode.tab_id : null;
}

export function clearAllFlags(tabsData) {
	let newTabsData = {};
	const flagsArr = ['isDirty', 'hasError', 'hasUpdate', 'hasWarning'];
	for (let [tabId, tab] of Object.entries(tabsData)) {
		newTabsData[tabId] = omit(tab, flagsArr);
	}
	return newTabsData;
}

export function getActiveSessionTab(tabsData) {
	if (!tabsData) return null;
	let { selectedTabId } = tabsData.root;
	if (selectedTabId) return tabsData[selectedTabId];
	return null;
}

function _buildContentKeySet(tabsData) {
	const contentKeySet = new Set();
	Object.values(tabsData).forEach((tab) => {
		const { contentKey, selectedContent } = tab;
		if (tab.isPrimaryTab !== true) contentKeySet.add(contentKey);

		//check drilldowns
		const { drillDowns } = selectedContent || {};
		if (drillDowns && Array.isArray(drillDowns)) {
			drillDowns.forEach((drilldown) => {
				const { route, fields } = drilldown;
				const key = generateContentKey(fields, route);
				contentKeySet.add(key);
			});
		}
	});
	return contentKeySet;
}

function findRemovedTabIds(oldTabIds = [], newTabIds = []) {
	const set = new Set(),
		_results = [];
	if (oldTabIds.length - newTabIds.length < 0) return _results;
	newTabIds.forEach((index) => set.add(index));
	oldTabIds.forEach((index) => {
		if (!set.has(index)) _results.unshift(index);
	});
	return _results;
}

const findParentKey = (tabs = [], tabId) => {
	if (!tabId || tabId.length === 0) return;
	const currentTab = tabs[tabId];
	while (currentTab.parentTabId) {
		if (currentTab.parentTabId === 'root') return currentTab.contentKey;
	}
};

const annotateRemovedSubTab = (tab = {}) => {
	const _tab = { ...tab };
	const isPrimaryTab = tab.isPrimaryTab;
	const isMainTab = tab.parentTabId === 'root';
	const withNoSubTabs = tab.tabList && tab.tabList.length === 0;
	const isCloseable = tab.canClose;
	const primaryOrMainTab = isPrimaryTab || isMainTab;
	const isLeafSubTab = !primaryOrMainTab && withNoSubTabs && isCloseable;
	if (isLeafSubTab)
		return {
			..._tab,
			isLeafTab: isLeafSubTab,
			annotation: { operation: 'closed' }
		};
	return _tab;
};

const _buildRemovedTabs = (tabsData, removedTabIds) => {
	return removedTabIds
		.map((tabId) => ({ ...tabsData[tabId] }))
		.map((t) => annotateRemovedSubTab(t))
		.map((t) => {
			if (t.isLeafTab) {
				const parentKey = findParentKey(tabsData, t.parentTabId);
				if (parentKey)
					return { ...t, annotation: { ...t.annotation, parentKey } };
				return t;
			}
			return t;
		})
		.map((t) => ({ [t.contentKey]: { ...t } }));
};

export function getRemovedTabs(tabsData, newTabsData) {
	let _tabsData = tabsData || {};
	let _newTabsData = newTabsData || {};
	if (Object.keys(_tabsData).length < Object.keys(_newTabsData).length) {
		return;
	}
	const removedTabIds = findRemovedTabIds(
		Object.keys(tabsData),
		Object.keys(newTabsData)
	);

	//check if any existing tab is pointing to same contentKey/screen
	const oldContentKeySet = _buildContentKeySet(_tabsData);
	const newContentKeySet = _buildContentKeySet(_newTabsData);
	const removedContentKeys = new Set();
	for (let contentKey of oldContentKeySet) {
		if (!newContentKeySet.has(contentKey)) {
			removedContentKeys.add(contentKey);
		}
	}

	const removedTabs = _buildRemovedTabs(tabsData, removedTabIds);

	return {
		removedContentKeys: [...removedContentKeys],
		removedTabs: [...removedTabs]
	};
}

export function getPrimaryTab(tabsData) {
	// eslint-disable-next-line no-unused-vars
	for (let [tabId, tab] of Object.entries(tabsData)) {
		if (tab && tab.isPrimaryTab === true && tab.parentTabId === 'root')
			return tab;
	}
}

export function getViewPortInfo(tabsData) {
	const {
		root: { selectedTabId }
	} = tabsData;
	const primaryTab = getPrimaryTab(tabsData);
	if (selectedTabId && primaryTab && selectedTabId === primaryTab.tab_id) {
		return primaryTab.viewportInfo;
	}
}

export function filterSelectedContent(selectedContent) {
	if (!selectedContent) return;
	const {
		// eslint-disable-next-line no-unused-vars
		__uxfCompositionElId,
		// eslint-disable-next-line no-unused-vars
		_timestamp,
		// eslint-disable-next-line no-unused-vars
		updateUrl,
		// eslint-disable-next-line no-unused-vars
		redirect,
		...rest
	} = selectedContent;
	return rest;
}

export function locateTabs(tabsData, selectedContent) {
	let tabNode = tabsData.root;
	const selectedContentStack = [];
	let _selectedContent = { ...selectedContent };
	while (_selectedContent) {
		// eslint-disable-next-line no-unused-vars
		const { parent, ...content } = _selectedContent;
		selectedContentStack.push(content);
		_selectedContent = _selectedContent.parent;
	}
	const tabs = [];
	while (selectedContentStack.length > 0 && tabNode) {
		const content = selectedContentStack.pop();
		const contentKey = getContentKey(content);
		const { childContentKeys } = tabNode;
		if (childContentKeys && childContentKeys[contentKey]) {
			const tabId = childContentKeys[contentKey];
			const tab = {
				...tabsData[tabId],
				selectedContent: {
					...tabsData[tabId].selectedContent,
					...content
				}
			};
			tabs.push(tab);
		} else {
			selectedContentStack.push(content);
			break;
		}
		tabNode = tabsData[childContentKeys[contentKey]];
	}

	return { selectedContentStack, tabs };
}

export function primaryTabContentUpdate(payload, tabsData) {
	const { primaryTabPropData } = payload;
	const primaryTab = getPrimaryTab(tabsData);
	const { contentKey, selectedContent } = primaryTab;
	const { viewportInfo, routeInfo, title, icon, id } = primaryTabPropData;

	if (!isEmpty(viewportInfo)) {
		// viewportInfo
		return {
			selectedTabId: get(tabsData, 'root.tabList')[0],
			contentKey: get(routeInfo, 'route'),
			selectedContent: {
				id,
				icon,
				label: title,
				route: get(routeInfo, 'route'),
				fields: get(routeInfo, 'fields', {}),
				params: get(routeInfo, 'params', {})
			}
		};
	} else {
		// no viewports involved
		return {
			rootSelectedTabId: get(tabsData, 'root.tabList')[0],
			contentKey,
			selectedContent
		};
	}
}

export const tabResolutionStrategyMap = {
	ACTIVE_OR_FIRST_SESSION_TAB: (tabsData) => {
		let _tab = tabsData[tabsData.root.selectedTabId];
		if (!_tab) return;
		if (_tab.isPrimaryTab) {
			_tab = tabsData.root.tabList
				.map((t) => tabsData[t])
				.find((t) => t.isPrimaryTab !== true);
		}
		return _tab ? _tab.tab_id : undefined;
	}
};
