import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snCanvasCornerDialogStyles from './_sn-canvas-corner-dialog.scss';
import { t } from 'sn-translate';
import '@servicenow/now-heading';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import positions from './positions';
import absolutePositions from './absolutePositions';
import {
	includes as _includes,
	keys as _keys,
	isEmpty as _isEmpty
} from 'lodash';

const { COMPONENT_CONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const _handleClose = (state, dispatch) => {
	let { beforeClose } = state.properties;
	if (beforeClose && typeof beforeClose === 'function') {
		let callbackFn = new Promise((resolve) => {
			return resolve(beforeClose());
		});
		callbackFn.then((res) => {
			res
				? dispatch('SN_CANVAS_CORNER_DIALOG#TOGGLE_OPENED', { opened: false })
				: null;
		});
	} else {
		dispatch('SN_CANVAS_CORNER_DIALOG#TOGGLE_OPENED', { opened: false });
	}
};

const snCanvasCornerDialog = (state, { dispatch }) => {
	const {
		opened,
		position,
		containerProps,
		hasCloseIcon,
		heading
	} = state.properties;

	return (
		<div
			className="sn-canvas-corner-dialog"
			hidden={!opened}
			style={{
				...containerProps,
				...absolutePositions[
					_includes(_keys(absolutePositions), position)
						? position
						: positions.BOTTOM_RIGHT
				],
				background: '#ffffff'
			}}>
			<header className="sn-canvas-corner-dialog-header">
				<slot name="header">
					<div className="header">
						<now-icon icon="lightbulb-outline" size="md" />
						<now-heading
							label={heading}
							level="3"
							variant="header-secondary"
							hasNoMargin
						/>
					</div>
				</slot>
				{hasCloseIcon ? (
					<div className="sn-canvas-corner-dialog-close-icon">
						<now-button
							icon-name="close-outline"
							variant="tertiary"
							size="sm"
							config-aria={{ 'aria-label': t('Close') }}
							bare
							on-click={(evt) => {
								_handleClose(state, dispatch);
								evt.stopPropagation();
							}}
						/>
					</div>
				) : null}
			</header>
			<div className="sn-canvas-corner-dialog-content">
				<slot name="content">
					<span>content goes here..</span>
				</slot>
			</div>
			<footer className="sn-canvas-corner-dialog-footer">
				<slot name="footer">
					<span>footer goes here..</span>
				</slot>
			</footer>
		</div>
	);
};

/**
 * Corner dialog aims at UI needs to display content at the configured corners of the viewport.
 *
 * @seismicElement sn-canvas-corner-dialog
 * @summary A corner dialog component to display visual announcements at the corners of the viewport
 */
createCustomElement('sn-canvas-corner-dialog', {
	properties: {
		/**
		 * Property opened sets the state of the corner dialog
		 * @type {Boolean} set state on corner dialog
		 */
		opened: { default: false, manageable: true },
		/**
		 * Property position to set the position of the corner dialog
		 * @type {('top-left'|'top-right'|'bottom-left'|'bottom-right')} Position - Possible values are:
		 * 	- top-left,
		 *  - top-right,
		 *  - bottom-left,
		 *  - bottom-right
		 */
		position: { default: positions.TOP_RIGHT, manageable: true },
		/**
		 * Property to set the styles for the corner dialog container
		 * @type {string} Style for container element
		 */
		containerProps: { default: null },
		/**
		 * Property sets the close icon to dismiss the corner dialog.
		 * @type {string} Icon in the header of corner dialog
		 */
		hasCloseIcon: { default: true },
		/**
		 * Property sets the heading for the corner dialog if not sent as slot.
		 * @type {string} Heading in the header of corner dialog
		 */
		heading: { default: 'Guided Action' },
		/**
		 * Property sets a callback function to handle before corner dialog is closed.
		 * @type {function} beforeClose A callback function called when user tries to close corner dialog
		 */
		beforeClose: { default: null }
	},
	renderer: {
		type: snabbdom,
		view: snCanvasCornerDialog
	},
	styles: snCanvasCornerDialogStyles,
	dispatches: {
		/**
		 * Dispatched when the corner dialog is opened/closed.
		 * @type {{}}
		 */
		'SN_CANVAS_CORNER_DIALOG#TOGGLE_OPENED': {}
	},
	actionHandlers: {
		[COMPONENT_CONNECTED]: {
			effect({ host }) {
				if (!_isEmpty(host)) {
					host.parentElement.style.position = 'relative';
				}
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: {
			effect({ state, dispatch }) {
				if (state.properties.opened) {
					dispatch('SN_CANVAS_CORNER_DIALOG#TOGGLE_OPENED', { opened: true });
				}
			}
		}
	}
});
