import '@servicenow/now-text-link';
import '@servicenow/now-button';
import { PUBLIC_ACTION_BUTTON_CLICKED } from '../../../constants/constants';

const handleLogin = (dispatch, value) => {
	dispatch(PUBLIC_ACTION_BUTTON_CLICKED, {
		publicActionRoute: value
	});
};

const PublicActions = ({ actions, dispatch }) => {
	// TODO: Should update this button to sn-nav-item once changes to login modal are finalized
	return (
		<ul className="sn-canvas-global-tools">
			{actions.map(({ label, bare, value }) => (
				<li className="sn-canvas-global-tools-list-item sn-canvas-global-tools-list-item-public-action">
					{bare ? (
						<button
							className="sn-canvas-global-tools-public-action -bare"
							on-click={() => handleLogin(dispatch, value)}>
							{label}
						</button>
					) : (
						<now-button
							className="sn-canvas-global-tools-public-action"
							label={label}
							size="md"
							variant="primary"
							append-to-payload={{ publicActionRoute: value }}
						/>
					)}
				</li>
			))}
		</ul>
	);
};

export default PublicActions;
