import { actionTypes } from '@servicenow/ui-core';
import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import get from 'lodash/get';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;

const RECORD_WATCHER_ACTIONS = {
	SUBSCRIBED_STARTED: 'RECORD_WATCHER_SUBSCRIBE_STARTED',
	SUBSCRIBED: 'RECORD_WATCHER_SUBSCRIBED',
	SUBSCRIBED_FAILED: 'RECORD_WATCHER_SUBSCRIBE_FAILED',
	UNSUBSCRIBED: 'RECORD_WATCHER_UNSUBSCRIBED',
	ITEM_CHANGED: 'RECORD_WATCHER_ITEM_CHANGED',
	SUBSCRIPTION_CHANGED: 'RECORD_WATCHER_SUBSCRIPTION_CHANGED'
};

const AGGREGATE_COUNT_ACTIONS = {
	FETCH: 'AGGREGATE_COUNT_FETCH',
	STARTED: 'AGGREGATE_COUNT_STARTED',
	SUCCEEDED: 'AGGREGATE_COUNT_SUCCEEDED',
	FAILED: 'AGGREGATE_COUNT_FAILED'
};

const aggregateCountQuery = `query($table: String!, $filter: String!) {
    GlideAggregate_Query(tableName: $table, queryConditions: $filter) {
      count
    }
  }`;

const fetchAggregateCountEffect = createGraphQLEffect(aggregateCountQuery, {
	variableList: ['table', 'filter'],
	startActionType: AGGREGATE_COUNT_ACTIONS.STARTED,
	successActionType: AGGREGATE_COUNT_ACTIONS.SUCCEEDED,
	errorActionType: AGGREGATE_COUNT_ACTIONS.FAILED
});

const recordWatcherHandler = createAmbSubscriptionEffect(
	'/rw/count/:table/:filter',
	{
		subscribeStartedActionType: RECORD_WATCHER_ACTIONS.SUBSCRIBE_STARTED,
		subscribeSucceededActionType: RECORD_WATCHER_ACTIONS.SUBSCRIBED,
		subscribeFailedActionType: RECORD_WATCHER_ACTIONS.SUBSCRIBE_FAILED,
		unsubscribeSucceededActionType: RECORD_WATCHER_ACTIONS.UNSUBSCRIBED,
		messageReceivedActionType: RECORD_WATCHER_ACTIONS.ITEM_CHANGED
	}
);

const getSubscriptionArgs = (table, filter, subscribe) => ({
	subscribe,
	table,
	filter: btoa(filter).replace(/=/g, '-')
});

const lifeCycleActionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch }) => {
		const { configurations, type } = properties;
		switch (type) {
			case 'record': {
				const { table, filter } = configurations;
				if (table && filter) {
					// Fetch initial data
					dispatch(AGGREGATE_COUNT_ACTIONS.FETCH, {
						table,
						filter
					});

					// Subscribe to changes
					dispatch(
						RECORD_WATCHER_ACTIONS.SUBSCRIPTION_CHANGED,
						getSubscriptionArgs(table, filter, true)
					);
				}
				break;
			}
		}
	},
	[COMPONENT_DISCONNECTED]: ({ properties, dispatch }) => {
		const { configurations } = properties;
		const { table, filter } = configurations;
		if (table && filter) {
			// unsubscribe to changes
			dispatch(
				RECORD_WATCHER_ACTIONS.SUBSCRIPTION_CHANGED,
				getSubscriptionArgs(table, filter, false)
			);
		}
	}
};

const aggregateCountActionHandlers = {
	[AGGREGATE_COUNT_ACTIONS.FETCH]: fetchAggregateCountEffect,
	[AGGREGATE_COUNT_ACTIONS.SUCCEEDED]: ({ action, updateProperties }) => {
		const count = get(action, 'payload.data.GlideAggregate_Query[0].count', 0);
		updateProperties({ count });
	},
	[AGGREGATE_COUNT_ACTIONS.FAILED]: ({ properties, updateProperties }) => {
		const { table, filter } = properties;
		// eslint-disable-next-line no-console
		console.error(
			`Failed to fetch aggregate counts with for table, ${table} with filter, ${filter}`
		);
		updateProperties({ count: 0 });
	}
};

const recordWatcherActionHandlers = {
	[RECORD_WATCHER_ACTIONS.SUBSCRIPTION_CHANGED]: recordWatcherHandler,
	[RECORD_WATCHER_ACTIONS.ITEM_CHANGED]: ({
		action,
		properties,
		updateProperties
	}) => {
		const countUpdate = get(action, 'payload.data.count', '').split('');
		const { count } = properties;
		if (countUpdate[0] === '+') {
			updateProperties({ count: count + parseInt(countUpdate[1]) });
		} else if (countUpdate[0] === '-') {
			updateProperties({ count: count - parseInt(countUpdate[1]) });
		}
	}
};

export const badgeDataProvider = {
	name: 'BADGE_DATA_PROVIDER',
	properties: {
		configurations: {
			default: {}
		},
		type: {
			default: 'record'
		}
	},
	actions: {
		[AGGREGATE_COUNT_ACTIONS.FETCH]: {
			private: true
		},
		[AGGREGATE_COUNT_ACTIONS.STARTED]: {
			private: true
		},
		[AGGREGATE_COUNT_ACTIONS.SUCCEEDED]: {
			private: true
		},
		[AGGREGATE_COUNT_ACTIONS.FAILED]: {
			private: true
		},
		[RECORD_WATCHER_ACTIONS.SUBSCRIBED_STARTED]: {
			private: true
		},
		[RECORD_WATCHER_ACTIONS.SUBSCRIBED]: {
			private: true
		},
		[RECORD_WATCHER_ACTIONS.SUBSCRIBED_FAILED]: {
			private: true
		},
		[RECORD_WATCHER_ACTIONS.UNSUBSCRIBED]: {
			private: true
		},
		[RECORD_WATCHER_ACTIONS.ITEM_CHANGED]: {
			private: true
		},
		[RECORD_WATCHER_ACTIONS.SUBSCRIPTION_CHANGED]: {
			private: true
		}
	},
	actionHandlers: {
		...aggregateCountActionHandlers,
		...recordWatcherActionHandlers,
		...lifeCycleActionHandlers
	}
};
