import { debounce, noop } from 'lodash';
import * as Constants from '../constants';

const onArrowUp = (currentIndex, optionItems) => {
	let _current = currentIndex,
		newIndex = -1;
	if (currentIndex <= 0) {
		_current = optionItems.length - 1;
		newIndex = _current;
	} else {
		_current = _current - 1;
		newIndex = _current % optionItems.length;
	}

	while (optionItems[newIndex].disabled) {
		_current = _current - 1;
		newIndex = _current % optionItems.length;
		if (_current === -1) return optionItems.length - 1;
	}
	return newIndex;
};

const onArrowDown = (currentIndex, optionItems) => {
	let _current = currentIndex,
		newIndex = -1;
	if (currentIndex === -1 || currentIndex === optionItems.length - 1) {
		_current = 0;
		newIndex = 0;
	} else {
		_current = _current + 1;
		newIndex = _current % optionItems.length;
	}
	while (optionItems[newIndex].disabled) {
		_current = _current + 1;
		newIndex = _current % optionItems.length;
		if (_current === optionItems.length) return 0;
	}
	return newIndex;
};

const handlerMap = {
	arrowUp: {
		fn: onArrowUp,
		action: Constants.POPOVER_HIGHLIGHT_INDEX_CHANGED
	},
	arrowDown: {
		fn: onArrowDown,
		action: Constants.POPOVER_HIGHLIGHT_INDEX_CHANGED
	},
	execute: {
		fn: noop,
		action: Constants.POPOVER_CONTENT_CLICKED
	},
	reset: {
		fn: noop,
		action: Constants.POPOVER_HIGHLIGHT_INDEX_RESET_REQUESTED
	}
};

const _handleKeyPressed = (event, dispatch) => {
	let handler = undefined;
	switch (event.keyCode) {
		case 38: //ArrowUp
			handler = 'arrowUp';
			break;
		case 40: //ArrowDown
			handler = 'arrowDown';
			break;
		case 13: //Enter
		case 32: //Space
			handler = 'execute';
			break;
		case 9: // Tab
			handler = 'reset';
			break;
		default:
			break;
	}
	if (handler && handlerMap[handler])
		dispatch(handlerMap[handler].action, { handler: handlerMap[handler].fn });
};

export const handleKeyPressed = debounce(_handleKeyPressed, 150);
