import { actionTypes } from '@servicenow/ui-core';
import draggableEffect from './draggableEffect';

const {
	bindDraggableEvents,
	unbindDraggableEvents,
	clearStyles
} = draggableEffect.effectHandlers;

const {
	COMPONENT_DOM_READY,
	COMPONENT_DISCONNECTED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

export const draggableBehavior = {
	name: 'draggableBehavior',
	initialState: {
		targetRef: null,
		parentTargetRef: null
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: bindDraggableEvents,
		[COMPONENT_DISCONNECTED]: unbindDraggableEvents,
		[COMPONENT_PROPERTY_CHANGED]: clearStyles
	}
};
