import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import has from 'lodash/has';
import size from 'lodash/size';
import memoize from 'lodash/memoize';
import { t } from 'sn-translate';

import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-icon';
import '@servicenow/now-badge';
import '@servicenow/now-avatar';
import rtlBehavior from '@servicenow/behavior-rtl';

import {
	NAV_ITEM_SELECTED,
	VIEWPORT_MODAL_SELECTED,
	PUBLIC_ACTION_BUTTON_CLICKED,
	NOW_BUTTON_CLICKED,
	ROUTE_LINK_TYPE,
	EXTERNAL_LINK_TYPE,
	VIEWPORT_MODAL_TYPE
} from '../../constants/constants';
import snCanvasHeaderStyles from './_sn-canvas-header.scss';
import { sizeSchema, experienceSchema, configurationsSchema } from './schemas';
import GlobalTools from './views/global-tools';
import PublicActions from './views/public-actions';
import { getLogoSource } from '../../utils/logo-source-util';
import GlobalResizeListener from '../../utils/global-resize-listener';

const { COMPONENT_DISCONNECTED, COMPONENT_DOM_TREE_READY } = actionTypes;

const evaluateCurrentScreenConfigurationLink = (link, configurations) => {
	if (isEmpty(configurations) || isEmpty(link)) return {};
	const { position } = configurations;
	return { link, position };
};

const publicActionEffect = ({ dispatch, action: { payload } }) => {
	const { publicActionRoute } = payload;
	const { type, value } = publicActionRoute;
	if (type === ROUTE_LINK_TYPE) {
		dispatch(NAV_ITEM_SELECTED, value);
	} else if (type === VIEWPORT_MODAL_TYPE) {
		dispatch(VIEWPORT_MODAL_SELECTED, value);
	} else if (type === EXTERNAL_LINK_TYPE) {
		window.location.href = publicActionRoute;
	}
};

// Compute rendered sizes of header elements to aid resizing SecondaryItems.
const getHeaderSizingInfo = (host) => {
	const getSecondaryItems = (x, ind) => {
		const childWidth = x.getBoundingClientRect().width + itemMargin;
		return { itemOrder: ind, width: childWidth };
	};

	const hostInfo = getComputedStyle(host);
	const itemMargin = parseInt(
		hostInfo.getPropertyValue('--sn-canvas-global-tools-list-item-margin')
	);
	const searchMinWidth = parseInt(
		hostInfo.getPropertyValue('--sn-canvas-global-tools-search-min-width')
	);
	const brandLogoWidth = host.shadowRoot
		.querySelector('.sn-canvas-header-nav-brand-logo')
		.getBoundingClientRect().width;
	const primaryItemsWidth = host.shadowRoot
		.querySelector('.sn-canvas-global-tools-primary-items')
		.getBoundingClientRect().width;
	const globalToolsSecondaryItemsContainer = host.shadowRoot.querySelector(
		'.sn-canvas-global-tools-secondary-items'
	);
	let secondaryItemsSize = Array.from(
		globalToolsSecondaryItemsContainer
			? globalToolsSecondaryItemsContainer.children
			: [],
		getSecondaryItems
	);
	return {
		itemMargin,
		brandLogoWidth,
		searchMinWidth,
		primaryItemsWidth,
		secondaryItemsSize
	};
};

const snCanvasHeader = (state, { dispatch, updateState }) => {
	const { properties, showUserMenuTooltip, userMenuRef } = state;
	const {
		authenticated,
		size,
		experience,
		configurations,
		currentRouteKey,
		logoAltText,
		logoSource,
		userAvatar,
		userName,
		screenConfigurationLink,
		notificationsEnabled = true,
		edgeToEdge,
		userPrefs,
		showBottomBorder
	} = properties;

	const { privatePage, publicPage } = configurations[currentRouteKey];

	const {
		userPrefsEnabled,
		searchEnabled: privateSearchEnabled,
		currentScreenLinkConfiguration = {},
		globalTools
	} = privatePage;

	const {
		actionButtons,
		searchEnabled: publicSearchEnabled,
		menuEnabled,
		logoRoute = {}
	} = publicPage;

	const currentConfigurationLinkData = evaluateCurrentScreenConfigurationLink(
		screenConfigurationLink,
		currentScreenLinkConfiguration
	);
	const evaluatedLogoSource = getLogoSource(logoSource, 'header_logo');
	const searchEnabled = authenticated
		? privateSearchEnabled
		: publicSearchEnabled;

	const HeaderLogo = ({ size }) => {
		if (!evaluatedLogoSource) {
			return null;
		}
		return isEmpty(logoRoute) ? (
			<img
				class={{
					'sn-canvas-logo': true,
					[`--${size}`]: true
				}}
				alt={logoAltText}
				src={evaluatedLogoSource}
			/>
		) : (
			<sn-nav-item
				variant="simple-link-nav"
				type={logoRoute.type}
				itemValue={logoRoute.value}>
				<img
					slot="startsWith"
					class={{
						'sn-canvas-logo': true,
						[`--${size}`]: true
					}}
					alt={logoAltText}
					src={evaluatedLogoSource}
				/>
			</sn-nav-item>
		);
	};

	return (
		<header
			class={{
				'sn-canvas-header': true,
				[`--${experience}`]: experience === 'workspace'
			}}
			role="banner"
			aria-label={t('Header')}>
			<div
				class={{
					'sn-canvas-header-container': true,
					[`--${size}`]: true,
					'show-bottom-border': showBottomBorder
				}}>
				<div
					class={{
						'sn-canvas-header-nav': true,
						'-fixed-center-aligned': !edgeToEdge
					}}>
					<div className="sn-canvas-header-nav-brand-logo">
						<HeaderLogo size={size} />
					</div>
					<div className="sn-canvas-header-nav-global">
						<div className="sn-canvas-header-nav-global-search">
							{searchEnabled ? <slot name="search-input" /> : null}
						</div>
						<div
							class={{
								'sn-canvas-header-nav-global-tools': true
							}}>
							<nav
								className={`sn-canvas-global-tools`}
								aria-label={t('Header')}>
								{authenticated ? (
									<GlobalTools
										screenConfigurationLink={currentConfigurationLinkData}
										globalToolsItems={{
											...globalTools,
											...state.globalToolsSizingInfo
										}}
										notificationsEnabled={notificationsEnabled}
										userPreferencesEnabled={userPrefsEnabled}
										userName={userName}
										userAvatar={userAvatar}
										userPrefs={userPrefs}
										experience={experience}
										showUserMenuTooltip={showUserMenuTooltip}
										userMenuRef={userMenuRef}
										updateState={updateState}
									/>
								) : (
									<PublicActions dispatch={dispatch} actions={actionButtons} />
								)}
							</nav>
						</div>
					</div>
				</div>
			</div>
			{menuEnabled ? <slot name="navigation-menu" /> : null}
		</header>
	);
};

createCustomElement('sn-canvas-header', {
	initialState: {
		showUserMenuTooltip: false,
		userMenuRef: null
	},
	properties: {
		// control global tools secondary items responsiveness: temporary switch
		// since responsiveness is temporarily depriortized
		allowResizeBehavior: {
			default: false
		},
		currentRouteKey: {
			computed: ({ properties: { currentRoute, configurations } }) => {
				if (isEmpty(currentRoute)) return 'global';

				const getFieldCount = size(currentRoute.fields);
				const routeKey = currentRoute.route
					? `${currentRoute.route}~${getFieldCount}`
					: 'global';

				const result = has(configurations, routeKey) ? routeKey : 'global';
				return result;
			},
			readOnly: true,
			schema: { type: 'string' }
		},
		userPrefs: { default: {}, schema: { type: 'object' } },
		authenticated: {
			default: false,
			schema: { type: 'boolean' }
		},
		notificationsEnabled: {
			default: true,
			schema: { type: 'boolean' }
		},
		experience: {
			default: 'default',
			schema: experienceSchema
		},
		size: {
			default: 'short',
			schema: sizeSchema
		},
		configurations: {
			default: {
				global: {
					privatePage: {
						searchEnabled: false,
						userPrefsEnabled: false,
						globalTools: {}
					},
					publicPage: {
						searchEnabled: false,
						actionButtons: []
					}
				}
			},
			schema: configurationsSchema
		},
		screenConfigurationLink: {
			default: {}
		},
		currentRoute: {
			default: {},
			schema: { type: 'object' }
		},
		logoAltText: {
			default: 'Logo configuration',
			schema: { type: 'string' }
		},
		logoSource: {
			default: '',
			schema: { type: 'string' }
		},
		userAvatar: {
			default: '',
			schema: { type: 'string' }
		},
		userName: {
			default: '',
			schema: { type: 'string' }
		},
		edgeToEdge: {
			default: false,
			schema: { type: 'boolean' }
		}
	},
	actionHandlers: {
		[PUBLIC_ACTION_BUTTON_CLICKED]: {
			effect: publicActionEffect
		},
		[NOW_BUTTON_CLICKED]: {
			effect: publicActionEffect
		},
		[COMPONENT_DOM_TREE_READY]: ({ host, properties, updateState }) => {
			const { allowResizeBehavior } = properties;
			if (allowResizeBehavior) {
				const headerSizingInfo = memoize(getHeaderSizingInfo);

				GlobalResizeListener.add(
					host,
					() => {
						const headerWidth = host.getBoundingClientRect().width;
						const savedGTWidths = headerSizingInfo(host);

						const nonSecondaryItemContainers =
							get(savedGTWidths, 'brandLogoWidth', 0) +
							get(savedGTWidths, 'searchMinWidth', 0) +
							get(savedGTWidths, 'primaryItemsWidth', 0) +
							get(savedGTWidths, 'itemMargin', 0);
						const secondaryItemContainerWidth =
							headerWidth - nonSecondaryItemContainers;
						const { secondaryItemsSize } = savedGTWidths;

						updateState({
							globalToolsSizingInfo: {
								secondaryItemContainerWidth,
								secondaryItemsSize
							}
						});
					},
					true
				);
			}
		},
		[COMPONENT_DISCONNECTED]: ({ host }) => {
			GlobalResizeListener.remove(host);
		}
	},
	behaviors: [rtlBehavior],
	renderer: {
		type: snabbdom,
		view: snCanvasHeader
	},
	styles: snCanvasHeaderStyles,
	slots: {
		notificationTrigger: {}
	}
});
