import { forEach as _forEach, forOwn as _forOwn, get as _get } from 'lodash';

/**
 * getFieldValueFromViewportInfo
 * @param viewportInfo
 * @param field
 * @return {*}
 */
export const getFieldValueFromViewportInfo = (viewportInfo, field) => {
	const { fields } = viewportInfo;
	return fields[field];
};

/**
 * getFieldsFromViewportRoutes
 * @param viewportRoutes
 * @param properties
 * @return {{}}
 */
export const getFieldsFromViewportRoutes = (viewportRoutes, properties) => {
	const {
		options: { viewportInfo }
	} = properties;
	let newFields = {};
	_forOwn(viewportRoutes, (viewportRouteConfig, route) => {
		if (route === _get(viewportInfo, 'route')) {
			const fields = _get(
				viewportRouteConfig.find((rc) => rc.routeType === route),
				'fields'
			);
			newFields = {};
			_forEach(fields, (f) => {
				if (f === 'opened') {
					newFields[f] = viewportOpened(viewportInfo);
				} else {
					newFields[f] = !properties[f]
						? getFieldValueFromViewportInfo(viewportInfo, f)
						: properties[f];
				}
			});
		}
	});

	return newFields;
};

/**
 * getParamsFromViewportRoutes
 * @param viewportRoutes
 * @param properties
 * @return {{}}
 */
export const getParamsFromViewportRoutes = (viewportRoutes, properties) => {
	const {
		options: { viewportInfo }
	} = properties;
	let newParams = {};
	_forOwn(viewportRoutes, (viewportRouteConfig, route) => {
		if (route === _get(viewportInfo, 'route')) {
			const params = _get(
				viewportRouteConfig.find((rc) => rc.routeType === route),
				'optionalParameters'
			);
			newParams = {};
			_forEach(params, (p) => {
				newParams[p] = properties[p];
			});
		}
	});

	return newParams;
};

/**
 * getFields
 * @param viewportRoutes
 * @param viewportInfo
 * @return {[]}
 */
export const getFields = (viewportRoutes, viewportInfo) => {
	let fields = [];
	_forOwn(viewportRoutes, (viewportRouteConfig, route) => {
		if (route === _get(viewportInfo, 'route')) {
			fields = _get(
				viewportRouteConfig.find((rc) => rc.routeType === route),
				'fields',
				[]
			);
		}
	});
	return fields;
};

/**
 * getParams
 * @param viewportRoutes
 * @param viewportInfo
 * @return {[]}
 */
export const getParams = (viewportRoutes, viewportInfo) => {
	let params = [];
	_forOwn(viewportRoutes, (viewportRouteConfig, route) => {
		if (route === _get(viewportInfo, 'route')) {
			params = _get(
				viewportRouteConfig.find((rc) => rc.routeType === route),
				'optionalParameters',
				[]
			);
		}
	});
	return params;
};

/**
 *
 * @return {*}
 */
const currentRoute = () => {
	const activeButton = JSON.parse(
		window.sessionStorage.getItem('canvasToolbar')
	);
	return _get(activeButton, 'routeInfo.route', '');
};

/**
 * skipPreload
 * @param viewportInfo
 * @return {boolean}
 */
export const skipPreload = (viewportInfo) => {
	const route = currentRoute();
	return _get(viewportInfo, 'route', '').indexOf(route) !== -1;
};

/**
 * viewportOpened
 * @param viewportInfo
 * @returns {boolean}
 */
const viewportOpened = (viewportInfo) => {
	const currentActiveButton = JSON.parse(
		window.sessionStorage.getItem('canvasToolbar')
	);
	const route = _get(viewportInfo, 'route');
	let opened = false;
	if (route === _get(currentActiveButton, 'viewportInfo.route')) {
		opened = _get(currentActiveButton, 'viewportInfo.fields.opened');
	}
	return opened;
};
