import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, { createRef } from '@servicenow/ui-renderer-snabbdom';
import isEmpty from 'lodash/isEmpty';
import isEqual from 'lodash/isEqual';
import isNil from 'lodash/isNil';
import get from 'lodash/get';

import '@servicenow/now-icon';
import '@servicenow/now-popover';
import rtlBehavior from '@servicenow/behavior-rtl';

import '../sn-nav-item';
import '../internal/sn-canvas-menu-dropdown';
import '../internal/sn-canvas-mega-menu';

import { menuItemSchema } from './schemas';
import { menubarKeyBindingEventHandler } from './behaviors';
import snCanvasMenuStyles from './_sn-canvas-menu.scss';
import { NAV_ITEM_SELECTED } from '../../constants/constants';
import { t } from 'sn-translate';
/* eslint-disable no-console */
const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

const linkIDGenerator = (function*() {
	let linkID = 0;
	while (true) yield 'link_' + linkID++;
})();

const rootIDGenerator = (function*() {
	let rootID = 0;
	while (true) yield 'root_' + rootID++;
})();

const generateScreenKey = (fields, route) => {
	return Object.keys(fields)
		.sort()
		.reduce((hash, key) => `${hash}~${key}~${fields[key]}`, route);
};

const _shouldIncludeNegativeTabIndex = (idx) =>
	idx !== 0 ? { tabindex: '-1' } : {};

const _shouldRenderSingleLink = (root) => root.depth === 0;

const _normalizeArray = (value) => {
	if (isNil(value) || isEmpty(value)) return [];
	return Array.isArray(value) ? value : [value];
};

const _normalizeMenuItems = (menuItems, rootID) => {
	let stateMenuItems = {};
	let stateContentMap = {};
	let depth = 0;

	// collect roots;
	let queue = [
		{
			...menuItems,
			linkID: linkIDGenerator.next().value,
			parentID: null
		}
	];

	stateMenuItems['root'] = {
		parentID: null,
		childList: queue.map(({ linkID }) => linkID)
	};

	while (!isEmpty(queue)) {
		let size = queue.length;
		while (size--) {
			let current = queue.shift();

			// Get children, calculate the linkID for the children, then push into queue.
			// Store the linkID calculated into the children property of the object to store.
			let children = get(current, 'children.items', []);
			const isLeaf = isEmpty(children);
			if (!isLeaf) {
				children = children.map((item) => {
					return {
						...item,
						linkID: linkIDGenerator.next().value,
						parentID: current.linkID
					};
				});

				queue.push(...children);
			}

			const action = get(current, 'children.action', {});
			let actionLinkID = '';

			if (!isEmpty(action)) {
				actionLinkID = linkIDGenerator.next().value;
				const { fields = {}, route = '' } = action.value;
				const screenKey = generateScreenKey(fields, route);
				stateMenuItems[actionLinkID] = {
					...action,
					parentID: current.parentID
				};

				if (!stateContentMap.hasOwnProperty(screenKey)) {
					stateContentMap[screenKey] = {
						rootID,
						link: actionLinkID
					};
				}
			}

			if (isLeaf && current.value.type === 'route') {
				const { fields = {}, route = '' } = current.value.value;
				const screenKey = generateScreenKey(fields, route);
				stateContentMap[screenKey] = {
					rootID,
					link: current.linkID
				};

				if (!stateContentMap.hasOwnProperty(screenKey)) {
					stateContentMap[screenKey] = {
						rootID,
						link: actionLinkID
					};
				}
			}

			stateMenuItems[current.linkID] = {
				action: actionLinkID,
				childList: children.map(({ linkID }) => linkID),
				linkID: current.linkID,
				parentID: current.parentID,
				value: current.value
			};
		}
		depth++;
	}
	// Holds menu depth
	stateMenuItems['root'].depth = depth - 1;

	return {
		stateMenuItems,
		stateContentMap
	};
};

const _createNormalizedTree = (menuItems) => {
	let tree = {};
	let contentMap = {};

	menuItems.forEach((item) => {
		let rootID = rootIDGenerator.next().value;
		const { stateContentMap, stateMenuItems } = _normalizeMenuItems(
			item,
			rootID
		);
		tree[rootID] = {
			...stateMenuItems
		};

		// order matters here
		contentMap = {
			...stateContentMap,
			...contentMap
		};
	});

	return {
		contentMap,
		tree
	};
};

const snCanvasMenu = ({ menuItems, contentMap, properties }) => {
	const {
		items,
		menuWidth,
		verticalStackingEnabled,
		actionButtons,
		activeScreen,
		edgeToEdge
	} = properties;
	const normalizedActionButtons = _normalizeArray(actionButtons);
	const shouldRenderActionButtons = normalizedActionButtons.length > 0;
	const shouldRenderNavigationItems = items.length > 0;
	const selectedLinks = contentMap[activeScreen];
	const shouldRenderMenu =
		shouldRenderActionButtons || shouldRenderNavigationItems;
	return (
		<nav
			aria-label={t('Primary menu')}
			class={{
				'sn-canvas-menu': true,
				'-is-displayed': !shouldRenderMenu
			}}>
			<div
				class={{
					'sn-canvas-menu-nav': true,
					'-fixed-center-aligned': !edgeToEdge
				}}>
				{shouldRenderNavigationItems ? (
					<div className="sn-canvas-menu-nav-items">
						<ul className="sn-canvas-menu-nav-list" role="menubar">
							{Object.entries(menuItems).map(([rootID, item], idx) => {
								const positionTarget = createRef();

								const { root } = item;
								const linkID = get(root, 'childList[0]', '');
								const rootVal = get(item, `[${linkID}].value`, {});
								const isSelected = rootID === get(selectedLinks, 'rootID', '');

								if (_shouldRenderSingleLink(root)) {
									const {
										label,
										value,
										opensWindow = false,
										rightIcon = undefined,
										type = 'route'
									} = rootVal;
									return (
										<li
											className="sn-canvas-menu-nav-item"
											key={idx}
											role="none">
											<sn-nav-item
												is-selected={isSelected}
												data-id={idx}
												label={label}
												opens-window={opensWindow}
												tooltip-content={label}
												type={type}
												item-value={value}
												interaction-type="menubar"
												lines={1}
												variant="main-nav"
												{..._shouldIncludeNegativeTabIndex(idx)}>
												{rightIcon ? (
													<now-icon
														size="md"
														slot="endsWith"
														icon={rightIcon}
													/>
												) : null}
											</sn-nav-item>
										</li>
									);
								} else {
									const { label } = rootVal;

									const { [linkID]: newRoot, root, ...restOfLinks } = item;

									const newItem = {
										root: {
											action: newRoot.action,
											childList: newRoot.childList,
											depth: root.depth,
											parentID: newRoot.parentID,
											linkID: newRoot.linkID
										},
										...restOfLinks
									};

									return (
										<li
											className="sn-canvas-menu-nav-item"
											key={idx}
											ref={positionTarget}
											role="none">
											<sn-canvas-menu-dropdown
												is-selected={isSelected}
												selected-item={isSelected ? selectedLinks.link : null}
												data-id={idx}
												menu-items={newItem}
												menu-width={menuWidth}
												vertical-stacking-enabled={verticalStackingEnabled}
												label={label}
												position-target={positionTarget}
												{..._shouldIncludeNegativeTabIndex(idx)}
											/>
										</li>
									);
								}
							})}
						</ul>
					</div>
				) : null}
				{shouldRenderActionButtons ? (
					<div className="sn-canvas-menu-nav-actions">
						<ul className="sn-canvas-menu-nav-action-list">
							{normalizedActionButtons.map((item) => {
								const { label, value, size = 'sm', variant = 'primary' } = item;
								return (
									<li className="sn-canvas-menu-nav-action-item">
										<now-button
											className="sn-canvas-menu-nav-action-button"
											size={size}
											variant={variant}
											label={label}
											appendToPayload={{
												value,
												type: 'navigationButton'
											}}
										/>
									</li>
								);
							})}
						</ul>
					</div>
				) : null}
			</div>
		</nav>
	);
};

createCustomElement('sn-canvas-menu', {
	initialState: {
		menuItems: {},
		contentMap: {}
	},
	properties: {
		items: {
			default: [],
			schema: {
				type: 'array',
				items: menuItemSchema
			}
		},
		actionButtons: {
			default: {}
		},
		activeScreen: {
			default: ''
		},
		menuWidth: {
			default: 1600,
			schema: { type: 'number' }
		},
		edgeToEdge: {
			default: false,
			schema: { type: 'boolean' }
		},
		verticalStackingEnabled: {
			default: false,
			schema: { type: 'boolean' }
		}
	},
	behaviors: [rtlBehavior],
	renderer: {
		type: snabbdom,
		view: snCanvasMenu
	},
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
			const { name, value, previousValue } = action.payload;
			if (name === 'items' && !isEqual(value, previousValue)) {
				const { tree, contentMap } = _createNormalizedTree(value);
				updateState({
					contentMap,
					menuItems: tree
				});
			}
		},
		[COMPONENT_BOOTSTRAPPED]: ({ properties, updateState }) => {
			const { items } = properties;
			if (!isEmpty(items)) {
				const { tree, contentMap } = _createNormalizedTree(items);
				updateState({
					contentMap,
					menuItems: tree
				});
			}
		},
		['NOW_BUTTON#CLICKED']: ({ action, dispatch }) => {
			const { type, value } = action.payload;
			if (type === 'navigationButton') {
				dispatch(NAV_ITEM_SELECTED, value);
			}

			// Prevent this action from propogating further.
			action.stopPropagation();
		}
	},
	eventHandlers: menubarKeyBindingEventHandler,
	styles: snCanvasMenuStyles
});
