import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';

import '../../sn-nav-item';
import '../../internal/sn-canvas-badge-connected';

const getDisplayItems = (primaryDisplay, label, leftIcon) => {
	switch (primaryDisplay) {
		case 'both':
			return {
				label,
				leftIcon
			};
		case 'icon':
			return {
				leftIcon
			};
		case 'label':
			return {
				label
			};
	}
};
const ActivityCenterLink = ({ item, experience }) => {
	const {
		label,
		icon,
		primaryDisplay,
		type,
		alert,
		value: navItemValue
	} = item;
	let displayItems = getDisplayItems(primaryDisplay, label, icon);
	let startsWith = get(displayItems, 'leftIcon', null);

	switch (type) {
		case 'navigation':
		case 'action':
			return (
				<li
					role="presentation"
					className="sn-canvas-global-tools-list-item -collapsible">
					<sn-nav-item
						label={get(displayItems, 'label', '')}
						type={navItemValue.type}
						opens-window={navItemValue.opensWindow}
						item-value={get(navItemValue, 'value', {})}
						experience={experience}
						variant="simple-nav">
						{startsWith ? (
							<now-icon
								size="lg"
								className="menu-item-icon"
								slot="startsWith"
								icon={startsWith}
							/>
						) : null}
					</sn-nav-item>
					{!isEmpty(alert) ? (
						<sn-canvas-badge-connected
							className="gt-badge"
							configurations={{
								table: alert.table,
								filter: alert.filter
							}}
						/>
					) : null}
				</li>
			);
		default:
			break;
	}
};

export default ActivityCenterLink;
