import {
	forEach as _forEach,
	find as _find,
	isEmpty as _isEmpty,
	filter as _filter,
	get as _get,
	findIndex as _findIndex
} from 'lodash';

import {
	getActiveToolbarButtonIndex,
	getButtonElementRef,
	getToolbarButtonRefByIndex,
	getToolbarButtons,
	getToolbarButtonFromEventPath
} from './elements';

/**
 * setButtonSelectionByRoute
 * @param buttons
 * @param route
 */
const setButtonSelectionByRoute = (buttons, route) => {
	const button = _find(buttons, (b) => _get(b, 'routeInfo.route') === route);
	if (!_isEmpty(button)) {
		button.isSelected = true;
	}
	return button;
};

/**
 * updateToolbarButtonsWithActiveBtn
 * @param toolbarButtons
 * @param button
 * @param updateProperties
 */
const updateToolbarButtonsWithActiveBtn = (
	toolbarButtons,
	button,
	updateProperties
) => {
	// button found and set it's isSelected flag to true,
	// update teh activeButton state.
	if (!_isEmpty(button)) {
		// find button, replace and update properties

		const index = _findIndex(toolbarButtons, (b) => b.id === button.id);
		toolbarButtons.splice(index, 1, {
			...button
		});
		updateProperties({ toolbarButtons: [...toolbarButtons] });
	} else {
		button = toolbarButtons[0];
		if (!_isEmpty(button)) {
			button.isSelected = true;
		}
		toolbarButtons.splice(0, 1, {
			...button
		});
		updateProperties({ toolbarButtons: [...toolbarButtons] });

		// set to teh session storage
		window.sessionStorage.setItem(
			getSessionStorageKey(),
			JSON.stringify(button)
		);
	}
};

/**
 * preserveViewportOpened: In order to preserve the collapsed or expand, we need
 * to get the opened value from the last active viewport button.
 * @param viewportInfo
 * @returns {boolean}
 */
const preserveViewportOpened = (viewportInfo) => {
	const route = _get(viewportInfo, 'route');
	const lastActiveButton = getLastActiveButton();
	let opened = false;
	if (route === _get(lastActiveButton, 'viewportInfo.route')) {
		opened = _get(lastActiveButton, 'viewportInfo.fields.opened');
	}
	return opened;
};

/**
 * loadViewport
 * @param activeRecords
 * @param toolbarButtons
 * @param button
 * @param dispatch
 * @param operation
 */
export const loadViewport = (
	activeRecords,
	toolbarButtons,
	button,
	dispatch,
	operation = 'TOGGLE_VIEWPORT_OPENED'
) => {
	const { id, label, viewportInfo, routeInfo, group } = button;
	const isViewportWithRoute = !_isEmpty(routeInfo) && !_isEmpty(viewportInfo);
	const {
		fields: { opened }
	} = viewportInfo;

	if (operation === 'PRESERVE_VIEWPORT_OPENED' && isViewportWithRoute) {
		// only applicable for viewport with routes information where onload of records,
		// we need to preserve the viewport opened value if the button with viewport routes
		// is already selected
		dispatch('VIEWPORT_SELECTED', {
			id,
			label,
			group,
			routeInfo,
			viewportInfo: {
				...viewportInfo,
				fields: { opened: preserveViewportOpened(viewportInfo) }
			}
		});
	} else if (operation === 'TOGGLE_VIEWPORT_OPENED') {
		// when toolbar button with viewport is clicked, toggle the viewport opened value
		dispatch('VIEWPORT_SELECTED', {
			id,
			label,
			group,
			routeInfo,
			viewportInfo: {
				...viewportInfo,
				fields: { opened: !opened }
			}
		});
	}
};

/**
 * setToolbarButtonSelected
 * @param host
 * @param buttonClickedRef
 * @param selected
 */
export const setToolbarButtonSelected = (host, buttonClickedRef, selected) => {
	const toolbarButtons = getToolbarButtons(host);
	_forEach(toolbarButtons, (toolbarButton) => {
		if (buttonClickedRef === getButtonElementRef(toolbarButton)) {
			toolbarButton.setAttribute('is-selected', selected);
		} else {
			toolbarButton.setAttribute('is-selected', false);
		}
	});
};

/**
 * getRouteFromButtonData
 * @param toolbarButtons
 * @param label
 * @returns {*}
 */
export const getRouteFromButtonData = (toolbarButtons, label) => {
	const { routeInfo } = _find(
		toolbarButtons,
		(button) => button.label === label
	);
	return routeInfo;
};

/**
 * handleKeyPressed
 * @param host
 * @param event
 * @param dispatch
 */
export const handleKeyPressed = (host, event, dispatch) => {
	event.preventDefault(); // prevent content scroll
	const toolbarButtons = getToolbarButtons(host);
	const toolbarButtonFirstIndex = 0;
	const toolbarButtonsLastIndex = toolbarButtons.length - 1;
	const currentToolbarButtonIndex = getActiveToolbarButtonIndex(host);
	let nextToolbarButton = null;
	switch (event.keyCode) {
		case 38: {
			// ArrowUp
			if (currentToolbarButtonIndex === toolbarButtonFirstIndex) {
				nextToolbarButton = getToolbarButtonRefByIndex(
					host,
					toolbarButtonsLastIndex
				);
			} else {
				nextToolbarButton = getToolbarButtonRefByIndex(
					host,
					currentToolbarButtonIndex - 1
				);
			}
			if (nextToolbarButton) {
				setToolbarButtonSelected(
					host,
					getButtonElementRef(nextToolbarButton),
					true
				);
				getButtonElementRef(nextToolbarButton).focus();
			}
			break;
		}
		case 40: {
			// ArrowDown
			if (currentToolbarButtonIndex < toolbarButtonsLastIndex) {
				nextToolbarButton = getToolbarButtonRefByIndex(
					host,
					currentToolbarButtonIndex + 1
				);
			} else if (currentToolbarButtonIndex === toolbarButtonsLastIndex) {
				nextToolbarButton = getToolbarButtonRefByIndex(
					host,
					toolbarButtonFirstIndex
				);
			}
			if (nextToolbarButton) {
				setToolbarButtonSelected(
					host,
					getButtonElementRef(nextToolbarButton),
					true
				);
				getButtonElementRef(nextToolbarButton).focus();
			}
			break;
		}
		case 32:
		/* Space */ case 13: /* Enter */ {
			const { routeInfo } = getToolbarButtonFromEventPath(event.path);
			dispatch('NAV_ITEM_SELECTED', routeInfo);
			break;
		}
		default: {
			// do not block other keys
			break;
		}
	}
};

/**
 * resetViewportSelection
 * @param toolbarButtons
 * @param dispatch
 * @param updateProperties
 */
export const resetViewportSelection = (
	toolbarButtons,
	button,
	dispatch,
	updateProperties
) => {
	// collect all viewport toolbar buttons from top group
	const buttons = _filter(
		toolbarButtons,
		(b) => b.group === 'top' && !_isEmpty(b.viewportInfo) && b.id !== button.id
	);

	// reset all of the viewport visibility elements
	buttons.map((b) => {
		const { id } = b;
		const {
			fields: { opened }
		} = b.viewportInfo;
		const index = _findIndex(toolbarButtons, { id });

		if (opened && index > -1) {
			// reset the viewportInfo.fields.opened
			toolbarButtons.splice(index, 1, {
				...b,
				viewportInfo: {
					...b.viewportInfo,
					fields: { opened: false }
				}
			});

			updateProperties({ toolbarButtons: [...toolbarButtons] });

			dispatch('UXF_VIEWPORT#RENDER_BY_ID', {
				...b.viewportInfo,
				fields: { opened: !opened },
				reinitialize: true
			});
		}
	});
};

/**
 * setActiveButton
 * @param activeButton
 * @param toolbarButtons
 * @param activeRoute
 * @param updateProperties
 * @param updateState
 */
export const setActiveButton = (
	activeButton,
	toolbarButtons,
	activeRoute,
	activeRecords,
	dispatch,
	updateProperties,
	updateState
) => {
	if (_isEmpty(toolbarButtons)) return;

	const serializedLastActiveButton = window.sessionStorage.getItem(
		getSessionStorageKey()
	);
	let button, route;

	// check activeRoute match to button definition
	if (!_isEmpty(activeRoute)) {
		const activeRouteHasRoute =
			!_isEmpty(_get(activeRoute, 'parent.route')) ||
			!_isEmpty(_get(activeRoute, 'route'));

		if (activeRouteHasRoute) {
			route = _get(activeRoute, 'parent.route') || _get(activeRoute, 'route');
			button = setButtonSelectionByRoute(toolbarButtons, route);
		}
	}

	// activeRoute doesn't match to button definition, look for the
	// last active button from the session storage
	if (_isEmpty(button) && !_isEmpty(serializedLastActiveButton)) {
		const lastActiveButton = JSON.parse(serializedLastActiveButton);
		const lastActiveButtonRouteInfo = !_isEmpty(
			_get(lastActiveButton, 'routeInfo')
		);
		if (lastActiveButtonRouteInfo) {
			route = _get(lastActiveButton, 'routeInfo.route');
			button = setButtonSelectionByRoute(toolbarButtons, route);
		}
	}

	// button found but not in session storage or different than the one in session storage
	if (!_isEmpty(button)) {
		const lastActiveButton = getLastActiveButton();
		if (_isEmpty(lastActiveButton) || lastActiveButton.id !== button.id) {
			const serializedActiveButton = JSON.stringify(button);
			window.sessionStorage.setItem(
				getSessionStorageKey(),
				serializedActiveButton
			);
		}
	}

	// resolved the active button to toolbar button definitions, with
	// isSelected flag to true
	updateToolbarButtonsWithActiveBtn(toolbarButtons, button, updateProperties);

	// updating teh activeButton state
	updateState({ activeButton: button });

	// load the viewport for the button if exists
	loadViewport(
		activeRecords,
		toolbarButtons,
		button,
		dispatch,
		'PRESERVE_VIEWPORT_OPENED'
	);
};

const getLastActiveButton = () => {
	const serializedLastActiveButton = window.sessionStorage.getItem(
		getSessionStorageKey()
	);
	return !_isEmpty(serializedLastActiveButton)
		? JSON.parse(serializedLastActiveButton)
		: null;
};

/**
 * _getToolbarButtonByRoute
 * @param routeInfo
 * @param toolbarButtons
 * @returns {null|*}
 * @private
 */
const _getToolbarButtonByRoute = (routeInfo, toolbarButtons) => {
	if (_isEmpty(toolbarButtons) || _isEmpty(routeInfo)) {
		return null;
	}
	return _find(
		toolbarButtons,
		(b) => _get(b, 'routeInfo.route') === routeInfo.route
	);
};

/**
 * isRoute
 * @param activeRoute
 * @param toolbarButtons
 * @returns {boolean}
 */
export const isRoute = (activeRoute, toolbarButtons) => {
	const { routeInfo, viewportInfo } = _getToolbarButtonByRoute(
		activeRoute,
		toolbarButtons
	);
	return !_isEmpty(routeInfo) && _isEmpty(viewportInfo);
};

/**
 * isViewport
 * @param activeRoute
 * @param toolbarButtons
 * @returns {boolean}
 */
export const isViewport = (activeRoute, toolbarButtons) => {
	const { routeInfo, viewportInfo } = _getToolbarButtonByRoute(
		activeRoute,
		toolbarButtons
	);
	return _isEmpty(routeInfo) && !_isEmpty(viewportInfo);
};

/**
 * isViewportWithRoute
 * @param activeRoute
 * @param toolbarButtons
 * @returns {boolean}
 */
export const isViewportWithRoute = (activeRoute, toolbarButtons) => {
	const { routeInfo, viewportInfo } = _getToolbarButtonByRoute(
		activeRoute,
		toolbarButtons
	);
	return !_isEmpty(routeInfo) && !_isEmpty(viewportInfo);
};

/**
 * resetSizeAndViewportInfo
 * @param activeRoute
 * @param toolbarButtons
 */
export const resetSizeAndViewportInfo = (
	activeRoute,
	toolbarButtons,
	dispatch,
	updateProperties
) => {
	const button = _getToolbarButtonByRoute(activeRoute, toolbarButtons);

	if (!_isEmpty(button) && isRoute(activeRoute, toolbarButtons)) {
		// reset viewport selection
		resetViewportSelection(toolbarButtons, button, dispatch, updateProperties);

		// update the primary tab
		dispatch('PRIMARY_TAB_UPDATED', {
			button: { ...button, size: null, viewportInfo: null }
		});
	}
};

/**
 * isViewportEmptyPageLoaded
 * @param lastActiveButton
 * @parm viewportInfo
 * @returns {boolean}
 */
export const isViewportEmptyPageLoaded = (lastActiveButton, viewportInfo) => {
	let viewportEmptyPageLoaded = false;
	if (!_isEmpty(lastActiveButton) && !_isEmpty(viewportInfo)) {
		const currentViewportRoute = _get(viewportInfo, 'route');
		viewportEmptyPageLoaded =
			currentViewportRoute.indexOf(
				_get(lastActiveButton, 'routeInfo.route')
			) !== -1;
	}
	return viewportEmptyPageLoaded;
};

/**
 * getSessionStorageKey
 * @returns {string}
 */
export const getSessionStorageKey = () => 'canvasToolbar';
