import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { getFocusableNodes } from '@servicenow/behavior-focus';
import '@servicenow/now-popover';
import '@servicenow/now-tooltip';
import { t } from 'sn-translate';

import '../../internal/sn-canvas-menu-dropdown/sn-canvas-user-menu-dropdown-panel';

const UserMenu = ({
	userAvatar,
	userName,
	userPreferencesEnabled,
	items,
	userPrefs,
	experience,
	showUserMenuTooltip,
	userMenuRef,
	updateState
}) => {
	// Convert the current items schema into one that is acceptable by
	// sn-canvas-menu-dropdown-panel. Meaning, wrap each item in an object with
	// key of "value".
	const dropdownItems = items.map((item) => ({ value: item }));
	const hasDropdownItems = dropdownItems.length > 0;

	const _UserPanelVisibleListener = (evt) => {
		const popover_slot_content = evt.detail.payload.content[0];
		const { firstFocusableElement } = getFocusableNodes(popover_slot_content);
		if (firstFocusableElement) firstFocusableElement.focus();
	};

	const _showUserMenuTooltip = () => updateState({ showUserMenuTooltip: true });

	const _hideUserMenuTooltip = () =>
		updateState({ showUserMenuTooltip: false });

	const _handlePopoverInsert = ({ elm }) => {
		if (!elm) return;
		elm.addEventListener(
			'NOW_POPOVER#CONTENT_VISIBLE',
			_UserPanelVisibleListener
		);
		elm.addEventListener(
			'NOW_POPOVER#OPENED_SET',
			({
				detail: {
					payload: { value }
				}
			}) => {
				elm.opened = value;
				const button = elm.firstElementChild;

				if (button) {
					const icon = button.lastElementChild;
					const iconName = value
						? 'chevron-up-outline'
						: 'chevron-down-outline';
					if (icon) icon.setAttribute('icon', iconName);
					button.setAttribute('aria-expanded', value);
				}
			}
		);
	};
	const _handlePopoverDestroy = ({ elm }) => {
		if (!elm) return;
		elm.removeEventListener(
			'NOW_POPOVER#CONTENT_VISIBLE',
			_UserPanelVisibleListener
		);
		elm.removeEventListener(
			'NOW_POPOVER#OPENED_SET',
			({
				detail: {
					payload: { value }
				}
			}) => {
				elm.opened = value;
				const triggerBtn = elm.firstElementChild;
				if (triggerBtn) triggerBtn.setAttribute('aria-expanded', value);
			}
		);
	};

	return userPreferencesEnabled ? (
		<slot name="user-menu">
			{hasDropdownItems ? (
				<Fragment>
					<now-popover
						className="sn-canvas-user-menu-popover"
						interaction-type="listbox"
						hide-tail
						positions={[
							{ target: 'bottom-start', content: 'top-start' },
							{ target: 'bottom-end', content: 'top-end' }
						]}
						manage-opened
						hook-insert={_handlePopoverInsert}
						hook-destroy={_handlePopoverDestroy}>
						<button
							on-focus={_showUserMenuTooltip}
							on-blur={_hideUserMenuTooltip}
							class={{
								'sn-canvas-user-menu-trigger': true,
								['--' + experience]: experience === 'workspace'
							}}
							aria-label={t('User Menu')}
							aria-haspopup
							slot="trigger">
							<now-avatar
								aria-hidden="true"
								image-src={userAvatar}
								size="sm"
								user-name={userName}
							/>
							<div
								ref={(elm) => updateState({ userMenuRef: elm })}
								aria-describedby="user-menu-tooltip"
								on-mouseenter={_showUserMenuTooltip}
								on-mouseout={_hideUserMenuTooltip}>
								<now-icon
									aria-hidden="true"
									className="sn-canvas-user-menu-chevron"
									icon="chevron-down-outline"
									size="sm"
								/>
							</div>
						</button>

						<sn-canvas-user-menu-dropdown-panel
							menu-items={{ items: dropdownItems }}
							userPrefs={userPrefs}
							slot="content"
						/>
					</now-popover>
					<now-tooltip
						id="user-menu-tooltip"
						manage-opened
						opened={showUserMenuTooltip}
						target-ref={userMenuRef}
						content={t('User Menu')}
						position={['bottom-start top-center']}
					/>
				</Fragment>
			) : (
				<now-avatar
					aria-hidden="true"
					image-src={userAvatar}
					size="sm"
					user-name={userName}
				/>
			)}
		</slot>
	) : null;
};

export default UserMenu;
