import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { CONSTANTS } from './constants';
import '@servicenow/now-modal';

const { SN_CANVAS_ERROR_TAGNAME } = CONSTANTS;
const {
	NOW_MODAL_FOOTER_ACTION_CLICKED,
	NOW_MODAL_OPENED_SET
} = CONSTANTS.ACTION_TYPES;

const _execBtnActionFn = (btnClickFn) => {
	typeof btnClickFn === typeof (() => {}) ? btnClickFn() : null;
};

/** @seismicView sn-canvas-error */
const snCanvasError = (state) => {
	const { open, errorConfig } = state.properties;
	const { headerLabel = '', content = '', footerActions = [] } = errorConfig;
	return (
		<now-modal
			size="sm"
			opened={open}
			nowHoist={open}
			manageOpened
			header-label={headerLabel}
			content={content}
			footer-actions={footerActions}
		/>
	);
};

/**
 * Canvas error represents custom modal component that displays the
 * error message and actions.
 *
 * ```jsx
 * <sn-canvas-error errorConfig={errorConfig} open={true} />
 * ```
 *
 * @seismicElement sn-canvas-error
 * @summary Canvas error defines the error modal.
 *
 **/
createCustomElement(SN_CANVAS_ERROR_TAGNAME, {
	properties: {
		/**
		 * Property to set the error modal configuration - header label, content, footer actions etc.
		 * @type {Object} errorConfig - Canvas error modal configuration
		 * @default [{}]
		 */
		errorConfig: { default: {} },
		/**
		 * Property to set the card's primary title
		 * @type {boolean} [open=false] open - Error modal display state
		 * @default [false]
		 */
		open: { default: false },
		loginModal: { default: {}, unstableParse: true, schema: { type: 'object' } }
	},
	renderer: {
		type: snabbdom,
		view: snCanvasError
	},
	actionHandlers: {
		[NOW_MODAL_OPENED_SET]: ({
			action: {
				payload: { value }
			},
			dispatch
		}) => {
			if (!value) dispatch('CANVAS_HIDE_ERROR_MODAL', { errorConfig: {} });
		},
		[NOW_MODAL_FOOTER_ACTION_CLICKED]: ({
			action: { payload },
			dispatch,
			properties
		}) => {
			const {
				footerAction: { action }
			} = payload;
			const { loginModal } = properties;
			_execBtnActionFn(action(dispatch, loginModal));
			dispatch('CANVAS_HIDE_ERROR_MODAL', { errorConfig: {} });
		}
	}
});
