import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import isEqual from 'lodash/isEqual';
import get from 'lodash/get';

import {
	QUERY_NOTIFICATION_COUNT,
	QUERY_NOTIFICATION_COUNT_SUCCEEDED
} from '../constants';

const notificationCountQuery = `query($condition: String) {
	GlideAggregate_Query(tableName: "ui_notification_inbox", groupBy: "target", queryConditions: $condition) {
		count
		groupBy{
			value
		}
	}
}
`;

export const actionHandlers = {
	[QUERY_NOTIFICATION_COUNT]: createGraphQLEffect(notificationCountQuery, {
		variableList: ['condition'],
		successActionType: QUERY_NOTIFICATION_COUNT_SUCCEEDED
	}),
	[QUERY_NOTIFICATION_COUNT_SUCCEEDED]: ({
		properties,
		updateProperties,
		action
	}) => {
		const badgeListNew = get(
			action,
			'payload.data.GlideAggregate_Query',
			[]
		).map((result) => result.groupBy[0].value);
		const { badgeList } = properties.notification;

		if (!isEqual(badgeList, badgeListNew))
			updateProperties(() => ({
				path: 'notification',
				operation: 'assign',
				value: { badgeList: badgeListNew, badgeCount: badgeListNew.length }
			}));
	}
};
