import { CanvasOverflowItem } from './canvasOverflowItem';

const TabOverflow = (props) => {
	const {
		optionItems,
		highlightIndex,
		onItemClick,
		onItemClose,
		onNodeFocus,
		isNodeFocused
	} = props;
	return (
		<ul className="sn-chrome-popover-content-list" role="presentation">
			{optionItems.map((item, i) => {
				return (
					<CanvasOverflowItem
						key={item.tab_id}
						tab={item}
						isHighlighted={highlightIndex === i}
						index={i}
						isNodeFocused={isNodeFocused}
						onItemClick={onItemClick}
						onItemClose={onItemClose}
						onNodeFocus={onNodeFocus}
						hasUpdate={item.hasUpdate}
					/>
				);
			})}
		</ul>
	);
};

export { TabOverflow };
