import { actionTypes } from '@servicenow/ui-core';
import badgeCountEffect from './badgeCountEffect';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;

const { RECORD_WATCHER, AGGREGATE_COUNT } = badgeCountEffect.ACTION_TYPES;

const {
	handleBadgeCountFetch,
	handleBadgeCountFetchSuccess,
	handleBadgeCountFetchFailure,
	handleRecordWatcher,
	handleItemChanged,
	onConnect,
	onDisconnect
} = badgeCountEffect.effectHandlers;

export const badgeCountBehavior = {
	name: 'BADGE_COUNT_BEHAVIOR',
	properties: {
		/**
		 * Defines the badge information.
		 * @type {object}
		 *
		 * {
		 *    count: 2,
		 *    configurations: {
		 *      table: "task"
		 *      filter: "assigned_to=46c6f9efa9fe198101ddf5eed9adf6e7"
		 *    },
		 *    type: "record"
		 * }
		 */
		badge: { schema: { type: 'object' } }
	},
	actions: {
		[AGGREGATE_COUNT.FETCH]: { private: true },
		[AGGREGATE_COUNT.STARTED]: { private: true },
		[AGGREGATE_COUNT.SUCCEEDED]: { private: true },
		[AGGREGATE_COUNT.FAILED]: { private: true },
		[RECORD_WATCHER.SUBSCRIBED_STARTED]: { private: true },
		[RECORD_WATCHER.SUBSCRIBED]: { private: true },
		[RECORD_WATCHER.SUBSCRIBED_FAILED]: { private: true },
		[RECORD_WATCHER.UNSUBSCRIBED]: { private: true },
		[RECORD_WATCHER.ITEM_CHANGED]: { private: true },
		[RECORD_WATCHER.SUBSCRIPTION_CHANGED]: { private: true }
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: onConnect,
		[COMPONENT_DISCONNECTED]: onDisconnect,
		[AGGREGATE_COUNT.FETCH]: handleBadgeCountFetch,
		[AGGREGATE_COUNT.SUCCEEDED]: handleBadgeCountFetchSuccess,
		[AGGREGATE_COUNT.FAILED]: handleBadgeCountFetchFailure,
		[RECORD_WATCHER.SUBSCRIPTION_CHANGED]: handleRecordWatcher,
		[RECORD_WATCHER.ITEM_CHANGED]: handleItemChanged
	}
};
