import { t } from 'sn-translate';

import '@servicenow/now-button';

import CanvasTabBar from '../internal/canvas-tabbar';

import {
	MAIN_TAB_CLICKED,
	MAIN_TAB_CLOSE_CLICKED,
	NAV_ITEM_SELECTED,
	CLOSE_TAB_MENU_ITEM,
	SELECT_TAB,
	SUB_TAB_CLOSE_CLICKED,
	MODAL_SELECTED
} from '../../constants/constants';

export const canvasTabs = (state, { dispatch }) => {
	const {
		tabsLevel,
		tabsData,
		tabsToCloseQueue,
		stopperPrompt,
		tabConfig = {},
		dir
	} = state.properties;

	if (!tabsData || !tabConfig) return null;

	const { newTabMenu: newMenu } = tabConfig;

	const { closeMenu } = state.menuConf;

	const _onClickTab = (tab_id) => {
		dispatch(MAIN_TAB_CLICKED, { tab_id });
	};

	/** onCloseHandler sets a timeout to dispatch MAIN_TAB_CLOSE_CLICKED.
	 *  this async processing allows content to handle internal events
	 *  use case where this matters:  user typing into a text field in a record, where the
	 *  dirty state is only propagated up to tabs when the text field loses focus
	 */
	const _onCloseTab = (tab_id) => {
		const _onCloseTabHandler = () => {
			dispatch(MAIN_TAB_CLOSE_CLICKED, {
				operation: CLOSE_TAB_MENU_ITEM,
				tab_id
			});
		};
		window.setTimeout(_onCloseTabHandler, 100);
	};

	const _onClickSubTabs = (tab_id) => {
		dispatch(SELECT_TAB, { tab_ids: [...[], tab_id] });
	};

	const _onCloseSubTab = (tab_id) => {
		const _onCloseTabHandler = () => {
			dispatch(SUB_TAB_CLOSE_CLICKED, {
				tab_id
			});
		};
		window.setTimeout(_onCloseTabHandler, 100);
	};

	const _onClickCloseMenuItem = ({ operation, selectedTabId, type }) => {
		if (type === 'primary') {
			dispatch(MAIN_TAB_CLOSE_CLICKED, {
				operation,
				tab_id: selectedTabId
			});
		} else {
			dispatch(SUB_TAB_CLOSE_CLICKED, {
				operation,
				tab_id: selectedTabId
			});
		}
	};

	const _onClickNewMenuItem = (menuItem) => {
		const { routeInfo } = menuItem;
		if (routeInfo) {
			let _routeInfo = { ...routeInfo };
			dispatch(NAV_ITEM_SELECTED, { ..._routeInfo, title: menuItem.title });
		}
	};

	const _onShowModal = (modalID, showModal) => {
		dispatch(MODAL_SELECTED, { modalId: modalID, showModal: showModal });
	};

	const _renderBackButton = (drillDowns) => {
		return drillDowns && drillDowns.length > 0 ? (
			<now-button-bare
				className="sn-primary-back-button"
				iconStart="arrow-left-outline"
				label={t('Back')}
				size="md"
				variant="tertiary"
				append-to-payload={{ drillDowns }}
			/>
		) : null;
	};

	const _getDrillDowns = (tabsData, tabId) => {
		return tabsData[tabId] && tabsData[tabId].selectedContent
			? tabsData[tabId].selectedContent.drillDowns
			: null;
	};

	const _renderTabBars = (tabsData) => {
		if (!tabsData) return;
		let canvasTabBars = [];

		let currNode = tabsData.root;
		let depth = 0;

		let _type = 'primary';
		let _className = 'sn-canvas-tabs-container';
		let onClickTab = _onClickTab;
		let onCloseTab = _onCloseTab;

		while (currNode) {
			let currTabList = currNode.tabList.map((tab_id) => tabsData[tab_id]);
			let currSelectedTabId = currNode.selectedTabId;

			if (currSelectedTabId == '-1' || !currTabList || currTabList.length == 0)
				break;

			if (depth > 0) {
				_type = 'default';
				_className = 'sn-canvas-sub-tabs-container';
				onClickTab = _onClickSubTabs;
				onCloseTab = _onCloseSubTab;
			}
			const drillDowns = _getDrillDowns(tabsData, currSelectedTabId);

			canvasTabBars.push(
				<nav
					className={_className}
					aria-label={
						tabsLevel == 1 || tabsLevel == '1' ? t('Tabs') : t('Sub Tabs')
					}>
					<CanvasTabBar
						level={depth + 1}
						focusedTabIndex={state.focusedTabIndex[depth + 1]}
						dispatchFn={dispatch}
						type={_type}
						tabsMaxWidth={state.maxWidth}
						tabsMaxOpen={state.tabsMaxOpen}
						stopperPrompt={stopperPrompt}
						tabsToCloseQueue={tabsToCloseQueue}
						selectedTabId={currSelectedTabId}
						tabList={currTabList}
						newMenu={newMenu}
						closeMenu={closeMenu}
						onClickTab={onClickTab}
						onCloseTab={onCloseTab}
						onNewTab={_onClickNewMenuItem}
						onMenuClose={_onClickCloseMenuItem}
						onShowModal={_onShowModal}
						dir={dir}
					/>
					{_renderBackButton(drillDowns)}
				</nav>
			);
			if (tabsLevel == 1 || tabsLevel == '1') break;

			currNode = tabsData[currSelectedTabId];
			depth++;
		}
		if (tabsLevel == 2 || tabsLevel == '2') canvasTabBars.shift();
		return canvasTabBars;
	};

	return _renderTabBars(tabsData);
};
