import { navItemSchema } from '../../sn-nav-item/schemas';

const menuItemGroupSchema = {
	type: 'object',
	properties: {
		group: { type: 'array', items: { $ref: '#definitions/menuItem' } }
	}
};

export const menuItemSchema = {
	definitions: {
		menuItem: {
			type: 'object',
			properties: {
				value: navItemSchema,
				children: {
					type: 'object',
					properties: {
						action: navItemSchema,
						items: {
							type: 'array',
							items: {
								anyOf: [
									{ $ref: '#definitions/menuItem' },
									{ menuItemGroupSchema }
								]
							}
						}
					}
				}
			}
		}
	}
};
