export default class ObserverList {
	constructor() {
		this.observerList = [];
	}

	addObserver(item) {
		this.observerList.push(item);
	}

	removeAllObservers() {
		this.observerList = [];
	}

	isEmpty() {
		return this.observerList.length === 0;
	}

	notify(width) {
		this.observerList.forEach((observer) => {
			observer.update(width);
		});
	}
}
