export const modalPayloadSchema = {
	type: 'object',
	properties: {
		modalId: { type: 'string' },
		showModal: { type: 'boolean' },
		displayOptions: { type: 'object' }
	}
};

export const routePayloadSchema = {
	type: 'object',
	properties: {
		route: { type: 'string' },
		fields: { type: 'object' },
		params: { type: 'object' }
	}
};

const urlPayloadSchema = {
	type: 'object',
	properties: {
		href: { type: 'string' }
	}
};

export const urlSchema = {
	type: 'object',
	properties: {
		href: { type: 'string' }
	}
};

export const navItemSchema = {
	type: 'object',
	properties: {
		label: { type: 'string' },
		opensWindow: { type: 'boolean' },
		tooltip: { type: 'string' },
		download: { schema: { oneOf: [{ type: 'string' }, { type: 'boolean' }] } },
		variant: { type: 'string' },
		underlined: { type: 'boolean' },
		type: {
			type: 'string',
			enum: ['external', 'email', 'phone', 'modal', 'route']
		},
		itemValue: {
			anyOf: [urlPayloadSchema, routePayloadSchema, modalPayloadSchema]
		}
	}
};
