import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './_sn-canvas-iframe.scss';

const {
	COMPONENT_DOM_READY,
	COMPONENT_DISCONNECTED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

import {
	CANVAS_IFRAME_CLOSED,
	CANVAS_IFRAME_CONTENT,
	IFRAME_REF
} from './constants';

/**
 * API that handles communication from iframe, dispatches MODAL_SELECTED, SN_CANVAS_IFRAME#CLOSED actions
 * @param {*} e , standar postMessage payload
 */
const onMessage = (e) => {
	const { type, action, params = {} } = e.data || {};

	if (CANVAS_IFRAME_CONTENT === type) {
		e.dispatch('MODAL_SELECTED', { showModal: false });
		e.dispatch(CANVAS_IFRAME_CLOSED, {
			action,
			params
		});
	}
};

const sendInitMessage = (iFrameRef, params = {}) => {
	if (!iFrameRef) return;

	let data = {
		type: 'CAVAS_IFRAME_MODAL_INIT',
		params
	};
	iFrameRef.contentWindow.postMessage(data, location.origin);
};

const setIframeHeight = (iframeRef) => {
	if (
		iframeRef &&
		iframeRef.contentDocument &&
		iframeRef.contentDocument.documentElement
	)
		iframeRef.style.height =
			iframeRef.contentDocument.documentElement.scrollHeight + 'px';
};

const view = (state, { updateProperties }) => {
	const {
		src,
		headerLabel,
		iframeRef,
		disableSandbox = false
	} = state.properties;
	const includeIframe = !!src;
	if (!includeIframe) return null;

	const setRef = (ref) => {
		if (iframeRef) return;
		updateProperties({ iframeRef: ref });
	};

	const _renderHeader = () => {
		if (!headerLabel || headerLabel.length === 0) return null;
		return <h1> {headerLabel} </h1>;
	};

	return disableSandbox === true ? (
		<div className="sn-canvas-iframe">
			{_renderHeader()}
			<iframe
				ref={setRef}
				src={src}
				title={headerLabel}
				sandbox="allow-forms allow-modals allow-popups allow-presentation allow-same-origin allow-scripts allow-downloads"
			/>
		</div>
	) : (
		<div className="sn-canvas-iframe">
			{_renderHeader()}
			<iframe
				ref={setRef}
				src={src}
				title={headerLabel}
				sandbox="allow-scripts"
			/>
		</div>
	);
};

/**
 * A canvas component that provides ability to pass in header title and src url to create a content with iframe
 * . A use case of this component is it is used in OOB modal so the actual content will be displayed on modal
 * ```jsx
 * 	<sn-canvas-iframe header-title="My Header" src="https://myhost/sample.html" height="400px" />
 * ```
 *  @seismicElement sn-canvas-iframe
 *  @summary - A component to group tilte , iframe together
 */
createCustomElement('sn-canvas-iframe', {
	properties: {
		iframeRef: {
			private: true,
			default: null
		},
		/**
		 * Property to configure the canvas iframe title.
		 *
		 * A title for your iframe
		 * @type {string}
		 */
		headerLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},

		/**
		 * Property to configure the canvas iframe src or url.
		 *
		 * A src (url) for your iframe content
		 * @type {string}
		 */
		src: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Initial data to iframe content
		 */
		params: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		 * Optional. disable-sandbox, a boolean property to toggle sandbox restrictions.
		 */
		disableSandbox: {
			default: false,
			schema: {
				type: 'boolean'
			}
		}
	},
	renderer: {
		type: snabbdom,
		view: view
	},
	styles,
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({ dispatch, action }) => {
			action.stopPropagation();
			window.addEventListener('message', (e) => {
				onMessage({ data: e.data, dispatch });
			});
		},

		[COMPONENT_DISCONNECTED]: ({ dispatch, action }) => {
			action.stopPropagation();
			window.removeEventListener('message', (e) => {
				onMessage({ data: e.data, dispatch });
			});
		},

		[COMPONENT_PROPERTY_CHANGED]: ({
			action: { payload },
			properties: { params }
		}) => {
			if (payload.name === IFRAME_REF) {
				if (!payload.value) return;
				payload.value.addEventListener('load', () => {
					sendInitMessage(payload.value, params);
					setIframeHeight(payload.value);
				});
			}
		}
	}
});
