import { isEmpty } from 'lodash';
import {
	getPrimaryTabMap,
	getContentKey,
	getTabTitleCachedByScreen,
	contextualRouteSelected,
	primaryRouteSelected,
	locateTabs
} from '../utils/tabsData.utils';

import { getNextTabId } from './tabsData.storage';

//Process selectedContent

export function preProcessSelectedContent(selectedContent) {
	let _rootContent = { ...selectedContent };
	let drillDowns = [];
	while (_rootContent && _rootContent.parent) {
		let { parent, ...drillDown } = _rootContent;
		drillDowns.push(drillDown);
		_rootContent = parent;
	}

	const _contextualRouteSelected = contextualRouteSelected(_rootContent);

	if (_contextualRouteSelected)
		return {
			selectedContent,
			contextualRoute: _contextualRouteSelected,
			tabType: 'CONTEXTUAL'
		};

	const _primaryRouteSelected = primaryRouteSelected(_rootContent);

	if (drillDowns.length > 1) {
		drillDowns.reverse();
	}
	const _selectedContent = {
		..._rootContent,
		...(drillDowns.length > 0 && { drillDowns })
	};

	if (_primaryRouteSelected)
		return {
			selectedContent: _selectedContent,
			primaryRoute: _primaryRouteSelected,
			tabType: 'PRIMARY'
		};

	return { selectedContent: _selectedContent, tabType: 'MAIN_ONLY' };
}

//generate tabsData root object
export function generateTabsDataRoot() {
	return {
		root: {
			tab_id: 'root',
			parentTabId: null,
			selectedTabId: '-1',
			childContentKeys: {},
			tabList: []
		}
	};
}

export function getInitPrimaryTabData({ selectedContent, primaryRoute }) {
	const primaryTabMap = getPrimaryTabMap();
	if (Object.keys(primaryTabMap).length === 0) return;
	const primaryTab = primaryRoute
		? primaryRoute
		: !isEmpty(primaryTabMap)
		? primaryTabMap[Object.keys(primaryTabMap)[0]]
		: null;
	if (primaryTab) {
		const content = primaryRoute ? selectedContent : primaryTab.routeInfo;

		const { label, icon } = primaryTab;
		const options = {
			canClose: false,
			isPrimaryTab: true,
			size: null,
			viewportInfo: null
		};
		return getTabDefinition({
			selectedContent: { ...content, label, icon },
			options
		});
	}
}

export function getTabPayload(tabsData, payload) {
	const { selectedContent, tabType } = payload;
	let _selectedContent = { ...selectedContent };

	if (tabType === 'CONTEXTUAL' && payload.contextualRoute) {
		if (!_selectedContent.parent) {
			_selectedContent = {
				..._selectedContent,
				isDetailsTab: true,
				parent: _selectedContent
			};
		}
	}
	let { selectedContentStack, tabs } = locateTabs(tabsData, _selectedContent);

	while (selectedContentStack.length > 0) {
		//create tabs for content that are not located
		const tabId = getNextTabId();
		const content = selectedContentStack.pop();
		const tab = getTabDefinition({ tabId, selectedContent: content });
		tabs.push(tab);
	}

	let i = 0;
	while (tabs.length > 0 && i < tabs.length - 1) {
		//manage parent and child relations in payload
		const tab = tabs[i];
		const subTab = tabs[i + 1];
		if (i == 0) tab.parentTabId = 'root';
		tab.selectedTabId = subTab.tab_id;
		tab.childContentKeys = {
			...tab.childContentKeys,
			[subTab.contentKey]: subTab.tab_id
		};
		if (tab.tabList && tab.tabList.indexOf(subTab.tab_id) === -1)
			tab.tabList.push(subTab.tab_id);
		subTab.parentTabId = tab.tab_id;
		i++;
	}
	return tabs;
}

//generate tab definition
export function getTabDefinition({ tabId, selectedContent, options = {} }) {
	if (!tabId) tabId = getNextTabId();
	const { isDetailsTab = false, ...rest } = selectedContent;
	const { canClose, isPrimaryTab = false, size, viewportInfo } = options;
	const { label, title, icon } = rest;
	const contentKey = getContentKey(rest);
	const cachedTabTitle = getTabTitleCachedByScreen(contentKey);

	const tab = {
		tab_id: tabId,
		title: isDetailsTab
			? 'Details'
			: label || title || cachedTabTitle || 'Loading...',
		icon,
		selectedTabId: '-1',
		parentTabId: 'root',
		selectedContent: rest,
		contentKey,
		canClose: canClose === false ? canClose : !isDetailsTab,
		...(isPrimaryTab && { isPrimaryTab }),
		...(isDetailsTab && { isDetailsTab }),
		...(size && { size: null }),
		...(viewportInfo && { viewportInfo: null }),
		tabList: []
	};

	return tab;
}
