import { ARROW_DOWN_KEY, ARROW_UP_KEY } from '../../../../constants/constants';

const SUB_MENU_BASE_SELECTOR =
	'.sn-canvas-user-menu-dropdown-panel-container > .sn-canvas-user-menu-dropdown-list > li > now-collapse[aria-hidden="false"] > ul > .sn-canvas-user-menu-dropdown-list-item';
const FIRST_LEVEL_ITEM_BASE_SELECTOR =
	'.sn-canvas-user-menu-dropdown-panel-container > .sn-canvas-user-menu-dropdown-list > .sn-canvas-user-menu-dropdown-list-item';
const SUB_MENU_NAV_ITEM_SELECTOR = `${SUB_MENU_BASE_SELECTOR} > sn-nav-item`;
const SUB_MENU_TOGGLE_SELECTOR = `${SUB_MENU_BASE_SELECTOR} > now-toggle`;
const NAV_ITEM_SELECTOR = `${FIRST_LEVEL_ITEM_BASE_SELECTOR} > sn-nav-item`;
const SUB_MENU_SELECTOR = `${FIRST_LEVEL_ITEM_BASE_SELECTOR} > button`;
const TOGGLE_SELECTOR = `${FIRST_LEVEL_ITEM_BASE_SELECTOR} > now-toggle`;

const CANVAS_USER_DROP_DOWN_MENU_ITEMS_SELECTOR = `${SUB_MENU_NAV_ITEM_SELECTOR},${SUB_MENU_TOGGLE_SELECTOR},${NAV_ITEM_SELECTOR},${SUB_MENU_SELECTOR},${TOGGLE_SELECTOR}`;

/**
 * Get displayed user menu drop down items. This includes submenu items with
 * now-collapse that are expanded
 *
 * @param {HTMLElement} host
 */
const _getDropDownItems = (host) => [
	...host.nowRootNode.querySelectorAll(
		CANVAS_USER_DROP_DOWN_MENU_ITEMS_SELECTOR
	)
];

/**
 * @param {HTMLElement} host
 */
const _clearTabIndex = (host) => {
	const listItems = _getDropDownItems(host);
	listItems.forEach((menuItem) => menuItem.setAttribute('tabindex', '-1'));
};

/**
 * @param {HTMLElement} host
 */
const _getFocusedElement = (host) => host.nowRootNode.activeElement;

/**
 * @param {HTMLElement} host
 */
const _getFocusedElementIndex = (host) => {
	const menuItems = _getDropDownItems(host);
	const focusedEl = _getFocusedElement(host);
	if (focusedEl) {
		const { id } = focusedEl.dataset;
		return menuItems.findIndex((item) => item.dataset.id === id);
	} else {
		return 0;
	}
};

/**
 * Find currently focused item and the set focus on the next item.
 * @param {HTMLElement} host
 */
const _focusNextItem = (host) => {
	const currentFocusIndex = _getFocusedElementIndex(host);
	const menuItems = _getDropDownItems(host);
	_clearTabIndex(host);
	if (currentFocusIndex < menuItems.length - 1) {
		_setFocus(menuItems[currentFocusIndex + 1]);
	} else {
		_focusFirstItem(host);
	}
};

/**
 * Find currently focused item and the set focus on the prior item.
 * @param {HTMLElement} host
 */
const _focusPreviousItem = (host) => {
	const currentFocusIndex = _getFocusedElementIndex(host);
	const menuItems = _getDropDownItems(host);
	_clearTabIndex(host);
	if (currentFocusIndex == 0) {
		_focusLastItem(host);
	} else {
		_setFocus(menuItems[currentFocusIndex - 1]);
	}
};

/**
 * @param {HTMLElement} element
 */
const _setFocus = (element) => {
	element.removeAttribute('tabindex');
	if (element) element.focus();
};

/**
 * @param {HTMLElement} host
 */
const _focusFirstItem = (host) => {
	const menuItems = _getDropDownItems(host);
	_clearTabIndex(host);
	if (menuItems.length > 0) {
		_setFocus(menuItems[0]);
	}
};

/**
 * @param {HTMLElement} host
 */
const _focusLastItem = (host) => {
	const menuItems = _getDropDownItems(host);
	_clearTabIndex(host);
	if (menuItems.length > 0) {
		_setFocus(menuItems[menuItems.length - 1]);
	}
};

export default [
	{
		capture: false,
		events: ['keydown'],
		effect({
			host,
			action: {
				payload: { event }
			}
		}) {
			const { key } = event;
			if ([ARROW_DOWN_KEY, ARROW_UP_KEY].indexOf(key) > -1) {
				event.preventDefault();
				event.stopPropagation();
			}
			switch (key) {
				case ARROW_DOWN_KEY:
					_focusNextItem(host);
					break;
				case ARROW_UP_KEY:
					_focusPreviousItem(host);
					break;
			}
		}
	}
];
