import { isEqual, isEmpty } from 'lodash';

import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import { t } from 'sn-translate';
import '@servicenow/now-icon';
import '@servicenow/now-card';
import '@servicenow/now-tooltip';

import snNotificationToastStyles from './_sn-canvas-notification-toast.scss';
import {
	NOTIFICATION_ITEM_CLICKED,
	NOTIFICATION_TOAST,
	ALERT_STATUS_READ
} from '../../behaviors/ui-notifications/constants';

const { COMPONENT_RENDERED } = actionTypes;

const delayAutoClose = (updateProperties, duration) => {
	return setTimeout(() => {
		updateProperties({ toastItem: {} });
	}, duration);
};

const notificationToastView = (state, { updateProperties, updateState }) => {
	const { toastItem, displayLength, dir } = state.properties;
	const { autoTimeout, lastToastItem } = state;

	if (isEmpty(toastItem) || isEqual(lastToastItem, toastItem)) {
		return null;
	}

	updateState({
		path: 'lastToastItem',
		value: { ...toastItem },
		operation: 'set',
		shouldRender: false
	});

	const { message, infoIcon, title } = toastItem;
	if (autoTimeout) {
		clearTimeout(autoTimeout);
	}
	updateState({
		path: 'autoTimeout',
		value: delayAutoClose(updateProperties, displayLength),
		operation: 'set',
		shouldRender: false
	});

	return (
		<Fragment>
			<div role="alert" aria-live="assertive" className="hidden-alert-msg">
				{title}
			</div>
			<span
				class={{
					'sn-canvas-notification-toast': true,
					'-rtl': dir === 'rtl',
					'-ltr': dir === 'ltr'
				}}>
				<now-card
					id="toast-card"
					role="button"
					config-aria={{
						button: { 'aria-label': t(`${title} ${message}`) }
					}}
					className="sn-canvas-notification-toast-card"
					append-to-payload={toastItem}
					interaction="select">
					<now-card-header
						className="sn-canvas-notification-toast-card-header"
						heading={{ label: title }}>
						<now-icon
							className="sn-canvas-notification-toast-card-icon"
							slot="identifier"
							icon={infoIcon}
						/>
					</now-card-header>
					{message.length > 0 ? (
						<div className="sn-canvas-notification-toast-card-description">
							<p>{message}</p>
						</div>
					) : null}
				</now-card>
				<now-button
					className="sn-canvas-notification-toast-close-btn"
					icon="close-outline"
					tooltip-content={t('Delete Notification')}
					variant="inherit"
					size="md"
					bare
					config-aria={{ 'aria-label': t(`Delete Notification: ${title}`) }}
					appendToPayload={{
						type: 'CLOSE_TOAST'
					}}
				/>
				<now-tooltip
					id="toast-card-tooltip"
					content={t(`${title} ${message}`)}
				/>
			</span>
		</Fragment>
	);
};

createCustomElement('sn-canvas-notification-toast', {
	renderer: {
		type: snabbdom,
		view: notificationToastView
	},
	styles: snNotificationToastStyles,
	initialState: {
		autoTimeout: null,
		lastToastItem: null
	},
	properties: {
		/**
		 * Object containing notification display information.
		 * @type {object}
		 */
		toastItem: {
			default: {}
		},
		/**
		 * Length (in milliseconds) to show each notification toast.
		 * @type {number}
		 */
		displayLength: {
			default: 6000
		}
	},
	actionHandlers: {
		[COMPONENT_RENDERED]: ({ host }) => {
			const card = host.shadowRoot.getElementById('toast-card');
			const tooltip = host.shadowRoot.getElementById('toast-card-tooltip');
			if (tooltip && card) tooltip.targetRef = card;
		},
		['NOW_CARD#SELECTED_SET']: {
			effect: ({ action, dispatch, updateProperties }) => {
				const { table, sys_id, alert_sys_id } = action.payload;
				dispatch(NOTIFICATION_ITEM_CLICKED, {
					table,
					mode: NOTIFICATION_TOAST,
					sysId: sys_id,
					alertSysId: alert_sys_id,
					status: ALERT_STATUS_READ
				});
				updateProperties({ toastItem: {} });
			},
			stopPropagation: true
		},
		['NOW_BUTTON#CLICKED']: {
			effect: ({ action, updateProperties }) => {
				const { type } = action.payload;
				if (type === 'CLOSE_TOAST') updateProperties({ toastItem: {} });
			},
			stopPropagation: true
		}
	},
	behaviors: [rtlBehavior]
});
