import { actionTypes } from '@servicenow/ui-core';
import presenceEffect from './presenceEffect';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;

const { RECORD_WATCHER } = presenceEffect.ACTION_TYPES;

const {
	handleRecordWatcher,
	handleItemChanged,
	onConnect,
	onDisconnect
} = presenceEffect.effectHandlers;

export const presenceBehavior = {
	name: 'PRESENCE_BEHAVIOR',
	properties: {
		/**
		 * Presence of the user, displayed with an indicator icon in the corner of the toolbar button.
		 * @type {object}
		 *
		 * {
		 *    status: "available",
		 *    configurations: {
		 *      table: "sys_user_presence"
		 *      filter: "user_available=true^user=6816f79cc0a8016401c5a33be04be441"
		 *    },
		 *    type: "record"
		 * }
		 */
		presence: { schema: { type: 'object' } }
	},
	actions: {
		[RECORD_WATCHER.SUBSCRIBED_STARTED]: { private: true },
		[RECORD_WATCHER.SUBSCRIBED]: { private: true },
		[RECORD_WATCHER.SUBSCRIBED_FAILED]: { private: true },
		[RECORD_WATCHER.UNSUBSCRIBED]: { private: true },
		[RECORD_WATCHER.ITEM_CHANGED]: { private: true },
		[RECORD_WATCHER.SUBSCRIPTION_CHANGED]: { private: true }
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: onConnect,
		[COMPONENT_DISCONNECTED]: onDisconnect,
		[RECORD_WATCHER.SUBSCRIPTION_CHANGED]: handleRecordWatcher,
		[RECORD_WATCHER.ITEM_CHANGED]: handleItemChanged
	}
};
