import { OptionItem } from '../../subcomponents';
const ControlMenu = (props) => {
	const { id, optionItems, highlightIndex, onItemClick } = props;
	const _className = props._className
		? props._className
		: 'sn-chrome-popover-content';
	const _getMenuRef = (ref) => {
		if (ref) ref.focus();
	};

	return (
		<ul
			id={id}
			className={_className + '-list'}
			role="presentation"
			ref={_getMenuRef}>
			{optionItems.map((m, i) => {
				let key = m.itemType === 'tab' ? m.tab_id : m.name || m.title;
				const isHighlighted = highlightIndex === i;
				return (
					<OptionItem
						key={key}
						item={m}
						index={i}
						isHighlighted={isHighlighted}
						onItemClick={onItemClick}
						_className={_className}
					/>
				);
			})}
		</ul>
	);
};

export { ControlMenu };
