import {
	find as _find,
	findIndex as _findIndex,
	get as _get,
	isEmpty as _isEmpty,
	has as _has,
	forOwn as _forOwn
} from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

import {
	updateButtonPresenceStatus,
	updateButtonBadgeCount,
	setButtonSelection,
	setButtonVisibility,
	updateButtonIcon
} from './utils';

const ACTION_TYPES = {
	/* user meta actions */
	USER_META_REQUEST: 'USER_META_REQUEST',
	USER_META_REQUEST_STARTED: 'USER_META_REQUEST_STARTED',
	USER_META_REQUEST_SUCCEEDED: 'USER_META_REQUEST_SUCCEEDED',
	USER_META_REQUEST_FAILED: 'USER_META_REQUEST_FAILED',

	// preference update
	USER_PREF_UPDATED: 'USER_PREF_UPDATED',
	USER_PREFERENCE_UPDATE_REQUEST_STARTED:
		'USER_PREFERENCE_UPDATE_REQUEST_STARTED',
	USER_PREFERENCE_UPDATE_REQUEST_SUCCEEDED:
		'USER_PREFERENCE_UPDATE_REQUEST_SUCCEEDED',
	USER_PREFERENCE_UPDATE_REQUEST_FAILED:
		'USER_PREFERENCE_UPDATE_REQUEST_FAILED',

	// viewport actions
	VIEWPORT_SELECTED: 'VIEWPORT_SELECTED',
	PRIME_NAV_SOURCE_CHANGED: 'PRIME_NAV_SOURCE_CHANGED' // action from sn-inbox
};

const CURRENT_USER_GQL_QUERY = `
query getUserMeta{
  GlideDomain_Query {
    user: user {
      sys_id
      firstName
      lastName
      fullName
      roles
      avatar
      initials
      timeZone
      timeFormat
	  language
	  timeZoneOffset
    }
  }
}
`;

const USER_PREFERENCE_UPDATE_GQL_QUERY = `
	mutation($name: String!, $value: String!) {
	  GlideDomain_Mutation {
		user {
		  setPreference(name: $name, value: $value) {
			name
			value
		  }
		}
	  }
	}
`;

const handleUserMetaRequest = createGraphQLEffect(CURRENT_USER_GQL_QUERY, {
	startActionType: ACTION_TYPES.USER_META_REQUEST_STARTED,
	successActionType: ACTION_TYPES.USER_META_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.USER_META_REQUEST_FAILED
});

const handleUserMetaResponse = (coeffects) => {
	const {
		updateProperties,
		action: { payload }
	} = coeffects;

	const { errors = [] } = payload;

	if (errors.length) {
		// eslint-disable-next-line no-console
		console.error('GlideDomain_Query.user returned errors: ', errors);
		return;
	}

	const userMeta = _get(payload, 'data.GlideDomain_Query.user');
	if (!_isEmpty(userMeta)) {
		updateProperties({ userMeta });
	}
};

const handleButtonUpdate = (coeffects) => {
	const {
		action: { payload },
		updateProperties,
		state: {
			properties: { toolbarButtons }
		}
	} = coeffects;

	let { id, status, badge, select = false, show = true, icon = '' } = payload;
	let button, index;

	if (!_isEmpty(id)) {
		id = _get(payload, 'id');
		button = _find(toolbarButtons, (b) => b.id === id);
		index = _findIndex(toolbarButtons, { id });

		if (!_isEmpty(button) && index !== -1) {
			if (_has(payload, 'icon') && !_isEmpty(icon)) {
				updateButtonIcon(button, index, toolbarButtons, icon, updateProperties);
			}

			if (_has(payload, 'status') && !_isEmpty(status)) {
				updateButtonPresenceStatus(
					button,
					index,
					toolbarButtons,
					status,
					updateProperties
				);
			}

			if (_has(payload, 'badge') && !_isEmpty(badge) && _has(badge, 'count')) {
				updateButtonBadgeCount(
					button,
					index,
					toolbarButtons,
					badge,
					updateProperties
				);
			}

			if (_has(payload, 'select')) {
				setButtonSelection(
					button,
					index,
					toolbarButtons,
					select,
					updateProperties
				);
			}

			if (_has(payload, 'show')) {
				setButtonVisibility(
					button,
					index,
					toolbarButtons,
					show,
					updateProperties
				);
			}
		}
	}
};

const handlePropertyChange = ({ properties, updateProperties }) => {
	const {
		toolbarButtonsConfig,
		userPrefs,
		processedUserPrefs,
		toolbarButtons
	} = properties;

	if (!_isEmpty(processedUserPrefs) && _isEmpty(userPrefs)) {
		updateProperties({ userPrefs: processedUserPrefs });
	}

	if (!_isEmpty(toolbarButtonsConfig) && _isEmpty(toolbarButtons)) {
		updateProperties({ toolbarButtons: toolbarButtonsConfig });
	}
};

const handleViewportSelection = (coeffects) => {
	const {
		action: {
			payload: { id, viewportInfo }
		},
		updateProperties,
		state: {
			properties: { toolbarButtons }
		},
		dispatch
	} = coeffects;

	const button = _find(toolbarButtons, (b) => b.id === id);
	const index = _findIndex(toolbarButtons, { id });

	if (!_isEmpty(button) && index > -1) {
		toolbarButtons.splice(index, 1, { ...button, viewportInfo });
		updateProperties({ toolbarButtons: [...toolbarButtons] });

		// update primary tab only for top group of toolbar buttons that controls the viewports
		if (button.group === 'top') {
			dispatch('PRIMARY_TAB_UPDATED', {
				button: { ...button, viewportInfo, size: '240px' }
			});

			//update the toolbar button to session
			window.sessionStorage.setItem(
				'canvasToolbar',
				JSON.stringify({ ...button, viewportInfo })
			);
		}
	}
	dispatch('UXF_VIEWPORT#RENDER_BY_ID', {
		...viewportInfo,
		reinitialize: true
	});
};

const handlePrimaryTabUpdate = (coeffects) => {
	const {
		action: {
			payload: {
				button: { id, label, icon, routeInfo, viewportInfo, size }
			}
		},
		updateProperties
	} = coeffects;

	updateProperties({
		primaryTab: {
			id,
			title: label,
			icon,
			routeInfo,
			viewportInfo,
			size
		}
	});
};

const handleUserPreferenceUpdateRequest = createGraphQLEffect(
	USER_PREFERENCE_UPDATE_GQL_QUERY,
	{
		startActionType: ACTION_TYPES.USER_PREFERENCE_UPDATE_REQUEST_STARTED,
		successActionType: ACTION_TYPES.USER_PREFERENCE_UPDATE_REQUEST_SUCCEEDED,
		errorActionType: ACTION_TYPES.USER_PREFERENCE_UPDATE_REQUEST_FAILED,
		variableList: ['name', 'value']
	}
);

const handleUserPreferenceUpdateResponse = (coeffects) => {
	const {
		properties,
		updateProperties,
		action: { payload }
	} = coeffects;

	const { errors = [] } = payload;

	if (errors.length) {
		// eslint-disable-next-line no-console
		console.error(
			'GlideDomain_Mutation.user.setPreference returned errors: ',
			errors
		);
		return;
	}

	const { userPrefs } = properties;
	const pref = _get(payload, 'data.GlideDomain_Mutation.user.setPreference');

	if (!_isEmpty(pref) && !_isEmpty(userPrefs)) {
		// update userPrefs property for the pref that changed
		_forOwn(userPrefs, (value, name) => {
			if (name === pref.name) {
				userPrefs[name] = pref.value;
			}
		});
		updateProperties({ userPrefs: { ...userPrefs } });
	}
};

const effectHandlers = {
	handlePropertyChange,
	handleUserMetaRequest,
	handleUserMetaResponse,
	handleUserPreferenceUpdateRequest,
	handleUserPreferenceUpdateResponse,
	handleButtonUpdate,
	handleViewportSelection,
	handlePrimaryTabUpdate
};

export default {
	ACTION_TYPES,
	effectHandlers
};
