import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import map from 'lodash/map';
import isEmpty from 'lodash/isEmpty';
import sortBy from 'lodash/sortBy';

import ActivityCenterLink from './activity-center-link';
import UserMenu from './user-menu';

const screenConfigurationLinkToMenuItem = (
	screenConfigurationLink,
	position
) => {
	const { label, href } = screenConfigurationLink;
	return {
		position,
		label,
		type: 'navigation',
		value: {
			type: 'external',
			opensWindow: true,
			value: { href }
		}
	};
};

const _renderToolsItems = (secondaryItems, experience) => {
	return secondaryItems && secondaryItems.length ? (
		<ul className="sn-canvas-global-tools-secondary-items" role="presentation">
			{secondaryItems.map((item) =>
				item.type === 'divider' ? null : (
					<ActivityCenterLink experience={experience} item={item} />
				)
			)}
		</ul>
	) : null;
};

const GlobalTools = ({
	globalToolsItems,
	notificationsEnabled,
	userPreferencesEnabled,
	userAvatar,
	userName,
	screenConfigurationLink,
	userPrefs,
	experience,
	showUserMenuTooltip,
	userMenuRef,
	updateState
}) => {
	const {
		primaryItems = [],
		secondaryItems = [],
		collapsingMenuId = 0,
		secondaryItemContainerWidth,
		secondaryItemsSize
	} = globalToolsItems;

	let dropdownMenuItems = get(
		primaryItems,
		`[${collapsingMenuId}].value.children`,
		[]
	);

	if (!isEmpty(screenConfigurationLink)) {
		const { link, position } = screenConfigurationLink;
		dropdownMenuItems = [
			...dropdownMenuItems,
			screenConfigurationLinkToMenuItem(link, position)
		];
	}

	// Determine if our secondary items overflow. If so, identify the number
	// of items to be moved to the user menu so that all secondary items
	// fit within the alloted width.
	let overflowItemCount = 0;
	if (secondaryItemContainerWidth && secondaryItemsSize) {
		let widths = map(secondaryItemsSize, 'width');
		const totalItems = widths.length;
		let totalWidths = widths.reduce((acc, val) => acc + val, 0);

		while (totalWidths > secondaryItemContainerWidth) {
			totalWidths = totalWidths - widths[totalItems - overflowItemCount - 1];
			overflowItemCount++;
		}
	}

	const sortedSecondaryItems = sortBy(secondaryItems, ['position']);
	const appendToDropDownItems = sortedSecondaryItems.splice(
		sortedSecondaryItems.length - overflowItemCount,
		overflowItemCount
	);
	dropdownMenuItems = [...dropdownMenuItems, ...appendToDropDownItems];
	const sortedPrimaryItems = sortBy(primaryItems, ['position']);
	const sortedDropdownItems = sortBy(dropdownMenuItems, ['position']);

	return (
		<Fragment>
			{_renderToolsItems(sortedSecondaryItems, experience)}
			<ul className="sn-canvas-global-tools-primary-items" role="presentation">
				{sortedPrimaryItems.map((item, index) =>
					index == collapsingMenuId ? null : (
						<ActivityCenterLink experience={experience} item={item} />
					)
				)}
				{notificationsEnabled ? (
					<li
						role="presentation"
						className="sn-canvas-global-tools-list-item  -notification-trigger -collapsible">
						<slot name="notificationTrigger" />
					</li>
				) : null}
				<li
					role="presentation"
					className="sn-canvas-global-tools-list-item -user-menu -collapsible">
					<UserMenu
						role="menuitem"
						userAvatar={userAvatar}
						userName={userName}
						userPreferencesEnabled={userPreferencesEnabled}
						items={sortedDropdownItems}
						userPrefs={userPrefs}
						experience={experience}
						showUserMenuTooltip={showUserMenuTooltip}
						userMenuRef={userMenuRef}
						updateState={updateState}
					/>
				</li>
			</ul>
		</Fragment>
	);
};

export default GlobalTools;
