import get from 'lodash/get';
import isNil from 'lodash/isNil';

import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import '@servicenow/now-collapse';
import '@servicenow/now-toggle';
import '@servicenow/now-icon';

import snCanvasUserMenuDropDownStyles from './_sn-canvas-user-menu-dropdown-panel-container.scss';
import keyboardEventHandler from './utils/keyboardEventHandler';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const createToggleInput = (label, value) => {
	return {
		label,
		value: {
			toggleValue: get(value, 'value', null),
			toggleAction: get(value, 'action.name', null),
			togglePayload: get(value, 'action.payload', {})
		}
	};
};

const getToggleValue = (name, preferences, defaultValue) => {
	// return bool
	// defaultValue and element.value are both strings, converted to bool during "return" statement
	if (!preferences) return defaultValue;
	let toggleValue = defaultValue;
	preferences.forEach((element) => {
		if (element[0] === name) {
			toggleValue = isNil(element[1])
				? toggleValue
				: element[1].toString().toLowerCase() === 'true';
		}
	});
	return toggleValue;
};

const renderToggle = ({ item_value, idx, userPrefs }) => {
	const {
		label = '',
		value: { toggleAction, togglePayload, toggleValue }
	} = item_value;

	const resolvedToggleValue = getToggleValue(
		togglePayload.name,
		userPrefs ? Object.entries(userPrefs) : null,
		toggleValue
	);
	const toggleId = `toggle_${idx}`;
	const toggleLabelId = `toggle_label_${idx}`;
	return (
		<li
			key={toggleId}
			role="menuitem"
			className="sn-canvas-user-menu-dropdown-list-item -toggle-item"
			on-click={(evt) => {
				// if click is on toggle element, ignore click event and let
				// togggle component action handler handle event.
				if (evt.target.tagName === 'NOW-TOGGLE') {
					return;
				}
				// if click is on the list item (i.e. surrounding area of toggle),
				// find the toggle and click it.
				const toggle = evt.target.nextElementSibling;
				toggle.shadowRoot.querySelector('input').click();
			}}>
			<label htmlFor={toggleId} id={toggleLabelId} className="toggle-label">
				{label}
			</label>
			<now-toggle
				data-id={toggleId}
				id={toggleId}
				appendToPayload={{
					toggleAction,
					togglePayload,
					menuIdx: idx
				}}
				className="last-in-line"
				size="sm"
				checked={resolvedToggleValue ? true : false}
				config-aria={{
					switch: { 'aria-label': label }
				}}
			/>
		</li>
	);
};

const renderNavItem = ({ item_value, dispatch, idx }) => {
	const {
		label,
		icon,
		value: { value, type = 'route', opensWindow = false }
	} = item_value;

	return (
		<li
			className="sn-canvas-user-menu-dropdown-list-item"
			on-click={() => {
				dispatch('NOW_POPOVER#OPENED_SET', { value: false });
			}}
			role="presentation">
			<sn-nav-item
				className="nav-item"
				data-id={idx}
				label={label}
				role="menuitem"
				variant="sub-nav"
				max-display
				tooltip={label}
				opens-window={opensWindow}
				type={type}
				item-value={value}>
				{icon ? <now-icon size="md" slot="startsWith" icon={icon} /> : null}
			</sn-nav-item>
		</li>
	);
};

const renderNavMenu = ({
	item,
	children,
	idx,
	expandedSubMenus,
	updateState,
	dispatch,
	userPrefs
}) => {
	const menuId = `menu_${idx}`;
	const isExpanded = expandedSubMenus.includes(menuId);
	return (
		<Fragment>
			<li
				className="sn-canvas-user-menu-dropdown-list-item"
				role="presentation">
				<button
					data-id={idx}
					role="menuitem"
					on-click={() => {
						isExpanded
							? updateState({
									path: 'expandedSubMenus',
									operation: 'splice',
									start: expandedSubMenus.indexOf(menuId),
									deleteCount: 1
							  })
							: updateState({
									path: 'expandedSubMenus',
									value: menuId,
									operation: 'push'
							  });
					}}>
					<span>{get(item, 'label', '')}</span>
					<now-icon
						size="md"
						className="last-in-line"
						icon={isExpanded ? 'chevron-up-outline' : 'chevron-down-outline'}
					/>
				</button>
			</li>
			<li role="presentation">
				<now-collapse id={`menu_${idx}`} expanded={isExpanded}>
					<ul
						role="menu"
						className="sn-canvas-user-menu-dropdown-list-sub-menu">
						{children.map(({ label, opensWindow, icon, type, value }, indx) => {
							if (type === 'divider') {
								return renderDivider(label);
							} else if (type === 'toggle') {
								return renderToggle({
									item_value: createToggleInput(label, value),
									idx: `${idx}${indx}`,
									userPrefs
								});
							} else {
								const item_value = {
									label,
									icon,
									value: { value, type, opensWindow }
								};
								return renderNavItem({
									item_value,
									dispatch,
									idx: `${idx}${indx}`
								});
							}
						})}
					</ul>
				</now-collapse>
			</li>
		</Fragment>
	);
};

const renderDivider = (label) => {
	return !label ? (
		<hr className="sn-canvas-user-menu-dropdown-list-divider" />
	) : (
		<li
			className="sn-canvas-user-menu-dropdown-list-divider -label"
			role="none">
			{label}
		</li>
	);
};

const snCanvasUserMenuDropDownPanel = (state, { dispatch, updateState }) => {
	const { expandedSubMenus, properties } = state;
	const { menuItems = {}, userPrefs } = properties;
	const { items = [] } = menuItems;
	return (
		<div
			className="sn-canvas-user-menu-dropdown-panel-container"
			role="presentation">
			<ul className="sn-canvas-user-menu-dropdown-list" role="menu">
				{items.map((item, idx) => {
					const { value: item_value = {} } = item;
					const { type, value, label = '' } = item_value;

					switch (type) {
						case 'navigation':
							return renderNavItem({ item_value, dispatch, idx });
						case 'toggle':
							return renderToggle({
								item_value: createToggleInput(label, value),
								idx,
								userPrefs
							});
						case 'menu':
							return renderNavMenu({
								item: item_value,
								children: get(get(item_value, 'value', {}), 'children', []),
								idx,
								expandedSubMenus,
								updateState,
								userPrefs
							});
						case 'divider':
							return renderDivider(item_value.label);
						default:
							break;
					}
				})}
			</ul>
		</div>
	);
};

createCustomElement('sn-canvas-user-menu-dropdown-panel', {
	properties: {
		menuItems: {
			default: {
				items: []
			}
		},
		userPrefs: {
			default: {}
		}
	},
	initialState: {
		expandedSubMenus: []
	},
	renderer: {
		type: snabbdom,
		view: snCanvasUserMenuDropDownPanel
	},
	styles: snCanvasUserMenuDropDownStyles,
	eventHandlers: [...keyboardEventHandler],
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
			const { name, value } = action.payload;
			if (name === 'nowHoist' && !value) updateState({ expandedSubMenus: [] });
		},
		['NOW_TOGGLE#CHECKED_SET']: {
			effect: ({ action, dispatch }) => {
				const { value, toggleAction, togglePayload } = action.payload;

				if (toggleAction) {
					dispatch(toggleAction, { ...togglePayload, value });
				}
			}
		}
	},
	behaviors: [rtlBehavior]
});
