import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import * as utils from '../utils/utils';
import {
	ITEM_CHANGED,
	SUBSCRIBED_STARTED,
	SUBSCRIBED,
	SUBSCRIBED_FAILED,
	UNSUBSCRIBED,
	ALERT_STATUS_NEW,
	SCREEN_STATUS_CHANGED,
	SUBSCRIPTION_CHANGED
} from '../constants';
import { buildAlertItem } from '../utils/buildAlertItem';

export const actionHandlers = {
	// AMB Record watcher
	[SUBSCRIPTION_CHANGED]: createAmbSubscriptionEffect(
		'/rw/default/:table/:filter',
		{
			messageReceivedActionType: ITEM_CHANGED,
			subscribeStartedActionType: SUBSCRIBED_STARTED,
			subscribeSucceededActionType: SUBSCRIBED,
			subscribeFailedActionType: SUBSCRIBED_FAILED,
			unsubscribeSucceededActionType: UNSUBSCRIBED
		}
	),
	[ITEM_CHANGED]: ({
		state,
		properties,
		action,
		dispatch,
		updateProperties,
		updateState
	}) => {
		const { record, sys_id: sys_id_val } = action.payload.data;
		if (record.status.value !== ALERT_STATUS_NEW) return;

		const rawAlert = { ...record, sys_id: { value: sys_id_val } };
		// Assume userMeta present in properties. This is coming from sn-canvas-chrome-state
		const { notification, userMeta } = properties;
		const {
			timeZoneOffset: userTimeZoneOffset,
			sys_id: userId,
			preferences = []
		} = userMeta;
		const showToast = utils.shouldShowToast(preferences);

		const transformedAlert = utils.getTransformedAlert(
			rawAlert,
			userTimeZoneOffset
		);
		const { alertPayload, updatePayload } = transformedAlert;

		const uiNotificationBehaviorState = utils.getUINotificationBehaviorState(
			state
		);
		const { offset, totalRowCount } = uiNotificationBehaviorState;

		const { queue, badgeList, trayDisplayed } = notification;

		const { sys_id } = alertPayload;

		// build toast item and ask to show toast
		const alertItem = buildAlertItem(alertPayload, userId);

		// Get all current alerts
		const { alertsToKeep, alertsToDelete } = utils.purgeOldAlerts(
			queue,
			alertItem,
			uiNotificationBehaviorState,
			'sys_id'
		);
		//Group correctly
		const newAlertQueue = utils.addDateGroupings(alertsToKeep);
		const shouldCalculateBadgeCount =
			!trayDisplayed && badgeList.indexOf(sys_id) === -1;
		// Calculate badge count
		if (shouldCalculateBadgeCount) {
			const newBadgeList = badgeList.concat(sys_id);

			updateProperties(() => ({
				path: 'notification',
				operation: 'assign',
				value: {
					badgeList: newBadgeList,
					badgeCount: newBadgeList.length
				}
			}));
		}

		updateState({
			path: utils.UI_NOTIFICATION_BEHAVIOR_STATE_PATH,
			value: {
				deleteQueue: alertsToDelete,
				offset: offset + 1,
				totalRowCount: totalRowCount + 1
			},
			operation: 'assign'
		});

		updateProperties(() => ({
			path: 'notification',
			operation: 'assign',
			value: {
				queue: newAlertQueue,
				trayIsLoading: utils.isNotificationTrayLoading(
					offset,
					totalRowCount,
					alertsToDelete
				),
				toastItem: showToast ? { ...alertItem } : {}
			}
		}));

		dispatch(SCREEN_STATUS_CHANGED, updatePayload);
	}
};
