import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import snCanvasFooterStyles from './_sn-canvas-footer.scss';
import { has as _has, slice as _slice } from 'lodash';
import rtlBehavior from '@servicenow/behavior-rtl';
import '../sn-nav-item';
import '@servicenow/now-icon';
import '@servicenow/now-link-set';
import '@servicenow/now-button';
import { isLinksMultiRow } from './utils/helpers';
import { getLogoSource } from '../../utils/logo-source-util';
import { t } from 'sn-translate';

const { COMPONENT_DOM_TREE_READY } = actionTypes;

const FooterLogo = ({ enableLogo, logoSource, logoAltText }) => {
	return enableLogo ? (
		<div className="sn-canvas-footer-nav-company-logo">
			<img
				className="sn-canvas-footer-nav-company-logo-img"
				src={logoSource}
				alt={logoAltText}
			/>
		</div>
	) : null;
};

const CompanyAddress = ({ enableAddress, address }) => {
	return enableAddress ? (
		<address className="sn-canvas-footer-nav-company-address">
			<a className="link" href={`mailto:${address.email}`}>
				{address.email}
			</a>
			<div className="address-first-line">
				<span className="text">{address.first_line.street}</span>
			</div>
			<div className="address-second-line">
				<span className="text">{address.second_line.city}</span>
				<span className="comma">,</span>
				<span className="text">{address.second_line.state}</span>
				<span className="text">{address.second_line.zip}</span>
			</div>
		</address>
	) : null;
};

const FooterLinkSet = ({ linksetGroup, linkSetPerRow }) => {
	return (
		<now-link-set-group
			size="md"
			linkSetPerRow={linkSetPerRow}
			items={linksetGroup}
		/>
	);
};

const FooterNav = ({
	logoSource,
	logoAltText,
	enableLogo,
	enableAddress,
	address,
	enableLinksetGroup,
	linkSetPerRow,
	linksetGroup
}) => {
	return (
		<Fragment>
			{enableLogo || enableAddress ? (
				<div className="sn-canvas-footer-nav-company">
					<FooterLogo
						enableLogo={enableLogo}
						logoSource={logoSource}
						logoAltText={logoAltText}
					/>
					<CompanyAddress enableAddress={enableAddress} address={address} />
				</div>
			) : null}
			{enableLinksetGroup ? (
				<div className="sn-canvas-footer-nav-linkset-group">
					<FooterLinkSet
						linksetGroup={linksetGroup}
						linkSetPerRow={linkSetPerRow}
					/>
				</div>
			) : null}
		</Fragment>
	);
};

const FooterTopbar = ({
	enable,
	logoSource,
	logoAltText,
	options,
	linksetGroup
}) => {
	const {
		enable_logo: enableLogo = false,
		enable_address: enableAddress = false,
		address = {},
		enable_linkset_group: enableLinksetGroup = false,
		linkset_per_row: linkSetPerRow = 5
	} = options;
	const resolvedLogoSource = getLogoSource(logoSource, 'footer_logo');

	return enable ? (
		<div className="sn-canvas-footer-nav">
			<FooterNav
				logoSource={resolvedLogoSource}
				logoAltText={logoAltText}
				enableLogo={enableLogo}
				enableAddress={enableAddress}
				address={address}
				enableLinksetGroup={enableLinksetGroup}
				linkSetPerRow={linkSetPerRow}
				linksetGroup={linksetGroup}
			/>
		</div>
	) : null;
};

const Links = ({ enable, links, maxLinksCount, linksWithMultiRow }) => {
	const getLinks = () => {
		return _slice(links, 0, maxLinksCount);
	};

	const BuildLink = ({ link }) => {
		const {
			type,
			opensWindow,
			label,
			itemValue,
			variant = 'simple-link-nav',
			download = false
		} = link;

		return (
			<li className="sn-canvas-footer-sub-nav-link">
				<sn-nav-item
					type={type}
					opens-window={opensWindow}
					label={label}
					item-value={itemValue}
					variant={variant}
					download={download}
					config-aria={{
						link: { 'aria-label': label }
					}}
				/>
			</li>
		);
	};

	return enable ? (
		<nav className="sn-canvas-footer-sub-nav-links">
			<ul class={{ '-clear-top-margin': linksWithMultiRow }}>
				{getLinks().map((link) => (
					<BuildLink link={link} />
				))}
			</ul>
		</nav>
	) : null;
};

const CopyrightText = ({ enable, text, linksWithMultiRow }) => {
	return enable ? (
		<p
			class={{
				'sn-canvas-footer-sub-nav-copyright-text': true,
				'-align-bottom': linksWithMultiRow
			}}>
			{text}
		</p>
	) : null;
};

const SocialLinks = ({ enable, socialLinks, maxSocialLinksCount }) => {
	const getSocialLinks = () => {
		return _slice(socialLinks, 0, maxSocialLinksCount);
	};

	const BuildSocialLink = ({ link }) => {
		const { icon, label, url } = link;

		return (
			<li className="sn-canvas-footer-sub-nav-social-link">
				<now-button
					icon={icon}
					variant="inherit"
					size="md"
					bare="true"
					configAria={{ button: { 'aria-label': label } }}
					tooltipContent={label}
					on-click={() => window.open(url, '_blank')}
				/>
			</li>
		);
	};

	return enable ? (
		<nav className="sn-canvas-footer-sub-nav-social-links">
			<ul>
				{getSocialLinks().map((link) => (
					<BuildSocialLink link={link} />
				))}
			</ul>
		</nav>
	) : null;
};

const FooterBar = ({ hideTopPadding, enable, options, linksWithMultiRow }) => {
	const {
		enable_links: enableLinks,
		max_links_count: maxLinksCount,
		links,
		enable_social_links: enableSocialLinks,
		max_social_links_count: maxSocialLinksCount = 7,
		social_links: socialLinks,
		enable_copyright_text: enableCopyrightText,
		copyright_text: copyrightText
	} = options;

	return enable ? (
		<div
			class={{
				'sn-canvas-footer-sub-nav': true,
				'-no-top-padding': hideTopPadding,
				'-align-center': !linksWithMultiRow
			}}>
			{/* footer bar links*/}
			<Links
				enable={enableLinks}
				links={links}
				maxLinksCount={maxLinksCount}
				linksWithMultiRow={linksWithMultiRow}
			/>
			{/* social links */}
			<SocialLinks
				enable={enableSocialLinks}
				socialLinks={socialLinks}
				maxSocialLinksCount={maxSocialLinksCount}
			/>
			{/* copyright text */}
			<CopyrightText
				enable={enableCopyrightText}
				text={copyrightText}
				linksWithMultiRow={linksWithMultiRow}
			/>
		</div>
	) : null;
};

const snCanvasFooter = (state) => {
	const {
		logoSource,
		logoAltText,
		footerConfig = {},
		footerLinksetConfig = {}
	} = state.properties;
	const { public_page: publicPageFooterConfig } = _has(footerConfig, 'global')
		? footerConfig['global']
		: { public_page: {} }; // footer will have global configs across all pages
	const { linkset_group: linksetGroup } = _has(footerLinksetConfig, 'global')
		? footerLinksetConfig['global']
		: { linkset_group: {} };

	const {
		enable_footer_topbar: enableFooterTopbar = false,
		footer_topbar_options: footerTopbarOptions = {},
		enable_footer_bar: enableFooterBar = false,
		footer_bar_options: footerBarOptions = {}
	} = publicPageFooterConfig;
	const { linksWithMultiRow } = state;

	return enableFooterTopbar || enableFooterBar ? (
		<footer
			className="sn-canvas-footer"
			role="contentinfo"
			aria-label={t('Footer')}>
			<FooterTopbar
				enable={enableFooterTopbar}
				logoSource={logoSource}
				logoAltText={logoAltText}
				options={footerTopbarOptions}
				linksetGroup={linksetGroup}
			/>
			<FooterBar
				hideTopPadding={!enableFooterTopbar}
				enable={enableFooterBar}
				options={footerBarOptions}
				linksWithMultiRow={linksWithMultiRow}
			/>
		</footer>
	) : null;
};

/**
 * Common footer component to be used in Applications built with UX Framework / UI Builder, which includes : 
 *  - **Footer Topbar**: logo, address and Linkset group
 *  - **Footer Bar**: Links, Social links, Copyright text
 *
 * ```jsx
 * const footerData = {
 *   global: {
 * 		public_page: {
 * 			enable_footer_topbar: true,
 * 			footer_topbar_options: {
 * 				enable_logo: true,
 * 				enable_address: true,
 * 				address: {
 * 					email: 'info@servicenow.com',
 * 					first_line: {
 * 						street: '2225 Lawson Lane'
 * 					},
 * 					second_line: {
 * 						city: 'Santa Clara',
 * 						state: 'CA',
 * 						zip: '95054'
 * 					}
 * 				},
 * 				enable_linkset_group: true,
 * 				linkset_per_row: 5
 * 			},
 * 			enable_footer_bar: true,
 * 			footer_bar_options: {
 * 				enable_links: true,
 * 				max_links_count: 3,
 * 				links: [
 * 					{
 * 						type: 'external',
 * 						opensWindow: '_blank',
 * 						label: 'Terms and conditions',
 * 						itemValue: {
 * 							url: 'https://www.servicenow.com/terms-of-use.html'
 * 						}
 * 					},
 * 					{
 * 						type: 'external',
 * 						opensWindow: '_blank',
 * 						label: 'Privacy policy',
 * 						itemValue: {
 * 							url: 'https://www.servicenow.com/service-privacy.html'
 * 						}
 * 					},
 * 					{
 * 						type: 'external',
 * 						opensWindow: '_blank',
 * 						label: 'GDPR',
 * 						itemValue: {
 * 							url: 'https://www.servicenow.com/terms-of-use.html'
 * 						}
 * 					}
 * 				],
 * 				enable_social_links: true,
 * 				max_social_links_count: 3,
 * 				social_links: [
 * 					{
 * 						icon: 'envelope-fill',
 * 						label: 'twitter',
 * 						url: 'https://twitter.com/servicenow'
 * 					},
 * 					{
 * 						icon: 'envelope-fill',
 * 						label: 'linkedin',
 * 						url: 'https://www.linkedin.com/company/servicenow/'
 * 					},
 * 					{
 * 						icon: 'envelope-fill',
 * 						label: 'facebook',
 * 						url: 'https://www.facebook.com/servicenow'
 * 					}
 * 				],
 * 				enable_copyright_text: true,
 * 				copyright_text: 'Â© 2020 ServiceNow. All rights reserved.'
 * 			}
 * 		}
 * 	}
 * };
 *  // See link set data structure https://code.devsnc.com/dev/now-link-set#now-link-set-api
 *  const const linksetGroupFooter = {
 * 	  global: {
 *         linkset_group: [
 *             { 
 *                 heading: {...}, 
 *                 links: [{...}], 
 *                 browseLink: {} 
 *             }]
 *     }
 * 
 *  <sn-canvas-footer
 *     logo-alt-text="Company logo"
 *     logo-source="/footer_logo_dark_bg.svg"
 *     footer-config={footerData}
 *     footer-linkset-config={linksetGroupFooter}
 *  />
 
 * ```
 *
 */
createCustomElement('sn-canvas-footer', {
	initialState: {
		linksWithMultiRow: false
	},
	properties: {
		/**
		 * Defines the logo alt text.
		 *
		 * @type {(string)}
		 */
		logoAltText: {
			default: 'Company logo',
			schema: { type: 'string' }
		},
		/**
		 * Defines the logo source.
		 *
		 * @type {(string)}
		 */
		logoSource: {
			default: '',
			schema: { type: 'string' }
		},
		/**
		 * Properties to configure the Footer links, social links, logo, address and copyright.
		 *
		 * @type {(Object)}
		 */
		footerConfig: {
			default: {},
			schema: { type: 'object' }
		},
		/**
		 * Properties to configure the canvas footer linkset.
		 *
		 * @type {(Object)}
		 */
		footerLinksetConfig: {
			default: {},
			schema: { type: 'object' }
		}
	},
	renderer: {
		type: snabbdom,
		view: snCanvasFooter
	},
	styles: snCanvasFooterStyles,
	behaviors: [rtlBehavior],
	actionHandlers: {
		[COMPONENT_DOM_TREE_READY]: ({ host, updateState }) => {
			isLinksMultiRow(host) ? updateState({ linksWithMultiRow: true }) : null;
		}
	}
});
