import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { MAX_DEPTH, LEVEL_ONE, LEVEL_TWO, LEVEL_THREE } from '../constants';

/**
 * Get the total internal grid width to set for the megamenu panel. The width will be set based on how many
 * grid items can fit within the max width. Takes account of padding and grid column gaps.
 *
 * @param {Object}
 */
export const getTotalGridWidth = ({
	itemCount,
	maxWidth,
	columnGap,
	itemWidth,
	padding
}) => {
	let width = itemWidth + padding * 2;
	const totalWidthOfNewGridItem = itemWidth + columnGap;

	let idx = 1;

	while (idx < itemCount) {
		if (maxWidth < width + totalWidthOfNewGridItem) {
			break;
		}
		width += totalWidthOfNewGridItem;
		idx++;
	}

	return width;
};

/**
 * Find the depth of the menu items. We assume that menu items are balanced, so we only need to check the first item
 * list in children.
 *
 * @param {Object} item
 */
export const findDepth = (item) => {
	let depth = 0;
	let children = item;
	while (children || depth === MAX_DEPTH) {
		children = get(children, 'children.items[0]', false);
		if (isEmpty(children)) {
			return depth;
		}
		depth++;
	}
	return depth;
};

/**
 * Get count of linkset items for a megamenu.
 * @param {Object} items
 * @param {number} depth
 */
export const maxItemCount = (items) => {
	const { depth, childList } = items.root;

	switch (depth) {
		// For level one menus the item count will always be 1.
		case LEVEL_ONE:
			return 1;
		// Return the count of items in the linkset group.
		case LEVEL_TWO:
			return childList.length;
		// Return the linkset group with the highest linkset count
		case LEVEL_THREE:
			return childList.reduce(
				(acc, curr) => Math.max(items[curr].childList.length, acc),
				0
			);
	}
};
