import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { uxfViewportBehavior } from '@devsnc/library-uxf';
import snCanvasViewportEffect from './snCanvasViewportEffect';
import snCanvasViewportStyles from './_sn-canvas-viewport.scss';

const { COMPONENT_DOM_READY, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const {
	handleComponentDOMReady,
	handleComponentPropertyChanged
} = snCanvasViewportEffect.effectHandlers;

const snCanvasViewport = () => {
	return (
		<div className="sn-canvas-viewport">
			<slot name="viewport" />
		</div>
	);
};

createCustomElement('sn-canvas-viewport', {
	properties: {
		preload: { default: false, schema: { type: 'boolean' } },
		includesFieldsParams: { default: false, schema: { type: 'boolean' } },
		options: { default: {}, schema: { type: 'object' } },
		activeParentId: { default: null, schema: { type: 'string' } },
		activeRecords: { default: null, schema: { type: 'array' } },
		maxTabLimit: { default: 0, schema: { type: 'number' } }
	},
	renderer: {
		type: snabbdom,
		view: snCanvasViewport
	},
	styles: snCanvasViewportStyles,
	actionHandlers: {
		[COMPONENT_DOM_READY]: handleComponentDOMReady,
		[COMPONENT_PROPERTY_CHANGED]: handleComponentPropertyChanged
	},
	behaviors: [uxfViewportBehavior]
});
