import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { filterAriaAttributes } from '@servicenow/library-enhanced-utils';
import { t } from 'sn-translate';
import '@servicenow/now-button';
import '@servicenow/now-tooltip';
import '@servicenow/now-badge';
import snCanvasToolbarButtonStyles from './_sn-canvas-toolbar-button.scss';
import { badgeCountBehavior } from '../../behaviors/badge-count';
import { presenceBehavior } from '../../behaviors/presence';
import { isEmpty as _isEmpty, get as _get } from 'lodash';
import rtlBehavior from '@servicenow/behavior-rtl';

const { COMPONENT_DOM_READY, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const SN_CANVAS_TOOLBAR_BUTTON_CLICKED = 'SN_CANVAS_TOOLBAR_BUTTON#CLICKED';

const _renderIcon = (icon) => {
	return icon ? (
		<now-icon icon={icon} size="lg" aria-hidden="true" />
	) : (
		undefined
	);
};

const snCanvasToolbarButton = (state, { dispatch, updateProperties }) => {
	const {
		id,
		label,
		icon,
		badge,
		presence,
		isSelected,
		group
	} = state.properties;
	const status = _get(presence, 'status', '');
	const configAria = { button: { 'aria-label': label } };
	const tooltipContent = label;
	const { targetRef } = state;

	const renderTooltip =
		targetRef && tooltipContent ? (
			<now-tooltip
				id="tooltip"
				target-ref={targetRef}
				content={tooltipContent}
				offset={[0, 0, -12, -12, -12, -12]}
			/>
		) : (
			undefined
		);

	const _renderBadge = (badge) => {
		return !_isEmpty(badge) && !!badge.count ? (
			<now-badge
				class={{ '-badge-count': true }}
				round
				size="sm"
				status="critical"
				value={badge.count}
				aria-label={t('badge counter ' + badge.count)}
				variant="primary"
			/>
		) : (
			undefined
		);
	};

	const _renderPresence = (status) => {
		return status ? (
			<now-icon-presence
				class={{ '-presence-indicator': true }}
				size="sm"
				presence={status}
				aria-hidden="true"
				show-pattern="true"
			/>
		) : (
			undefined
		);
	};

	const onButtonClicked = (evt) => {
		evt.preventDefault();
		const { targetRef } = state;
		const {
			isSelected,
			routeInfo,
			viewportInfo,
			group,
			toggleState
		} = state.properties;
		const isViewport = _isEmpty(routeInfo) && !_isEmpty(viewportInfo);

		// set the button selection for toggle buttons for viewports e.g. cti call
		isViewport ? updateProperties({ isSelected: !isSelected }) : null;

		dispatch(SN_CANVAS_TOOLBAR_BUTTON_CLICKED, {
			value: !toggleState,
			buttonClicked: targetRef,
			routeInfo: routeInfo,
			viewportInfo: viewportInfo,
			group: group
		});
		updateProperties({ toggleState: !state.properties.toggleState });
	};

	const getAriaRole = (group) => (group === 'top' ? 'radio' : 'button');

	const ariaPressedCheckedOrSelected = (ariaRole, selected) => {
		if (ariaRole === 'button') {
			return { ['aria-pressed']: selected ? 'true' : 'false' };
		}
		if (ariaRole === 'radio') {
			return { ['aria-checked']: selected ? 'true' : 'false' };
		}
	};

	return (
		<Fragment>
			<button
				id={`${id.toLowerCase()}-btn`}
				type="button"
				value={label}
				class={{
					'sn-canvas-toolbar-button': true,
					'is-selected': isSelected
				}}
				tabIndex={isSelected ? '0' : '-1'}
				role={getAriaRole(group)}
				{...ariaPressedCheckedOrSelected(getAriaRole(group), isSelected)}
				{...filterAriaAttributes(configAria, 'button')}
				on-click={onButtonClicked}>
				{_renderIcon(icon)}
				{_renderBadge(badge)}
				{_renderPresence(status)}
			</button>
			{renderTooltip}
		</Fragment>
	);
};
/*
 * A toolbar button component that appears on the toolbar.
 *
 * *button configuration*
 * ```json
 *
 * {
 * 		"id": "home",
 *  	"label": "Home",
 *  	"icon": "home-outline",
 *  	"viewportInfo": {},
 *  	"routeInfo": {
 *    	"route": "home"
 *  	},
 *  	"group": "top",
 *  	"badge": {},
 *  	"presence": {},
 *  	"availability": {}
 * }
 *
 * ```
 * @seismicElement sn-canvas-toolbar-button
 * @summary A toolbar button that appears on the toolbar that refers to a resource. The target resource must be an
 * internal page of current application.
 */
createCustomElement('sn-canvas-toolbar-button', {
	properties: {
		/**
		 * Properties to configure the canvas toolbar button.
		 *
		 * Defines the button id.
		 * @type {string}
		 */
		id: { required: true, schema: { type: 'string' } },
		/**
		 * Defines the button value.
		 * @type {string}
		 */
		label: { required: true, schema: { type: 'string' } },
		/**
		 * Defines the icon displayed.
		 * @type {string}
		 */
		icon: { required: true, schema: { type: 'string' } },
		/**
		 * Defines the viewport information.
		 * @type {object}
		 */
		viewportInfo: { schema: { type: 'object' } },
		/**
		 * Defines the badge information.
		 * @type {object}
		 */
		badge: { schema: { type: 'object' } },
		/**
		 * Presence of the user, displayed with an indicator icon in the corner of the toolbar button.
		 * @type {object}
		 */
		presence: { schema: { type: 'object' } },
		/**
		 * Indicates the selected state of the button.
		 * Maps to `aria-checked` for a radio button
		 * @type {boolean}
		 */
		isSelected: { default: false, schema: { type: 'boolean' } },
		/**
		 * Defines the route information.
		 * @type {object}
		 */
		routeInfo: { schema: { type: 'object' } },
		/**
		 * Defines the button availability criteria based on user roles, plugin activation.
		 * @type {object}
		 */
		availability: { schema: { type: 'object' } },
		/**
		 * Defines the button placement group - top vs bottom.
		 * @type {string}
		 */
		group: { required: true, schema: { type: 'string' } },
		/**
		 * Indicates the oggle state of the button.
		 * @type {boolean}
		 */
		toggleState: { default: false, schema: { type: 'boolean' } }
	},
	renderer: {
		type: snabbdom,
		view: snCanvasToolbarButton
	},
	styles: snCanvasToolbarButtonStyles,
	dispatches: {
		/**
		 * Dispatched when a link points to an internal application page.
		 * @type {{value: true, buttonClicked: targetRef, routeInfo: {}}}
		 */
		SN_CANVAS_TOOLBAR_BUTTON_CLICKED: {}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({
			host,
			properties,
			updateState,
			updateProperties
		}) => {
			const targetRef = host.shadowRoot.querySelector(
				'.sn-canvas-toolbar-button'
			);
			updateState({ targetRef });
			// initial toggleState for the button, that manages the toggle
			// state for button with every click
			updateProperties({ toggleState: properties.isSelected });
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			properties,
			updateProperties
		}) => {
			const { isSelected } = properties;
			const { name, value, previousValue } = action.payload;
			if (!_isEmpty(name) && name === 'isSelected' && previousValue !== value) {
				updateProperties({ toggleState: isSelected });
			}
		}
	},
	behaviors: [badgeCountBehavior, presenceBehavior, rtlBehavior]
});
