import { isEmpty as _isEmpty, find as _find } from 'lodash';

import {
	resetViewportSelection,
	setToolbarButtonSelected,
	getSessionStorageKey,
	setActiveButton,
	resetSizeAndViewportInfo,
	isViewportEmptyPageLoaded,
	loadViewport
} from './utils/helpers';

const ACTION_TYPES = {
	TOOLBAR_BUTTON_CLICKED: 'SN_CANVAS_TOOLBAR_BUTTON#CLICKED',
	TOOLBAR_ACTIVE_BUTTON_SET: 'SN_CANVAS_TOOLBAR#ACTIVE_BUTTON_SET',
	TOOLBAR_ACTIVE_BUTTON_UPDATED: 'SN_CANVAS_TOOLBAR#ACTIVE_BUTTON_UPDATED'
};

const handleToolbarButtonClicked = ({
	host,
	action,
	dispatch,
	state,
	updateProperties
}) => {
	const { buttonClicked, viewportInfo, routeInfo, group } = action.payload;
	const { toolbarButtons, activeRecords } = state.properties;
	const { activeButton } = state;
	const isRoute = !_isEmpty(routeInfo) && _isEmpty(viewportInfo);
	const isViewport = _isEmpty(routeInfo) && !_isEmpty(viewportInfo);
	const isViewportWithRoute = !_isEmpty(routeInfo) && !_isEmpty(viewportInfo);
	const button = _find(
		toolbarButtons,
		(b) => buttonClicked.id.indexOf(b.id) !== -1
	);

	// Buttons in toolbar configured for navigational purposes will have routeInfo in their payload
	// otherwise, it's a toggle switch that simply dispatches to indicate that toolbar's button got selected

	//clear the previous selection and set the clicked button as selected.  Store it as state.route
	if (group === 'top') {
		const serializedActiveButton = JSON.stringify(button);
		window.sessionStorage.setItem(
			getSessionStorageKey(),
			serializedActiveButton
		);
		dispatch('SN_CANVAS_TOOLBAR#ACTIVE_BUTTON_UPDATED', { button });
		setToolbarButtonSelected(host, buttonClicked, true);
	}

	if (isRoute) {
		// For all of toolbar button with viewport that behaves as a toggle needs to turn off
		// and enable that's are the part of route
		// should this be determined from button configuration/
		resetViewportSelection(toolbarButtons, button, dispatch, updateProperties);

		// navigate to route
		dispatch('NAV_ITEM_SELECTED', routeInfo);

		// update the primary tab
		dispatch('PRIMARY_TAB_UPDATED', {
			button: { ...button, size: null, viewportInfo: null }
		});
	} else if (isViewport) {
		// load the viewport
		loadViewport(activeRecords, toolbarButtons, button, dispatch);
	} else if (isViewportWithRoute) {
		// For all of toolbar button with viewport that behaves as a toggle needs to turn off
		resetViewportSelection(toolbarButtons, button, dispatch, updateProperties);

		//load the viewport
		loadViewport(activeRecords, toolbarButtons, button, dispatch);

		// when there are no active tabs, navigate to empty page route
		// currently we have inbox & ongoing only for top group
		if (
			_isEmpty(activeRecords) &&
			group === 'top' &&
			!_isEmpty(routeInfo) &&
			!isViewportEmptyPageLoaded(activeButton, viewportInfo)
		) {
			dispatch('NAV_ITEM_SELECTED', routeInfo);
		} else if (
			!_isEmpty(activeRecords) &&
			group === 'top' &&
			!_isEmpty(routeInfo)
		) {
			// resolve to the first tab implicitly
			dispatch('SELECT_TAB', { tab_ids: [], _implicitTabResolution: true });
		}
	}
};

/**
 * handleActiveButtonSet
 * @param activeButton
 * @param toolbarButtons
 * @param activeRoute
 * @param updateProperties
 * @param updateState
 * @param dispatch
 */
const handleActiveButtonSet = ({
	state: {
		activeButton,
		properties: { toolbarButtons, activeRecords }
	},
	action: {
		payload: { activeRoute }
	},
	updateProperties,
	updateState,
	dispatch
}) => {
	setActiveButton(
		activeButton,
		toolbarButtons,
		activeRoute,
		activeRecords,
		dispatch,
		updateProperties,
		updateState
	);
};

/**
 * handleActiveButtonUpdated
 * @param payload
 * @param updateState
 */
const handleActiveButtonUpdated = ({ action: { payload }, updateState }) => {
	updateState({ activeButton: payload.button });
};

/**
 * handlePropertyChanged
 * @param state
 * @param action
 * @param dispatch
 * @param updateProperties
 */
const handlePropertyChanged = ({
	state,
	action,
	dispatch,
	updateProperties
}) => {
	const { name, value, previousValue } = action.payload;
	const {
		activeButton,
		properties: { toolbarButtons, activeRecords }
	} = state;
	const { id, viewportInfo, routeInfo, group } = activeButton;
	const isViewportWithRoute = !_isEmpty(routeInfo) && !_isEmpty(viewportInfo);

	if (
		name === 'activeRecords' &&
		!_isEmpty(id) &&
		isViewportWithRoute &&
		group === 'top' &&
		!_isEmpty(previousValue) &&
		_isEmpty(value)
	) {
		// when there are no active tabs, navigate to empty page route
		// currently we have inbox & ongoing only for top group
		dispatch('NAV_ITEM_SELECTED', routeInfo);
	}

	if (
		name === 'activeRoute' &&
		typeof value === 'object' &&
		!_isEmpty(value) &&
		!_isEmpty(toolbarButtons) &&
		Array.isArray(activeRecords)
	) {
		// handle browser back & forward to resolve the
		//  - active button and
		//  - reset the primary tab size
		const { passiveNavigation } = value;
		if (passiveNavigation === true) return;

		// reset the primary tab size
		resetSizeAndViewportInfo(value, toolbarButtons, dispatch, updateProperties);

		// set the active button for the toolbar
		dispatch(ACTION_TYPES.TOOLBAR_ACTIVE_BUTTON_SET, { activeRoute: value });
	}
};

/**
 *
 * @type {{
 *	 handleActiveButtonUpdated:  handleActiveButtonUpdated,
 *	 handleToolbarButtonClicked: handleToolbarButtonClicked,
 *	 handleActiveButtonSet: handleActiveButtonSet,
 *	 handleActiveButtonViewportLoaded: handleActiveButtonViewportLoaded
 * }}
 */
const effectHandlers = {
	handleToolbarButtonClicked,
	handleActiveButtonSet,
	handleActiveButtonUpdated,
	handlePropertyChanged
};

export default {
	effectHandlers,
	ACTION_TYPES
};
