import clamp from 'lodash/clamp';
import { tabsConfig } from '../../../constants/canvas_config';

let _tabsLimit = null;
export function getMaxTabLimit(tabsConfigProperty) {
	const { valid_max_tabs_min, valid_max_tabs_max } = tabsConfig;

	if (!_tabsLimit) {
		let mainLimit =
			tabsConfigProperty && tabsConfigProperty.maxMainTabLimit
				? tabsConfigProperty.maxMainTabLimit
				: 10;
		mainLimit = clamp(mainLimit, valid_max_tabs_min, valid_max_tabs_max) + 1;
		// +1 because the primary tab occupies the first tab slot and doesn't count against the limit

		let subLimit =
			tabsConfigProperty && tabsConfigProperty.maxTotalSubTabLimit
				? tabsConfigProperty.maxTotalSubTabLimit
				: 30;
		subLimit = clamp(subLimit, mainLimit, valid_max_tabs_max + 1);
		// +1 becasuse mainLimit can be valid_max_tabs_max + 1 from line 10

		_tabsLimit = {
			mainLimit,
			subLimit
		};
	}
	return _tabsLimit;
}
