import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snCanvasSidepanelStyles from './_sn-canvas-sidepanel.scss';
import rtlBehavior from '@servicenow/behavior-rtl';
import { t } from 'sn-translate';
import '@servicenow/now-heading';
import '@servicenow/now-button';
import { POSITIONS } from './positions';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const _handleClose = (state, dispatch) => {
	let { beforeClose } = state.properties;
	if (beforeClose && typeof beforeClose === 'function') {
		let callbackFn = new Promise((resolve) => {
			return resolve(beforeClose());
		});
		callbackFn.then((res) => {
			res
				? dispatch('SN_CANVAS_SIDEPANEL#TOGGLE_OPENED', { opened: false })
				: null;
		});
	} else {
		dispatch('SN_CANVAS_SIDEPANEL#TOGGLE_OPENED', { opened: false });
	}
};

const SnCanvasSidepanel = (state, { dispatch }) => {
	const {
		opened,
		backdrop,
		position,
		containerProps,
		canClose,
		heading,
		isTitleClickable
	} = state.properties;

	return (
		<div
			role="dialog"
			class={{ 'sn-canvas-sidepanel': true, '-backdrop': backdrop }}
			hidden={!opened}
			on-click={() => {
				_handleClose(state, dispatch);
			}}>
			<div
				className={'sn-canvas-sidepanel-overlay -' + position.toLowerCase()}
				style={containerProps}
				on-click={(evt) => evt.stopPropagation()}>
				<div className="sn-canvas-sidepanel-overlay-panel">
					{heading ? (
						<div
							className="sn-canvas-sidepanel-op-header"
							style={{ cursor: isTitleClickable ? 'pointer' : 'auto' }}
							on-click={() => {
								isTitleClickable
									? dispatch('SN_CANVAS_SIDEPANEL#TITLE_CLICKED')
									: null;
							}}>
							<div className="sn-canvas-sidepanel-oph-title">
								<slot name="title">
									<now-heading
										label={heading}
										level="1"
										variant="header-secondary"
										hasNoMargin
									/>
								</slot>
							</div>
							{canClose ? (
								<div className="sn-canvas-sidepanel-oph-icon">
									<now-button
										icon-name="close-outline"
										variant="tertiary"
										size="md"
										config-aria={{ 'aria-label': t('Close') }}
										bare
										on-click={(evt) => {
											_handleClose(state, dispatch);
											evt.stopPropagation();
										}}
									/>
								</div>
							) : null}
						</div>
					) : null}
					<slot name="content" />
				</div>
			</div>
		</div>
	);
};

/**
 *
 * ## Overview
 * The sidepanel component is a panel overlaying the screen on the right side.
 * It is used to show more details about a specific object in context. A sidepanel is often triggered by clicking a button
 * or icon that dispatches the action to toggle the visibility.
 *
 * ## Use cases
 * - Showing a sidepanel menu for mobile responsiveness.
 * - Showing detailed information about a transaction (such as items in a cart).
 * - Showing a list of notifications in a notifications tray (sidepanel).
 *
 * Sidepanel aims at UI needs to display workspace notification tray, portal mobile menu panel, and it could also be used
 * for contextual overlay panel across the viewport sides.
 *
 * @seismicElement sn-canvas-panel
 * @summary A panel component to display workspace notifications, portal menus and other contextual overlay on the viewport
 */
createCustomElement('sn-canvas-sidepanel', {
	properties: {
		/**
		 * Property opened sets the state of the panel
		 * @type {boolean} set state on side panel
		 */
		opened: { default: false, manageable: true, schema: { type: 'boolean' } },
		/**
		 * Property backdrop sets the background opacity for the region outside the sidepanel
		 * @type {boolean} set the backdrop to enable background opacity for the region outside the sidepanel
		 */
		backdrop: { default: true, schema: { type: 'boolean' } },
		/**
		 * Property position to set the position of the panel
		 * @type {string} Position - Possible values - TOP,  LEFT, BOTTOM, RIGHT
		 */
		position: { default: POSITIONS.RIGHT, schema: { type: 'string' } },
		/**
		 * Property to set the styles for the panel container
		 * @type {object} Style for container element
		 */
		containerProps: { default: {}, schema: { type: 'object' } },
		/**
		 * Property sets the icon of the panel header.
		 * @type {boolean} Icon in the header of the panel
		 */
		canClose: { default: true, schema: { type: 'boolean' } },
		/**
		 * Property sets the title of the panel content if title is not sent as slot.
		 * @type {string} Title in the header of panel
		 */
		heading: { default: '', schema: { type: 'string' } },
		/**
		 * Property sets a callback function to handle before panel is closed.
		 * @type {function} beforeClose A callback function called when user tries to close the panel
		 */
		beforeClose: { default: null },
		/**
		 * If this property is true, header will be clickable.
		 * @type {Boolean} True if header should be clickable
		 */
		isTitleClickable: { default: false, schema: { type: 'boolean' } }
	},
	renderer: {
		type: snabbdom,
		view: SnCanvasSidepanel
	},
	styles: snCanvasSidepanelStyles,
	dispatches: {
		/**
		 * Dispatched when the side panel is opened/closed.
		 * @type {{opened: false}}
		 */
		'SN_CANVAS_SIDEPANEL#TOGGLE_OPENED': {},
		/**
		 * Dispatched when the panel title is clicked.
		 * @type {{}}
		 */
		'SN_CANVAS_SIDEPANEL#TITLE_CLICKED': {}
	},
	slots: {
		/**
		 * Sidepanel title appears in this named slot. A title can be clickable, and
		 * dispatches `SN_CANVAS_SIDEPANEL#TITLE_CLICKED` action when clicked.
		 */
		title: {},
		/**
		 * Content placed in this named slot that appears inside of the sidepanel. It takes care of enabling
		 * the vertical scrollbar for large content.
		 */
		content: {}
	},
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: {
			effect({ state, dispatch }) {
				const { opened } = state.properties;
				if (opened) {
					dispatch('SN_CANVAS_SIDEPANEL#TOGGLE_OPENED', { opened: true });
					// To avoid parent scrollbar while the panel is shown.
					//  panel may have vertical scrollbar for overflowing content
					document.body.style.cssText = 'height:100vh !important';
				} else {
					//reset back the height to enable back the parent scrollbar
					document.body.style.cssText = 'height:auto !important';
				}
			}
		}
	},
	behaviors: [rtlBehavior]
});
