import get from 'lodash/get';

/**
 * Looks for header logo by position identifier within sys_ux_theme assets.
 * If none specified, use default_src.
 * Currently this is used by canvas-header and footer, default_src represents
 * the logoSource propertyfor each of those components.
 *
 * @param {string} default_src
 * @param {string} position
 * @returns {string}
 */
export const getLogoSource = (default_scr = '', position = '') => {
	const image_obj = get(
		window,
		'ux_globals.theme.assets.images.' + position,
		{}
	);
	const image_uri = get(image_obj, 'uri', null);

	return image_uri || default_scr;
};
