import isNumber from 'lodash/isNumber';
import isFunction from 'lodash/isFunction';
/**
 * Get reference of the element that is being hoisted.
 *
 * @param {WebElement} host
 * @returns {WebElement}
 */
export const getHoistedContent = (host) => {
	return host.nowHoistAppendTo.querySelector('seismic-hoist').shadowRoot;
};

/**
 * Get the integer value of a style property on an element.
 *
 * @param {WebElement} host
 * @param {string} propertyName
 * @returns {number}
 */
export const getStyePropertyIntegerValue = (host, propertyName) =>
	parseInt(getComputedStyle(host).getPropertyValue(propertyName));

/**
 * Utility to truncate a string to a given maxWidth. Needed because text truncation
 * is not supported in now-button.
 *
 * @param {string} str
 * @param {number} maxWidth
 */
export const measureText = (str, maxWidth, font) => {
	if (!isNumber(maxWidth) || !str) {
		return {};
	}
	const canvasEl = document.createElement('canvas');
	const ctx = canvasEl.getContext('2d');
	const ellipsis = '...';
	const ellipsisWidth = ctx.measureText(ellipsis).width;
	ctx.font = font;
	let strWidth = ctx.measureText(str).width;
	// No truncation needed. Truncation and str is the same.
	if (strWidth <= maxWidth) {
		return {
			isTruncated: false,
			label: str,
			str
		};
	}

	let strBuilder = str;
	let strLength = str.length;

	while (strWidth + ellipsisWidth > maxWidth) {
		strBuilder = str.substring(0, strLength);
		strWidth = ctx.measureText(strBuilder).width;
		strLength--;
	}

	return {
		isTruncated: true,
		label: strBuilder + ellipsis,
		str
	};
};

const isVisible = (element) => {
	// Check inline style first to save a re-flow. If looks good, check also
	// computed style.
	let style = element.style;
	if (style.visibility !== 'hidden' && style.display !== 'none') {
		style = window.getComputedStyle(element);
		return style.visibility !== 'hidden' && style.display !== 'none';
	}
	return false;
};

export const deferUntilVisible = (
	el,
	callback = () => {},
	time = 500,
	tick = 10
) => {
	let elapsedTime = 0;
	if (!el || !isFunction(callback)) {
		return;
	}
	let interval = setInterval(() => {
		if (elapsedTime >= time) {
			clearInterval(interval);
		}
		if (isVisible(el)) {
			callback();
			clearInterval(interval);
		}
		elapsedTime + tick;
	}, tick);
};
