import { get, isNil } from 'lodash';
import { t } from 'sn-translate';
import {
	UI_NOTIFICATION_BEHAVIOR,
	PAGE_SIZE,
	UPDATE_NOTIFICATIONS,
	QUERY_NOTIFICATIONS,
	ALERT_STATUS_READ,
	ALERT_STATUS_DELETED,
	ALERT_BADGE_RESET_TIMESTAMP
} from '../constants';
import { daysBetween, parseSystemTime } from './timeAgo';

import { buildAlertItem } from './buildAlertItem';

export const UI_NOTIFICATION_BEHAVIOR_STATE_PATH = `behaviors[${UI_NOTIFICATION_BEHAVIOR}]`;

const TODAY_QUEUE = [{ item_type: 'group', name: 'today', label: t('Today') }];
const YESTERDAY_QUEUE = [
	{ item_type: 'group', name: 'yesterday', label: t('Yesterday') }
];
const OLDER_QUEUE = [{ item_type: 'group', name: 'older', label: t('Older') }];

function getBasicCondition(userId, route) {
	return `recipient=${userId}^route=${route}`;
}

function decodePayload(alert, timeZoneOffset) {
	let payload = null;
	try {
		const { displayValue = '', tableLabel = '', changes = {} } = JSON.parse(
			alert.payload.value
		);

		const raw_time_stamp = alert.sys_created_on.value;

		const baseProps = {
			table: alert.target_table.value,
			sys_id: alert.target.value,
			record_display_value: displayValue,
			raw_time_stamp,
			time_stamp: parseSystemTime(raw_time_stamp, timeZoneOffset),
			alert_sys_id: alert.sys_id.value,
			updated_by: getTriggeredBy(alert.triggered_by),
			is_read: alert.status.value === ALERT_STATUS_READ
		};

		payload = { baseProps, tableLabel, changes };
	} catch (e) {
		// eslint-disable-next-line no-console
		console.error('invalid notification payload');
		payload = null;
	}
	return payload;
}

function buildAlertPayload(baseProps, tableLabel, changes) {
	const { assigned_to = {}, priority = {}, short_description = {} } = changes;
	const fields = Object.keys(changes).reduce((result, key) => {
		const change = changes[key];

		if (change) {
			const { value, previousValue } = changes[key];
			if (value !== previousValue) {
				const { displayValue, fieldLabel } = changes[key];
				result = result.concat({
					field_name: key,
					field_label: fieldLabel,
					field_value: value,
					field_displayValue: displayValue
				});
			}
		}
		return result;
	}, []);

	return {
		...baseProps,
		table_label: tableLabel,
		short_description:
			(short_description && short_description.displayValue) || '',
		fields,
		priority: (priority && priority.displayValue) || '',
		assigned_to: assigned_to || ''
	};
}

function transformGraphQLAlert(alert, timeZoneOffset) {
	const payload = decodePayload(alert, timeZoneOffset);
	if (!payload) return null;

	const { baseProps, tableLabel, changes } = payload;
	return buildAlertPayload(baseProps, tableLabel, changes);
}

function getTriggeredBy(triggered_by) {
	const { displayValue, display_value, value } = triggered_by; // graphql and amb format is different
	if (!displayValue && !display_value && value)
		return value[0].toUpperCase() + value.slice(1);
	else return displayValue || display_value;
}

export function getUINotificationBehaviorState(state) {
	return get(state, UI_NOTIFICATION_BEHAVIOR_STATE_PATH, {});
}

export function addDateGroupings(alertQueue) {
	const baseDate = new Date(Date.now());

	let todayQueue = [...TODAY_QUEUE],
		yesterdayQueue = [...YESTERDAY_QUEUE],
		olderQueue = [...OLDER_QUEUE];

	alertQueue.forEach((item) => {
		if (item.item_type === 'notification') {
			const { time_stamp } = item;
			const _date = new Date(time_stamp);
			const daysDiff = daysBetween(baseDate, _date);
			if (daysDiff === 0)
				todayQueue = todayQueue.concat({ ...item, group: 'today' });
			else if (daysDiff === 1)
				yesterdayQueue = yesterdayQueue.concat({ ...item, group: 'yesterday' });
			else olderQueue = olderQueue.concat({ ...item, group: 'older' });
		}
	});

	return todayQueue.concat(yesterdayQueue).concat(olderQueue);
}

export function storeBadgeCount(userId) {
	if (window.localStorage) {
		try {
			let reset = window.localStorage.getItem(ALERT_BADGE_RESET_TIMESTAMP);
			reset = JSON.parse(reset) || {};
			reset = { ...reset, [userId]: Date.now() };
			let reset_str = JSON.stringify(reset);
			window.localStorage.setItem(ALERT_BADGE_RESET_TIMESTAMP, reset_str);
		} catch (e) {
			// eslint-disable-next-line no-console
			console.warn(e);
		}
	}
}

// Take record watcher format and transform to client side format.
export function getTransformedAlert(alert, timeZoneOffset) {
	const payload = decodePayload(alert, timeZoneOffset);
	if (!payload) return null;

	const { baseProps, tableLabel, changes } = payload;
	const updatePayload = { ...baseProps, status: 'liveUpdated' };

	return {
		alertPayload: buildAlertPayload(baseProps, tableLabel, changes),
		updatePayload
	};
}

export function purgeOldAlerts(alertQueue, alertItem, trayState) {
	//item arrived
	const record_sys_id = alertItem.sys_id;

	let alertsToKeep = alertQueue.filter(
			(it) => it.item_type === 'notification' && it.sys_id !== record_sys_id
		),
		alertsToDelete = trayState.deleteQueue.slice(); //[alertPayload]

	const existingAlert = alertQueue.find((it) => it.sys_id === record_sys_id);
	if (existingAlert) alertsToDelete.push(existingAlert.alert_sys_id);

	alertsToKeep.unshift(alertItem);

	return {
		alertsToKeep,
		alertsToDelete
	};
}

export function purgeOldAlerts2(
	alertQueue,
	rawAlerts,
	trayState,
	userId,
	timeZoneOffset,
	target = 'target.value'
) {
	// initial loading/scrolling
	let alertsToKeep = alertQueue.filter((it) => it.item_type === 'notification'),
		alertsToDelete = trayState.deleteQueue.slice(); // [alert_sys_ids_to_delete]
	let set = new Set(alertQueue.map((al) => al.sys_id));
	const deleteTarget =
		target === 'target.value' ? 'sys_id.value' : 'alert_sys_id'; // initial or item arrived

	rawAlerts.forEach((alert) => {
		const record_sys_id = get(alert, target, '');
		if (!set.has(record_sys_id)) {
			set.add(record_sys_id);
			let payload = transformGraphQLAlert(alert, timeZoneOffset);
			if (payload) {
				let item = buildAlertItem(payload, userId);
				alertsToKeep.push(item);
			}
		} else {
			const alert_sys_id = get(alert, deleteTarget, '');
			alertsToDelete.push(alert_sys_id);
		}
	});

	return {
		alertsToKeep,
		alertsToDelete
	};
}

export function buildAlertSubscriptionFilter(userId, route) {
	const filter = `${getBasicCondition(userId, route)}^status=new`;
	return btoa(filter).replace(/=/g, '-');
}

export function askToQueryAlerts(
	offset,
	totalRowCount,
	userId,
	route,
	dispatch,
	initial = false
) {
	if (initial || offset < totalRowCount) {
		const condition = `${getBasicCondition(
			userId,
			route
		)}^sys_created_on>=javascript:gs.beginningOfLast7Days()^status!=deleted^ORDERBYDESCsys_created_on`;
		dispatch(QUERY_NOTIFICATIONS, { condition, limit: PAGE_SIZE, offset });
	}
}

export function askToUpdateNotifications(
	dispatch,
	sys_ids,
	status,
	deleteQueue = []
) {
	let _buildUpdateItems = (items, queue, theStatus) => {
		const newItems = queue.map((sys_id) => ({ sys_id, status: theStatus }));
		return items.concat(newItems);
	};

	let items = [];
	items = _buildUpdateItems(items, sys_ids, status);
	items = _buildUpdateItems(items, deleteQueue, ALERT_STATUS_DELETED); //clear delete queue on each update

	if (items.length > 0) dispatch(UPDATE_NOTIFICATIONS, { items });
}

export function buildBadgeQueryCondition(currentUserId, route) {
	let count_time_str = 'gs.beginningOfLast7Days()';

	if (window.localStorage) {
		try {
			let reset = window.localStorage.getItem(ALERT_BADGE_RESET_TIMESTAMP);
			reset = JSON.parse(reset);
			if (reset && reset[currentUserId]) {
				const reset_timestamp = reset[currentUserId];
				const reset_date_obj = new Date(reset_timestamp);
				const dd = reset_date_obj.getDate(),
					mm = reset_date_obj.getMonth() + 1,
					yyyy = reset_date_obj.getFullYear(),
					hh = reset_date_obj.getHours(),
					min = reset_date_obj.getMinutes(),
					sec = reset_date_obj.getSeconds();
				const reset_date = `${yyyy}-${mm}-${dd}`,
					reset_time = `${hh}:${min}:${sec}`;
				count_time_str = `gs.dateGenerate('${reset_date}','${reset_time}')`;
			}
		} catch (e) {
			// eslint-disable-next-line no-console
			console.warn(e);
		}
	}

	return `${getBasicCondition(
		currentUserId,
		route
	)}^status=new^sys_updated_on>javascript:${count_time_str}`;
}

export function updateNotificationProperty(
	updateProperties,
	properties,
	payload
) {
	const { notification } = properties;
	const newNotificationProperty = {
		...notification,
		...payload
	};

	updateProperties({
		notification: newNotificationProperty
	});
}

export function isNotificationTrayLoading(offset, totalRowCount, deleteQueue) {
	if (offset && totalRowCount && deleteQueue)
		return offset < totalRowCount - deleteQueue.length;
	return false;
}

export function getPreferenceValue(preferences, value) {
	const pref = preferences.find((pref) => get(pref, 'name', '') === value);
	return get(pref, 'value', null);
}

export function shouldShowToast(preferences) {
	const prefValue = getPreferenceValue(
		preferences,
		'workspace.notifications.showPopups'
	);
	const showToast = isNil(prefValue)
		? true
		: prefValue.toString().toLowerCase() === 'true';
	return showToast;
}
