import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-popover';
import focusBehavior, { getFocusableNodes } from '@servicenow/behavior-focus';
import '../sn-canvas-mega-menu';
import '../../sn-nav-item';
import { getHoistedContent } from '../sn-canvas-mega-menu/utils/dom';
import {
	NOW_POPOVER_OPENED_SET,
	ARROW_DOWN_KEY,
	ESCAPE_KEY,
	ENTER_KEY,
	SPACE_KEY
} from '../../../constants/constants';

const snCanvasMenuDropdown = (state) => {
	const { properties, contentVisible } = state;
	const {
		depth,
		label,
		opened,
		positionTarget,
		selectedItem,
		menuItems,
		menuWidth,
		verticalStackingEnabled,
		isSelected
	} = properties;

	const menuSectionID = `menu_content_${menuItems.root.linkID}`;
	const endsWithIcon =
		contentVisible === true ? 'chevron-up-outline' : 'chevron-down-outline';
	return (
		<now-popover
			hide-tail
			manage-opened
			interaction-type="none"
			opened={opened}
			hook-insert={({ elm }) => (elm.positionTarget = positionTarget.current)}
			positions={[
				{ target: 'bottom-start', content: 'top-start' },
				{ target: 'bottom-end', content: 'top-end' }
			]}>
			<sn-nav-item
				label={label}
				tooltipContent={label}
				interactionType="menubar"
				type="action"
				slot="trigger"
				variant="main-nav"
				isSelected={isSelected || opened}
				configAria={{
					...(isSelected && { 'aria-current': 'page' }),
					'aria-expanded': opened,
					'aria-haspopup': 'true',
					'aria-controls': menuSectionID
				}}
				lines={1}>
				<now-icon size="md" slot="endsWith" icon={endsWithIcon} />
			</sn-nav-item>
			<sn-canvas-mega-menu
				depth={depth}
				menu-items={menuItems}
				selected-item={selectedItem}
				opened={contentVisible}
				position-target={positionTarget}
				slot="content"
				vertical-stacking-enabled={verticalStackingEnabled}
				width={menuWidth}
				id={menuSectionID}
			/>
		</now-popover>
	);
};

createCustomElement('sn-canvas-menu-dropdown', {
	initialState: {
		contentVisible: false
	},
	properties: {
		depth: {
			default: 1
		},
		label: {
			default: ''
		},
		menuItems: {
			default: {}
		},
		menuWidth: {
			default: 1600
		},
		opened: {
			default: false
		},
		positionTarget: {
			default: {}
		},
		isSelected: {
			default: false
		},
		selectedItem: {
			default: {}
		},
		verticalStackingEnabled: {
			default: false
		}
	},
	actionHandlers: {
		[NOW_POPOVER_OPENED_SET]: ({ action, updateProperties }) => {
			const { payload } = action;
			updateProperties({ opened: payload.value });
		},
		['NOW_POPOVER#CONTENT_VISIBLE']: ({ host, updateState }) => {
			const megamenu = host.shadowRoot.querySelector('sn-canvas-mega-menu');
			const hoistedMegamenu = getHoistedContent(megamenu);
			const focusableNodes = getFocusableNodes(
				hoistedMegamenu.querySelector('.sn-canvas-mega-menu')
			);
			if (focusableNodes.firstFocusableElement) {
				focusableNodes.firstFocusableElement.focus();
			}
			updateState({ contentVisible: true });
		},
		['NOW_POPOVER#CONTENT_HIDDEN']: ({ updateState }) => {
			updateState({ contentVisible: false });
		}
	},
	eventHandlers: [
		{
			capture: false,
			events: ['keydown'],
			effect({
				updateProperties,
				action: {
					payload: { event }
				},
				properties
			}) {
				const { key } = event;
				const { opened } = properties;
				switch (key) {
					case ENTER_KEY:
					case SPACE_KEY:
					case ARROW_DOWN_KEY:
						// Need to prevent default because on ENTER_KEY, SPACE_KEY triggers a click event
						// on the window. `now-popover` has code to close popover panel on click event.
						// However we still want the default action to trigger for items within `sn-canvas-mega-menu`
						// Which is why we only prevent default on elements within `sn-canvas-menu-dropdown`.
						if (event.srcElement.tagName === 'SN-CANVAS-MENU-DROPDOWN')
							event.preventDefault();

						if (!opened) updateProperties({ opened: true });
						break;
					case ESCAPE_KEY:
						updateProperties({ opened: false });
						break;
				}
			}
		}
	],
	behaviors: [focusBehavior],
	renderer: {
		type: snabbdom,
		view: snCanvasMenuDropdown
	}
});
